import React, {Component} from 'react'
import {connect} from 'react-redux'

import Button from "@material-ui/core/Button";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCamera} from "@fortawesome/free-solid-svg-icons";

import {User} from "../../../models";
import {updateUserProfileImage} from "../../../actions/users";
import {playClickSound, playHoverSound} from "../../layout/sounds";
import CircularProgress from "@material-ui/core/CircularProgress";
import PropTypes from "prop-types";


class UploadProfileImageButton extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
        /** Classnames for the loader */
        loader_classnames: PropTypes.object.isRequired,
    };

    constructor() {
        super(...arguments);
        this.onUpdateProfile = this.onUpdateProfile.bind(this);
        this.state = {};
    }

    onUpdateProfile(e) {
        this.props.updateUserProfileImage(e.target.files[0], this.props.user[User.id]);
    }

    render() {
        return (
            <div className="upload-img-btn-container">
                {this.props.uploading_file ? <CircularProgress className={this.props.loader_classnames}/> : <>
                    <input
                        accept="image/*"
                        style={{display: 'none'}}
                        id="upload-profile-img"
                        multiple={false}
                        type="file"
                        onChange={this.onUpdateProfile}
                    />
                    <label htmlFor="upload-profile-img" className="upload-img-btn">
                        <Button variant="outlined" color="primary" component="span"
                                onMouseEnter={playHoverSound} onClick={playClickSound}>
                            <FontAwesomeIcon icon={faCamera}/>
                        </Button>
                    </label>
                </>}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    uploading_file: state.users.uploading_file,
});

export default connect(mapStateToProps, {updateUserProfileImage})(UploadProfileImageButton)
