import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Grid from '@material-ui/core/Grid';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Grow from '@material-ui/core/Grow';

import {ApiResult} from "../../models";
import {filterTitles, getFiltersFromBranch, getItemsPerPageFromBranch, getTabName} from "./functions";
import CyberNavbar from "../layout/navbar/CyberNavbar";
import {playClickSound, playExpand3Sound} from "../layout/sounds";
import {getBrowseGridListCols, isEmpty} from "../functions";
import TitleCard from "../layout/cards/TitleCard";
import Pagination from "@material-ui/lab/Pagination";
import Loading from "../layout/common/Loading";
import GoToPageDialog from "../layout/pagination/goToPageDialog";
import CyberFooter from "../layout/footer/CyberFooter";

import '../../assets/css/browse.css'


class Browse extends Component {
    static propTypes = {
        /** Branch of component. (Anime, Manga, Eroge..) */
        branch: PropTypes.string.isRequired,
        /** Redux prop of titles_loading */
        titles_loading: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        filtered_titles_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        filtered_titles: PropTypes.object.isRequired,
        /** Redux prop of user_entry_statuses */
        user_entry_statuses: PropTypes.object,
        /** Redux prop of title_filters */
        title_filters: PropTypes.object,
        /** Selected season */
        season: PropTypes.object,
    };

    constructor() {
        super(...arguments);
        this.updateGridListCols = this.updateGridListCols.bind(this);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            items_per_page: getItemsPerPageFromBranch(this.props.branch),
            page_dialog_open: false,
            gridlist_cols: getBrowseGridListCols(),
        };
    }

    componentDidMount() {
        document.title = getTabName(this.props.branch);
        window.scrollTo(0, 0);
        window.addEventListener('resize', this.updateGridListCols);
        filterTitles(this.props.branch, 1, this.props.title_filters, this.props.season);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.titles_loading && !this.props.titles_loading) {
            window.scrollTo(0, 0);
            playExpand3Sound();
        }

        this.updateCurrentPage(prevState);

        if (this.props.season && prevProps.season.season !== this.props.season.season)
            filterTitles(this.props.branch, 1, this.props.title_filters, this.props.season)
    }

    updateCurrentPage(prevState) {
        let page = this.state.current_page;
        try {
            page = this.props.filtered_titles[ApiResult.next].split('page=');
            page = parseInt(page[page.length - 1]) - 1;
        } catch (e) {
            page = Math.ceil(this.props.filtered_titles[ApiResult.count] / this.state.items_per_page);
        }

        if (page && prevState.current_page !== page) {
            this.setState({current_page: page})
        }
    }

    updateGridListCols() {
        if (this.state.gridlist_cols !== getBrowseGridListCols())
            this.setState({gridlist_cols: getBrowseGridListCols()})
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.filtered_titles[ApiResult.count] / this.state.items_per_page)) {
            window.scrollTo(0, 0);
            this.setState({current_page: page});
            filterTitles(this.props.branch, page, this.props.title_filters, this.props.season);
        }
    }

    render() {
        return (
            <>
                <CyberNavbar/>
                <div id="Browse-Titles">
                    <Grid container>
                        <Grid item xs={12}>
                            {getFiltersFromBranch(this.props.branch, this.props.season)}
                            {this.props.filtered_titles_loaded ? <>
                                <GridList cellHeight={400} cols={this.state.gridlist_cols}
                                          style={{margin: '0 0 10px 0'}}>
                                    {this.props.filtered_titles[ApiResult.results].map((title, i) => (
                                        <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                                            <GridListTile cols={1}>
                                                <TitleCard
                                                    branch={this.props.branch}
                                                    title={title}
                                                    user_buttons={true}
                                                    index={i + ((this.state.current_page * this.state.items_per_page) - (this.state.items_per_page - 1))}
                                                    user_status={!isEmpty(this.props.user_entry_statuses) ?
                                                        this.props.user_entry_statuses[`${title['id']}`] : null}
                                                />
                                            </GridListTile>
                                        </Grow>
                                    ))}
                                </GridList>
                                <div className="pagination">
                                    <Pagination
                                        count={Math.ceil(this.props.filtered_titles[ApiResult.count] / this.state.items_per_page)}
                                        color="primary"
                                        siblingCount={3}
                                        page={this.state.current_page}
                                        onChange={(type, page) => {
                                            playClickSound();
                                            if (page !== this.state.current_page)
                                                this.goToPage(page);
                                            else
                                                this.setState({page_dialog_open: true})
                                        }}
                                    />
                                    <GoToPageDialog
                                        open={this.state.page_dialog_open}
                                        current_page={this.state.current_page}
                                        total_pages={Math.ceil(this.props.filtered_titles[ApiResult.count] / this.state.items_per_page)}
                                        goToPage={this.goToPage}
                                        onClose={this.closePageDialog}/>
                                </div>
                            </> : this.props.titles_loading ?
                                <Loading text={`${this.props.branch} Loading`} offset={200}/> : null}
                        </Grid>
                    </Grid>
                </div>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {})(Browse)
