import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';

import {ANIME_TAB_NAME, BRANCH_ANIME} from "../../../globals";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import AnimeStatistics from "./AnimeStatistics";
import CyberFooter from "../../layout/footer/CyberFooter";
import AnimeCurrentSeason from "../seasons/AnimeCurrentSeason";
import {getAnimeData, getAnimeDataForHomePage} from "../../../actions/anime";
import {Anime, AnimeData, ApiResult} from "../../../models";
import TitleCard from "../../layout/cards/TitleCard";
import {isEmpty} from "../../functions";
import Loading from "../../layout/common/Loading";
import {getLoggedInUserAnimeEntryStatuses} from "../../../actions/users";

import '../../../assets/css/anime.css'
import '../../../assets/css/seasons.css'
import TopAnime from "./TopAnime";


class AnimeHome extends Component {
    componentDidMount() {
        document.title = ANIME_TAB_NAME;
        window.scrollTo(0, 0);
        this.props.getAnimeDataForHomePage();
        this.props.getLoggedInUserAnimeEntryStatuses();
    }

    render() {
        return (
            <div id="Anime-Home" className="title-home-page">
                <CyberNavbar/>
                <Grid container>
                    <TopAnime/>
                    <Grid xs={12}>
                        <AnimeStatistics/>
                    </Grid>
                </Grid>
                <CyberFooter/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {getAnimeDataForHomePage, getLoggedInUserAnimeEntryStatuses})(AnimeHome)
