import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Link} from "react-router-dom";

import {Parallax} from 'react-parallax';

import CyberNavbar from "../layout/navbar/CyberNavbar";
import {AuthIndexHero, NonAuthIndexHero} from "./snippets"

import '../../assets/css/home.css';
import {Grid} from "@material-ui/core";
import {ADMIN, HOME, REQUIEM} from "../../urls";
import CyberFooter from "../layout/footer/CyberFooter";
import {LoadGifs} from "../layout/common/common";


class Index extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            img_1_loaded: false,
            img_2_loaded: false,
        };
    }

    componentDidMount() {
        document.title = 'Cyber-Space';
        window.scrollTo(0, 0);
    }

    // noinspection FunctionWithMoreThanThreeNegationsJS
    render() {
        // noinspection HtmlUnknownTarget
        return (
            <div id="Index">
                {this.props.user_authenticated ?
                    <>
                        <CyberNavbar/>
                        <NonAuthIndexHero/>
                        <Grid container>
                            <Grid item xs={12} className="project-about">
                                <div className="project-about-wrapper">
                                    <Parallax
                                        blur={0}
                                        bgImage={'images/neo-tokyo-2-lq.jpg'}
                                        bgImageAlt="the cat"
                                        strength={300}
                                        className={this.state.img_1_loaded ? 'fade-in' : ''}
                                    >
                                        <img src={'images/neo-tokyo-2-lq.jpg'} className="lazy-loaded-img"
                                             alt="loader-img"
                                             onLoad={() => {
                                                 this.setState({img_1_loaded: true})
                                             }}/>
                                        <div className="img-content-container">
                                            <img src="images/cyber-genesis-logo.png" alt={'cg-logo'}/>
                                            <div className="about-container">
                                                <span className="color-blue">project:</span>&nbsp;
                                                <h1>Cyber-Genesis</h1>
                                                <p><Link to={HOME}><i>Cyber-Genesis</i></Link> is a platform for Asian
                                                    culture mainly <i>Japanese</i> animated content.</p>
                                            </div>
                                        </div>
                                    </Parallax>
                                    <Parallax
                                        blur={0}
                                        bgImage={'images/neo-tokyo-3-lq.jpg'}
                                        bgImageAlt="the cat"
                                        strength={300}
                                        className={this.state.img_2_loaded ? 'fade-in' : ''}
                                    >
                                        <img src={'images/neo-tokyo-3-lq.jpg'} className="lazy-loaded-img"
                                             alt="loader-img"
                                             onLoad={() => {
                                                 this.setState({img_2_loaded: true})
                                             }}/>
                                        <div className="img-content-container text-right">
                                            <img className="cyber-req-logo-top"
                                                 src="images/cyber-requiem-logo-magenta.png"
                                                 alt={'cg-logo'}/>
                                            <div className="about-container">
                                                <span>project:</span>&nbsp;
                                                <h1>Cyber-Requiem</h1>
                                                <p><Link to={REQUIEM}><i>
                                                    Cyber-Requiem</i></Link> is a system designed to keep <i>
                                                    Cyber-Genesis</i> up to date by crawling the web for relative
                                                    information and downloading multimedia.</p>
                                            </div>
                                            <img className="cyber-req-logo-bottom"
                                                 src="images/cyber-requiem-logo-magenta.png"
                                                 alt={'cg-logo'}/>
                                        </div>
                                    </Parallax>
                                    <Parallax
                                        blur={0}
                                        bgImage={'images/neo-tokyo-1.jpg'}
                                        bgImageAlt="the cat"
                                        strength={300}
                                        className={'fade-in'}
                                    >
                                        <div className="img-content-container">
                                            <img
                                                src="images/cyber-administration-logo-cyan.png"
                                                alt={'ca-logo'}/>
                                            <div className="about-container margin-left-20">
                                                <span>project:</span>&nbsp;
                                                <h1>Cyber-Administration</h1>
                                                <p><a
                                                    href={ADMIN}><i>Cyber-Administration</i></a> is a software
                                                    for user interaction
                                                    with <i>Cyber-Genesis</i> and <i>Cyber-Requiem</i>.
                                                </p>
                                            </div>
                                        </div>
                                    </Parallax>
                                </div>
                            </Grid>
                        </Grid>
                        <CyberFooter margin={false}/>
                    </> :
                    <AuthIndexHero/>}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated,
    user_loading: state.auth.user_loading,
});

export default connect(mapStateToProps, {})(Index)
