import React from 'react';
import {Link} from "react-router-dom";
import {
    ANIME_BROWSE,
    ANIME_FRANCHISES,
    ANIME_HOME,
    ANIME_SEASONS,
    CHARACTERS_BROWSE,
    CHARACTERS_HOME,
    EROGE_BROWSE,
    EROGE_HOME,
    LOGIN,
    MANGA_BROWSE,
    MANGA_FRANCHISES,
    MANGA_HOME,
    MANGA_SEASONS,
    PEOPLE_BROWSE,
    SETTINGS,
    USER_ANIME_LIST,
    USER_ANIME_STATISTICS,
    USER_EROGE_LIST,
    USER_MANGA_LIST
} from "../../../urls";
import {playClickSound, playHoverSound} from "../sounds";
import store from "../../../store";
import {logout} from "../../../actions/auth";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    faCalendarAlt,
    faChartLine,
    faCog,
    faHome,
    faSearch, faSignOutAlt,
    faSitemap,
    faThList
} from "@fortawesome/free-solid-svg-icons";


export default function AnimeMenu(props) {
    const {show, onLeave} = props;

    return (
        <div className={show ? 'desktop-menu show-dropdown' : 'desktop-menu'}
             style={{pointerEvents: show ? 'initial' : 'none'}}
             onMouseLeave={onLeave}>
            <Link to={ANIME_HOME} className="menu-link" onMouseEnter={playHoverSound} onClick={playClickSound}><FontAwesomeIcon className="navbar-icon" icon={faHome}/> Home</Link>
            <Link to={ANIME_BROWSE} className="menu-link" onMouseEnter={playHoverSound} onClick={playClickSound}><FontAwesomeIcon className="navbar-icon" icon={faSearch}/> Browse</Link>
            <Link to={ANIME_SEASONS} className="menu-link" onMouseEnter={playHoverSound} onClick={playClickSound}><FontAwesomeIcon className="navbar-icon" icon={faCalendarAlt}/> Seasons</Link>
            <Link to={ANIME_FRANCHISES} className="menu-link" onMouseEnter={playHoverSound} onClick={playClickSound}><FontAwesomeIcon className="navbar-icon" icon={faSitemap}/> Franchises</Link>
        </div>
    );
}

