import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import {playClickSound, playHoverSound} from "../../../layout/sounds";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBookOpen, faGamepad, faTrophy, faTv, faUser} from "@fortawesome/free-solid-svg-icons";
import AboutTab from "./AboutTab";
import AnimeTab from "./AnimeTab";
import MangaTab from "./MangaTab";
import AchievementsTab from "./AchievementsTab";


function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Tabs for the visited user
 * @param props.visited_user {Object} Visited user object
 * @returns {JSX.Element}
 * @constructor
 */
export function ProfileTabs(props) {
    const {visited_user} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root + ' tabs-root profile-tabs'}>
            <AppBar position="static" className="app-bar">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    textColor="default"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    <Tab label={<p><FontAwesomeIcon className="list-icon" icon={faUser}/> About</p>} {...a11yProps(0)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p><FontAwesomeIcon className="list-icon" icon={faTv}/> Anime</p>} {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p><FontAwesomeIcon className="list-icon" icon={faBookOpen}/> Manga</p>} {...a11yProps(2)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab disabled={true} label={<p><FontAwesomeIcon className="list-icon" icon={faGamepad}/> Eroge</p>} {...a11yProps(3)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    {/*<Tab disabled={true} label={<p><FontAwesomeIcon className="list-icon" icon={faChartLine}/> Statistics</p>} {...a11yProps(4)} onClick={playClickSound} onMouseEnter={playHoverSound}/>*/}
                    <Tab label={<p><FontAwesomeIcon className="list-icon" icon={faTrophy}/> Achievements</p>} {...a11yProps(4)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel className="about-tab" value={value} index={0} dir={theme.direction}>
                    <AboutTab/>
                </TabPanel>
                <TabPanel className="anime-tab box-light" value={value} index={1} dir={theme.direction}>
                    <AnimeTab/>
                </TabPanel>
                <TabPanel className="manga-tab box-light" value={value} index={2} dir={theme.direction}>
                    <MangaTab/>
                </TabPanel>
                <TabPanel className="eroge-tab box-light" value={value} index={3} dir={theme.direction}>
                    EROGE
                </TabPanel>
                {/*<TabPanel value={value} index={4} dir={theme.direction}>*/}
                {/*    STATISTICS*/}
                {/*</TabPanel>*/}
                <TabPanel value={value} index={4} dir={theme.direction}>
                    <AchievementsTab/>
                </TabPanel>
            </SwipeableViews>
        </div>
    );
}