import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import {playClickSound, playHoverSound} from "../../../layout/sounds";
import Grid from "@material-ui/core/Grid";
import {Anime, AnimeData} from "../../../../models";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_ANIME} from "../../../../globals";
import {isEmpty} from "../../../functions";
import Loading from "../../../layout/common/Loading";



function RecentlyAdded(props) {
    const {anime_data, anime_loading, anime_data_loaded, user_anime_entry_statuses} = props;
    return <Grid xs={12} className="horizontal-titles">
        {/*<div className="horizontal-titles-header">*/}
        {/*    <h3>Recently Added Anime</h3>*/}
        {/*</div>*/}
        <div className="horizontal-titles-container horizontal-scroll">
            {anime_data_loaded && anime_data && anime_data[AnimeData.recently_added_anime] ?
                <div className="horizontal-titles-wrapper">
                    {console.log('renders tab rendering: ', anime_data[AnimeData.recently_added_anime])}

                    {anime_data[AnimeData.recently_added_anime].map(anime => (
                        <TitleCard
                            containerClassNames="horizontal-titles-card-container"
                            branch={BRANCH_ANIME}
                            title={anime}
                            user_status={
                                !isEmpty(user_anime_entry_statuses) ?
                                    user_anime_entry_statuses[anime[Anime.id].toString()] : null}
                            skip_img_preload={true}
                        />
                    ))}
                </div> : anime_loading ? <Loading text={'Anime Loading'} block={true}/> : null}
        </div>
    </Grid>;
}

