import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'

import Moment from 'react-moment'
import moment from 'moment'
import {Grid} from "@material-ui/core";

import {getVisitedUserAnimeHistory, getVisitedUserMangaHistory} from '../../../actions/users'
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";

import '../../../assets/css/users.css'
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../globals";
import TitleCard from "../../layout/cards/TitleCard";
import Loading from "../../layout/common/Loading";
import Chip from "@material-ui/core/Chip";
import Pagination from "@material-ui/lab/Pagination";
import {ApiResult, User} from "../../../models";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";
import {filterTitles, getTabName} from "../../browse/functions";
import {getBrowseGridListCols} from "../../functions";
import {getUser} from "../../../actions/auth";


function displayHistory(user_history, branch) {
    let lastId = 0;
    let newId = 0;
    let lastMonth = 0;
    let lastDate = 0;
    let lastYear = 0;
    let formatted = [];
    let title;
    let item;

    try {
        for (let x = 0; x < user_history['results'].length; x++) {

            let obj = {};
            let nextYear = moment(user_history['results'][x]['timestamp']).date();
            let nextMonth = moment(user_history['results'][x]['timestamp']).month() + 1;
            let nextDate = moment(user_history['results'][x]['timestamp']).date();

            if (nextYear != lastYear || nextMonth != lastMonth || nextDate != lastDate) {
                lastYear = moment(user_history['results'][x]['timestamp']).date();
                lastMonth = moment(user_history['results'][x]['timestamp']).month() + 1;
                lastDate = moment(user_history['results'][x]['timestamp']).date();
                if (branch === BRANCH_ANIME) {
                    lastId = user_history['results'][x]['anime_list_entry']['anime']['id'];
                    title = user_history['results'][x]['anime_list_entry']['anime'];
                    item = user_history['results'][x]['episode'];
                }
                else if (branch === BRANCH_MANGA) {
                    lastId = user_history['results'][x]['manga_list_entry']['manga']['id'];
                    title = user_history['results'][x]['manga_list_entry']['manga'];
                    item = user_history['results'][x]['chapter'];
                }
                obj = {
                    date: user_history['results'][x]['timestamp'],
                    entries: [
                        {
                            title: title,
                            history: [{
                                item: item,
                                timestamp: user_history['results'][x]['timestamp']
                            }]
                        }
                    ]
                };
                formatted.push(obj)
            } else {
                if (branch === BRANCH_ANIME) {
                    newId = user_history['results'][x]['anime_list_entry']['anime']['id'];
                    title = user_history['results'][x]['anime_list_entry']['anime'];
                    item = user_history['results'][x]['episode'];
                }
                else if (branch === BRANCH_MANGA) {
                    newId = user_history['results'][x]['manga_list_entry']['manga']['id'];
                    title = user_history['results'][x]['manga_list_entry']['manga'];
                    item = user_history['results'][x]['chapter'];
                }

                if (newId != lastId) {
                    if (branch === BRANCH_ANIME)
                        lastId = user_history['results'][x]['anime_list_entry']['anime']['id'];
                    else if (branch === BRANCH_MANGA)
                        lastId = user_history['results'][x]['manga_list_entry']['manga']['id'];

                    formatted[formatted.length - 1]['entries'].push({
                        title: title,
                        history: [{
                            item: item,
                            timestamp: user_history['results'][x]['timestamp']
                        }]
                    })
                } else {
                    let last = formatted.length - 1;
                    let lastHistory = formatted[last]['entries'].length - 1;
                    formatted[last]['entries'][lastHistory]['history'].push({
                        item: item,
                        timestamp: user_history['results'][x]['timestamp']
                    })
                }
            }
        }
    } catch (e) {
        return null
    }

    // noinspection CheckTagEmptyBody
    return formatted.map((date, i) => (
        <li key={i} className="timeline-event">
            <label className="timeline-event-icon"></label>
            <div className="timeline-event-copy">
                <p className="timeline-event-thumbnail">
                    <Moment format="Do MMMM YYYY - dddd" date={date['date']}/></p>
                {date['entries'].map((entry, x) => (
                    <div key={x} className="box-light timeline-history-entry">
                        <div className="timeline-history-segment timeline-history-segment-card">
                            <TitleCard
                                branch={branch}
                                title={entry['title']}
                            />
                        </div>
                        <div className="timeline-history-segment timeline-history-segment-episodes">
                            {entry['history'].map((history, y) => (
                                <p key={y}>{branch === BRANCH_ANIME ? 'Episode ' :
                                    branch === BRANCH_MANGA ? 'Chapter ' : null}
                                    <Chip className="chip-info" size="small" label={history['item']}/> completed
                                    at <Chip className="chip-info" size="small" label={<Moment format="HH:mm:ss"
                                        date={history['timestamp']}/>}/>
                                </p>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </li>
    ))
}


class DisplayUserHistory extends Component {
    static propTypes = {
        /** Branch of component. (Anime, Manga, Eroge..) */
        branch: PropTypes.string.isRequired,
        /** Branch of component. (Anime, Manga, Eroge..) */
        visited_user_id: PropTypes.string.isRequired,
    };

    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        document.title = `${this.props.visited_user[User.username]} History`;
        window.scrollTo(0, 0);

        if (this.props.branch === BRANCH_ANIME) this.props.getVisitedUserAnimeHistory(this.props.visited_user_id);
        else if (this.props.branch === BRANCH_MANGA) this.props.getVisitedUserMangaHistory(this.props.visited_user_id)
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.current_page !== this.state.current_page)
            window.scrollTo(0, 0);
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        let total;
        if (this.props.branch === BRANCH_ANIME)
            total = Math.ceil(this.props.user_anime_history[ApiResult.count] / 100);
        else if (this.props.branch === BRANCH_MANGA)
            total = Math.ceil(this.props.user_manga_history[ApiResult.count] / 100);

        if (page > 0 && page <= total) {
            this.setState({current_page: page});
            if (this.props.branch === BRANCH_ANIME)
                this.props.getVisitedUserAnimeHistory(this.props.match.params.id, page);
            else if (this.props.branch === BRANCH_MANGA)
                this.props.getVisitedUserMangaHistory(this.props.match.params.id, page)
        }
    }

    render() {
        let history;

        try {
            if (this.props.branch === BRANCH_ANIME) {
                history = displayHistory(this.props.user_anime_history, this.props.branch);
            }
            else if (this.props.branch === BRANCH_MANGA) {
                history = displayHistory(this.props.user_manga_history, this.props.branch);
            }
        } catch (e) {
        }

        return (
            <>
                <CyberNavbar/>
                <Grid id="History-Container" container>
                    <Grid item xs={12}>
                        <h1>{this.props.branch} History</h1>
                    </Grid>
                    {!this.props.user_info_loading ? <>
                        <Grid item xs={false} lg={1}/>
                        <Grid item xs={12} lg={10}>
                            <ul className="timeline">
                                {history}
                            </ul>
                        </Grid>
                        </>
                        : <Loading text={'History Loading'}/>}
                    {!this.props.user_info_loading ?
                        <div className="pagination">
                            <Pagination
                                count={Math.ceil(this.props.user_anime_history[ApiResult.count] / 100)}
                                color="primary"
                                siblingCount={3}
                                page={this.state.current_page}
                                onChange={(type, page) => {
                                    playClickSound();
                                    if (page !== this.state.current_page)
                                        this.goToPage(page);
                                    else
                                        this.setState({page_dialog_open: true})
                                }}
                            />
                            <GoToPageDialog
                                open={this.state.page_dialog_open}
                                current_page={this.state.current_page}
                                total_pages={Math.ceil(this.props.user_anime_history[ApiResult.count] / 100)}
                                goToPage={this.goToPage}
                                onClose={this.closePageDialog}/>
                        </div> : null}
                </Grid>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    user_anime_history: state.users.user_anime_history,
    user_manga_history: state.users.user_manga_history,
    user_info_loading: state.users.user_info_loading,
});

export default connect(mapStateToProps, {getUser, getVisitedUserAnimeHistory, getVisitedUserMangaHistory})(DisplayUserHistory)