import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from "@material-ui/core/Grid";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    faCheck,
    faCloudDownloadAlt,
    faEdit,
    faFileImport,
    faFileUpload, faQuestion, faTimes,
    faTools,
    faVolumeMute
} from "@fortawesome/free-solid-svg-icons";
import {
    EditAboutMeBBCodeDialog,
    ImportAnimeListFromMal, ImportMangaListFromMal,
    ScrapeEpisodeHistoryFromMalDialog, ScrapeEpisodeHistoryFromMalForm
} from "../../forms/shortForms";
import {CyberButton} from "../../../layout/common/common";
import {Profile, Profile as ProfileModel, User, UserList} from "../../../../models";
import {isEmpty} from "../../../functions";
import CircularProgress from "@material-ui/core/CircularProgress";
import {
    fixUserStartAndFinishDateForAnimeEntries,
    getUserEpisodeHistoryScraperProgress,
    getUserList
} from "../../../../actions/users";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import LinearProgress from "@material-ui/core/LinearProgress";
import {playClickSound, playHoverSound} from "../../../layout/sounds";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import Switch from "@material-ui/core/Switch";
import {
    ToggleUserAnimeHistoryPrivacyForm,
    ToggleUserAnimeListPrivacyForm, ToggleUserMangaHistoryPrivacyForm, ToggleUserMangaListPrivacyForm
} from "../../forms/list_privacy/listPrivacyForms";


function CircularProgressWithLabel(props) {
    return (
        <Box position="relative" display="inline-flex">
            <CircularProgress variant="determinate" {...props} />
            <Box
                top={0}
                left={0}
                bottom={0}
                right={0}
                position="absolute"
                display="flex"
                alignItems="center"
                justifyContent="center"
            >
                <Typography variant="caption" component="div" color="textPrimary">{`${props.value}%`}</Typography>
            </Box>
        </Box>
    );
}

class ListSettings extends Component {
    constructor() {
        super(...arguments);
        this.openScrapeHistoryDialog = this.openScrapeHistoryDialog.bind(this);
        this.closeScrapeHistoryDialog = this.closeScrapeHistoryDialog.bind(this);
        this.state = {
            scrape_episode_history_dialog_open: false,
            scraping_info_reading: false,
            update_interval: null,
            scraping_history_info_modal_open: false,
        };
    }

    componentDidMount() {
        this.props.getUserList(this.props.user[User.id]);
        this.props.getUserEpisodeHistoryScraperProgress();
        const {getUserEpisodeHistoryScraperProgress} = this.props;
        if (!isEmpty(this.props.user_list) && this.props.user_list[UserList.is_scraping_episode_history_from_mal]) {
            getUserEpisodeHistoryScraperProgress();
            let update_interval = setInterval(function () {
                getUserEpisodeHistoryScraperProgress();
            }, 10000);
            this.setState({scraping_info_reading: true, update_interval: update_interval});
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        // setTimeout(() => { getUserEpisodeHistoryScraperProgress() }, 1000);
        const {getUserEpisodeHistoryScraperProgress} = this.props;

        if (!this.state.scraping_info_reading) {
            if (!isEmpty(this.props.user_list) && this.props.user_list[UserList.is_scraping_episode_history_from_mal]) {
                getUserEpisodeHistoryScraperProgress();
                let update_interval = setInterval(function () {
                    getUserEpisodeHistoryScraperProgress();
                }, 10000);
                this.setState({scraping_info_reading: true, update_interval: update_interval});
            }
        }
    }

    componentWillUnmount() {
        clearInterval(this.state.update_interval);
    }

    openScrapeHistoryDialog() {
        this.setState({scrape_episode_history_dialog_open: true})
    }

    closeScrapeHistoryDialog() {
        this.setState({scrape_episode_history_dialog_open: false})
    }

    render() {
        return this.props.user_list_loading || isEmpty(this.props.user_list) ? <CircularProgress/> :
            <Grid container className="settings-tab">
                <Grid item xs={12}>
                    <h1>List Settings</h1>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={11}>
                    <h2>Anime settings</h2>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={5}>
                    <TableContainer>
                        <Table aria-label="simple table" className="box-light">
                            <TableBody>
                                <TableRow>
                                    <TableCell>
                                        Your Anime List is {this.props.user[User.profile][Profile.is_anime_list_private] ? 'Private' : 'Public'}
                                    </TableCell>
                                    <TableCell align="center">
                                        <ToggleUserAnimeListPrivacyForm user={this.props.user}/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Your Anime History is {this.props.user[User.profile][Profile.is_anime_history_private] ? 'Private' : 'Public'}
                                    </TableCell>
                                    <TableCell align="center">
                                        <ToggleUserAnimeHistoryPrivacyForm user={this.props.user}/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Import from MAL
                                    </TableCell>
                                    <TableCell align="center">
                                        <ImportAnimeListFromMal/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Scrape episode history from MAL
                                    </TableCell>
                                    <TableCell align="center">
                                        {isEmpty(this.props.user_list) || !this.props.user_list[UserList.is_scraping_episode_history_from_mal] ?
                                            this.props.user_list_loading ? <CircularProgress/> :
                                                <CyberButton variant="outlined" onClick={this.openScrapeHistoryDialog}>
                                                    <FontAwesomeIcon icon={faCloudDownloadAlt}/>
                                                </CyberButton>
                                        : <>
                                            <p>{this.props.progress}%</p>
                                            <LinearProgress variant="determinate" value={this.props.progress}/>
                                        </>}
                                        <ScrapeEpisodeHistoryFromMalDialog
                                            open={this.state.scrape_episode_history_dialog_open}
                                            onClose={this.closeScrapeHistoryDialog}/>
                                    </TableCell>
                                    <div className="help-btn" onMouseEnter={playHoverSound} onClick={() => {
                                        playClickSound();
                                        this.setState({scraping_history_info_modal_open: true})
                                    }}>
                                        <FontAwesomeIcon icon={faQuestion}/>
                                    </div>
                                    <Dialog
                                        aria-labelledby="scrape-episode-history-info-title"
                                        open={this.state.scraping_history_info_modal_open}
                                        onClose={() => {
                                            this.setState({scraping_history_info_modal_open: false})
                                        }}>
                                        <div id="scrape-episode-history-info">
                                            <DialogTitle id="scrape-episode-history-info-title">Quick
                                                Guide</DialogTitle>
                                            <Typography variant="body1" paragraph={true}>
                                                To scrape your episode history from MAL you first must provide your
                                                authentication cookies of your account on MAL.
                                            </Typography>
                                            <Typography variant="body1">
                                                To find these cookies go to MAL, log in to your account and open Inspect
                                                Element on your browser:
                                            </Typography>
                                            <ul>
                                                <li>Click on the Application tab</li>
                                                <li>Click on Cookies under the Storage segment on the left</li>
                                                <li>Click on the MAL url under Cookies</li>
                                                <li>In the value column copy the value of MALSESSIONID and MALHLOGSESSID
                                                    and paste them in the form accordingly
                                                </li>
                                            </ul>
                                            <img src="images/user_scrape_guide_1.jpg"/>
                                            <Typography variant="body1">
                                                The scraper will use these cookies only to view your episode history and
                                                scrape it.
                                            </Typography>
                                            <Typography variant="body1">
                                                When the scraper starts:
                                            </Typography>
                                            <ul>
                                                <li>Don't log out of MAL during the scraping process.</li>
                                                <li>The scraper might last up to few hours depending on your list
                                                    size.
                                                </li>
                                                <li>You will get progress of the scraper when it starts.</li>
                                                <li>When the scraper finishes you can log out of MAL and log in again to
                                                    refresh your cookies. (For your security)
                                                </li>
                                            </ul>
                                            <Typography>
                                                NOTE: Only one user can scrape from MAL at the time, this is to
                                                prevent over-clogging MAL with requests. If someone is already
                                                scraping please wait and try again later.
                                            </Typography>

                                            <div className="close-btn">
                                                <CyberButton classNames="btn-contained-primary" onClick={() => {
                                                    this.setState({scraping_history_info_modal_open: false})
                                                }}>
                                                    <FontAwesomeIcon icon={faTimes}/>
                                                </CyberButton>
                                            </div>
                                        </div>
                                    </Dialog>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Fix Start Date and Finish Date for your anime list entries
                                    </TableCell>
                                    <TableCell align="center">
                                        <CyberButton variant="outlined"
                                                     onClick={() => {
                                                         if (window.confirm('Are you sure?'))
                                                             this.props.fixUserStartAndFinishDateForAnimeEntries()
                                                     }}>
                                            <FontAwesomeIcon icon={faTools}/>
                                        </CyberButton>
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
                <Grid item xs={6}/>

                <Grid item xs={1}/>
                <Grid item xs={11}>
                    <h2>Manga settings</h2>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={5}>
                    <TableContainer>
                        <Table aria-label="simple table" className="box-light">
                            <TableBody>
                                <TableRow>
                                    <TableCell>
                                        Your Manga List is {this.props.user[User.profile][Profile.is_manga_list_private] ? 'Private' : 'Public'}
                                    </TableCell>
                                    <TableCell align="center">
                                        <ToggleUserMangaListPrivacyForm user={this.props.user}/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Your Manga History is {this.props.user[User.profile][Profile.is_manga_history_private] ? 'Private' : 'Public'}
                                    </TableCell>
                                    <TableCell align="center">
                                        <ToggleUserMangaHistoryPrivacyForm user={this.props.user}/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Import from MAL
                                    </TableCell>
                                    <TableCell align="center">
                                        <ImportMangaListFromMal/>
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_list: state.users.user_list,
    user_list_loading: state.users.user_list_loading,
    progress: state.users.user_list[UserList.scraping_episode_history_from_mal_progress],
});

export default connect(mapStateToProps, {
    getUserList, getUserEpisodeHistoryScraperProgress, fixUserStartAndFinishDateForAnimeEntries
})(ListSettings)
