import React from 'react';
import {Howl} from 'howler';
import {Profile, User} from "../../models";
import store from "../../store";


export function loadSounds() {
    playClickSound(0);
    playHoverSound(0);
    playExpand3Sound(0);
    playExpand2Sound(0);
    playExpandSound(0);
    playErrorSound(0);
    playWarningSound(0);
    playDeploySound(0);
}

export function playWelcomeSound(v) {
    try {
        if (!store.getState().auth.user[User.profile][Profile.theme_sounds]) return;
    } catch (e) {
        return
    }
    let vol = 1;
    if (Number.isFinite(v))
        vol = v;
    let sound = new Howl({
        src: ['/sounds/welcome.ogg'],
        volume: vol,
    });

    sound.play(undefined, false);
}

