import {
    ANIME_LOADING, CANCEL_ANIME_LOADING,
    CANCEL_CHARACTERS_LOADING,
    CHARACTERS_LOADING, FILTER_CHARACTERS, GET_ANIME_DATA,
    GET_CHARACTER, GET_CHARACTERS_DATA, GET_INDUSTRY_DATA, INDUSTRY_DATA_LOADING
} from "./types";
import axios from "axios";
import {
    API_FILTER_CHARACTERS,
    API_GET_ANIME_DATA_FOR_HOME_PAGE,
    API_GET_CHARACTERS_DATA_FOR_HOME_PAGE, API_GET_INDUSTRY_DATA_FOR_HOME_PAGE,
    API_GET_ONE_CHARACTER
} from "./api_urls";
import {returnErrors} from "./messages";
import {tokenConfig} from "./auth";
import {isEmpty} from "../components/functions";
import {setCookieCharactersFilters} from "./cookies";
import {CharactersFilterNames} from "../components/layout/filters/field_names";
import {cancelAnimeLoading} from "./anime";


// GET A ONE CHARACTER
export const getCharacter = id => (dispatch, getState) => {
    dispatch({type: CHARACTERS_LOADING});

    axios
        .get(API_GET_ONE_CHARACTER(id), tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_CHARACTER,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch({type: CANCEL_CHARACTERS_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// FILTER ALL CHARACTERS
export const filterCharacters = (page, filters, no_loading) => (dispatch, getState) => {
    dispatch({type: CHARACTERS_LOADING});

    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;
    if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
        config.params = setCharactersFilterParams(config.params, filters);

    axios
        .get(API_FILTER_CHARACTERS, config)
        .then(res => {
            dispatch({
                type: FILTER_CHARACTERS,
                payload: res.data
            });
            if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
                dispatch(setCookieCharactersFilters(filters))
        })
        .catch(err => {
            dispatch({type: CANCEL_CHARACTERS_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


export function setCharactersFilterParams(params, filters) {
    if (filters[CharactersFilterNames.search] && typeof filters[CharactersFilterNames.search] == 'string')
        params[CharactersFilterNames.search] = filters[CharactersFilterNames.search];
    if (filters[CharactersFilterNames.ordering])
        params[CharactersFilterNames.ordering] = filters[CharactersFilterNames.ordering];
    return params
}


// CANCEL CHARACTERS LOADING
export const cancelCharactersLoading = () => dispatch => {
    dispatch({
        type: CANCEL_CHARACTERS_LOADING,
    })
};
