import React, {Component} from 'react'
import {connect} from 'react-redux'

import {Anime, AnimeFranchise} from "../../../../models";
import {getAnimeFranchise} from "../../../../actions/anime";
import {isEmpty} from "../../../functions";
import FranchiseTree from "../../../franchises/display/FranchiseTree";
import Loading from "../../../layout/common/Loading";
import {ANIME_FRANCHISE_DISPLAY} from "../../../../urls";
import {CyberButton, CyberLink} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSitemap} from "@fortawesome/free-solid-svg-icons";


class FranchiseTab extends Component {

    constructor() {
        super(...arguments);
        this.state = {};
    }

    componentDidMount() {
        if (isEmpty(this.props.anime_franchise))
            this.props.getAnimeFranchise(this.props.anime[Anime.franchise])
    }

    render() {
        return (
            <div id="Display-Franchise" className="franchise-tab">
                <CyberLink to={ANIME_FRANCHISE_DISPLAY(this.props.anime[Anime.franchise])}><CyberButton variant="outlined"><FontAwesomeIcon icon={faSitemap}/></CyberButton></CyberLink>
                {!isEmpty(this.props.anime_franchise) ?
                    <FranchiseTree titles={this.props.anime_franchise[AnimeFranchise.anime_franchise]}/>
                    : this.props.anime_franchise_loading ? <Loading text={'Franchise Loading'}/> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
    anime_franchise_loading: state.anime.anime_franchise_loading,
    anime_franchise: state.anime.anime_franchise,
});

export default connect(mapStateToProps, {getAnimeFranchise})(FranchiseTab)
