import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import {playClickSound, playHoverSound} from "../../../layout/sounds";
import Grid from "@material-ui/core/Grid";
import {ErogeData} from "../../../../models";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_EROGE} from "../../../../globals";
import {isEmpty} from "../../../functions";
import Loading from "../../../layout/common/Loading";
import TopEroge from "../TopEroge";



function RecentlyAdded(props) {
    const {eroge_data, eroge_loading, eroge_data_loaded} = props;
    return <Grid xs={12} className="horizontal-titles">
        {/*<div className="horizontal-titles-header">*/}
        {/*    <h3>Recently Added</h3>*/}
        {/*</div>*/}
        <div className="horizontal-titles-container horizontal-scroll">
            {eroge_data_loaded && eroge_data && eroge_data[ErogeData.recently_added_eroge] ?
                <div className="horizontal-titles-wrapper">
                    {eroge_data[ErogeData.recently_added_eroge].map(eroge => (
                        <TitleCard
                            containerClassNames="horizontal-titles-card-container"
                            branch={BRANCH_EROGE}
                            title={eroge}
                            skip_img_preload={true}
                        />
                    ))}
                </div> : eroge_loading ? <Loading text={'Eroge Loading'} block={true}/> : null}
        </div>
    </Grid>;
}

