function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Tabs for the visited user
 * @param props.visited_user {Object} Visited user object
 * @returns {JSX.Element}
 * @constructor
 */
export function TopErogeTabs(props) {
    const {eroge_data, eroge_loading, eroge_data_loaded} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root + ' tabs-root top-titles-tabs'}>
            <AppBar position="static" className="app-bar">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="default"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    <Tab label={<p>Recently Added</p>} {...a11yProps(0)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top Scored</p>} {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top Windows</p>} {...a11yProps(2)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top Android</p>} {...a11yProps(3)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Largest</p>} {...a11yProps(4)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel className="top-titles-tab" value={value} index={0} dir={theme.direction}>
                    <RecentlyAdded
                        eroge_data={eroge_data}
                        eroge_loading={eroge_loading}
                        eroge_data_loaded={eroge_data_loaded}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={1} dir={theme.direction}>
                    <TopScored
                        eroge_data={eroge_data}
                        eroge_loading={eroge_loading}
                        eroge_data_loaded={eroge_data_loaded}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={2} dir={theme.direction}>
                    <TopWindows
                        eroge_data={eroge_data}
                        eroge_loading={eroge_loading}
                        eroge_data_loaded={eroge_data_loaded}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={3} dir={theme.direction}>
                    <TopAndroid
                        eroge_data={eroge_data}
                        eroge_loading={eroge_loading}
                        eroge_data_loaded={eroge_data_loaded}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={4} dir={theme.direction}>
                    <LargestSize
                        eroge_data={eroge_data}
                        eroge_loading={eroge_loading}
                        eroge_data_loaded={eroge_data_loaded}
                    />
                </TabPanel>

            </SwipeableViews>
        </div>
    );
}
