import React, {Component} from 'react'
import PropTypes, {func} from 'prop-types'
import {connect} from 'react-redux'
import {Link} from "react-router-dom";

import {faLeaf, faSeedling, faSun} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";

import {BRANCH_ANIME, BRANCH_CHARACTERS, FILTER_ANIME_INITIAL_VALUES} from "../../../globals";
import {getSeasonLabelFromSeasonNumber, isEmpty, isTimeLessThanAnHourAgo} from "../../functions";
import {getLoggedInUserAnimeEntryStatuses} from "../../../actions/users";
import {ANIME_SEASON_DISPLAY} from "../../../urls";
import {Anime, AnimeData, ApiResult, IndustryData} from "../../../models";
import TitleCard from "../../layout/cards/TitleCard";
import Loading from "../../layout/common/Loading";
import {filterAnime} from "../../../actions/anime";
import {CyberLink} from "../../layout/common/common";

import '../../../assets/css/home.css'


class TopCharacters extends Component {
    constructor() {
        super(...arguments);
    }

    render() {
        return <>
            <Grid xs={12} className="horizontal-titles">
                <div className="horizontal-titles-header">
                    <h3>Most Popular Characters</h3>
                </div>
                <div className="horizontal-titles-container horizontal-scroll">
                    {this.props.industry_data_loaded && this.props.industry_data &&
                        this.props.industry_data[IndustryData.most_popular_characters] ?
                        <div className="horizontal-titles-wrapper">
                            {this.props.industry_data[IndustryData.most_popular_characters].map(character => (
                                <TitleCard
                                    containerClassNames="horizontal-titles-card-container"
                                    branch={BRANCH_CHARACTERS}
                                    title={character}
                                    skip_img_preload={true}
                                />
                            ))}
                        </div> : this.props.industry_data_loading ?
                            <Loading text={'Characters Loading'} block={true}/> : null}
                </div>
            </Grid>
            <Grid item xs={12}>
                <Divider variant="middle" className="charts-divider"/>
            </Grid>
            <Grid xs={12} className="horizontal-titles">
                <div className="horizontal-titles-header">
                    <h3>Recently Added Characters</h3>
                </div>
                <div className="horizontal-titles-container horizontal-scroll">
                    {this.props.industry_data_loaded && this.props.industry_data &&
                        this.props.industry_data[IndustryData.recently_added_characters] ?
                        <div className="horizontal-titles-wrapper">
                            {this.props.industry_data[IndustryData.recently_added_characters].map(character => (
                                <TitleCard
                                    containerClassNames="horizontal-titles-card-container"
                                    branch={BRANCH_CHARACTERS}
                                    title={character}
                                    skip_img_preload={true}
                                />
                            ))}
                        </div> : this.props.industry_data_loading ?
                            <Loading text={'Characters Loading'} block={true}/> : null}
                </div>
            </Grid>
        </>
    }
}

const mapStateToProps = state => ({
    industry_data: state.industry.industry_data,
    industry_data_loading: state.industry.industry_data_loading,
    industry_data_loaded: state.industry.industry_data_loaded,
});

export default connect(mapStateToProps, {})(TopCharacters)
