import React, {Component} from 'react'
import {connect} from 'react-redux'

import {ColorPicker} from 'material-ui-color';

import '../../../../../assets/css/bbcode_editor.css'
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputAdornment from "@material-ui/core/InputAdornment";
import Input from "@material-ui/core/Input";
import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import {CyberButton} from "../../../../layout/common/common";


/**
 * Custom attribute for bbCode [img] tag, users can set width in '%' for dynamic width of images,
 * this function checks for that attribute and applies it to actual img html tag.
 */
class BackgroundEditorTab extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <Grid contaienr className="editor-container box-light">
                <Grid item xs={12}>
                    <p className="color-gray">Background color</p>
                    <ColorPicker
                        onChange={(e) => {
                            this.props.updateBackgroundImage(e, 'background_color')
                        }}
                        value={this.props.background_image.background_color}
                    />
                </Grid>

                <Grid className="background-image-segment" item xs={12}>
                    <p className="color-gray">Background image</p>
                    <TextField
                        id="Background-Image-Field"
                        label="Image URL"
                        className="form-control"
                        value={this.props.background_image.url}
                        name="bbcode-editor"
                        onChange={(e) => {
                            this.props.updateBackgroundImage(e, 'url')
                        }}
                    />
                </Grid>


                <Grid item xs={12}>
                    <p className="color-gray">Image options</p>
                </Grid>
                <Grid className="form-control-container" item xs={6}>
                    <FormControl variant="outlined" className="form-control">
                        <InputLabel id="select-repeat-label">repeat</InputLabel>
                        <Select
                            labelId="select-repeat-label"
                            id="select-repeat"
                            value={this.props.background_image.repeat}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'repeat')
                            }}
                            label="repeat"
                        >
                            <MenuItem value={'no-repeat'}>no-repeat</MenuItem>
                            <MenuItem value={'repeat'}>repeat</MenuItem>
                            <MenuItem value={'repeat-x'}>repeat-x</MenuItem>
                            <MenuItem value={'repeat-y'}>repeat-y</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid className="form-control-container" item xs={6}>
                    <FormControl variant="outlined" className="form-control">
                        <InputLabel id="select-size-label">size</InputLabel>
                        <Select
                            labelId="select-size-label"
                            id="select-size"
                            value={this.props.background_image.size}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'size')
                            }}
                            label="size"
                        >
                            <MenuItem value={'contain'}>contain</MenuItem>
                            <MenuItem value={'cover'}>cover</MenuItem>
                            <MenuItem value={'auto'}>auto</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid className="form-control-container" item xs={6}>
                    <FormControl variant="outlined" className="form-control">
                        <InputLabel id="select-position-1-label">position</InputLabel>
                        <Select
                            labelId="select-position-1-label"
                            id="select-position-1"
                            value={this.props.background_image.position_1}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'position_1')
                            }}
                            label="position"
                        >
                            <MenuItem value={'center'}>center</MenuItem>
                            <MenuItem value={'left'}>left</MenuItem>
                            <MenuItem value={'right'}>right</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>

                <Grid className="form-control-container" item xs={6}>
                    <FormControl variant="outlined" className="form-control">
                        <InputLabel id="select-position-2-label">position</InputLabel>
                        <Select
                            labelId="select-position-2-label"
                            id="select-position-1"
                            value={this.props.background_image.position_2}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'position_2')
                            }}
                            label="position"
                        >
                            <MenuItem value={'bottom'}>bottom</MenuItem>
                            <MenuItem value={'center'}>center</MenuItem>
                            <MenuItem value={'top'}>top</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>

                <Grid className="form-control-container" item xs={6}>
                    <FormControl className="form-control" variant="outlined">
                        <InputLabel htmlFor="select-position-x-label">position-x</InputLabel>
                        <OutlinedInput
                            id="select-position-x-label"
                            value={this.props.background_image.position_x}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'position_x')
                            }}
                            type="number"
                            endAdornment={<InputAdornment position="end">px</InputAdornment>}
                            aria-describedby="outlined-weight-helper-text"
                            inputProps={{
                                'aria-label': 'position-x',
                            }}
                            labelWidth={0}
                        />
                    </FormControl>
                </Grid>
                <Grid className="form-control-container" item xs={6}>
                    <FormControl className="form-control" variant="outlined">
                        <InputLabel htmlFor="select-position-y-label">position-y</InputLabel>
                        <OutlinedInput
                            id="select-position-y-label"
                            value={this.props.background_image.position_y}
                            onChange={(e) => {
                                this.props.updateBackgroundImage(e, 'position_y')
                            }}
                            type="number"
                            endAdornment={<InputAdornment position="end">px</InputAdornment>}
                            aria-describedby="outlined-weight-helper-text"
                            inputProps={{
                                'aria-label': 'position-y',
                            }}
                            labelWidth={0}
                        />
                    </FormControl>
                </Grid>
                <Grid className="form-control-submit" item xs={12}>
                    <CyberButton variant={'contained'} classNames={'btn-contained-success'}
                                 onClick={this.props.submitAboutMe}
                    >
                        Upload
                    </CyberButton>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {})(BackgroundEditorTab)
