import React, {Component} from 'react';
import PropTypes from "prop-types";
import {connect} from 'react-redux'
import Tree from 'react-d3-tree';

import {
    BRANCH_ANIME

} from "../../../globals";

import {VALID_ANIME_RELATION_NAMES, VALID_ANIME_RELATIONS} from "../../../models";
import TitleCard from "../../layout/cards/TitleCard";
import {useCenteredTree} from "./helpers";
import {CyberButton} from "../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faAngleDoubleDown, faAngleDoubleUp} from "@fortawesome/free-solid-svg-icons";


let TITLES = [];
let LOCAL_VALID_ANIME_RELATIONS = VALID_ANIME_RELATIONS;
let b = LOCAL_VALID_ANIME_RELATIONS[1];
LOCAL_VALID_ANIME_RELATIONS[1] = LOCAL_VALID_ANIME_RELATIONS[3];
LOCAL_VALID_ANIME_RELATIONS[3] = b;
let FINAL_TREE = [];


function setTitles(titles) {
    TITLES = titles;
}

function compareByAired(a, b) {
    let now = new Date();
    let a_value = false;
    let b_value = false;
    if (a['air_start']) {
        let date_a = new Date(a['air_start']);
        a_value = Math.abs(now - date_a);
    }
    if (a['air_start']) {
        let date_b = new Date(b['air_start']);
        b_value = Math.abs(now - date_b);
    }
    return compare(a_value, b_value);
}

function compare(first, second) {
    if (!first) return -1;
    if (!second) return 1;
    let comparison = 0;
    if (first > second) {
        comparison = 1;
    } else if (first < second) {
        comparison = -1;
    }
    return comparison
}

function getFullAnime(id) {
    for (let x = 0; x < TITLES.length; x++) {
        if (TITLES[x]['id'] === id) {
            return TITLES[x];
        }
    }
    return false
}


function markAnimeAsAdded(id) {
    for (let x = 0; x < TITLES.length; x++) {
        if (TITLES[x]['id'] === id) {
            TITLES[x]['ADDED'] = true;
            return true
        }
    }
    return false
}


function isAdded(id) {
    for (let x = 0; x < TITLES.length; x++) {
        if (TITLES[x]['id'] === id) {
            return TITLES[x]['ADDED'];
        }
    }
}


function getChildren(parent_node) {
    markAnimeAsAdded(parent_node['attributes']['info']['id']);

    // console.log(parent_node);
    if (!parent_node['name']) return;

    for (let z = 0; z < LOCAL_VALID_ANIME_RELATIONS.length; z++) {
        if (LOCAL_VALID_ANIME_RELATIONS[z] === 'anime_relation_prequel' || LOCAL_VALID_ANIME_RELATIONS[z] === 'anime_relation_adaptation') continue;
        let relation = parent_node['attributes']['info']['anime_relations'][LOCAL_VALID_ANIME_RELATIONS[z]];
        for (let x = 0; x < relation.length; x++) {
            let anime = getFullAnime(relation[x]['id']);
            if (!anime || anime['ADDED'] === true) continue;
            if (anime['anime_relations']['anime_relation_prequel'].length > 0) {
                for (let y = 0; y < anime['anime_relations']['anime_relation_prequel'].length; y++) {
                    if (!isAdded(anime['anime_relations']['anime_relation_prequel'][y]['id'])) {
                        console.log(anime['anime_relations']['anime_relation_prequel'][y]);
                    }
                }
            }
            markAnimeAsAdded(relation[x]['id']);
            let node = {
                name: anime['title'],
                attributes: {
                    info: anime,
                    parent_relation: VALID_ANIME_RELATION_NAMES[LOCAL_VALID_ANIME_RELATIONS[z]]
                },
                children: []
            };

            parent_node.children.push(node)
        }
    }

    if (parent_node['children'].length > 0) {
        for (let x = 0; x < parent_node['children'].length; x++) {
            getChildren(parent_node['children'][x])
        }
    }
}


function checkChildrenForFixingSequel(parent_node, sequel_to_find) {
    if (!parent_node) return;
    for (let x = 0; x < parent_node['children'].length; x++) {
        if (parent_node['children'][x]['attributes']['info']['id'] === sequel_to_find['id']) {
            let node_to_return = parent_node['children'][x];
            const index = parent_node['children'].indexOf(parent_node['children'][x]);
            if (index > -1) {
                parent_node['children'].splice(index, 1);
            }
            return node_to_return;
        }
        let node_to_return = checkChildrenForFixingSequel(parent_node['children'][x], sequel_to_find);
        if (node_to_return) return node_to_return;
    }
}


function fixSequelPriority(parent_node) {
    let parent_anime = getFullAnime(parent_node['attributes']['info']['id']);

    if (parent_anime['anime_relations']['anime_relation_sequel'].length > 0 &&
        parent_anime['anime_relations']['anime_relation_full_story'].length <= 0) {
        let in_children = false;
        let sequel_to_find;
        for (let x = 0; x < parent_anime['anime_relations']['anime_relation_sequel'].length; x++) {
            let sequel_anime = getFullAnime(parent_anime['anime_relations']['anime_relation_sequel'][x]['id']);

            for (let y = 0; y < parent_node['children'].length; y++) {
                let anime = getFullAnime(parent_node['children'][y]['attributes']['info']['id']);
                if (sequel_anime['id'] === anime['id']) {
                    in_children = true;
                    break;
                }
            }
            if (sequel_anime['anime_relations']) {
                if (sequel_anime['anime_relations']['anime_relation_prequel'].length > 1 && parent_anime['type'] != 'TV') {
                    in_children = true;
                }
            }
            sequel_to_find = sequel_anime;
        }

        if (!in_children) {
            // NOW FIND IT IN THE TREE AND CUT IT FROM THERE AND PASTE IT AS ITS SEQUEL IN HIS CHILDREN
            let root_node = FINAL_TREE[0];
            let node_to_fix = checkChildrenForFixingSequel(root_node, sequel_to_find);
            if (node_to_fix) {
                node_to_fix['attributes']['parent_relation'] = 'Sequel';
                parent_node['children'].push(node_to_fix);
            }
        }
    }
    if (parent_node['children'].length > 0) {
        for (let x = 0; x < parent_node['children'].length; x++) {
            fixSequelPriority(parent_node['children'][x])
        }
    }
}


function getRootNode(titles) {
    // First find the root title
    // Root is valid if it doesnt have (Parent Story,
    // Full Story, or Other that is a TV or Movie and current title is not TV)
    let root;
    let root_node;

    for (let x = 0; x < titles.length; x++) {
        let _continue = false;
        if (titles[x]['anime_relations']['anime_relation_full_story'].length > 0)
            continue;
        else if (titles[x]['anime_relations']['anime_relation_parent_story'].length > 0)
            continue;
        else if (titles[x]['anime_relations']['anime_relation_prequel'].length > 0)
            continue;
        else if (titles[x]['type'] !== 'TV' && titles[x]['anime_relations']['anime_relation_other'].length > 0 && titles[x]['anime_relations']['anime_relation_sequel'].length <= 0) {
            for (let y = 0; y < titles[x]['anime_relations']['anime_relation_other'].length; y++) {
                if (titles[x]['anime_relations']['anime_relation_other'][y]['type'] === 'TV' ||
                    titles[x]['anime_relations']['anime_relation_other'][y]['type'] === 'Movie' ||
                    titles[x]['anime_relations']['anime_relation_other'][y]['type'] === 'OVA') {
                    _continue = true;
                    break
                }
            }
            if (_continue) continue;
        }
        if (titles[x]['type'] !== 'TV' && titles[x]['anime_relations']['anime_relation_spin_off'].length > 0) {
            let check = true;
            for (let z = 0; z < LOCAL_VALID_ANIME_RELATIONS.length; z++) {
                if (LOCAL_VALID_ANIME_RELATIONS[z] === 'anime_relation_spin_off' || LOCAL_VALID_ANIME_RELATIONS[z] === 'anime_relation_adaptation') continue;
                if (titles[x]['anime_relations'][LOCAL_VALID_ANIME_RELATIONS[z]].length > 0) {
                    check = false;
                    break;
                }
            }
            if (check) {
                for (let y = 0; y < titles[x]['anime_relations']['anime_relation_spin_off'].length; y++) {
                    if (titles[x]['anime_relations']['anime_relation_spin_off'][y]['type'] === 'TV') {
                        _continue = true;
                        break
                    }
                }
                if (_continue) continue;
            }
        }
        if (titles[x]['type'] === 'TV' && titles[x]['anime_relations']['anime_relation_alternative_setting'].length > 0) {
            for (let y = 0; y < titles[x]['anime_relations']['anime_relation_alternative_setting'].length; y++) {
                let anime = getFullAnime(titles[x]['anime_relations']['anime_relation_alternative_setting'][y]['id']);
                if (anime) {
                    if (anime['anime_relations']['anime_relation_prequel'].length > 0) {
                        _continue = true;
                        break
                    }
                }
            }
            if (_continue) continue;
        }
        if (titles[x]['type'] === 'Movie' && titles[x]['anime_relations']['anime_relation_alternative_version'].length > 0) {
            for (let y = 0; y < titles[x]['anime_relations']['anime_relation_alternative_version'].length; y++) {
                if (titles[x]['anime_relations']['anime_relation_alternative_version'][y]['type'] === 'TV') {
                    _continue = true;
                    break
                }
            }
            if (_continue) continue
        }

        root = titles[x];
        root_node = {
            name: root['title'],
            attributes: {
                info: root,
                parent_relation: null
            },
            children: []
        };
        return root_node;
    }
    return false
}

const renderForeignObjectNode = ({nodeDatum, toggleNode, foreignObjectProps, user_statuses}) => (
    <g>
        {/* `foreignObject` requires width & height to be explicitly set. */}
        <foreignObject {...foreignObjectProps} className="foreign-object">
            {console.log('statuses', user_statuses)}
            <div style={{width: "50%"}}>
                <p className="card-relation-name">{nodeDatum['attributes']['parent_relation']}</p>
                <div className="card-wrapper">
                    <TitleCard
                        classNames="franchise-card"
                        branch={BRANCH_ANIME}
                        title={nodeDatum['attributes']['info']}
                        user_status={user_statuses[nodeDatum['attributes']['info']['id'].toString()]}
                    />
                </div>
                {nodeDatum.children.length > 0 && (
                    <CyberButton classNames="btn-contained-primary" style={{width: "100%"}} onClick={toggleNode}>
                        <FontAwesomeIcon icon={nodeDatum.__rd3t.collapsed ? faAngleDoubleDown : faAngleDoubleUp}/>
                    </CyberButton>
                )}
            </div>
        </foreignObject>
    </g>
);

function TreeBuilder(props) {
    const {data, user_statuses} = props;
    const [translate, containerRef] = useCenteredTree();
    const nodeSize = {x: 600, y: 600};
    const foreignObjectProps = {width: nodeSize.x, height: nodeSize.y, x: -150, y: -250};
    console.log('in tree builder', user_statuses);
    return (
        <div style={{width: "100%", height: "100vh"}} ref={containerRef}>
            <Tree
                data={data}
                translate={translate}
                nodeSize={nodeSize}
                pathFunc={'step'}
                renderCustomNodeElement={(rd3tProps) =>
                    renderForeignObjectNode({...rd3tProps, user_statuses, foreignObjectProps})
                }
                orientation="vertical"
            />
        </div>
    );
}


class FranchiseTree extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {titles, user_anime_entry_statuses, mini} = this.props;

        // let translate = {x: innerWidth / 2, y: innerHeight / 3};
        let node_size = {x: 294, y: 500};
        let foreign_object_wrapper = {x: -132, y: -230};
        let svgSquare = {
            shape: 'rect',
            shapeProps: {
                width: 265,
                height: 450,
                x: -125,
                y: -230,
            }
        };

        // console.log(mini);

        if (mini) {
            svgSquare.shapeProps = {
                width: 120,
                height: 200,
                x: -80,
                y: -100,
            };
            foreign_object_wrapper = {x: -75, y: -120};
            node_size = {x: 184, y: 300};
            // translate.y = innerHeight / 4;
        }

        for (let x = 0; x < titles.length; x++) {
            titles[x]['ADDED'] = false
        }

        setTitles(titles);
        titles.sort(compareByAired).reverse();

        let root_node = getRootNode(titles);

        FINAL_TREE = [
            root_node
        ];

        if (root_node) {
            getChildren(root_node);
            FINAL_TREE = [
                root_node
            ];
            try {
                fixSequelPriority(root_node);
            } catch (e) {
            }
            FINAL_TREE = [
                root_node
            ];
            // fixSequelPriority(root_node);

            // let missing = false;
            // for (let x = 0; x < titles.length; x++) {
            //     if (!titles[x]['ADDED']) {
            //         missing = true;
            //     }
            // }

            // if (missing) {
            //     getMissingChildren(root_node);
            // }

            // let missing = true;
            // while (missing) {
            //     missing = false;
            //     for (let x = 0; x < titles.length; x++) {
            //         if (!titles[x]['ADDED']) {
            //             missing = true;
            //         }
            //     }
            //     fixMissingTitles(root_node, titles)
            // }
        }

        let TreeData = FINAL_TREE;
        let missing = 0;
        for (let x = 0; x < titles.length; x++) {
            if (!titles[x]['ADDED']) {
                console.log(titles[x]['id']);
                missing += 1;
            }
        }

        if (root_node) {
            return (
                <div id="Franchise-Tree">
                    <div style={{position: 'absolute'}}>{missing}</div>
                    {/*<div className="card-size-btn-container"><Button color={'primary'} onClick={this.toggleCard}>X</Button></div>*/}
                    <TreeBuilder data={TreeData} user_statuses={this.props.user_anime_entry_statuses}/>
                </div>
            )
        } else {
            return (
                <p className="color-blue">Could not generate franchise</p>
            )
        }
    }
}

const mapStateToProps = state => ({
    // mini: state.anime.display_franchise_mini,
    user_anime_entry_statuses: state.users.user_anime_entry_statuses
});

export default connect(mapStateToProps, {})(FranchiseTree)
