import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';

import {MANGA_TAB_NAME} from "../../../globals";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";
import {getLoggedInUserMangaEntryStatuses} from "../../../actions/users";
import {getMangaDataForHomePage} from "../../../actions/manga";
import TopManga from "./TopManga";


class MangaHome extends Component {
    componentDidMount() {
        document.title = MANGA_TAB_NAME;
        window.scrollTo(0, 0);
        this.props.getMangaDataForHomePage();
        this.props.getLoggedInUserMangaEntryStatuses();
    }

    render() {
        return (
            <div id="Manga-Home" className="title-home-page">
                <CyberNavbar/>
                <Grid container>
                    <TopManga/>
                    <Grid xs={12}>
                        {/*<AnimeStatistics/>*/}
                    </Grid>
                </Grid>
                <CyberFooter/>
            </div>
        )
    }
}

const mapStateToProps = state => ({});

export default connect(mapStateToProps, {getMangaDataForHomePage, getLoggedInUserMangaEntryStatuses})(MangaHome)
