import axios from "axios";
import {GET_INDUSTRY_DATA, INDUSTRY_DATA_LOADING} from "./types";
import {API_GET_INDUSTRY_DATA_FOR_HOME_PAGE} from "./api_urls";
import {tokenConfig} from "./auth";
import {returnErrors} from "./messages";


// GET INDUSTRY DATA FOR INDUSTRY HOME PAGE
export const getIndustryDataForHomePage = () => (dispatch, getState) => {
    dispatch({type: INDUSTRY_DATA_LOADING});

    axios
        .get(API_GET_INDUSTRY_DATA_FOR_HOME_PAGE, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_INDUSTRY_DATA,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};
