import LazyHero from "react-lazy-hero";
import React, {useState} from "react";
import {Link} from "react-router-dom";
import {LOGIN} from "../../urls";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Typography} from "@material-ui/core";
import Loading from "../layout/common/Loading";


export function NonAuthIndexHero() {
    return <LazyHero
        imageSrc={'images/neo-tokyo-1.jpg'}
        minHeight={'100vh'}
        opacity={0}
        transitionDuration={200}
        color={'transparent'}>
        <div className="welcome-img-container">
            <div className="title-container">
                <h5>welcome to</h5>
                <h1>Cyber-Genesis</h1>
            </div>
        </div>
    </LazyHero>
}


export function AuthIndexHero() {
    return <div className="logged-out-welcome-container">
        <video autoPlay={true} muted={true} loop={true} id="Index-Video">
            <source src="images/neo-city.mp4" type="video/mp4"/>
        </video>

        <div className="welcome-img-container">
            <div className="title-container">
                <h5>welcome to</h5>
                <h1>Cyber-Genesis</h1>
            </div>
        </div>
        <div className="home-login-link-container">
            <Link to={LOGIN} className="color-blue">Login</Link>
        </div>
    </div>
}
