import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Redirect} from "react-router-dom";

import Grid from '@material-ui/core/Grid';

import {SETTINGS_TAB_NAME} from "../../../globals";
import {LOGIN} from "../../../urls";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";
import {SettingsTabs} from "./tabs/SettingsTabs";

import '../../../assets/css/users.css'


class Settings extends Component {
    componentDidMount() {
        document.title = SETTINGS_TAB_NAME;
        window.scrollTo(0, 0);
    }

    render() {
        if (!this.props.user_authenticated)
            return <Redirect to={LOGIN}/>;
        return (
            <>
                <CyberNavbar/>
                <div id="Settings">
                    <Grid container>
                        <Grid item xs={12}>
                            <SettingsTabs/>
                        </Grid>
                    </Grid>
                </div>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated,
});

export default connect(mapStateToProps, {})(Settings)
