import React from "react";
import CyberNavbar from "../../layout/navbar/CyberNavbar";

import '../../../assets/css/private_page.css'
import Grid from "@material-ui/core/Grid";

/**
 *
 * @param {String} props.username
 * @param {String} props.branch
 * @returns {JSX.Element}
 * @constructor
 */
export function PrivatePage(props) {
    const {text} = props;

    return <>
        <CyberNavbar/>
        <div id="Private-Page">
            <Grid container className="container">
                <Grid xs={12}>
                    <h1>404</h1>
                    <p>{text}</p>
                </Grid>
            </Grid>
        </div>
    </>
}