import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import GridList from "@material-ui/core/GridList";

import {BRANCH_ANIME, BRANCH_MANGA} from "../../../../globals";
import {Anime, Manga, VALID_MANGA_RELATION_NAMES, VALID_MANGA_RELATIONS} from "../../../../models";
import {isEmpty} from "../../../functions";
import {getRelationsForManga} from "../../../../actions/manga";
import TitleCard from "../../../layout/cards/TitleCard";

import '../../../../assets/css/anime.css'


class RelationsTab extends Component {

    constructor() {
        super(...arguments);
        this.state = {};
    }

    componentDidMount() {
        if (isEmpty(this.props.manga_relations))
            this.props.getRelationsForManga(this.props.manga[Manga.id])
    }

    render() {
        return (
            <div className="relations-tab">
                {!isEmpty(this.props.manga_relations) ? <>
                    {this.props.manga_relations['manga_relation_adaptation'].length > 0 ?
                        <div>
                            <h2 className="relation-name">Adaptation</h2>
                            <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                                {this.props.manga_relations['manga_relation_adaptation'].map((anime, x) => (
                                    <Grow key={x} in {...({timeout: 200 + (x * 50)})}>
                                        <GridListTile cols={1}>
                                            <TitleCard
                                                branch={BRANCH_ANIME}
                                                title={anime}
                                                user_status={!isEmpty(this.props.user_anime_entry_statuses) ?
                                                    this.props.user_anime_entry_statuses[`${anime[Anime.id]}`] : null}
                                            />
                                        </GridListTile>
                                    </Grow>
                                ))}
                            </GridList>
                        </div> : null}

                    {VALID_MANGA_RELATIONS.map((relation_name, i) => (
                        relation_name !== 'manga_relation_adaptation' ?
                            <div key={i}>
                                {this.props.manga_relations[relation_name].length > 0 ?
                                    <h2 className="relation-name">{VALID_MANGA_RELATION_NAMES[relation_name]}</h2> : null}

                                <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                                    {this.props.manga_relations[relation_name].map((manga, y) => (
                                        <Grow key={y} in {...({timeout: 200 + (y * 50)})}>
                                            <GridListTile cols={1}>
                                                <TitleCard
                                                    branch={BRANCH_MANGA}
                                                    title={manga}
                                                    user_status={!isEmpty(this.props.user_manga_entry_statuses) ?
                                                            this.props.user_manga_entry_statuses[`${manga[Manga.id]}`] : null}
                                                />
                                            </GridListTile>
                                        </Grow>
                                    ))}
                                </GridList>
                            </div> : null))}
                </> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    manga: state.manga.manga,
    manga_relations: state.manga.manga[Manga.manga_relations],
    user_anime_entry_statuses: state.users.user_anime_entry_statuses,
    user_manga_entry_statuses: state.users.user_manga_entry_statuses,
});

export default connect(mapStateToProps, {getRelationsForManga})(RelationsTab)
