import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from "@material-ui/core/Grid";
import ImageGallery from 'react-image-gallery';

import {CYBER_GENESIS_URL} from "../../../../globals";
import {CoverImages, Eroge} from "../../../../models";

import '../../../../assets/css/anime.css'
import '../../../../../node_modules/react-image-gallery/styles/css/image-gallery.css'


class GeneralTab extends Component {
    render() {
        let images = [];
        if (this.props.eroge[Eroge.eroge_screenshots])
            for (let i = 0; i < this.props.eroge[Eroge.eroge_screenshots].length; i++)
                images.push({
                    original: `${CYBER_GENESIS_URL}${this.props.eroge[Eroge.eroge_screenshots][i][CoverImages.image]}`,
                    thumbnail: `${CYBER_GENESIS_URL}${this.props.eroge[Eroge.eroge_screenshots][i][CoverImages.image]}`
                });

        return (
            <div className="general-tab">
                <div className="general-description">
                    <h2>Synopsys</h2>
                    <p>{this.props.eroge[Eroge.description]}</p>

                    <h2>Screenshots</h2>
                    <Grid container>
                        <Grid item xs={2}/>
                        <Grid item xs={8} className="image-gallery">
                            <ImageGallery items={images}/>
                        </Grid>
                    </Grid>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    eroge: state.eroge.eroge,
});

export default connect(mapStateToProps, {})(GeneralTab)
