import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import Particles from 'react-particles-js';

import CyberNavbar from "../layout/navbar/CyberNavbar";
import CyberFooter from "../layout/footer/CyberFooter";

import '../../assets/css/home.css'
import {getAllUsers} from "../../actions/users";
import Loading from "../layout/common/Loading";
import {Profile as ProfileModel, User} from "../../models";
import {CyberLink, PreLoadImage} from "../layout/common/common";
import {CYBER_GENESIS_URL} from "../../globals";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import {getBrowseGridListCols} from "../functions";
import {PROFILE} from "../../urls";
import News from "../requiem/snippets/News";
import {particles_params} from "./functions";


class Home extends Component {
    constructor() {
        super(...arguments);
        this.updateGridListCols = this.updateGridListCols.bind(this);
        this.state = {
            gridlist_cols: getBrowseGridListCols(),
        };
    }

    componentDidMount() {
        document.title = 'Cyber-Space';
        window.scrollTo(0, 0);
        window.addEventListener('resize', this.updateGridListCols);
        this.props.getAllUsers();
    }

    updateGridListCols() {
        if (this.state.gridlist_cols !== getBrowseGridListCols())
            this.setState({gridlist_cols: getBrowseGridListCols()})
    }

    render() {
        // const video_url = 'https://finkiukim-my.sharepoint.com/personal/adrian_kostoski_students_finki_ukim_mk/_layouts/15/download.aspx?SourceUrl=%2Fpersonal%2Fadrian_kostoski_students_finki_ukim_mk%2FDocuments%2FAnime-Hosting%2F%5BCoalgirls%5D_Infinite_Stratos_01_(1920x1080_Blu-Ray_FLAC)_%5BA9B39255%5D.mkv&correlationId=a16fc107-db97-49a0-8f22-34972ccd6a98&psi=a98eab6f-0e64-4e87-ae49-72aef5c08037&ccat=2&cs=fHxPbmVVcFdlYlBsYXllcg%3D%3D&App=OneDriveWebVideo';
        // const video_url2 = 'https://finkiukim-my.sharepoint.com/personal/adrian_kostoski_students_finki_ukim_mk/_layouts/15/download.aspx?SourceUrl=%2Fpersonal%2Fadrian_kostoski_students_finki_ukim_mk%2FDocuments%2FAnime-Hosting%2F%5BCoalgirls%5D_Infinite_Stratos_01_(1920x1080_Blu-Ray_FLAC)_%5BA9B39255%5D.mkv&correlationId=a16fc107-db97-49a0-8f22-34972ccd6a98&psi=a98eab6f-0e64-4e87-ae49-72aef5c08037&ccat=2&cs=fHxPbmVVcFdlYlBsYXllcg%3D%3D&App=download';

        const video_url_dropbox = 'https://www.dropbox.com/s/zoy9wf489bvyhmq/%5BHorribleSubs%5D%20Kateikyoushi%20Hitman%20Reborn%21%20-%2001%20%5B480p%5D.mkv?raw=1';
        return (
            <>
                <CyberNavbar/>
                <Grid id="Home" container>
                    {/*<Grid item xs={12}>*/}
                    {/*    <News/>*/}
                    {/*</Grid>*/}

                    <Grid item xs={12}>
                        <Particles className="bg-particles" params={particles_params}/>
                    </Grid>

                    {/*<Grid item xs={12}>*/}
                    {/*    <iframe*/}
                    {/*        src="https://www.facebook.com/plugins/video.php?href=https%3A%2F%2Fwww.facebook.com%2Fmijomir.bulgarin%2Fposts%2F467447447764547&width=500&show_text=false&height=281&appId"*/}
                    {/*        width="500" height="281" style="border:none;overflow:hidden" scrolling="no" frameBorder="0"*/}
                    {/*        allowFullScreen=true*/}
                    {/*        allow="autoplay; clipboard-write; encrypted-media; picture-in-picture; web-share"*/}
                    {/*        allowFullScreen="true"></iframe>*/}
                    {/*</Grid>*/}

                    {/*<Grid item xs={12}>*/}
                    {/*    <p>Video:</p>*/}
                    {/*    <video width="30%" controls>*/}
                    {/*        <source*/}
                    {/*            src={video_url_dropbox}*/}
                    {/*        />*/}
                    {/*        Your browser does not support the video tag.*/}
                    {/*    </video>*/}
                    {/*    /!*<video width="30%" controls>*!/*/}
                    {/*    /!*    <source*!/*/}
                    {/*    /!*        src={video_url}*!/*/}
                    {/*    /!*    />*!/*/}
                    {/*    /!*    Your browser does not support the video tag.*!/*/}
                    {/*    /!*</video>*!/*/}
                    {/*</Grid>*/}

                    <Grid item xs={12}>
                        <h1>Users</h1>
                    </Grid>

                    {this.props.all_users_loaded ? <Grid item xs={12}>
                        <GridList cellHeight={400} className="user-card-container" cols={this.state.gridlist_cols}>
                            {this.props.all_users.map((user, x) => (
                                <GridListTile key={x} cols={1}>
                                    <CyberLink classNames="user-card" to={PROFILE(user[User.id])}>
                                        <div className="card-text">
                                            <h2>{user[User.username]}</h2>
                                        </div>
                                        <PreLoadImage
                                            src={`${CYBER_GENESIS_URL}${user[User.profile][ProfileModel.profile_image]}`}
                                            alt={'Avatar'}/>
                                    </CyberLink>
                                </GridListTile>

                                // <Card className="user-card" variant="outlined">
                                //     <CardContent>
                                //         <Typography gutterBottom>{user[User.username]}</Typography>
                                //         <PreLoadImage
                                //             src={`${CYBER_GENESIS_URL}${user[User.profile][ProfileModel.profile_image]}`}
                                //             alt={'Avatar'}/>
                                //     </CardContent>
                                //     <CardActions>
                                //     </CardActions>
                                // </Card>
                            ))}
                        </GridList>
                    </Grid> : this.props.users_loading ? <Loading/> : null}
                </Grid>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    users_loading: state.users.users_loading,
    all_users_loaded: state.users.all_users_loaded,
    all_users: state.users.all_users,
});

export default connect(mapStateToProps, {getAllUsers})(Home)
