import React, {Component} from 'react'
import {connect} from 'react-redux'

import {Anime, ApiResult} from "../../../../models";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_CHARACTERS} from "../../../../globals";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import GridList from "@material-ui/core/GridList";
import Grid from "@material-ui/core/Grid";

import '../../../../assets/css/anime.css'
import {NONE} from "../../../display/Display";


export function getSynopsys(synopsys) {
    if (!synopsys || synopsys.indexOf('No synopsis information has been added to this title') >= 0)
        return null;
    return synopsys
}


class GeneralTab extends Component {
    render() {
        const synopsys = getSynopsys(this.props.anime[Anime.synopsis]);

        return (
            <div>
                <div className="general-description">
                    <h2>Synopsis</h2>
                    <p>{synopsys ? synopsys : NONE}</p>
                    {this.props.anime[Anime.background] ?
                        <>
                            <h2>Background</h2>
                            <p>{this.props.anime[Anime.background]}</p>
                        </> : null}
                    {this.props.anime[Anime.opening_theme] || this.props.anime[Anime.ending_theme] ? <>
                        <h2>OST</h2>
                        <p><span>Openings:</span> {this.props.anime[Anime.opening_theme] ?
                            this.props.anime[Anime.opening_theme] : NONE}</p>
                        <p><span>Endings:</span> {this.props.anime[Anime.ending_theme] ?
                            this.props.anime[Anime.ending_theme] : NONE}</p>
                    </> : null}

                    <h2>Main Characters</h2>
                </div>

                {this.props.anime[Anime.main_characters].length > 0 ?
                    <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                        {this.props.anime[Anime.main_characters].map((character, i) => (
                            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                                <GridListTile cols={1}>
                                    <TitleCard branch={BRANCH_CHARACTERS} title={character}/>
                                </GridListTile>
                            </Grow>
                        ))}
                    </GridList> : <p className="none-characters">{NONE}</p>}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
});

export default connect(mapStateToProps, {})(GeneralTab)
