import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_PEOPLE} from "../../../globals";
import Display from "../../display/Display";


class PersonDisplay extends Component {
    render() {
        return <Display
            branch={BRANCH_PEOPLE}
            param_id={this.props.match.params.id}
            title={this.props.person}
            title_loaded={this.props.people_loaded}
            title_loading={this.props.people_loading}
        />
    }
}

const mapStateToProps = state => ({
    person: state.people.person,
    people_loaded: state.people.people_loaded,
    people_loading: state.people.people_loading,
});

export default connect(mapStateToProps, {})(PersonDisplay)
