import {Anime, Character, Manga, Profile, User} from "../../../models";
import Grid from "@material-ui/core/Grid";
import TitleCard from "../../layout/cards/TitleCard";
import {BRANCH_ANIME, BRANCH_CHARACTERS, BRANCH_MANGA, BRANCH_PEOPLE} from "../../../globals";
import React from "react";
import {useSelector} from "react-redux";
import {isEmpty} from "../../functions";


function getTitlesFromBranch(branch, visited_user) {
    let titles = [];
    switch (branch) {
        case BRANCH_ANIME:
            titles = visited_user[User.profile][Profile.favorite_anime];
            break;
        case BRANCH_MANGA:
            titles = visited_user[User.profile][Profile.favorite_manga];
            break;
        case BRANCH_CHARACTERS:
            titles = visited_user[User.profile][Profile.favorite_characters];
            break;
        case BRANCH_PEOPLE:
            titles = visited_user[User.profile][Profile.favorite_people];
            break;
        default:
            return null
    }
    return titles
}


/**
 *
 * @param {String} props.branch
 * @param {Object} props.style
 * @param {Number} props.limit
 * @param {Boolean} props.mini
 * @returns {JSX.Element|null}
 * @constructor
 */
export function FavoriteTitles(props) {
    const {branch, style, limit, mini} = props;
    const visited_user = useSelector(state => state.auth.visited_user);
    let parsed = parseInt(limit);
    parsed = isNaN(parsed) ? 100 : parsed;
    if (isEmpty(visited_user))
        return null;
    const titles = getTitlesFromBranch(branch, visited_user);
    return <div style={style} className="Quoted-Favorites-Container">
        {titles.length === 0 ? <p>You haven't added any favorite {branch} yet.</p> : titles.map((title, i) => (
            i < parsed ? <div key={title['id']} className={mini ? 'favorites-title-card favorites-title-card-mini' : 'favorites-title-card'}>
                <TitleCard branch={branch} title={title} mini={mini}/>
            </div> : null
        ))}
    </div>
}

