import {
    CANCEL_EROGE_LOADING,
    EROGE_LOADING,
    FILTER_EROGE,
    GET_ANIME_DATA,
    GET_EROGE,
    GET_EROGE_DATA
} from "../actions/types";
import {playExpandSound} from "../components/layout/sounds";

const initialState = {
    eroge_loading: false,
    eroge: {},
    eroge_loaded: false,

    filtered_eroge: {},
    filtered_eroge_loaded: false,

    eroge_data: {},
    eroge_data_statistics: {},
    eroge_data_loaded: false,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default function (state = initialState, action) {
    switch (action.type) {
        case EROGE_LOADING:
            return {
                ...state,
                eroge_loading: true,
                eroge_loaded: false,
                filtered_eroge_loaded: false,
            };
        case CANCEL_EROGE_LOADING:
            return {
                ...state,
                eroge_loading: false,
                eroge_loaded: false,
                filtered_eroge_loaded: false,
            };
        case GET_EROGE:
            playExpandSound();
            return {
                ...state,
                eroge: action.payload,
                eroge_loading: false,
                eroge_loaded: true
            };
        case FILTER_EROGE:
            return {
                ...state,
                filtered_eroge: action.payload,
                eroge_loading: false,
                filtered_eroge_loaded: true
            };
        case GET_EROGE_DATA:
            return {
                ...state,
                eroge_loading: false,
                eroge_data: action.payload,
                eroge_data_loaded: true,
            };
        default:
            return state
    }
}
