/* eslint-disable */
import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import Tooltip from "@material-ui/core/Tooltip";
import LinearProgress from "@material-ui/core/LinearProgress";

import {isEmpty} from "../../../functions";
import {User, UserStatistics} from "../../../../models";
import {getAnimeAchievementsThresholds} from "../../../../actions/anime";
import {clearUserAchievements, getUserAchievements} from "../../../../actions/users";
import {GenreAchievement, getUserGenreAchievements} from "../../achievements/achievements";

import '../../../../assets/css/achievements.css'


class AchievementsTab extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            user_achievements: {
                the_crusade: false,
                the_crusade_progress: 0.0,
                romance_progress: 0,
                romance_1: false,
                romance_2: false,
                romance_3: false,
                romance_4: false,
                pervert_progress: 0,
                pervert_1: false,
                pervert_2: false,
                pervert_3: false,
                pervert_4: false,
                classics_progress: 0,
                classics_1: false,
                classics_2: false,
                classics_3: false,
                classics_4: false,
            },
            anime_achievement_thresholds: {},
            achievements_loaded: false
        };
    }

    componentDidMount() {
        this.props.getAnimeAchievementsThresholds();
        this.props.getUserAchievements(this.props.visited_user[User.id]);
    }

    componentWillUnmount() {
        this.props.clearUserAchievements();
    }

    render() {
        let genre_achievements = getUserGenreAchievements(this.state.user_achievements, this.state.anime_achievement_thresholds);
        if (!this.state.achievements_loaded && !isEmpty(this.props.user_achievements) && !isEmpty(this.props.anime_achievement_thresholds))
            this.setState({
                user_achievements: this.props.user_achievements,
                anime_achievement_thresholds: this.props.anime_achievement_thresholds,
                achievements_loaded: true
            });

        // genre_achievements = getUserGenreAchievements(this.state.user_achievements, this.state.anime_achievement_thresholds);

        return (
            <Grid id="Achievements" container>
                <Grid item xs={12}>
                    <h3>Achievements</h3>
                </Grid>
                {this.props.visited_user[User.id] === 15 &&
                    this.props.visited_user[User.id] === this.props.user[User.id] ?
                    <Grid item xs={12} className="achievement-container">
                        <Tooltip title="All Anime where the Goddess is present, count towards this achievement.">
                            <h5>The Crusade</h5>
                        </Tooltip>
                        <p>I am going on pilgrimage, I am going to walk the holy path.</p>
                        <Tooltip title={`${this.state.user_achievements[UserStatistics.the_crusade_progress]}%`}>
                            <LinearProgress className="achievement-progress" variant="buffer" value={this.state.user_achievements[UserStatistics.the_crusade_progress]} valueBuffer={0} />
                        </Tooltip>
                    </Grid> : null}
                {genre_achievements ? genre_achievements.map(achievement => (
                    <GenreAchievement achievement={achievement}/>
                )) : null}
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    visited_user: state.auth.visited_user,
    anime_loading: state.anime.anime_loading,
    anime_data_loaded: state.anime.anime_data_loaded,
    anime_achievement_thresholds: state.anime.anime_achievement_thresholds,
    user_achievements_loading: state.users.user_achievements_loading,
    user_achievements: state.users.user_achievements,
});

export default connect(mapStateToProps, {getAnimeAchievementsThresholds, getUserAchievements, clearUserAchievements})(AchievementsTab)
