import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";

import {MostPopular, TopErogeTabs} from "./tabs/TopErogeTabs";

import '../../../assets/css/home.css'
import '../../../assets/css/seasons.css'


class TopEroge extends Component {
    constructor() {
        super(...arguments);
    }

    render() {
        return <>
            <MostPopular
                eroge_data={this.props.eroge_data}
                eroge_loading={this.props.eroge_loading}
                eroge_data_loaded={this.props.eroge_data_loaded}/>
            <Grid item xs={12}>
                <Divider variant="middle" className="charts-divider"/>
            </Grid>
            <TopErogeTabs
                eroge_data={this.props.eroge_data}
                eroge_loading={this.props.eroge_loading}
                eroge_data_loaded={this.props.eroge_data_loaded}
            />
        </>
    }
}

const mapStateToProps = state => ({
    eroge_data: state.eroge.eroge_data,
    eroge_loading: state.eroge.eroge_loading,
    eroge_data_loaded: state.eroge.eroge_data_loaded,
});

export default connect(mapStateToProps, {})(TopEroge)
