import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Moment from "react-moment";

import {PROFILE_TAB_NAME} from "../../../globals";
import {User, Profile as ProfileModel} from "../../../models";
import {getUser} from "../../../actions/auth";
import {CyberLink, PreLoadImage} from "../../layout/common/common";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import Loading from "../../layout/common/Loading";
import {
    convertUTCDateToLocalDate, isMoreThanAYear, isTimeLessThanAnHourAgo, isTimeLessThanSixHoursAgo,
    isToday,
    isWithinAMonth,
    isWithinAWeek, isWithinAYear, isWithinSixMonths,
    isYesterday
} from "../../functions";
import {ProfileTabs} from "./tabs/ProfileTabs";
import UploadProfileImageButton from "../forms/UploadProfileImageButton";
import {getUserList, updateUserList} from "../../../actions/users";
import {NOT_FOUND, USER_ANIME_HISTORY, USER_ANIME_LIST, USER_MANGA_HISTORY, USER_MANGA_LIST} from "../../../urls";

import '../../../assets/css/users.css'


function getLastLoginLabel(last_login) {
    const last_login_date = convertUTCDateToLocalDate(new Date(Date.parse(last_login)));
    if (isTimeLessThanAnHourAgo(last_login_date))
        return <span className="color-green">Now</span>;
    else if (isTimeLessThanSixHoursAgo(last_login_date))
        return <span className="color-success">Few hours ago</span>;
    else if (isToday(last_login_date))
        return <span className="color-success">Today</span>;
    else if (isYesterday(last_login_date))
        return <span className="color-warning">Yesterday</span>;
    else if (isWithinAWeek(last_login_date))
        return <span className="color-warning">Few days ago</span>;
    else if (isWithinAMonth(last_login_date))
        return <span className="color-warning">Few weeks ago</span>;
    else if (isWithinSixMonths(last_login_date))
        return <span className="color-warning">Few months ago</span>;
    else if (isWithinAYear(last_login_date))
        return <span className="color-warning">More than six months ago</span>;
    else if (isMoreThanAYear(last_login_date))
        return <span className="color-danger">More than a year ago</span>;
    else return <span className="color-danger">A long time ago</span>
}


class Profile extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            img_cover_loaded: false,
            current_id: this.props.match.params.id,
        };
    }

    componentDidMount() {
        window.scrollTo(0, 0);
        this.props.getUser(this.props.match.params.id);
        this.props.getUserList(this.props.match.params.id);
        this.props.updateUserList(this.props.match.params.id);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {match} = this.props;

        if (prevState.current_id !== match.params.id) {
            this.setState({current_id: this.props.match.params.id});
            this.props.getUser(this.props.match.params.id);
            this.props.getUserList(this.props.match.params.id);
            this.props.updateUserList(this.props.match.params.id);

            window.scrollTo(0, 0);
        }
    }

    render() {
        document.title = this.props.visited_user_loaded ? this.props.visited_user[User.username] : PROFILE_TAB_NAME;

        let loaded = this.props.visited_user_loaded;
        if (loaded && parseInt(this.props.match.params.id) !== this.props.visited_user[User.id])
            loaded = false;

        return (
            <>
                <CyberNavbar/>
                {loaded ? <Grid id="Profile" container xs={12}>
                    <Grid item xs={12}>
                        <h1>{this.props.visited_user[User.username]}</h1>
                    </Grid>
                    <Grid item xs={2} className="side-panel">
                        <div className="side-panel-sticky-helper"/>
                        <div className="side-panel-container">
                            <div className="avatar-image-container">
                                <PreLoadImage src={this.props.visited_user[User.profile][ProfileModel.profile_image]}
                                              alt={'Avatar'}/>
                                {this.props.user && this.props.user[User.id] === parseInt(this.props.match.params.id) ?
                                    <UploadProfileImageButton loader_classnames="upload-img-loader"/> : null}
                            </div>
                            <List className="info-list box-light">
                                <ListItem>
                                    <ListItemText
                                        primary="Title"
                                        secondary={this.props.visited_user[User.profile][ProfileModel.title]}/>
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary="Bio"
                                        secondary={this.props.visited_user[User.profile][ProfileModel.bio]}/>
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary="Email"
                                        secondary={this.props.visited_user[User.email]}/>
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary="Last Active"
                                        secondary={getLastLoginLabel(this.props.visited_user[User.last_login])}/>
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary="Joined"
                                        secondary={<Moment format={'Do MMM YYYY'} date={this.props.visited_user[User.date_joined]}/>}/>
                                </ListItem>
                            </List>
                            <Divider/>
                            <List className="box-light" component="nav" aria-label="main mailbox folders">
                                <CyberLink to={USER_ANIME_LIST(this.props.visited_user[User.id])}>
                                    <ListItem button>
                                        Anime List
                                    </ListItem>
                                </CyberLink>
                                <CyberLink to={USER_ANIME_HISTORY(this.props.visited_user[User.id])}>
                                    <ListItem button>
                                        Anime History
                                    </ListItem>
                                </CyberLink>
                                <CyberLink to={USER_MANGA_LIST(this.props.visited_user[User.id])}>
                                    <ListItem button>
                                        Manga List
                                    </ListItem>
                                </CyberLink>
                                <CyberLink to={USER_MANGA_HISTORY(this.props.visited_user[User.id])}>
                                    <ListItem button>
                                        Manga History
                                    </ListItem>
                                </CyberLink>
                                <CyberLink to={NOT_FOUND}>
                                    <ListItem button>
                                        Eroge List
                                    </ListItem>
                                </CyberLink>
                            </List>
                        </div>
                    </Grid>
                    <Grid item xs={10} className="main-panel">
                        <ProfileTabs visited_user={this.props.visited_user}/>
                    </Grid>
                </Grid> : this.props.user_loading ? <Loading text={'User Loading'}/> : null}
            </>
        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
});

export default connect(mapStateToProps, {getUser, getUserList, updateUserList})(Profile)
