import {
    MANGA_LOADING,
    CANCEL_MANGA_LOADING,
    GET_MANGA,
    FILTER_MANGA,
    GET_MANGA_DATA,
    GET_ALL_CHARACTERS_FOR_MANGA,
    GET_RELATIONS_FOR_MANGA, CANCEL_CHARACTERS_LOADING
} from "./types";
import axios from "axios";
import {
    API_FILTER_MANGA,
    API_GET_ALL_CHARACTERS_FOR_MANGA,
    API_GET_MANGA_DATA, API_GET_MANGA_DATA_FOR_HOME_PAGE,
    API_GET_ONE_MANGA,
    API_GET_RELATIONS_FOR_MANGA
} from "./api_urls";
import {returnErrors} from "./messages";
import {MangaFilterNames} from "../components/layout/filters/field_names";
import {setCookieMangaFilters} from "./cookies";
import {isEmpty} from "../components/functions";
import {tokenConfig} from "./auth";
import {TYPE_BROWSE, TYPE_LIST, TYPE_SEASON, USER_MANGA_ORDERINGS} from "../globals";


// GET MANGA DATA
export const getMangaData = () => (dispatch, getState) => {
    dispatch({type: MANGA_LOADING});
    axios
        .get(API_GET_MANGA_DATA, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_MANGA_DATA,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch({type: CANCEL_MANGA_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


