// GET ALL CHARACTERS FOR MANGA
export const getAllCharactersForManga = (id, page) => (dispatch, getState) => {
    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;

    axios
        .get(API_GET_ALL_CHARACTERS_FOR_MANGA(id), config)
        .then(res => {
            dispatch({
                type: GET_ALL_CHARACTERS_FOR_MANGA,
                payload: res.data
            })
        })
        .catch(err => {
        })
};


