// FILTER ALL MANGA
export const filterManga = (page, filters, season, list, loading = true, save_cookie = true) => (dispatch, getState) => {
    if (loading)
        dispatch({type: MANGA_LOADING});

    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;

    let page_type = TYPE_BROWSE;
    if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
        config.params = setMangaFilterParams(config.params, filters);
    if (season !== undefined && season !== 'undefined' && !isEmpty(season)) {
        config.params = setMangaSeasonParams(config.params, season);
        page_type = TYPE_SEASON;
    }
    if (list !== undefined && list !== 'undefined' && !isEmpty(list)) {
        config.params = setMangaListParams(config.params, list);
        page_type = TYPE_LIST;
    } else if (USER_MANGA_ORDERINGS.indexOf(config.params[MangaFilterNames.ordering].replace('-', '')) > -1)
        config.params[MangaFilterNames.ordering] = '-members';

    axios
        .get(API_FILTER_MANGA, config)
        .then(res => {
            dispatch({
                type: FILTER_MANGA,
                payload: res.data
            });
            if (save_cookie && filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
                dispatch(setCookieMangaFilters(filters, page_type))
        })
        .catch(err => {
            dispatch({type: CANCEL_MANGA_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


export function setMangaFilterParams(params, filters) {
    if (filters[MangaFilterNames.search] && typeof filters[MangaFilterNames.search] == 'string') params[MangaFilterNames.search] = filters[MangaFilterNames.search];
    if (filters[MangaFilterNames.downloaded]) params[MangaFilterNames.downloaded] = filters[MangaFilterNames.downloaded];
    if (filters[MangaFilterNames.is_downloading]) params[MangaFilterNames.is_downloading] = filters[MangaFilterNames.is_downloading];
    if (filters[MangaFilterNames.cannot_download]) params[MangaFilterNames.cannot_download] = filters[MangaFilterNames.cannot_download];
    if (filters[MangaFilterNames.deleted_by_MAL]) params[MangaFilterNames.deleted_by_MAL] = filters[MangaFilterNames.deleted_by_MAL];
    if (filters[MangaFilterNames.pending_by_MAL]) params[MangaFilterNames.pending_by_MAL] = filters[MangaFilterNames.pending_by_MAL];
    if (filters[MangaFilterNames.volumes_range].length > 0) params[MangaFilterNames.volumes_range] = filters[MangaFilterNames.volumes_range].join();
    if (filters[MangaFilterNames.chapters_range].length > 0) params[MangaFilterNames.chapters_range] = filters[MangaFilterNames.chapters_range].join();
    if (filters[MangaFilterNames.genres].length > 0) params[MangaFilterNames.genres] = filters[MangaFilterNames.genres].join();
    if (filters[MangaFilterNames.type].length > 0) params[MangaFilterNames.type] = filters[MangaFilterNames.type].join();
    if (filters[MangaFilterNames.status].length > 0) params[MangaFilterNames.status] = filters[MangaFilterNames.status].join();
    if (filters[MangaFilterNames.user_exclude].length > 0) params[MangaFilterNames.user_exclude] = filters[MangaFilterNames.user_exclude].join();
    if (filters[MangaFilterNames.ordering]) params[MangaFilterNames.ordering] = filters[MangaFilterNames.ordering];
    return params
}

export function setMangaSeasonParams(params, season) {
    params[MangaFilterNames.year] = season[MangaFilterNames.year];
    params[MangaFilterNames.season] = season[MangaFilterNames.season];
    return params
}

export function setMangaListParams(params, season) {
    params[MangaFilterNames.user_list_filter] = season[MangaFilterNames.user_list_filter];
    params[MangaFilterNames.user_list_pk] = season[MangaFilterNames.user_list_pk];
    if (params[MangaFilterNames.user_exclude] && params[MangaFilterNames.user_exclude].length > 0)
        params[MangaFilterNames.user_exclude] = null;
    return params
}


// CANCEL MANGA LOADING
export const cancelMangaLoading = () => dispatch => {
    dispatch({
        type: CANCEL_MANGA_LOADING,
    })
};
