import axios from 'axios';

import {BRANCH_ANIME, BRANCH_CHARACTERS, BRANCH_EROGE, BRANCH_MANGA, BRANCH_PEOPLE} from "../globals";
import {getAnime} from "./anime";
import {createMessage, returnErrors} from "./messages";
import store from "../store";
import {getUserAnimeEntry, getUserMangaEntry} from "./users";
import {
    API_ADD_ANIME_TO_SERVER, API_GET_NEWS, API_OPEN_ANIME_FOLDER, API_OPEN_EROGE_FOLDER,
    API_REQUIEM_DELETE_ANIME_SERVER_INFO_AND_TORRENTS,
    API_REQUIEM_DOWNLOAD_ANIME_TORRENT, API_REQUIEM_GET_STORAGE_INFO, API_REQUIEM_GET_TORRENT_PROGRESS,
    API_REQUIEM_UPDATE_ANIME_MAL,
    API_REQUIEM_UPDATE_CHARACTER_MAL,
    API_REQUIEM_UPDATE_EROGE_HENTAIGAMER,
    API_REQUIEM_UPDATE_MANGA_MAL, API_REQUIEM_UPDATE_PERSON_MAL, API_UPDATE_ANIME_TO_SERVER
} from "./api_urls";
import {tokenConfig} from "./auth";
import {Alert, Anime} from "../models";
import {getManga} from "./manga";
import {getCharacter} from "./characters";
import {getEroge} from "./eroge";
import {
    ANIME_LOADING,
    CANCEL_ANIME_LOADING, CANCEL_CHARACTERS_LOADING, CANCEL_EROGE_LOADING,
    CANCEL_MANGA_LOADING, CANCEL_PEOPLE_LOADING,
    CHARACTERS_LOADING, DOWNLOAD_TORRENT,
    EROGE_LOADING, GET_NEWS, GET_STORAGE_INFO, GET_TORRENT_PROGRESS,
    MANGA_LOADING, NEWS_LOADING, PEOPLE_LOADING, REQUIEM_LOADING
} from "./types";
import {getPerson} from "./people";


function scrapeUpdateTitleApiUrl(branch, id) {
    switch (branch) {
        case BRANCH_ANIME:
            return API_REQUIEM_UPDATE_ANIME_MAL(id);
        case BRANCH_MANGA:
            return API_REQUIEM_UPDATE_MANGA_MAL(id);
        case BRANCH_CHARACTERS:
            return API_REQUIEM_UPDATE_CHARACTER_MAL(id);
        case BRANCH_PEOPLE:
            return API_REQUIEM_UPDATE_PERSON_MAL(id);
        case BRANCH_EROGE:
            return API_REQUIEM_UPDATE_EROGE_HENTAIGAMER(id);
        default:
            return ''
    }
}


// SCRAPE UPDATE SINGLE TITLE
export const scrapeUpdateTitle = (id, branch) => (dispatch, getState) => {
    if (branch === BRANCH_ANIME)
        dispatch({type: ANIME_LOADING});
    else if (branch === BRANCH_MANGA)
        dispatch({type: MANGA_LOADING});
    else if (branch === BRANCH_CHARACTERS)
        dispatch({type: CHARACTERS_LOADING});
    else if (branch === BRANCH_EROGE)
        dispatch({type: EROGE_LOADING});
    else if (branch === BRANCH_PEOPLE)
        dispatch({type: PEOPLE_LOADING});
    else return;

    axios
        .get(scrapeUpdateTitleApiUrl(branch, id), tokenConfig(getState))
        .then(res => {
            dispatch(createMessage(`${branch} Scraped & Updated`, Alert.type_success));
            if (branch === BRANCH_ANIME) dispatch(getAnime(id));
            else if (branch === BRANCH_MANGA) dispatch(getManga(id));
            else if (branch === BRANCH_CHARACTERS) dispatch(getCharacter(id));
            else if (branch === BRANCH_EROGE) dispatch(getEroge(id));
            else if (branch === BRANCH_PEOPLE) dispatch(getPerson(id));
        })
        .catch(err => {
            if (branch === BRANCH_ANIME)
                dispatch({type: CANCEL_ANIME_LOADING});
            else if (branch === BRANCH_MANGA)
                dispatch({type: CANCEL_MANGA_LOADING});
            else if (branch === BRANCH_CHARACTERS)
                dispatch({type: CANCEL_CHARACTERS_LOADING});
            else if (branch === BRANCH_EROGE)
                dispatch({type: CANCEL_EROGE_LOADING});
            else if (branch === BRANCH_PEOPLE)
                dispatch({type: CANCEL_PEOPLE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// GET STORAGE INFO
export const getStorageInfo = () => (dispatch, getState) => {
    dispatch({type: REQUIEM_LOADING});
    axios
        .get(API_REQUIEM_GET_STORAGE_INFO, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_STORAGE_INFO,
                payload: res.data
            })
        })
        .catch(err => {
        })
};


// DOWNLOAD TORRENT
export const downloadTorrent = (id, data) => (dispatch, getState) => {
    const token = getState().auth.token;
    const formData = new FormData();
    for (let x = 0; x < data['files'].length; x++) formData.append('file-' + x, data['files'][x]);
    formData.append('disk', data['disk']);
    formData.append('magnet', data['magnet']);
    formData.append('extra_ids', data['extra_ids']);
    formData.append('extract_subs', data['extract_subs']);
    formData.append('additional_info', data['additional_info']);

    const config = {
        headers: {
            'content-type': 'multipart/form-data',
            'Content-Type': 'application/json'
        }
    };

    if (token) config.headers['Authorization'] = `Token ${token}`;

    axios
        .post(API_REQUIEM_DOWNLOAD_ANIME_TORRENT(id), formData, config)
        .then(res => {
            dispatch(createMessage('Torrent is downloading', Alert.type_success));
            dispatch(getAnime(id));
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// DELETE ANIME_SERVE_INFO AND ITS TORRENTS
export const deleteAnimeServerInfoAndTorrents = (id) => (dispatch, getState) => {
    let config = tokenConfig(getState);
    config['headers']['withCredentials'] = true;

    axios
        .get(API_REQUIEM_DELETE_ANIME_SERVER_INFO_AND_TORRENTS(id), config)
        .then(res => {
            dispatch(getAnime(id))
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


