// ADD ANIME TO SERVER
export const addAnimeToServer = animeInfo => (dispatch, getState) => {
    const body = JSON.stringify(animeInfo);

    axios
        .post(API_ADD_ANIME_TO_SERVER, body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('The Anime has been added to the server', Alert.type_success));
            dispatch(getAnime(animeInfo['anime']))
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// UPDATE ANIME TO SERVER
export const updateAnimeToServer = animeInfo => (dispatch, getState) => {
    const body = JSON.stringify(animeInfo);

    axios
        .put(API_UPDATE_ANIME_TO_SERVER(animeInfo['anime']), body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('The Anime Server Info has been updated successfully!', Alert.type_success));
            dispatch(getAnime(animeInfo['anime']))
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// GET NEWS
export const getNews = (page) => (dispatch, getState) => {
    dispatch({type: NEWS_LOADING});
    let config = tokenConfig(getState);
    if (page) if (page !== 1) config.params = {'page': page};


    axios
        .get(API_GET_NEWS, config)
        .then(res => {
            dispatch({
                type: GET_NEWS,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch(returnErrors(err.response.data, err.response.status));
        })
};


