import React, {useEffect, useRef, useState} from "react";
import {CyberButton} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlus} from "@fortawesome/free-solid-svg-icons";
import store from "../../../../store";
import {updateAnimeEpisode} from "../../../../actions/users";
import {Anime, UserAnimeEntry} from "../../../../models";

import '../../../../assets/css/users.css'
import {playClickSound, playHoverSound} from "../../../layout/sounds";
import CircularProgress from "@material-ui/core/CircularProgress";
import {isEmpty} from "../../../functions";


/**
 * Increase episode number button.
 * @param props.classNames {String} Classnames for the button
 * @param props.id {Number} ID of the anime to update
 * @param props.entry {Object} The user anime entry
 * @param props.list {Object} List object of {user_list_pk: user_list_filter:} if its called from an anime list
 * @returns {JSX.Element}
 * @constructor
 */
export default function UpdateAnimeEpisodeButton(props) {
    const {classNames, id, entry, list} = props;
    const [loading, setLoading] = useState(false);

    if (!store.getState().users.entry_loading && loading)
        setLoading(false);

    if (entry[UserAnimeEntry.watched_episodes] === entry[UserAnimeEntry.anime][Anime.episodes])
        return null;
    return !loading ?
        <span
            className="update-anime-episode-btn"
            onMouseEnter={playHoverSound}
            onClick={() => {
                playClickSound();
                setLoading(true);
                if (entry[UserAnimeEntry.watched_episodes] !== entry[UserAnimeEntry.anime][Anime.episodes])
                    store.dispatch(updateAnimeEpisode(id, list))
            }}
        >
            <FontAwesomeIcon icon={faPlus}/>
        </span> : <span className="update-anime-episode-btn"><CircularProgress thickness={6}/></span>
}
