import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload} from "@fortawesome/free-solid-svg-icons";

import {CyberButton} from "../../layout/common/common";
import DownloadTorrentForm from "./DownloadTorrentForm";


class DownloadTorrentModalForm extends Component {
    static propTypes = {
        /** classNames */
        classNames: PropTypes.string,
    };

    constructor() {
        super(...arguments);
        this.handleClickOpen = this.handleClickOpen.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.state = {
            open: false,
        };
    }

    handleClickOpen() {
        this.setState({open: true})
    };

    handleClose() {
        this.setState({open: false})
    };

    render() {
        return (
            <div id="Download-Anime-Torrent-Modal" className={this.props.classNames}>
                <CyberButton classNames="btn-contained-secondary" onClick={this.handleClickOpen}>
                    <FontAwesomeIcon icon={faDownload}/>
                </CyberButton>
                <Dialog open={this.state.open} onClose={this.handleClose} aria-labelledby="form-dialog-title"
                        className="download-torrent-dialog">
                        <DialogTitle id="form-dialog-title">Download torrent</DialogTitle>
                        <DialogContent>
                            <DownloadTorrentForm handleClose={this.handleClose}/>
                        </DialogContent>
                </Dialog>
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {})(DownloadTorrentModalForm)
