import {
    CANCEL_CHARACTERS_LOADING,
    CHARACTERS_LOADING,
    FILTER_CHARACTERS,
    GET_CHARACTER,
} from "../actions/types";
import {playExpandSound} from "../components/layout/sounds";

const initialState = {
    characters_loading: false,
    character: {},
    characters_loaded: false,

    filtered_characters: {},
    filtered_characters_loaded: false,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default function (state = initialState, action) {
    switch (action.type) {
        case CHARACTERS_LOADING:
            return {
                ...state,
                characters_loading: true,
                characters_loaded: false,
                filtered_characters_loaded: false,
            };
        case CANCEL_CHARACTERS_LOADING:
            return {
                ...state,
                characters_loading: false,
                characters_loaded: false,
                filtered_characters_loaded: false,
            };
        case GET_CHARACTER:
            playExpandSound();
            return {
                ...state,
                character: action.payload,
                characters_loading: false,
                characters_loaded: true
            };
        case FILTER_CHARACTERS:
            return {
                ...state,
                filtered_characters: action.payload,
                characters_loading: false,
                filtered_characters_loaded: true
            };
        default:
            return state
    }
}
