import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME} from "../../../globals";
import BrowseSeasons from "../../seasons/BrowseSeasons";


class BrowseAnimeSeasons extends Component {
    render() {
        return <BrowseSeasons
            branch={BRANCH_ANIME}
            data_loading={this.props.anime_loading}
            data_loaded={this.props.anime_data_loaded}
            data={this.props.anime_data}
        />
    }
}

const mapStateToProps = state => ({
    anime_data: state.anime.anime_data,
    anime_data_loaded: state.anime.anime_data_loaded,
    anime_loading: state.anime.anime_loading,
});

export default connect(mapStateToProps, {})(BrowseAnimeSeasons)
