import {CREATE_MESSAGE, GET_ERRORS, CLEAN_ERRORS, CLEAR_MESSAGE} from "./types";

// CREATE MESSAGE
export const createMessage = (msg, type) => {
    return {
        type: CREATE_MESSAGE,
        payload: {msg, type}
    };
};

// CLEAR MESSAGE
export const clearMessage = () => {
    return {
        type: CLEAR_MESSAGE,
    };
};

// RETURN ERRORS
export const returnErrors = (msg, status) => {
    return {
        type: GET_ERRORS,
        payload: {msg, status}
    };
};

// CLEAN ERRORS
export const cleanErrors = () => {
    return {
        type: CLEAN_ERRORS,
    };
};
