import * as Yup from "yup";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import {Form, Formik} from "formik";
import TextField from "@material-ui/core/TextField";
import {CyberButton} from "../common/common";
import React from "react";


export default function GoToPageDialog(props) {
    const {open, current_page, total_pages, onClose, goToPage} = props;

    const GoToPageSchema = Yup.object().shape({
        page_number: Yup.number()
            .min(1, "Page number must be above 1.")
            .max(total_pages, `Page number can't exceed the last page ${total_pages}.`)
            .required('Required'),
    });

    return (
        <Dialog onClose={onClose} aria-labelledby="go-to-page-dialog-title" open={open}>
            <div className="go-to-page-dialog">
                <DialogTitle id="go-to-page-dialog-title">Go to page</DialogTitle>

                <Formik
                    initialValues={{page_number: current_page}}
                    validationSchema={GoToPageSchema}
                    onSubmit={(values, {setSubmitting}) => {
                        goToPage(values.page_number);
                        onClose();
                        setSubmitting(false);
                    }}>
                    {({isSubmitting, errors, touched, values, handleChange, handleBlur}) => (
                        <Form className="content-container">
                            <TextField
                                label="Page"
                                type="number"
                                name="page_number"
                                value={values.page_number}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                error={errors.page_number && touched.page_number}
                                helperText={(errors.page_number && touched.page_number) && errors.page_number}/>
                            <CyberButton color="primary" type="submit" variant="contained"
                                         disabled={isSubmitting}>Go</CyberButton>
                        </Form>
                    )}
                </Formik>
            </div>
        </Dialog>
    );
}
