import React, {Component} from 'react'
import {connect} from 'react-redux'

import {
    BRANCH_CHARACTERS, CHARACTERS_ORDERINGS,
} from "../../../../globals";
import TitlesFilter from "../TitlesFilter";


class CharactersFilters extends Component {
    render() {
        return (
            <TitlesFilter
                branch={BRANCH_CHARACTERS}
                titles_loading={this.props.characters_loading}
                filtered_titles_loaded={this.props.filtered_characters_loaded}
                filtered_titles={this.props.filtered_characters}
                title_filters={this.props.characters_filters}
                order_filters={CHARACTERS_ORDERINGS}
            />
        )
    }
}

const mapStateToProps = state => ({
    characters_loading: state.characters.characters_loading,
    filtered_characters_loaded: state.characters.filtered_characters_loaded,
    filtered_characters: state.characters.filtered_characters,
    characters_filters: state.cookies.characters_filters,
});

export default connect(mapStateToProps, {})(CharactersFilters)
