import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_PEOPLE, PEOPLE_ORDERINGS} from "../../../../globals";
import TitlesFilter from "../TitlesFilter";


class PeopleFilters extends Component {
    render() {
        return (
            <TitlesFilter
                branch={BRANCH_PEOPLE}
                titles_loading={this.props.people_loading}
                filtered_titles_loaded={this.props.filtered_people_loaded}
                filtered_titles={this.props.filtered_people}
                title_filters={this.props.people_filters}
                order_filters={PEOPLE_ORDERINGS}
            />
        )
    }
}

const mapStateToProps = state => ({
    people_loading: state.people.people_loading,
    filtered_people_loaded: state.people.filtered_people_loaded,
    filtered_people: state.people.filtered_people,
    people_filters: state.cookies.people_filters,
});

export default connect(mapStateToProps, {})(PeopleFilters)
