export function getTitle(branch, id) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(getAnime(id));
            return;
        case BRANCH_MANGA:
            store.dispatch(getManga(id));
            return;
        case BRANCH_CHARACTERS:
            store.dispatch(getCharacter(id));
            return;
        case BRANCH_EROGE:
            store.dispatch(getEroge(id));
            return;
        case BRANCH_PEOPLE:
            store.dispatch(getPerson(id));
            return;
        default:
            return
    }
}


export function getUserTitleStatus(branch, id) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(getUserAnimeEntry(id));
            return;
        case BRANCH_MANGA:
            store.dispatch(getUserMangaEntry(id));
            return;
        default:
            return
    }
}

export function getUserTitleStatusSummary(branch, entry) {
    switch (branch) {
        case BRANCH_ANIME:
            return <UserAnimeEntryStatus entry={entry}/>;
        case BRANCH_MANGA:
            return <UserMangaEntryStatus entry={entry}/>;
        default:
            return null;
    }
}


function checkFavorites(favoriteTitles, currentTitleId) {
    if (favoriteTitles.length <= 0) return false;
    else
        for (let x = 0; x < favoriteTitles.length; x++) {
            if (favoriteTitles[x]['id'] === currentTitleId) return true;
        }
    return false
}


export function isTitleInFavorites(branch, id) {
    switch (branch) {
        case BRANCH_ANIME:
            let favorite_anime = store.getState().auth.user[User.profile][Profile.favorite_anime];
            return checkFavorites(favorite_anime, parseInt(id));
        case BRANCH_MANGA:
            let favorite_manga = store.getState().auth.user[User.profile][Profile.favorite_manga];
            return checkFavorites(favorite_manga, parseInt(id));
        case BRANCH_CHARACTERS:
            let favorite_characters = store.getState().auth.user[User.profile][Profile.favorite_characters];
            return checkFavorites(favorite_characters, parseInt(id));
        case BRANCH_PEOPLE:
            let favorite_people = store.getState().auth.user[User.profile][Profile.favorite_people];
            return checkFavorites(favorite_people, parseInt(id));
        default:
            return false;
    }
}


/**
 *
 * @param props.branch {String} Title Branch
 * @param props.id {Number} Title ID
 * @param props.entry {Object} Entry of the user
 * @param props.title {String} Title
 * @returns {JSX.Element}
 * @constructor
 */
export function UserModalForms(props) {
    const {branch, id, entry, title} = props;
    const in_favorites = isTitleInFavorites(branch, id);

    switch (branch) {
        case BRANCH_ANIME:
            return <>
                <AnimeEntryModalForm
                    id={id} title={title}
                    user_anime_entry={entry}/>
                {isEmpty(entry) ? <AddAnimeEntryInstantDropButton anime_id={parseInt(id)}/> : null}
                <CyberButton variant="outlined" color="primary" onClick={() => {
                    if (in_favorites)
                        store.dispatch(removeFromFavorites(branch, parseInt(id)));
                    else store.dispatch(addToFavorites(branch, parseInt(id)));
                }}>
                    <FontAwesomeIcon icon={in_favorites ? faStarSolid : faStar}/>
                </CyberButton>
                <CyberButton disabled={true} variant="outlined" color="primary">
                    <FontAwesomeIcon icon={faPlusSquare}/>
                </CyberButton>
                <AnimeEpisodeHistoryModal title={title} entry={entry}/>
            </>;
        case BRANCH_MANGA:
            return <>
                <MangaEntryModalForm
                    id={id} title={title}
                    user_manga_entry={entry}/>
                {isEmpty(entry) ? <AddMangaEntryInstantDropButton manga_id={parseInt(id)}/> : null}
                <CyberButton variant="outlined" color="primary" onClick={() => {
                    if (in_favorites)
                        store.dispatch(removeFromFavorites(branch, parseInt(id)));
                    else store.dispatch(addToFavorites(branch, parseInt(id)));
                }}>
                    <FontAwesomeIcon icon={in_favorites ? faStarSolid : faStar}/>
                </CyberButton>
                <CyberButton disabled={true} variant="outlined" color="primary">
                    <FontAwesomeIcon icon={faPlusSquare}/>
                </CyberButton>
                <MangaVolumeChapterHistoryModal title={title} entry={entry}/>
            </>;
        case BRANCH_CHARACTERS:
            return <>
                <CyberButton variant="outlined" color="primary" onClick={() => {
                    if (in_favorites)
                        store.dispatch(removeFromFavorites(branch, parseInt(id)));
                    else store.dispatch(addToFavorites(branch, parseInt(id)));
                }}>
                    <FontAwesomeIcon icon={in_favorites ? faStarSolid : faStar}/>
                </CyberButton>
            </>;
        case BRANCH_PEOPLE:
            return <>
                <CyberButton variant="outlined" color="primary" onClick={() => {
                    if (in_favorites)
                        store.dispatch(removeFromFavorites(branch, parseInt(id)));
                    else store.dispatch(addToFavorites(branch, parseInt(id)));
                }}>
                    <FontAwesomeIcon icon={in_favorites ? faStarSolid : faStar}/>
                </CyberButton>
            </>;
        default:
            return null;
    }
}


