export function formatTitle(branch, title, superuser) {
    let display_title = {
        id: 0,
        cover_images: [],
        cdn_cover_images: [],
        title: null,
        title_1: null,
        title_2: null,
        title_3: null,
        info_set: null,
        db_info_set: null,
        users_info_set: null,
        primary_set: null,
        primary_set_wide: null,
        secondary_set: null,
        tabs: null
    };

    switch (branch) {
        case BRANCH_ANIME:
            display_title.id = title[Anime.id];
            display_title.cover_images = title[Anime.anime_cover_images] ? title[Anime.anime_cover_images] : [];
            display_title.cdn_cover_images = title[Anime.anime_cdn_cover_images] ? title[Anime.anime_cdn_cover_images] : [];
            display_title.title = title[Anime.title];
            display_title.title_1 = {label: 'English', title: title[Anime.title_english]};
            display_title.title_2 = {label: 'Synonyms', title: title[Anime.title_synonyms]};
            display_title.title_3 = {label: 'Japanese', title: title[Anime.title_japanese]};
            display_title.info_set = [
                {label: 'Sources', info: <a href={title[Anime.mal_link]}>MAL</a>},
                {label: 'Aired', info: title[Anime.aired_string]},
                {label: 'Premiered', info: title[Anime.premiered]},
                {label: 'Broadcast', info: title[Anime.broadcast]},
                {label: 'Producer', info: title[Anime.producer]},
                {label: 'Licensor', info: title[Anime.licensor]},
                {label: 'Studio', info: title[Anime.studio]},
                {label: 'Source', info: title[Anime.source]},
            ];
            display_title.db_info_set = [
                {
                    label: 'Last Modified',
                    info: <Moment date={title[Anime.date_last_modified]} format={'Do MMM YYYY'}/>
                },
                {label: 'Added on', info: <Moment date={title[Anime.date_added]} format={'Do MMM YYYY'}/>},
            ];
            display_title.users_info_set = [
                {label: 'Score', info: title[Anime.score]},
                {label: 'Scored by', info: title[Anime.scored_by]},
                {label: 'Rank', info: title[Anime.rank]},
                {label: 'Popularity', info: title[Anime.popularity]},
                {label: 'Members', info: title[Anime.members]},
                {label: 'Favorites', info: title[Anime.favorites]},
            ];
            display_title.primary_set = [
                {label: 'Status', info: title[Anime.status]},
                {label: 'Type', info: title[Anime.type]},
                {label: 'Episodes', info: title[Anime.episodes] ? title[Anime.episodes] : '?'},
                {
                    label: 'Duration',
                    info: title[Anime.minutes_per_episode] ? `${title[Anime.minutes_per_episode]}m per ep` : '?'
                },
                {label: 'Rating', info: title[Anime.rating_short]},
                {
                    label: 'Aired',
                    info: formatContentDateByKnown(title[Anime.air_start], title[Anime.air_start_known], true)
                },
            ];
            display_title.tabs = [
                {
                    disabled: false,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faInfo}/> General</p>,
                    tab: <GeneralTab/>
                },
                {
                    disabled: title[Anime.main_characters].length === 0,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faUserFriends}/> Characters</p>,
                    tab: <CharactersTab/>
                },
                {
                    disabled: !title[Anime.anime_relations],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faCodeBranch}/> Relations</p>,
                    tab: <RelationsTab/>
                },
                {
                    disabled: !title[Anime.franchise],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faSitemap}/> Franchise</p>,
                    tab: <FranchiseTab/>
                },
            ];

            if (display_title.primary_set && title[Anime.genres_csv]) {
                const genres = title[Anime.genres_csv].split(', ');
                let info = [];
                genres.forEach(genre => {
                    info.push(<span className="genre-spacing">{genre}</span>)
                });
                display_title.primary_set_wide = [
                    {label: 'Genres', info: info},
                ];
            }
            if (title[Anime.anime_on_server]) {
                if (title[Anime.anime_on_server][AnimeOnServer.downloading]) {
                    if (superuser)
                        display_title.secondary_set = [{
                            label: null, info: <DisplayTorrentProgress id={title[Anime.id]}/>
                        }];
                    else
                        display_title.secondary_set = [{
                            label: 'Currently Downloading', info: <CircularProgress color="secondary"/>
                        }];
                } else
                    display_title.secondary_set = [
                        {label: 'Size', info: `${title[Anime.anime_on_server][AnimeOnServer.size_on_HDD_GB]}GB`},
                        {label: 'Quality', info: title[Anime.anime_on_server][AnimeOnServer.video_quality]},
                        {label: 'Channel', info: title[Anime.anime_on_server][AnimeOnServer.audio_channel]},
                        {
                            label: 'Japanese Dub',
                            info: title[Anime.anime_on_server][AnimeOnServer.japanese_dub] ? 'Yes' : 'No'
                        },
                        {
                            label: 'English  Sub',
                            info: title[Anime.anime_on_server][AnimeOnServer.english_sub] ? 'Yes' : 'No'
                        },
                        {
                            label: null,
                            info: <CyberButton variant="outlined" color="secondary"><FontAwesomeIcon
                                icon={faPlay}/></CyberButton>
                        },
                    ];
                    display_title.tabs.push({
                    disabled: false,
                    label: <p className="list-icon color-secondary"><FontAwesomeIcon icon={faServer}/> Server</p>,
                    tab: <ServerTab/>
                });
            }
            return display_title;
        case BRANCH_MANGA:
            display_title.id = title[Manga.id];
            display_title.cover_images = title[Manga.manga_cover_images];
            display_title.cdn_cover_images = title[Manga.manga_cdn_cover_images] ? title[Manga.manga_cdn_cover_images] : [];
            display_title.title = title[Manga.title];
            display_title.title_1 = {label: 'English', title: title[Manga.title_english]};
            display_title.title_2 = {label: 'Synonyms', title: title[Manga.title_synonyms]};
            display_title.title_3 = {label: 'Japanese', title: title[Manga.title_japanese]};
            display_title.info_set = [
                {label: 'Sources', info: <a href={title[Manga.mal_link]}>MAL</a>},
                {label: 'Published', info: title[Manga.published_string]},
                {label: 'Authors', info: title[Manga.authors]},
                {label: 'Serialization', info: title[Manga.serialization]},
            ];
            display_title.db_info_set = [
                {
                    label: 'Last Modified',
                    info: <Moment date={title[Manga.date_last_modified]} format={'Do MMM YYYY'}/>
                },
                {label: 'Added on', info: <Moment date={title[Manga.date_added]} format={'Do MMM YYYY'}/>},
            ];
            display_title.users_info_set = [
                {label: 'Score', info: title[Manga.score]},
                {label: 'Scored by', info: title[Manga.scored_by]},
                {label: 'Rank', info: title[Manga.rank]},
                {label: 'Popularity', info: title[Manga.popularity]},
                {label: 'Members', info: title[Manga.members]},
                {label: 'Favorites', info: title[Manga.favorites]},
            ];
            display_title.primary_set = [
                {label: 'Status', info: title[Manga.status]},
                {label: 'Type', info: title[Manga.type]},
                {label: 'Volumes', info: title[Manga.volumes] ? title[Manga.volumes] : '?'},
                {label: 'Chapters', info: title[Manga.chapters] ? title[Manga.chapters] : '?'},
                {
                    label: 'Published',
                    info: formatContentDateByKnown(title[Manga.air_start], title[Manga.air_start_known], true)
                },
            ];
            display_title.tabs = [
                {
                    disabled: false,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faInfo}/> General</p>,
                    tab: <MangaGeneralTab/>
                },
                {
                    disabled: title[Manga.main_characters].length === 0,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faUserFriends}/> Characters</p>,
                    tab: <MangaCharactersTab/>
                },
                {
                    disabled: !title[Manga.manga_relations],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faCodeBranch}/> Relations</p>,
                    tab: <MangaRelationsTab/>
                },
            ];
            if (display_title.primary_set && title[Manga.genres_csv]) {
                const genres = title[Manga.genres_csv].split(', ');
                let info = [];
                genres.forEach(genre => {
                    info.push(<span className="genre-spacing">{genre}</span>)
                });
                display_title.primary_set_wide = [
                    {label: 'Genres', info: info},
                ];
            }
            return display_title;
        case BRANCH_CHARACTERS:
            display_title.id = title[Character.id];
            display_title.cover_images = title[Character.character_cover_images] ? title[Character.character_cover_images] : [];
            display_title.cdn_cover_images = title[Character.character_cdn_cover_images] ? title[Character.character_cdn_cover_images] : [];
            display_title.title = title[Character.short_name] ? title[Character.short_name] : title[Character.character_name];
            display_title.title_1 = {label: 'Full Name', title: title[Character.character_name]};
            display_title.title_2 = {label: 'Nickname', title: title[Character.nickname]};
            display_title.title_3 = {label: 'Name', title: title[Character.short_name]};
            display_title.info_set = [
                {label: 'Sources', info: <a href={title[Character.mal_link]}>MAL</a>},
            ];
            display_title.db_info_set = [
                {
                    label: 'Last Modified',
                    info: <Moment date={title[Character.date_last_modified]} format={'Do MMM YYYY'}/>
                },
                {label: 'Added on', info: <Moment date={title[Character.date_added]} format={'Do MMM YYYY'}/>},
            ];
            display_title.users_info_set = [
                {label: 'Favorites', info: title[Character.favorites]},
            ];
            display_title.tabs = [
                {
                    disabled: false,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faInfo}/> General</p>,
                    tab: <CharactersGeneralTab/>
                },
            ];
            return display_title;
        case BRANCH_EROGE:
            display_title.id = title[Eroge.id];
            if (title[Eroge.eroge_cover_images])
                display_title.cover_images = title[Eroge.eroge_cover_images];
            display_title.title = title[Eroge.eroge_name];
            display_title.info_set = [
                {label: 'Sources', info: <a href={title[Eroge.game_url]}>HG</a>},
                {
                    label: 'External download',
                    info: <a href={title[Eroge.game_url]}><FontAwesomeIcon icon={faDownload}/></a>
                },
            ];
            display_title.db_info_set = [
                {label: 'Last Modified', info: <Moment date={title[Eroge.date_last_modified]} format={'Do MMM YYYY'}/>},
                {label: 'Added on', info: <Moment date={title[Eroge.date_added]} format={'Do MMM YYYY'}/>},
                {label: 'Last Updated', info: <Moment date={title[Eroge.date_updated]} format={'Do MMM YYYY'}/>},
            ];
            display_title.users_info_set = [
                {label: 'Score', info: title[Eroge.rating]},
                {label: 'Scored by', info: title[Eroge.rated_by]},
            ];
            display_title.primary_set = [
                {label: 'Size', info: formatBytes(title[Eroge.size])},
                {label: 'Type', info: title[Eroge.type]},
                {label: 'Censorship', info: title[Eroge.censorship]},
                {label: 'Language', info: title[Eroge.language]},
                {label: 'Platforms', info: title[Eroge.platforms]},
            ];
            display_title.tabs = [
                {
                    disabled: false,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faInfo}/> General</p>,
                    tab: <ErogeGeneralTab/>
                },
            ];
            if (display_title.primary_set && title[Eroge.tags]) {
                const tags = title[Eroge.tags].split(', ');
                let info = [];
                tags.forEach(tag => {
                    info.push(<span className="genre-spacing">{tag}</span>)
                });
                display_title.primary_set_wide = [
                    {label: 'Tags', info: info},
                ];
            }
            return display_title;
        case BRANCH_PEOPLE:
            display_title.id = title[Person.id];
            display_title.cover_images = title[Person.person_cover_images] ? title[Person.person_cover_images] : [];
            display_title.cdn_cover_images = title[Person.person_cdn_cover_images] ? title[Person.person_cdn_cover_images] : [];
            display_title.title = title[Person.person_name];
            display_title.title_1 = {label: 'Family Name', title: title[Person.family_name]};
            display_title.title_2 = {label: 'Given Name', title: title[Person.given_name]};
            display_title.title_3 = {label: 'Other Names', title: title[Person.alternative_names]};
            display_title.info_set = [
                {label: 'Sources', info: <a href={title[Person.mal_link]}>MAL</a>},
                {label: 'Birthday', info: title[Person.birthday] ?
                        <Moment date={title[Person.birthday]} format={'Do MMM YYYY'}/> :
                        <span className="color-gray">---</span>},
            ];
            display_title.db_info_set = [
                {
                    label: 'Last Modified',
                    info: <Moment date={title[Person.date_last_modified]} format={'Do MMM YYYY'}/>
                },
                {label: 'Added on', info: <Moment date={title[Person.date_added]} format={'Do MMM YYYY'}/>},
            ];
            display_title.users_info_set = [
                {label: 'Favorites', info: title[Person.favorites]},
            ];
            display_title.tabs = [
                {
                    disabled: false,
                    label: <p className="list-icon"><FontAwesomeIcon icon={faInfo}/> General</p>,
                    tab: <PeopleGeneralTab/>
                },
                {
                    disabled: !title[Person.has_roles],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faUserFriends}/> Character Roles</p>,
                    tab: <VoiceActorRolesTab/>
                },
                {
                    disabled: !title[Person.anime_staff_positions_roles],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faUserCog}/> Anime Staff</p>,
                    tab: <AnimeStaffPositionsTab/>
                },
                {
                    disabled: !title[Person.published_manga_roles],
                    label: <p className="list-icon"><FontAwesomeIcon icon={faBookOpen}/> Published Manga</p>,
                    tab: <PublishedMangaTab/>
                },
            ];
            return display_title;
        default:
            return display_title
    }
}
