import React, {Component, Fragment} from "react";
import {withAlert} from "react-alert";
import {connect} from "react-redux";
import {Alert} from "../../../models";
import store from "../../../store";
import {cleanErrors, clearMessage} from "../../../actions/messages";
import {isEmpty} from "../../functions";


export class Alerts extends Component {
    componentDidUpdate(prevProps, prevState, snapshot) {
        const {error, alert, message} = this.props;
        if (error.status !== prevProps.error.status) {
            console.log(error.message);
            let msg = '';
            if (!isEmpty(error.message))
                Object.keys(error.message).forEach(key=>{
                   msg += `\n${error.message[key]}`
                });

            if (error.status !== null || error.message !== null) {
                alert.error(`${error.status ? `Status: ${error.status}` : ''} ${msg}`, {
                    onOpen: () => {
                        store.dispatch(cleanErrors());
                    }
                });
            }
        }

        if (message !== prevProps.message) {
            if (this.props.type === Alert.type_success)
                alert.success(message, {
                    onOpen: () => {
                        store.dispatch(clearMessage());
                        store.dispatch(cleanErrors());
                    }
                });
            else if (this.props.type === Alert.type_info)
                alert.info(message, {
                    onOpen: () => {
                        store.dispatch(clearMessage());
                        store.dispatch(cleanErrors());
                    }
                });
            else if (this.props.type === Alert.type_error)
                alert.error(message, {
                    onOpen: () => {
                        store.dispatch(clearMessage());
                        store.dispatch(cleanErrors());
                    }
                });
        }

    }

    render() {
        return <Fragment/>;
    }
}

const mapStateToProps = state => ({
    error: state.errors,
    message: state.success.message,
    type: state.success.type
});

export default connect(mapStateToProps)(withAlert()(Alerts));
