import {
    ANIME_LOADING,
    CANCEL_CHARACTERS_LOADING,
    CANCEL_PEOPLE_LOADING,
    CHARACTERS_LOADING,
    FILTER_CHARACTERS,
    FILTER_PEOPLE,
    GET_ALL_CHARACTERS_FOR_ANIME, GET_ANIME_STAFF_POSITIONS_FOR_PERSON,
    GET_PERSON, GET_PUBLISHED_MANGA_FOR_PERSON, GET_VOICE_ACTOR_ROLES_FOR_PERSON, MANGA_LOADING,
    PEOPLE_LOADING
} from "./types";
import axios from "axios";
import {
    API_FILTER_PEOPLE,
    API_GET_ANIME_STAFF_POSITIONS_FOR_PERSON,
    API_GET_ONE_PERSON, API_GET_PUBLISHED_MANGA_FOR_PERSON,
    API_GET_VOICE_ACTOR_ROLES_FOR_PERSON
} from "./api_urls";
import {returnErrors} from "./messages";
import {tokenConfig} from "./auth";
import {isEmpty} from "../components/functions";
import {setCookieCharactersFilters, setCookiePeopleFilters} from "./cookies";
import {CharactersFilterNames, PeopleFilterNames} from "../components/layout/filters/field_names";
import {cancelAnimeLoading} from "./anime";
import {cancelCharactersLoading} from "./characters";
import {cancelMangaLoading} from "./manga";
import {MANGA_ORDERINGS} from "../globals";


// GET A ONE PERSON
export const getPerson = id => (dispatch, getState) => {
    dispatch({type: PEOPLE_LOADING});
    axios
        .get(API_GET_ONE_PERSON(id), tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_PERSON,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch({type: CANCEL_PEOPLE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// GET VOICE ACTOR ROLES (CHARACTERS) FOR PERSON
export const getVoiceActorRolesForPerson = (id, page) => (dispatch, getState) => {
    dispatch({type: CHARACTERS_LOADING});
    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;

    axios
        .get(API_GET_VOICE_ACTOR_ROLES_FOR_PERSON(id), config)
        .then(res => {
            dispatch(cancelCharactersLoading());
            dispatch({
                type: GET_VOICE_ACTOR_ROLES_FOR_PERSON,
                payload: res.data
            })
        })
        .catch(err => {
        })
};


