// FILTER ALL PEOPLE
export const filterPeople = (page, filters, no_loading) => (dispatch, getState) => {
    dispatch({type: PEOPLE_LOADING});

    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;
    if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
        config.params = setPeopleFilterParams(config.params, filters);

    axios
        .get(API_FILTER_PEOPLE, config)
        .then(res => {
            dispatch({
                type: FILTER_PEOPLE,
                payload: res.data
            });
            if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
                dispatch(setCookiePeopleFilters(filters))
        })
        .catch(err => {
            dispatch({type: CANCEL_PEOPLE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


export function setPeopleFilterParams(params, filters) {
    if (filters[PeopleFilterNames.search] && typeof filters[PeopleFilterNames.search] == 'string')
        params[PeopleFilterNames.search] = filters[PeopleFilterNames.search];
    if (filters[PeopleFilterNames.ordering])
        params[PeopleFilterNames.ordering] = filters[PeopleFilterNames.ordering];
    return params
}
