import React from 'react';
import ReactDOM from 'react-dom';
import {Provider} from 'react-redux'
import store from './store'
import reportWebVitals from './reportWebVitals';

import {createMuiTheme, ThemeProvider} from '@material-ui/core/styles';
import {Provider as AlertProvider} from 'react-alert'
import yourRawTheme from './theme.json';

import {AlertOptions, MyAlertTemplate} from "./components/layout/alerts/AlertTemplate";
import App from './components/App';

import './index.css';
import './assets/css/cyber-ui.css';

const theme = createMuiTheme(yourRawTheme);


ReactDOM.render(
    <React.StrictMode>
        <Provider store={store}>
            <ThemeProvider theme={theme}>
                <AlertProvider template={MyAlertTemplate} {...AlertOptions}>
                    <App/>
                </AlertProvider>
            </ThemeProvider>
        </Provider>
    </React.StrictMode>,
    document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
