import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'

import {getAnimeFranchise} from '../../../actions/anime'

import FranchiseTree from "./FranchiseTree";
import CyberFooter from "../../layout/footer/CyberFooter";
import CyberHeader from "../../layout/navbar/CyberHeader";
import {FRANCHISES_TAB_NAME} from "../../../globals";
import {isEmpty} from "../../functions";
import {AnimeFranchise} from "../../../models";
import Loading from "../../layout/common/Loading";

import '../../../assets/css/franchises.css'
import {getLoggedInUserAnimeEntryStatuses} from "../../../actions/users";


class FranchisesHome extends Component {
    constructor() {
        super(...arguments);
    }

    componentDidMount() {
        document.title = FRANCHISES_TAB_NAME;
        window.scrollTo(0, 0);
        this.props.getAnimeFranchise(this.props.match.params.id);
        this.props.getLoggedInUserAnimeEntryStatuses();
    }

    render() {
        // document.title = `${current_anime_franchise['title']}`;
        let titles = [];
        let avg_score;

        // try {
        //     if (current_anime_franchise['anime_franchise'].length > 0)
        //         titles = current_anime_franchise['anime_franchise'];
        //     avg_score = current_anime_franchise['average_score'].toFixed(2)
        // } catch (e) {
        // }

        return (
            <div id="Display-Franchise">
                <CyberHeader/>
                {!isEmpty(this.props.anime_franchise) ?
                    <FranchiseTree titles={this.props.anime_franchise[AnimeFranchise.anime_franchise]}/>
                : this.props.anime_franchise_loading ? <Loading text={'Franchise Loading'}/> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime_franchise_loading: state.anime.anime_franchise_loading,
    anime_franchise: state.anime.anime_franchise,
});

export default connect(mapStateToProps, {getAnimeFranchise, getLoggedInUserAnimeEntryStatuses})(FranchisesHome)