import React, { useState } from 'react';

import Switch from "@material-ui/core/Switch";

import store from "../../../../store";
import {Profile, User} from "../../../../models";
import {
    updateUserAnimeHistoryPrivacy,
    updateUserAnimeListPrivacy, updateUserMangaHistoryPrivacy,
    updateUserMangaListPrivacy
} from "../../../../actions/users";

/**
 *
 * @param {Object} props.user
 * @returns {JSX.Element}
 * @constructor
 */
export function ToggleUserAnimeListPrivacyForm(props) {
    const {user} = props;
    const [IsPrivate, setIsPrivate] = useState(user[User.profile][Profile.is_anime_list_private]);

    return <Switch
        checked={IsPrivate}
        onChange={() => {
            setIsPrivate(!IsPrivate);
            store.dispatch(updateUserAnimeListPrivacy(!IsPrivate));
        }}
        color="primary"
        name="checkedB"
        inputProps={{'aria-label': 'primary checkbox'}}
    />
}

