import React from "react";


import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import LinearProgress from "@material-ui/core/LinearProgress";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheckCircle} from "@fortawesome/free-regular-svg-icons";
import Tooltip from "@material-ui/core/Tooltip";
import BBCode from '@bbob/react/es/Component'
import reactPreset from '@bbob/preset-react/es'

import {isEmpty} from "../../functions";
import {AchievementInfo, AchievementTier, AnimeDataStatistics, UserStatistics} from "../../../models";

const unknown = 'https://ik.imagekit.io/r0d4cpkoj/Achievements/unknown_g6qPdfP00nE.jpg';


/**
 *
 * @param tiers
 * @param achievements_info
 * @param user_achievements
 * @param user_progress
 * @param user_tier_1
 * @param user_tier_2
 * @param user_tier_3
 * @param user_tier_4
 * @returns {{user_progress: *, requirement: *, title: *, tier_2: {image: *, description: *, threshold: *, title: *, user_completed: *}, tier_1: {image: *, description: *, threshold: *, title: *, user_completed: *}, tier_4: {image: *, description: *, threshold: *, title: *, user_completed: *}, tier_3: {image: *, description: *, threshold: *, title: *, user_completed: *}}}
 */
function parseGenreAchievementFromResult(tiers, achievements_info, user_achievements, user_progress, user_tier_1, user_tier_2, user_tier_3, user_tier_4) {
    return {
        title: achievements_info[AchievementInfo.title],
        requirement: achievements_info?.[AchievementInfo.requirement],
        user_progress: user_progress,
        tier_1: {
            title: achievements_info[AchievementInfo.tier_1][AchievementTier.title],
            description: achievements_info[AchievementInfo.tier_1][AchievementTier.description],
            user_completed: user_tier_1,
            image: achievements_info[AchievementInfo.tier_1][AchievementTier.image],
            threshold: tiers[0],
        },
        tier_2: {
            title: achievements_info[AchievementInfo.tier_2][AchievementTier.title],
            description: achievements_info[AchievementInfo.tier_2][AchievementTier.description],
            user_completed: user_tier_2,
            image: achievements_info[AchievementInfo.tier_2][AchievementTier.image],
            threshold: tiers[1],
        },
        tier_3: {
            title: achievements_info[AchievementInfo.tier_3][AchievementTier.title],
            description: achievements_info[AchievementInfo.tier_3][AchievementTier.description],
            user_completed: user_tier_3,
            image: achievements_info[AchievementInfo.tier_3][AchievementTier.image],
            threshold: tiers[2],
        },
        tier_4: {
            title: achievements_info[AchievementInfo.tier_4][AchievementTier.title],
            description: achievements_info[AchievementInfo.tier_4][AchievementTier.description],
            user_completed: user_tier_4,
            image: achievements_info[AchievementInfo.tier_4][AchievementTier.image],
            threshold: tiers[3],
        }
    }
}


export function getUserGenreAchievements(user_achievements, achievement_thresholds) {
    let achievements = [];
    let tiers = [20, 40, 80, 160];
    let achievements_info = {
        title: <CircularProgress/>,
        requirement: '',
        user_progress: 0,
        tier_1: {
            title: '',
            description: '',
            user_completed: 0,
            image: unknown,
            threshold: tiers[0],
        },
        tier_2: {
            title: '',
            description: '',
            user_completed: 0,
            image: unknown,
            threshold: tiers[1],
        },
        tier_3: {
            title: '',
            description: '',
            user_completed: 0,
            image: unknown,
            threshold: tiers[2],
        },
        tier_4: {
            title: '',
            description: '',
            user_completed: 0,
            image: unknown,
            threshold: tiers[3],
        }
    };

    if (isEmpty(achievement_thresholds)) return;

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_romance_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_romance_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.romance_progress],
        user_achievements[UserStatistics.romance_1], user_achievements[UserStatistics.romance_2],
        user_achievements[UserStatistics.romance_3], user_achievements[UserStatistics.romance_4]
    ));

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_pervert_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_pervert_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.pervert_progress],
        user_achievements[UserStatistics.pervert_1], user_achievements[UserStatistics.pervert_2],
        user_achievements[UserStatistics.pervert_3], user_achievements[UserStatistics.pervert_4]
    ));

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_classics_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_classics_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.classics_progress],
        user_achievements[UserStatistics.classics_1], user_achievements[UserStatistics.classics_2],
        user_achievements[UserStatistics.classics_3], user_achievements[UserStatistics.classics_4]
    ));

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_dementia_psychological_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_dementia_psychological_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.dementia_psychological_progress],
        user_achievements[UserStatistics.dementia_psychological_1], user_achievements[UserStatistics.dementia_psychological_2],
        user_achievements[UserStatistics.dementia_psychological_3], user_achievements[UserStatistics.dementia_psychological_4]
    ));

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_horror_thriller_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_horror_thriller_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.horror_thriller_progress],
        user_achievements[UserStatistics.horror_thriller_1], user_achievements[UserStatistics.horror_thriller_2],
        user_achievements[UserStatistics.horror_thriller_3], user_achievements[UserStatistics.horror_thriller_4]
    ));

    tiers = achievement_thresholds[AnimeDataStatistics.anime_achievement_mystery_thresholds].split(',');
    achievements_info = JSON.parse(achievement_thresholds[AnimeDataStatistics.anime_achievement_mystery_info]);
    achievements.push(parseGenreAchievementFromResult(
        tiers, achievements_info, user_achievements, user_achievements[UserStatistics.mystery_progress],
        user_achievements[UserStatistics.mystery_1], user_achievements[UserStatistics.mystery_2],
        user_achievements[UserStatistics.mystery_3], user_achievements[UserStatistics.mystery_4]
    ));

    return achievements;
}


/**
 *
 * @param {Object} achievement
 * @param {string} achievement.title
 * @param {int} achievement.user_progress
 * @param {string} achievement.tier_1.title
 * @param {string} achievement.tier_1.description
 * @param {string} achievement.tier_1.image
 * @param {int} achievement.tier_1.user_completed
 * @param {int} achievement.tier_1.threshold
 * @param {string} achievement.tier_2.title
 * @param {string} achievement.tier_2.description
 * @param {string} achievement.tier_2.image
 * @param {int} achievement.tier_2.user_completed
 * @param {int} achievement.tier_2.threshold
 * @param {string} achievement.tier_3.title
 * @param {string} achievement.tier_3.description
 * @param {string} achievement.tier_3.image
 * @param {int} achievement.tier_3.user_completed
 * @param {int} achievement.tier_3.threshold
 * @param {string} achievement.tier_4.title
 * @param {string} achievement.tier_4.description
 * @param {string} achievement.tier_4.image
 * @param {int} achievement.tier_4.user_completed
 * @param {int} achievement.tier_4.threshold
 */
export function getActiveGenreDescription(achievement) {
    if (achievement.tier_4.user_completed)
        return achievement.tier_4.description;
    else if (achievement.tier_3.user_completed)
        return achievement.tier_3.description;
    else if (achievement.tier_2.user_completed)
        return achievement.tier_2.description;
    else if (achievement.tier_1.user_completed)
        return achievement.tier_1.description;
    return '???'
}


/**
 *
 * @param {string} props.title
 * @param {boolean} props.previous_completed
 * @param {int} props.tier_number
 * @param {boolean} props.user_completed
 * @param {int} props.user_progress
 * @param {int} props.threshold
 * @param {string} props.image
 * @returns {JSX.Element}
 * @constructor
 */
export function GenreAchievementSegment(props) {
    const {title, previous_completed, tier_number, user_completed, user_progress, threshold, image} = props;
    let tier_1_value;
    let tier_1_buffer;
    if (user_completed) {
        tier_1_value = 100;
        tier_1_buffer = 0
    } else if (previous_completed) {
        let progress = user_progress;
        let new_threshold = threshold;
        if (tier_number !== 1) {
            progress = user_progress - (threshold / 2);
            new_threshold = threshold - (threshold / 2);
        }
        tier_1_value = Math.round((progress / new_threshold) * 100);
        tier_1_buffer = 100
    } else {
        tier_1_value = 0;
        tier_1_buffer = 100
    }
    return <Grid item xs={3}>
        <LinearProgress className="achievement-progress" variant="buffer" value={tier_1_value} valueBuffer={tier_1_buffer} />
        <div style={{opacity: !previous_completed && !user_completed || user_completed ? 0 : 0.7}}
             className="progress-number"
        >
            <span>
                {user_completed ? `${threshold}/${threshold}` : `${user_progress}/${threshold}`}
             </span>
        </div>
        <div className={user_completed ? 'achievement-image completed' : 'achievement-image'}>
            <span>{title}</span>
            <img src={user_completed ? image : unknown}/>
            <div className="check-icon"><FontAwesomeIcon icon={faCheckCircle} /></div>
        </div>
    </Grid>
}


/**
 *
 * @param {Object} props.achievement
 * @param {string} props.achievement.title
 * @param {string} props.achievement.requirement
 * @param {int} props.achievement.user_progress
 * @param {string} props.achievement.tier_1.title
 * @param {string} props.achievement.tier_1.description
 * @param {string} props.achievement.tier_1.image
 * @param {int} props.achievement.tier_1.user_completed
 * @param {int} props.achievement.tier_1.threshold
 * @param {string} props.achievement.tier_2.title
 * @param {string} props.achievement.tier_2.description
 * @param {string} props.achievement.tier_2.image
 * @param {int} props.achievement.tier_2.user_completed
 * @param {int} props.achievement.tier_2.threshold
 * @param {string} props.achievement.tier_3.title
 * @param {string} props.achievement.tier_3.description
 * @param {string} props.achievement.tier_3.image
 * @param {int} props.achievement.tier_3.user_completed
 * @param {int} props.achievement.tier_3.user_completed
 * @param {int} props.achievement.tier_3.threshold
 * @param {string} props.achievement.tier_4.title
 * @param {string} props.achievement.tier_4.description
 * @param {string} props.achievement.tier_4.image
 * @param {int} props.achievement.tier_4.user_completed
 * @param {int} props.achievement.tier_4.threshold
 * @returns {JSX.Element}
 * @constructor
 */
export function GenreAchievement(props) {
    const {achievement} = props;
    const requirement = <BBCode plugins={[reactPreset()]}>
        {achievement.requirement}
    </BBCode>;
    return <Grid item xs={12} className="achievement-container">
        <Tooltip title={requirement}>
            <h5>{achievement.title}</h5>
        </Tooltip>
        <p>{getActiveGenreDescription(achievement)}</p>
        <Grid container className="progress-bar">
            <GenreAchievementSegment
                title={achievement.tier_1.title}
                tier_number={1}
                previous_completed={true}
                user_completed={achievement.tier_1.user_completed}
                user_progress={achievement.user_progress}
                threshold={achievement.tier_1.threshold}
                image={achievement.tier_1.image}
            />
            <GenreAchievementSegment
                title={achievement.tier_2.title}
                tier_number={2}
                previous_completed={achievement.tier_1.user_completed}
                user_completed={achievement.tier_2.user_completed}
                user_progress={achievement.user_progress}
                threshold={achievement.tier_2.threshold}
                image={achievement.tier_2.image}
            />
            <GenreAchievementSegment
                title={achievement.tier_3.title}
                tier_number={3}
                previous_completed={achievement.tier_2.user_completed}
                user_completed={achievement.tier_3.user_completed}
                user_progress={achievement.user_progress}
                threshold={achievement.tier_3.threshold}
                image={achievement.tier_3.image}
            />
            <GenreAchievementSegment
                title={achievement.tier_4.title}
                tier_number={4}
                previous_completed={achievement.tier_3.user_completed}
                user_completed={achievement.tier_4.user_completed}
                user_progress={achievement.user_progress}
                threshold={achievement.tier_4.threshold}
                image={achievement.tier_4.image}
            />
        </Grid>
    </Grid>
}
