import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'

import {Bar, Doughnut} from 'react-chartjs-2';
import Grid from "@material-ui/core/Grid";

import {getAnimeDataWithStatistics} from '../../../actions/anime'
import {
    getDataGenres,
    getDataQualities,
    getDataRated,
    getDataSources,
    getDataStatuses,
    getDataTypes,
    getDataYears
} from "./statistics";
import {AnimeDataStatistics, UserList} from "../../../models";

import '../../../assets/css/anime.css'
import CountUp from "react-countup";
import Divider from "@material-ui/core/Divider";


class AnimeStatistics extends Component {
    constructor() {
        super(...arguments);
    }

    componentDidMount() {
        this.props.getAnimeDataWithStatistics();
    }

    render() {
        let data_types = getDataTypes(this.props.anime_data_statistics);
        let data_sources = getDataSources(this.props.anime_data_statistics)[0];
        let data_sources_options = getDataSources(this.props.anime_data_statistics)[1];
        let data_statuses = getDataStatuses(this.props.anime_data_statistics);
        let data_rated = getDataRated(this.props.anime_data_statistics);
        let data_genres = getDataGenres(this.props.anime_data_statistics);
        let data_qualities = getDataQualities(this.props.anime_data_statistics);
        let data_years = getDataYears(this.props.anime_data_statistics);

        return (
            <Grid container id="Anime-Statistics">
                <Grid item xs={12}>
                    <Divider variant="middle" className="charts-divider"/>
                </Grid>
                <Grid item xs={12}>
                    <h3>ANIME STATISTICS</h3>
                </Grid>
                <Grid item xl={2}/>
                <Grid item xs={12} md={12} lg={6} xl={4}>
                    <table>
                        <tbody>
                        <tr>
                            <td className="color-primary">Total anime:</td>
                            <td><p>{this.props.anime_data_statistics[AnimeDataStatistics.total_anime]} (
                                {this.props.anime_data_statistics[AnimeDataStatistics.total_anime] +
                                this.props.anime_data_statistics[AnimeDataStatistics.total_anime_pending_mal] +
                                this.props.anime_data_statistics[AnimeDataStatistics.total_anime_deleted_mal]}
                                )</p></td>
                        </tr>
                        <tr>
                            <td className="color-primary">Pending by MAL:</td>
                            <td>
                                <p className="color-warning">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_anime_pending_mal]}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-primary">Deleted by MAL:</td>
                            <td>
                                <p className="color-danger">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_anime_deleted_mal]}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-primary">Mean Score:</td>
                            <td>
                                <p className="color-white">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.mean_score] ?
                                        this.props.anime_data_statistics[AnimeDataStatistics.mean_score].toFixed(2) : null}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-primary">Total episodes:</td>
                            <td>
                                <p className="color-white">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_episodes]}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-primary">Total duration:</td>
                            <td>
                                <p className="color-white">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_minutes]}min (
                                    {(this.props.anime_data_statistics[AnimeDataStatistics.total_minutes] / 1440)
                                        .toFixed(2)} days)
                                </p>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </Grid>
                <Grid item xs={12} md={12} lg={6} xl={4}>
                    <table>
                        <tbody>
                        <tr>
                            <td className="color-secondary-dark">Downloaded:</td>
                            <td>
                                <p className="color-secondary">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_anime_downloaded]}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-secondary-dark">Downloading:</td>
                            <td>
                                <p className="color-secondary">
                                    {this.props.anime_data_statistics[AnimeDataStatistics.total_anime_downloading]}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="color-secondary-dark">Total size:</td>
                            <td>
                                <p className="color-secondary">
                                    {(this.props.anime_data_statistics[AnimeDataStatistics.total_downloaded_size] / 1024)
                                        .toFixed(1)}TB
                                </p>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </Grid>
                <Grid item xs={12}>
                    <Divider variant="middle" className="charts-divider"/>
                </Grid>
                <Grid item xs={12} md={12} lg={3} xl={2}>
                    <div className="pie-container">
                        <h5 className="text-center">Type</h5>
                        <Doughnut height={100} width={100} data={data_types}/>
                    </div>
                </Grid>
                <Grid item xs={12} md={12} lg={3} xl={2}>
                    <div className="pie-container">
                        <h5 className="text-center">Rated</h5>
                        <Doughnut height={100} width={100} data={data_rated}/>
                    </div>
                </Grid>
                <Grid item xs={12} md={12} lg={3} xl={2}>
                    <div className="pie-container">
                        <h5 className="text-center">Quality</h5>
                        <Doughnut height={100} width={100} data={data_qualities}/>
                    </div>
                </Grid>
                <Grid item xs={12} md={12} lg={3} xl={2}>
                    <div className="pie-container">
                        <h5 className="text-center">Status</h5>
                        <Doughnut height={100} width={100} data={data_statuses}/>
                    </div>
                </Grid>
                <Grid item xs={12}>
                    <Divider variant="middle" className="charts-divider"/>
                </Grid>
                <Grid item xs={12}>
                    <div className="bar-container">
                        <h5 className="text-center">Sources</h5>
                        <Bar
                            data={data_sources}
                            width={100}
                            height={50}
                            options={data_sources_options}
                        />
                    </div>
                </Grid>
                <Grid item xs={12}>
                    <Divider variant="middle" className="charts-divider"/>
                </Grid>
                <Grid item xs={12}>
                    <div className="bar-container">
                        <h5 className="text-center">Genres</h5>
                        <Bar
                            data={data_genres}
                            width={100}
                            height={50}
                            options={{
                                maintainAspectRatio: false
                            }}
                        />
                    </div>
                </Grid>
                <Grid item xs={12}>
                    <Divider variant="middle" className="charts-divider"/>
                </Grid>
                <Grid item xs={12}>
                    <div className="bar-container">
                        <h5 className="text-center">Anime per Year</h5>
                        <Bar
                            data={data_years}
                            width={100}
                            height={50}
                            options={{
                                maintainAspectRatio: false
                            }}
                        />
                    </div>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    anime_data_statistics: state.anime.anime_data_statistics,
});

export default connect(mapStateToProps, {getAnimeDataWithStatistics})(AnimeStatistics)