import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from "@material-ui/core/Grid";

import CyberNavbar from "../../../layout/navbar/CyberNavbar";
import {cyberBBCodeParser} from "./parser";
import {Profile, User} from "../../../../models";
import {editUserAboutMeBackground, editUserAboutMeBBCode, getUserList} from "../../../../actions/users";
import {getUser} from "../../../../actions/auth";
import {BBCodeEditorTabs} from "./tabs/bbCodeTabs";
import Loading from "../../../layout/common/Loading";
import {TEMPLATE_NONE_BACKGROUND} from "./templates";

import '../../../../assets/css/bbcode_editor.css'


/**
 * Custom attribute for bbCode [img] tag, users can set width in '%' for dynamic width of images,
 * this function checks for that attribute and applies it to actual img html tag.
 */
class BBCodeEditor extends Component {
    constructor(props) {
        super(props);
        this.updateEditor = this.updateEditor.bind(this);
        this.submitAboutMe = this.submitAboutMe.bind(this);
        this.updateBackgroundImage = this.updateBackgroundImage.bind(this);

        let background = TEMPLATE_NONE_BACKGROUND;
        if (this.props.user[User.profile][Profile.about_section_background_style])
            background = JSON.parse(this.props.user[User.profile][Profile.about_section_background_style]);

        this.state = {
            bbcode: this.props.user[User.profile][Profile.about_section_bbcode],
            background_image: background,
        };
    }

    componentDidMount() {
        document.title = 'BBCode Editor';
        window.scrollTo(0, 0);
        this.props.getUserList(this.props.user[User.id]);
        this.props.getUser(this.props.user[User.id]);
    }

    // componentDidUpdate(prevProps, prevState, snapshot) {
    //     if (prevProps.visited_user_loaded === false && this.props.visited_user_loaded)
    //         if (this.props.visited_user[User.profile][Profile.about_section_background_style]) {
    //             const background = JSON.parse(this.props.visited_user[User.profile][Profile.about_section_background_style]);
    //             this.setState({background_image: background})
    //         }
    // }

    updateEditor(e) {
        this.setState({bbcode: e.target.value});
    }

    updateBackgroundImage(e, attribute) {
        let bg_img = {};
        Object.assign(bg_img, this.state.background_image);
        if (attribute === 'background_color') bg_img[attribute] = e;
        else if (attribute === 'all') Object.assign(bg_img, e);
        else bg_img[attribute] = e.target.value;
        this.setState({background_image: bg_img})
    }

    submitAboutMe() {
        const bbcode = this.state.bbcode ? this.state.bbcode.replaceAll('\n', '[br][/br]') : this.state.bbcode;
        this.props.editUserAboutMeBBCode(bbcode);
        this.props.editUserAboutMeBackground(this.state.background_image)
    }

    render() {
        const bbcode = this.state.bbcode ? this.state.bbcode.replaceAll('\n', '[br][/br]') : this.state.bbcode;
        const style = {
             backgroundColor: `#${this.state.background_image.background_color['hex']}`,
             backgroundRepeat: this.state.background_image.repeat,
             backgroundSize: this.state.background_image.size,
             backgroundPosition: `${this.state.background_image.position_1} ${this.state.background_image.position_2}`,
        };
        if (this.state.background_image.url !== '')
            style.backgroundImage = `url("${this.state.background_image.url}")`;
        if (this.state.background_image.position_x)
            style.backgroundPositionX = `${this.state.background_image.position_x}px`;
        if (this.state.background_image.position_y)
            style.backgroundPositionY = `${this.state.background_image.position_y}px`;

        console.log('style', style);
        console.log('style', style);

        return (<>
            <CyberNavbar/>
            {this.props.user_loading ? <Loading text={'Loading'}/> :
                <Grid id="BBCode-Editor" container>
                    <Grid item xs={4} className="bbcode-editor-segment">
                        <BBCodeEditorTabs
                            bbcode={bbcode}
                            background_image={this.state.background_image}
                            updateEditor={this.updateEditor}
                            updateBackgroundImage={this.updateBackgroundImage}
                            submitAboutMe={this.submitAboutMe}
                        />
                    </Grid>
                    <Grid item xs={8} className="bbcode-editor-segment">
                        <div id="BBCode_Container" className="bbcode-container" style={style}>
                            {cyberBBCodeParser(bbcode)}
                        </div>
                    </Grid>
                </Grid>}
        </>)
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
    user_loaded: state.auth.user_loaded,
    // visited_user: state.auth.visited_user,
    // visited_user_loaded: state.auth.visited_user_loaded,
});

export default connect(mapStateToProps, {
    editUserAboutMeBBCode, editUserAboutMeBackground, getUser, getUserList
})(BBCodeEditor)
