import React from 'react';
import {Link} from 'react-router-dom';
import {makeStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import {Button} from "@material-ui/core";

import '../../../assets/css/layout.css'
import {ADMIN, HOME, INDEX, LOGIN, PROFILE} from "../../../urls";
import AnimeMenu, {AuthMenu, CharactersMenu, ErogeMenu, MangaMenu} from "./menus";
import {playClickSound, playHoverSound} from "../sounds";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faUser} from "@fortawesome/free-solid-svg-icons/faUser";

const STYLE = {top: 25, left: 25};

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        zIndex: 900
    },
    grow: {
        flexGrow: 1,
    },
    appBar: {
        background: 'none',
    },
    toolbar: {
        minHeight: 42,
    },
    title: {
        marginRight: theme.spacing(3),
        fontFamily: 'Quicksand',
        fontVariant: 'small-caps',
        userSelect: "none"
    },
    menuButton: {
        marginRight: theme.spacing(2),
        fontSize: "medium",
    },
    menuButtonLink: {
        color: "inherit",
        textDecoration: "none"
    },
    menuLinks: {
        top: 35,
        left: 25
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
}));

export default function MenuAppBar(props) {
    const {user_authenticated, user_id, username, superuser} = props;
    const classes = useStyles();
    const [profileAnchorEl, setProfileAnchorEl] = React.useState(null);
    const [animeAnchorEl, setAnimeAnchorEl] = React.useState(null);
    const [mobileAnchorEl, setMobileAnchorEl] = React.useState(null);
    const isProfileOpen = Boolean(profileAnchorEl);
    const isAnimeOpen = Boolean(animeAnchorEl);
    const isMobileMenuOpen = Boolean(mobileAnchorEl);

    const [desktopAnimeMenu, setDesktopAnimeMenu] = React.useState(false);
    const [desktopMangaMenu, setDesktopMangaMenu] = React.useState(false);
    const [desktopCharactersMenu, setDesktopCharactersMenu] = React.useState(false);
    const [desktopErogeMenu, setDesktopErogeMenu] = React.useState(false);
    const [desktopAuthMenu, setDesktopAuthMenu] = React.useState(false);
    const anime = 'anime';
    const manga = 'manga';
    const characters = 'characters';
    const eroge = 'eroge';
    const auth = 'auth';

    // Handle methods
    const handleProfileMenu = (event) => {
        setProfileAnchorEl(event.currentTarget);
    };

    const handleAnimeMenu = (event) => {
        setAnimeAnchorEl(event.currentTarget);
    };

    const handleMobileMenu = (event) => {
        setMobileAnchorEl(event.currentTarget);
    };

    // Close methods
    const handleProfileClose = () => {
        setProfileAnchorEl(null);
    };

    const handleAnimeClose = () => {
        setAnimeAnchorEl(null);
        handleMobileClose()
    };

    const handleMobileClose = () => {
        setMobileAnchorEl(null);
    };

    // Desktop methods
    const onDesktopMenuLeave = () => {
        setDesktopAnimeMenu(false);
        setDesktopMangaMenu(false);
        setDesktopCharactersMenu(false);
        setDesktopErogeMenu(false);
        setDesktopAuthMenu(false);
    };

    const onDesktopMenuEnter = (menuName) => {
        onDesktopMenuLeave();
        playHoverSound();
        if (menuName === anime) {
            setDesktopAnimeMenu(true);
        } else if (menuName === manga) {
            setDesktopMangaMenu(true)
        } else if (menuName === characters) {
            setDesktopCharactersMenu(true)
        } else if (menuName === eroge) {
            setDesktopErogeMenu(true)
        } else if (menuName === auth) {
            setDesktopAuthMenu(true)
        }
    };

    const handleScroll = () => {
        let navbar = document.getElementById('Cyber-Navbar');

        if (window.scrollY > 40) {
            navbar.classList.add('Cyber-Navbar-scrolled');
        } else {
            try {
                navbar.classList.remove('Cyber-Navbar-scrolled');
            } catch (e) {
            }
        }
    };

    const animeMobileMenu = 'anime-mobile-menu';
    const renderAnimeMenu = (
        <Menu
            id={animeMobileMenu}
            anchorEl={animeAnchorEl}
            anchorOrigin={{vertical: 'top', horizontal: 'center'}}
            transformOrigin={{vertical: 'top', horizontal: 'center'}}
            keepMounted
            open={isAnimeOpen}
            onClose={handleAnimeClose}
            style={STYLE}
        >
            <MenuItem onClick={handleAnimeClose}>Home</MenuItem>
            <MenuItem onClick={handleAnimeClose}>Browse</MenuItem>
            <MenuItem onClick={handleAnimeClose}>Seasons</MenuItem>
            <MenuItem onClick={handleAnimeClose}>Franchises</MenuItem>
        </Menu>
    );

    const mobileMenuId = 'primary-search-account-menu-mobile';
    const renderMobileMenu = (
        <Menu
            anchorEl={mobileAnchorEl}
            anchorOrigin={{vertical: 'top', horizontal: 'right'}}
            id={mobileMenuId}
            keepMounted
            transformOrigin={{vertical: 'top', horizontal: 'right'}}
            open={isMobileMenuOpen}
            onClose={handleMobileClose}
        >
            <MenuItem onClick={handleAnimeMenu}>
                <Button
                    aria-label="anime links"
                    aria-controls={animeMobileMenu}
                    aria-haspopup="true">
                    Anime
                </Button>
            </MenuItem>
        </Menu>
    );

    document.addEventListener('scroll', handleScroll);

    return (
        <div id="Cyber-Navbar" className={classes.root} onMouseLeave={onDesktopMenuLeave}>
            <AppBar position="static" className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <IconButton edge="start" className={classes.sectionMobile} color="inherit" aria-label="menu"
                                onClick={handleMobileMenu}>
                        <MenuIcon/>
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        <Link to={INDEX} className={classes.menuButtonLink} onMouseOver={playHoverSound}
                              onClick={playClickSound}>Cyber-Genesis</Link>
                    </Typography>

                    <div className={classes.sectionDesktop + ' desktop'}>
                        <span className={classes.menuButton} onMouseEnter={onDesktopMenuLeave}>
                            <Link to={HOME} className={classes.menuButtonLink} onMouseOver={playHoverSound}
                                  onClick={playClickSound}>Home</Link>
                        </span>

                        <span className={classes.menuButton} onMouseEnter={() => {
                            onDesktopMenuEnter(anime)
                        }}>
                            Anime
                            <AnimeMenu show={desktopAnimeMenu} onLeave={onDesktopMenuLeave}/>
                        </span>

                        <span className={classes.menuButton} onMouseEnter={() => {
                            onDesktopMenuEnter(manga)
                        }}>
                            Manga
                            <MangaMenu show={desktopMangaMenu} onLeave={onDesktopMenuLeave}/>
                        </span>

                        <span className={classes.menuButton} onMouseEnter={() => {
                            onDesktopMenuEnter(characters)
                        }}>
                            Industry
                            <CharactersMenu show={desktopCharactersMenu} onLeave={onDesktopMenuLeave}/>
                        </span>

                        <span className={classes.menuButton} onMouseEnter={() => {
                            onDesktopMenuEnter(eroge)
                        }}>
                            Eroge
                            <ErogeMenu show={desktopErogeMenu} onLeave={onDesktopMenuLeave}/>
                        </span>

                        {superuser ? <span className={classes.menuButton} onMouseEnter={onDesktopMenuLeave}>
                            <a href={ADMIN} className={classes.menuButtonLink} onMouseOver={playHoverSound}
                                  onClick={playClickSound}>Admin</a>
                        </span> : null}
                    </div>

                    <div className={classes.grow}/>

                    {user_authenticated ? <div className={classes.sectionDesktop + ' desktop'}>
                        <span onMouseEnter={() => {
                            onDesktopMenuEnter(auth)
                        }}>
                            <Link to={PROFILE(user_id)} onMouseOver={playHoverSound} onClick={playClickSound}>
                                {username} <FontAwesomeIcon icon={faUser}/></Link>
                            <AuthMenu user_id={user_id} show={desktopAuthMenu} onLeave={onDesktopMenuLeave}/>
                        </span>
                    </div> : user_authenticated != null ?
                        <span className={classes.menuButton} onMouseEnter={onDesktopMenuLeave}>
                            <Link to={LOGIN} className={classes.menuButtonLink} onMouseOver={playHoverSound}
                                  onClick={playClickSound}>Login</Link>
                        </span> : null}
                </Toolbar>
            </AppBar>
            {renderMobileMenu}
            {renderAnimeMenu}
        </div>
    );
}
