import React, {Component} from 'react'
import {connect} from 'react-redux'

import {Anime, ApiResult, Manga} from "../../../../models";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_CHARACTERS} from "../../../../globals";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import GridList from "@material-ui/core/GridList";

import '../../../../assets/css/anime.css'
import {getSynopsys} from "../../../anime/display/tabs/GeneralTab";
import {NONE} from "../../../display/Display";


class GeneralTab extends Component {
    render() {
        const synopsys = getSynopsys(this.props.manga[Manga.synopsis]);

        return (
            <div>
                <div className="general-description">
                    <h2>Synopsis</h2>
                    <p>{synopsys ? synopsys : NONE}</p>
                    {this.props.manga[Manga.background] ?
                        <>
                            <h2>Background</h2>
                            <p>{this.props.manga[Manga.background]}</p>
                        </> : null}

                    <h2>Main Characters</h2>
                </div>

                {this.props.manga[Manga.main_characters].length > 0 ?
                    <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                        {this.props.manga[Manga.main_characters].map((character, i) => (
                            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                                <GridListTile cols={1}>
                                    <TitleCard branch={BRANCH_CHARACTERS} title={character}/>
                                </GridListTile>
                            </Grow>
                        ))}
                    </GridList> : <p className="none-characters">{NONE}</p>}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    manga: state.manga.manga,
});

export default connect(mapStateToProps, {})(GeneralTab)
