import React, {Component} from 'react'
import {connect} from 'react-redux'

import TextField from "@material-ui/core/TextField";

import {CyberButton} from "../../../../layout/common/common";

import '../../../../../assets/css/bbcode_editor.css'
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import {
    TEMPLATE_DEFAULT_BACKGROUND,
    TEMPLATE_DEFAULT_BBCODE,
    TEMPLATE_HIGH_CONTRAST_BACKGROUND,
    TEMPLATE_HIGH_CONTRAST_BBCODE,
    TEMPLATE_MAL_BACKGROUND,
    TEMPLATE_MAL_BBCODE,
    TEMPLATE_NAME_DEFAULT,
    TEMPLATE_NAME_HIGH_CONTRAST,
    TEMPLATE_NAME_MAL,
    TEMPLATE_NAME_NONE
} from "../templates";
import {Profile, User} from "../../../../../models";


/**
 * returns True if the objects are the same
 * @param target
 * @param value
 * @returns {boolean}
 */
function compareImageObject(target, value) {
    if (target.background_color !== value.background_color)
        return false;
    if (target.position_1 !== value.position_1)
        return false;
    if (target.position_2 !== value.position_2)
        return false;
    if (target.position_x !== value.position_x)
        return false;
    if (target.position_y !== value.position_y)
        return false;
    if (target.repeat !== value.repeat)
        return false;
    if (target.size !== value.size)
        return false;
    return target.url === value.url;
}


/**
 * Custom attribute for bbCode [img] tag, users can set width in '%' for dynamic width of images,
 * this function checks for that attribute and applies it to actual img html tag.
 */
class BBCodeEditor extends Component {
    constructor(props) {
        super(props);
        this.updateSelectedPreset = this.updateSelectedPreset.bind(this);
        this.state = {
            selected_template: TEMPLATE_NAME_NONE
        };
    }

    updateSelectedPreset(e) {
        this.setState({selected_template: e.target.value});
        const background = JSON.parse(this.props.user[User.profile][Profile.about_section_background_style]);
        const bbcode = this.props.user[User.profile][Profile.about_section_bbcode].replaceAll('\n', '[br][/br]');

        // eslint-disable-next-line no-restricted-globals
        if (!(bbcode !== this.props.bbcode || !compareImageObject(background, this.props.background_image)) || ((bbcode !== this.props.bbcode || !compareImageObject(background, this.props.background_image)) && confirm('You have unsaved changes, changing the template will delete any unsaved changes!'))) {
            switch (e.target.value) {
                case TEMPLATE_NAME_DEFAULT:
                    this.props.updateEditor({target: {value: TEMPLATE_DEFAULT_BBCODE}});
                    this.props.updateBackgroundImage(TEMPLATE_DEFAULT_BACKGROUND, 'all');
                    return;
                case TEMPLATE_NAME_MAL:
                    this.props.updateEditor({target: {value: TEMPLATE_MAL_BBCODE}});
                    this.props.updateBackgroundImage(TEMPLATE_MAL_BACKGROUND, 'all');
                    return;
                case TEMPLATE_NAME_HIGH_CONTRAST:
                    this.props.updateEditor({target: {value: TEMPLATE_HIGH_CONTRAST_BBCODE}});
                    this.props.updateBackgroundImage(TEMPLATE_HIGH_CONTRAST_BACKGROUND, 'all');
                    return;
                default:
                    return
            }
        }
    }

    render() {
        return (<>
            <div className="editor-container box-light">
                <FormControl variant="outlined" className="form-control">
                    <InputLabel id="select-position-2-label">Template</InputLabel>
                    <Select
                        labelId="select-position-2-label"
                        id="select-position-1"
                        value={this.state.selected_template}
                        onChange={this.updateSelectedPreset}
                        label="position"
                    >
                        <MenuItem value={TEMPLATE_NAME_NONE}><span className="color-gray">Select Template</span></MenuItem>
                        <MenuItem value={TEMPLATE_NAME_DEFAULT}>Default</MenuItem>
                        <MenuItem value={TEMPLATE_NAME_MAL}>MAL</MenuItem>
                        <MenuItem value={TEMPLATE_NAME_HIGH_CONTRAST}>High Contrast</MenuItem>
                    </Select>
                </FormControl>
                <TextField
                    id="BBCode-Editor-Field"
                    label="BBCode Text Editor"
                    className="form-control"
                    multiline
                    value={this.props.bbcode ? this.props.bbcode.replaceAll('[br][/br]', '\n') : ''}
                    name="bbcode-editor"
                    onChange={this.props.updateEditor}
                />
                <p className="color-gray">Type your bbCode here and see it update in real time.</p>
                <p className="color-gray">Hit the Upload button to update your About Me bbCode.</p>
                <div className="submit-btn-container">
                    <CyberButton classNames={'btn-contained-success'} variant={'contained'} onClick={this.props.submitAboutMe}>
                        Upload
                    </CyberButton>
                </div>
            </div>

            <div className="available-tags box-light">
                <h2>Special tags</h2>
                <p><span className="tag-color">[animestats][/animestats]</span> - <span className="color-gray">Displays Anime Stats</span></p>
                <p><span className="tag-color">[mangastats][/mangastats]</span> - <span className="color-gray">Displays Manga Stats</span></p>
                <p><span className="color-gray">Available attributes</span> - <span className="tag-color">width=0-100/0-100%</span></p>
                <br/>
                <p><span className="tag-color">[favoriteanime][/favoriteanime]</span> - <span className="color-gray">Displays Favorite Anime</span></p>
                <p><span className="tag-color">[favoritemanga][/favoritemanga]</span> - <span className="color-gray">Displays Favorite Manga</span></p>
                <p><span className="tag-color">[favoritecharacters][/favoritecharacters]</span> - <span className="color-gray">Displays Favorite Characters</span></p>
                <p><span className="tag-color">[favoritepeople][/favoritepeople]</span> - <span className="color-gray">Displays Favorite People</span></p>
                <p><span className="color-gray">Available attributes</span> - <span className="tag-color">width=0-100/0-100%, center=true/false, small=true <span className="color-gray">(Makes cards small)</span>, limit=0-100 <span className="color-gray">(limits how many cards will show up)</span></span></p>
            </div>

            <div className="available-tags box-light">
                <h2>Available tags</h2>
                <p><span className="tag-color">[br][/br]</span> - <span className="color-gray">Start in a new line</span></p>
                <p><span className="tag-color">[b]</span>Bold<span className="tag-color">[/b]</span> - <span className="color-gray"><b>Bold</b></span></p>
                <p><span className="tag-color">[i]</span>Italic<span className="tag-color">[/i]</span> - <span className="color-gray"><i>Italic</i></span></p>
                <p><span className="tag-color">[u]</span>Underline<span className="tag-color">[/u]</span> - <span className="color-gray"><u>Underline</u></span></p>
                <p><span className="tag-color">[s]</span>Strikethrough<span className="tag-color">[/s]</span> - <span className="color-gray"><s>Strikethrough</s></span></p>
                <p><span className="tag-color">[color=red]</span>Colored Text<span className="tag-color">[/color]</span> - <span style={{color: 'red'}}>Colored Text</span></p>
                <p><span className="tag-color">[color=#ff0000]</span>Colored Text<span className="tag-color">[/color]</span> - <span style={{color: '#ff0000'}}>Colored Text with HEX</span></p>
                <p><span className="tag-color">[size=30]</span>Bigger text<span className="tag-color">[/size]</span> - <span className="color-gray" style={{fontSize: 30}}>Bigger text</span></p>
                <p><span className="tag-color">[center]</span>Center<span className="tag-color">[/center]</span> - <span className="color-gray">Centers elements (Text, Image)</span></p>
                <p><span className="tag-color">[left]</span>Left<span className="tag-color">[/left]</span> - <span className="color-gray">Left aligns elements (Text, Image)</span></p>
                <p><span className="tag-color">[right]</span>Right<span className="tag-color">[/right]</span> - <span className="color-gray">Right aligns elements (Text, Image)</span></p>
                <p><span className="tag-color">[quote]</span>quoted text<span className="tag-color">[/quote]</span> - <span className="color-gray">Quoted text</span></p>
                <p><span className="tag-color">[url]</span>https://cyber-genesis.rf.gd<span className="tag-color">[/url]</span> - <a href="https://cyber-genesis.rf.gd">https://cyber-genesis.rf.gd</a></p>
                <p><span className="tag-color">[url=https://cyber-genesis.rf.gd]</span>Name<span className="tag-color">[/url]</span> - <a href="https://cyber-genesis.rf.gd">Name</a></p>
                <p><span className="tag-color">[img]</span>https://i.imgur.com/WGEbacJ.jpg<span className="tag-color">[/img]</span> - <span className="color-gray">Image/Gif</span></p>
                <p><span className="tag-color">[img width=100% height=50]</span>https://i.imgur.com/WGEbacJ.jpg<span className="tag-color">[/img]</span> - <span className="color-gray">Image/Gif resized (in %/px)</span></p>
                <p><span className="tag-color">[list][*]</span>1<span className="tag-color">[*]</span>2<span className="tag-color">[/list]</span> - <span className="color-gray">List of items</span></p>
                <p><span className="tag-color">[table][tr][td]</span>cell<span className="tag-color">[/td][/tr][/table]</span> - <span className="color-gray">Table of items</span></p>
            </div>
        </>)
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
});

export default connect(mapStateToProps, {})(BBCodeEditor)
