import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'

import Moment from 'react-moment'

import {getNews} from '../../../actions/requiem'

import Grid from "@material-ui/core/Grid";
import Loading from "../../layout/common/Loading";
import {getTime} from "./snippets";
import Pagination from "@material-ui/lab/Pagination";
import {ApiResult} from "../../../models";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";


class News extends Component {
    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);

        this.state = {
            current_page: 1,
            page_dialog_open: false,
        }
    }

    componentDidMount() {
        this.props.getNews();
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.news[ApiResult.count] / 5)) {
            this.setState({current_page: page});
            this.props.getNews(page);
        }
    }

    render() {
        const {news, news_is_loading} = this.props;
        let news_results = [];

        try {
            if (news['results'].length > 0) news_results = news['results'];
        } catch (e) {
        }

        return (
            <Grid id="News-Container" container>
                {!news_is_loading ?
                    news_results.map((post, i) => (
                        <Grid item xs={12} className="report box-light" key={i}>
                            <h3 className="color-blue">{post['title']}</h3>
                            <p className="color-dark-blue indent-1em">{post['description']}</p>
                            {post['anime_entries'].length > 0 ?
                                <p className="color-dark-blue">Total <span className={post['title']
                                    .indexOf('removed') >= 0 ? 'color-red' : 'color-blue'}>
                                            {post['anime_entries'].length}</span></p>
                                : post['anime_server_entries'].length > 0 ?
                                    <p className="color-dark-blue">Total <span className="color-magenta">
                                                {post['anime_server_entries'].length}</span></p>
                                    : post['manga_entries'].length > 0 ?
                                        <p className="color-dark-blue">Total <span className={post['title']
                                            .indexOf('removed') >= 0 ? 'color-red' : 'color-blue'}>
                                                    {post['manga_entries'].length}</span></p>
                                        : post['manga_server_entries'].length > 0 ?
                                            <p className="color-dark-blue">Total&nbsp;
                                                <span className="color-magenta">
                                                            {post['manga_server_entries'].length}</span></p>
                                            : post['characters_entries'].length > 0 ?
                                                <p className="color-dark-blue">
                                                    Total <span className={post['title']
                                                    .indexOf('removed') >= 0 ? 'color-red'
                                                    : 'color-blue'}>
                                                            {post['characters_entries'].length}</span></p>
                                                : null}
                            {/*{post['anime_entries'] ?*/}
                            {/*    <DisplayEntries entries={post['anime_entries']} branch={BRANCH_TYPE_ANIME}*/}
                            {/*                    deleted={post['title'].indexOf('removed') >= 0}*/}
                            {/*                    server={false}/> : null}*/}
                            {/*{post['anime_server_entries'] ?*/}
                            {/*    <DisplayEntries entries={post['anime_server_entries']}*/}
                            {/*                    branch={BRANCH_TYPE_ANIME} server={true}/> : null}*/}
                            {/*{post['manga_entries'] ?*/}
                            {/*    <DisplayEntries entries={post['manga_entries']} branch={BRANCH_TYPE_MANGA}*/}
                            {/*                    deleted={post['title'].indexOf('removed') >= 0}*/}
                            {/*                    server={false}/> : null}*/}
                            {/*{post['manga_server_entries'] ?*/}
                            {/*    <DisplayEntries entries={post['manga_server_entries']}*/}
                            {/*                    branch={BRANCH_TYPE_MANGA} server={true}/> : null}*/}
                            {/*{post['characters_entries'] ?*/}
                            {/*    <DisplayEntries entries={post['characters_entries']}*/}
                            {/*                    deleted={post['title'].indexOf('removed') >= 0}*/}
                            {/*                    branch={BRANCH_TYPE_CHARACTERS} server={false}/> : null}*/}
                            <div className="duration">
                                <p className="color-dark-blue">
                                    Date: <span className="color-white"><Moment
                                    format="DD MMM YYYY HH:mm:ss"
                                    date={post['datetime_created']}/>
                                        </span>
                                </p>
                                {post['operation_duration_sec'] ?
                                    <p className="color-dark-blue margin-left-20">
                                        Duration: <span className="color-white">
                                                {getTime(post['operation_duration_sec'])}
                                            </span>
                                    </p>
                                    : null}
                            </div>
                        </Grid>))
                    : <Loading text={'News Loading'}/>}

                {!news_is_loading ? <div className="pagination">
                    <Pagination
                        count={Math.ceil(this.props.news[ApiResult.count] / 5)}
                        color="primary"
                        siblingCount={3}
                        page={this.state.current_page}
                        onChange={(type, page) => {
                            playClickSound();
                            if (page !== this.state.current_page)
                                this.goToPage(page);
                            else
                                this.setState({page_dialog_open: true})
                        }}
                    />
                    <GoToPageDialog
                        open={this.state.page_dialog_open}
                        current_page={this.state.current_page}
                        total_pages={Math.ceil(this.props.news[ApiResult.count] / 5)}
                        goToPage={this.goToPage}
                        onClose={this.closePageDialog}/>
                </div> : null}


                     {/*<Pagination page={news} items_per_page={5} getItems={getNews}*/}
                     {/*                            scroll_to={[0, 550]}/> : null}*/}
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    news_is_loading: state.requiem.news_is_loading,
    news: state.requiem.news,
});

export default connect(mapStateToProps, {getNews})(News)
