import {
    ANIME_BROWSE_TAB_NAME,
    BRANCH_ANIME,
    BRANCH_CHARACTERS,
    BRANCH_EROGE,
    BRANCH_MANGA,
    BRANCH_PEOPLE,
    BROWSE_ANIME_ITEMS_PER_PAGE,
    BROWSE_CHARACTERS_ITEMS_PER_PAGE, BROWSE_EROGE_ITEMS_PER_PAGE,
    BROWSE_MANGA_ITEMS_PER_PAGE, BROWSE_PEOPLE_ITEMS_PER_PAGE,
    CHARACTERS_BROWSE_TAB_NAME,
    EROGE_BROWSE_TAB_NAME,
    FILTER_ANIME_INITIAL_VALUES,
    FILTER_CHARACTERS_INITIAL_VALUES,
    FILTER_EROGE_INITIAL_VALUES,
    FILTER_MANGA_INITIAL_VALUES,
    MANGA_BROWSE_TAB_NAME,
    PEOPLE_BROWSE_TAB_NAME
} from "../../globals";
import store from "../../store";
import {filterAnime} from "../../actions/anime";
import {filterManga} from "../../actions/manga";
import {filterCharacters} from "../../actions/characters";
import {filterEroge} from "../../actions/eroge";
import AnimeFilters from "../layout/filters/anime/AnimeFilters";
import React from "react";
import MangaFilters from "../layout/filters/manga/MangaFilters";
import {getLoggedInUserAnimeEntryStatuses, getLoggedInUserMangaEntryStatuses} from "../../actions/users";
import ErogeFilters from "../layout/filters/eroge/ErogeFilters";
import CharactersFilters from "../layout/filters/characters/CharactersFilters";
import {filterPeople} from "../../actions/people";
import PeopleFilters from "../layout/filters/people/PeopleFilters";


export function getTabName(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return ANIME_BROWSE_TAB_NAME;
        case BRANCH_MANGA:
            return MANGA_BROWSE_TAB_NAME;
        case BRANCH_CHARACTERS:
            return CHARACTERS_BROWSE_TAB_NAME;
        case BRANCH_EROGE:
            return EROGE_BROWSE_TAB_NAME;
        case BRANCH_PEOPLE:
            return PEOPLE_BROWSE_TAB_NAME;
        default:
            return 'Browse'
    }
}


export function getFiltersFromBranch(branch, season, list) {
    switch (branch) {
        case BRANCH_ANIME:
            return <AnimeFilters season={season} list={list}/>;
        case BRANCH_MANGA:
            return <MangaFilters season={season} list={list}/>;
        case BRANCH_CHARACTERS:
            return <CharactersFilters/>;
        case BRANCH_EROGE:
            return <ErogeFilters/>;
        case BRANCH_PEOPLE:
            return <PeopleFilters/>;
        default:
            return null
    }
}


export function getItemsPerPageFromBranch(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return BROWSE_ANIME_ITEMS_PER_PAGE;
        case BRANCH_MANGA:
            return BROWSE_MANGA_ITEMS_PER_PAGE;
        case BRANCH_CHARACTERS:
            return BROWSE_CHARACTERS_ITEMS_PER_PAGE;
        case BRANCH_EROGE:
            return BROWSE_EROGE_ITEMS_PER_PAGE;
        case BRANCH_PEOPLE:
            return BROWSE_PEOPLE_ITEMS_PER_PAGE;
        default:
            return null
    }
}


export function getResetValuesFromBranch(branch) {
    let values;
    switch (branch) {
        case BRANCH_ANIME:
            values = new FILTER_ANIME_INITIAL_VALUES();
            return values.VALUES;
        case BRANCH_MANGA:
            values = new FILTER_MANGA_INITIAL_VALUES();
            return values.VALUES;
        case BRANCH_CHARACTERS:
            values = new FILTER_CHARACTERS_INITIAL_VALUES();
            return values.VALUES;
        case BRANCH_EROGE:
            values = new FILTER_EROGE_INITIAL_VALUES();
            return values.VALUES;
        default:
            return null
    }
}

export function filterTitles(branch, page = 1, filters = undefined, season = undefined, list = undefined) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(filterAnime(page, filters, season, list));
            // store.dispatch(getAnimeSearchableTitles());
            store.dispatch(getLoggedInUserAnimeEntryStatuses());
            return;
        case BRANCH_MANGA:
            store.dispatch(filterManga(page, filters, season, list));
            store.dispatch(getLoggedInUserMangaEntryStatuses());
            return;
        case BRANCH_CHARACTERS:
            store.dispatch(filterCharacters(page, filters));
            return;
        case BRANCH_PEOPLE:
            store.dispatch(filterPeople(page, filters));
            return;
        case BRANCH_EROGE:
            store.dispatch(filterEroge(page, filters));
            return;
        default:
            return
    }
}