import {BRANCH_ANIME, BRANCH_CHARACTERS, BRANCH_EROGE, BRANCH_MANGA} from "../../../globals";
import {AnimeFilterNames, ErogeFilterNames, MangaFilterNames} from "./field_names";
import {
    deleteAnimeFilterPreset, deleteErogeFilterPreset, deleteMangaFilterPreset,
    saveAnimeFilterPreset, saveErogeFilterPreset, saveMangaFilterPreset,
    setSelectedAnimePreset, setSelectedErogePreset,
    setSelectedMangaPreset,
    updateAnimeFilterPreset, updateErogeFilterPreset,
    updateMangaFilterPreset
} from "../../../actions/users";
import store from "../../../store";


export function setPreset(branch, preset, new_values, filters) {
    switch (branch) {
        case BRANCH_ANIME:
            if (filters) {
                if (filters[AnimeFilterNames.downloaded] === 'true') new_values[AnimeFilterNames.downloaded] = true;
                if (filters[AnimeFilterNames.is_downloading] === 'true') new_values[AnimeFilterNames.is_downloading] = true;
                if (filters[AnimeFilterNames.cannot_download] === 'true') new_values[AnimeFilterNames.cannot_download] = true;
                if (filters[AnimeFilterNames.deleted_by_MAL] === 'true') new_values[AnimeFilterNames.deleted_by_MAL] = true;
                if (filters[AnimeFilterNames.pending_by_MAL] === 'true') new_values[AnimeFilterNames.pending_by_MAL] = true;
                if (filters[AnimeFilterNames.episodes_range]) new_values[AnimeFilterNames.episodes_range] = filters[AnimeFilterNames.episodes_range].split(',');
                if (filters[AnimeFilterNames.length_range]) new_values[AnimeFilterNames.length_range] = filters[AnimeFilterNames.length_range].split(',');
                if (filters[AnimeFilterNames.genres]) new_values[AnimeFilterNames.genres] = filters[AnimeFilterNames.genres].split(',');
                if (filters[AnimeFilterNames.type]) new_values[AnimeFilterNames.type] = filters[AnimeFilterNames.type].split(',');
                if (filters[AnimeFilterNames.status]) new_values[AnimeFilterNames.status] = filters[AnimeFilterNames.status].split(',');
                if (filters[AnimeFilterNames.rating_short]) new_values[AnimeFilterNames.rating_short] = filters[AnimeFilterNames.rating_short].split(',');
                if (filters[AnimeFilterNames.video_quality]) new_values[AnimeFilterNames.video_quality] = filters[AnimeFilterNames.video_quality].split(',');
                if (filters[AnimeFilterNames.audio_channel]) new_values[AnimeFilterNames.audio_channel] = filters[AnimeFilterNames.audio_channel].split(',');
                if (filters[AnimeFilterNames.user_exclude]) new_values[AnimeFilterNames.user_exclude] = filters[AnimeFilterNames.user_exclude].split(',');
                new_values[AnimeFilterNames.ordering] = filters[AnimeFilterNames.ordering];
            }
            store.dispatch(setSelectedAnimePreset(preset));
            return new_values;
        case BRANCH_MANGA:
            if (filters) {
                if (filters[MangaFilterNames.downloaded] === 'true') new_values[MangaFilterNames.downloaded] = true;
                if (filters[MangaFilterNames.is_downloading] === 'true') new_values[MangaFilterNames.is_downloading] = true;
                if (filters[MangaFilterNames.cannot_download] === 'true') new_values[MangaFilterNames.cannot_download] = true;
                if (filters[MangaFilterNames.deleted_by_MAL] === 'true') new_values[MangaFilterNames.deleted_by_MAL] = true;
                if (filters[MangaFilterNames.pending_by_MAL] === 'true') new_values[MangaFilterNames.pending_by_MAL] = true;
                if (filters[MangaFilterNames.volumes_range]) new_values[MangaFilterNames.volumes_range] = filters[MangaFilterNames.volumes_range].split(',');
                if (filters[MangaFilterNames.chapters_range]) new_values[MangaFilterNames.chapters_range] = filters[MangaFilterNames.chapters_range].split(',');
                if (filters[MangaFilterNames.genres]) new_values[MangaFilterNames.genres] = filters[MangaFilterNames.genres].split(',');
                if (filters[MangaFilterNames.type]) new_values[MangaFilterNames.type] = filters[MangaFilterNames.type].split(',');
                if (filters[MangaFilterNames.status]) new_values[MangaFilterNames.status] = filters[MangaFilterNames.status].split(',');
                if (filters[MangaFilterNames.user_exclude]) new_values[MangaFilterNames.user_exclude] = filters[MangaFilterNames.user_exclude].split(',');
                new_values[MangaFilterNames.ordering] = filters[MangaFilterNames.ordering];
            }
            store.dispatch(setSelectedMangaPreset(preset));
            return new_values;
        case BRANCH_CHARACTERS:
            return new_values;
        case BRANCH_EROGE:
            if (filters) {
                if (filters[ErogeFilterNames.downloaded] === 'true') new_values[ErogeFilterNames.downloaded] = true;
                if (filters[ErogeFilterNames.type]) new_values[ErogeFilterNames.type] = filters[ErogeFilterNames.type].split(',');
                if (filters[ErogeFilterNames.platforms]) new_values[ErogeFilterNames.platforms] = filters[ErogeFilterNames.platforms].split(',');
                if (filters[ErogeFilterNames.censorship]) new_values[ErogeFilterNames.censorship] = filters[ErogeFilterNames.censorship].split(',');
                if (filters[ErogeFilterNames.language]) new_values[ErogeFilterNames.language] = filters[ErogeFilterNames.language].split(',');
                if (filters[ErogeFilterNames.tags]) new_values[ErogeFilterNames.tags] = filters[ErogeFilterNames.tags].split(',');
                new_values[ErogeFilterNames.ordering] = filters[ErogeFilterNames.ordering];
            }
            store.dispatch(setSelectedErogePreset(preset));
            return new_values;
        default:
            return new_values
    }
}


export function updateFilterPreset(branch, preset_id, filters) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(updateAnimeFilterPreset(preset_id, filters));
            return;
        case BRANCH_MANGA:
            store.dispatch(updateMangaFilterPreset(preset_id, filters));
            return;
        case BRANCH_CHARACTERS:
            return;
        case BRANCH_EROGE:
            store.dispatch(updateErogeFilterPreset(preset_id, filters));
            return;
        default:
            return
    }
}


export function saveFilterPreset(branch, preset_name, filters) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(saveAnimeFilterPreset(preset_name, filters));
            return;
        case BRANCH_MANGA:
            store.dispatch(saveMangaFilterPreset(preset_name, filters));
            return;
        case BRANCH_CHARACTERS:
            return;
        case BRANCH_EROGE:
            store.dispatch(saveErogeFilterPreset(preset_name, filters));
            return;
        default:
            return
    }
}


export function deleteFilterPreset(branch, preset_id) {
    switch (branch) {
        case BRANCH_ANIME:
            store.dispatch(deleteAnimeFilterPreset(preset_id));
            return;
        case BRANCH_MANGA:
            store.dispatch(deleteMangaFilterPreset(preset_id));
            return;
        case BRANCH_CHARACTERS:
            return;
        case BRANCH_EROGE:
            store.dispatch(deleteErogeFilterPreset(preset_id));
            return;
        default:
            return
    }
}


export function getSelectedPreset(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return store.getState().users.selected_anime_preset;
        case BRANCH_MANGA:
            return store.getState().users.selected_manga_preset;
        case BRANCH_CHARACTERS:
            return;
        case BRANCH_EROGE:
            return store.getState().users.selected_eroge_preset;
        default:
            return
    }
}
