import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faCircle, faPause, faPlay, faStop} from "@fortawesome/free-solid-svg-icons";
import CountUp from "react-countup";
import {User, UserList} from "../../../models";
import {faCalendarAlt} from "@fortawesome/free-regular-svg-icons";
import {CyberLink} from "../../layout/common/common";
import {USER_ANIME_STATISTICS} from "../../../urls";
import {Button} from "@material-ui/core";
import React from "react";
import {useSelector} from "react-redux";
import {Doughnut} from "react-chartjs-2";
import {isEmpty} from "../../functions";

function getData(pie_data) {
    let stat_data = [];
    let labels = [];
    for (let x = 0; x < pie_data.length; x++) {
        stat_data.push(pie_data[x].stat);
        labels.push(pie_data[x].label);
    }

    return  {
        labels: labels,
        legend: {
            display: false,
        },
        datasets: [{
            data: stat_data,
            backgroundColor: [
                // COLOR_INFO,
                'rgba(21, 255, 255, 0.4)',
                // COLOR_PRIMARY,
                'rgba(21, 107, 183, 0.4)',
                // COLOR_WARNING,
                'rgba(208, 156, 0, 0.5)',
                // COLOR_DANGER,
                'rgba(255, 0, 0, 0.2)',
                // COLOR_GRAY,
                'rgba(123, 123, 123, 0.4)',
            ],
            hoverBackgroundColor: [
                // COLOR_INFO,
                'rgba(21, 255, 255, 0.8)',
                // COLOR_PRIMARY,
                'rgba(21, 107, 183, 0.8)',
                // COLOR_WARNING,
                'rgba(208, 156, 0, 0.8)',
                // COLOR_DANGER,
                'rgba(255, 0, 0, 0.8)',
                // COLOR_GRAY,
                'rgba(123, 123, 123, 0.8)',
            ],
            borderColor: [
                // COLOR_INFO,
                'rgba(21, 255, 255, 0.3)',
                // COLOR_PRIMARY,
                'rgba(21, 107, 183, 0.3)',
                // COLOR_WARNING,
                'rgba(208, 156, 0, 0.3)',
                // COLOR_DANGER,
                'rgba(255, 0, 0, 0.1)',
                // COLOR_GRAY,
                'rgba(123, 123, 123, 0.3)',
            ],
            hoverBorderColor: [
                // COLOR_INFO,
                'rgba(21, 255, 255, 1)',
                // COLOR_PRIMARY,
                'rgba(21, 107, 183, 1)',
                // COLOR_WARNING,
                'rgba(208, 156, 0, 1)',
                // COLOR_DANGER,
                'rgba(255, 0, 0, 1)',
                // COLOR_GRAY,
                'rgba(123, 123, 123, 1)',
            ],
            // borderColor: 'rgba(255, 255, 255, 0.9)',
            // hoverBorderColor: '#156bb7',
            borderWidth: 0
        }]
    };
}


export function AnimeSimpleChartStats(props) {
    const {style} = props;
    const user_list = useSelector(state => state.users.user_list);
    if (isEmpty(user_list))
        return null;
    let pie_data = [
        {label: 'Watching', stat: user_list[UserList.anime_watching], color: '', hoverColor: ''},
        {label: 'Completed', stat: user_list[UserList.anime_completed], color: '', hoverColor: ''},
        {label: 'On Hold', stat: user_list[UserList.anime_on_hold], color: '', hoverColor: ''},
        {label: 'Dropped', stat: user_list[UserList.anime_dropped], color: '', hoverColor: ''},
        {label: 'Planned', stat: user_list[UserList.anime_plan_to_watch], color: '', hoverColor: ''},
    ];
    const data = getData(pie_data);

    return <div style={style} className="User-List-Stats-Container">
        <h2>Anime</h2>
        <div className="pie-wrapper">
            <Doughnut height={100} width={100} data={data} options={{ legend: false}}/>
        </div>
        <div className="chip-wrapper">
            <div className="stat-chip">
                <Chip className="border-info color-info"
                      variant="outlined"
                      label={<>
                          <FontAwesomeIcon className="list-icon" icon={faPlay}/>
                          <span>Watching<CountUp end={user_list[UserList.anime_watching]}/></span>
                      </>}/>
            </div>
            <div className="stat-chip">
                <Chip
                    className="border-primary color-primary"
                    variant="outlined"
                    label={<>
                        <FontAwesomeIcon className="list-icon" icon={faCheck}/>
                        <span>Completed<CountUp end={user_list[UserList.anime_completed]}/></span>
                    </>}/>
            </div>
            <div className="stat-chip">
                <Chip
                    className="border-warning color-warning"
                    variant="outlined"
                    label={<>
                        <FontAwesomeIcon className="list-icon" icon={faPause}/>
                        <span>On Hold<CountUp end={user_list[UserList.anime_on_hold]}/></span>
                    </>}/>
            </div>
            <div className="stat-chip">
                <Chip
                    className="border-danger color-danger"
                    variant="outlined"
                    label={<>
                        <FontAwesomeIcon className="list-icon" icon={faStop}/>
                        <span>Dropped<CountUp end={user_list[UserList.anime_dropped]}/></span>
                    </>}/>
            </div>
            <div className="stat-chip">
                <Chip
                    className="border-gray color-gray"
                    variant="outlined"
                    label={<>
                        <FontAwesomeIcon className="list-icon" icon={faCalendarAlt}/>
                        <span>Planned<CountUp end={user_list[UserList.anime_plan_to_watch]}/></span>
                    </>}/>
            </div>
            <div className="stat-chip">
                <Chip
                    className="border-white color-white"
                    variant="outlined"
                    label={<>
                        <FontAwesomeIcon className="list-icon" icon={faCircle}/>
                        <span>Total<CountUp end={user_list[UserList.anime_total_entries]}/></span>
                    </>}/>
            </div>
            {/*<div className="stat-chip">*/}
            {/*    <CyberLink to={USER_ANIME_STATISTICS(this.props.visited_user[User.id])}>*/}
            {/*        <Button variant="outlined" className="btn-info">Statistics</Button>*/}
            {/*    </CyberLink>*/}
            {/*</div>*/}
        </div>
    </div>
}


