import React, {Component} from 'react'
import PropTypes from "prop-types";
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import Chip from "@material-ui/core/Chip";
import Slider from "@material-ui/core/Slider";
import TextField from "@material-ui/core/TextField";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    faArrowDown,
    faFilter, faPlus, faSave, faSortAmountDown, faSortAmountDownAlt,
    faSortNumericDown,
    faSortNumericDownAlt,
    faTimes, faTrash,
    faUndoAlt
} from "@fortawesome/free-solid-svg-icons";
import Autocomplete from '@material-ui/lab/Autocomplete';

import {setCookieShowFilters} from "../../../actions/cookies";
import {filterTitles, getResetValuesFromBranch} from "../../browse/functions";
import {ApiResult, FiltersPreset} from "../../../models";
import {CyberButton} from "../common/common";
import {AnimeFilterNames, FilterSchema, FilterSchemaType} from "./field_names";
import store from "../../../store";

import '../../../assets/css/filters.css'
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {Filter} from "@material-ui/icons";
import CyberFooter from "../footer/CyberFooter";
import {saveAnimeFilterPreset, setSelectedAnimePreset} from "../../../actions/users";
import PresetFilters from "./PresetFilters";
// import SearchAutocomplete from "./SearchAutocomplete";
import {setPreset} from "./functions";
import {act} from "@testing-library/react";
import Virtualize from "./SearchAutocomplete";
import {playClickSound} from "../sounds";


function getSliderLabel(values, max, label_name) {
    let value_2 = `${values[1]}`;
    if (values[1] === max)
        value_2 = `${values[1] - 1}+`;
    return `${values[0]} to ${value_2} ${label_name.split('_')[0]}`
}

function checkSliderDefaults(values, min, max) {
    return !(parseInt(values[0]) === min && parseInt(values[1]) === max);
}

function getGenreImgSrc(name, values) {
    const img_name = name.toLowerCase().replaceAll(' ', '_');
    let ext = 'jpg';
    if (values.indexOf(name) >= 0) ext = 'gif';
    return `/images/genre_icons/${img_name}.${ext}`
}

function getOrderIcon(order, active) {
    let ordering = order;
    if (ordering.indexOf('-') === 0 && ordering.replace('-', '') === active)
        return faSortAmountDown;
    return faSortAmountDownAlt
}

function getOrderValue(order, active) {
    return order.replace('-', '') === active;
}

/**
 * Adjusts the height of the filters wrapper based on
 * active filters, if no filters are active, the height is set to 0
 */
function changeHeightOfActiveFiltersWrapperOnChange() {
    let active_filters = document.getElementById('Active-Filters');
    if (active_filters) {
        if (active_filters.childNodes.length > 0) active_filters.parentNode.style.height = 'auto';
        else active_filters.parentNode.style.height = '0';
    }
}

// noinspection JSIgnoredPromiseFromCall,JSUnresolvedVariable
class TitlesFilter extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
        /** Redux prop of titles_loading */
        titles_loading: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        filtered_titles_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        filtered_titles: PropTypes.object.isRequired,
        /** Previous filters from cookies */
        title_filters: PropTypes.object.isRequired,
        /** Selected season, if called from display season page */
        season: PropTypes.object,
        /** User list object {pk: selected filter:} if called from user list page */
        list: PropTypes.object,
        /** Main filters */
        main_filters: PropTypes.array,
        /** Regular filters */
        regular_filters: PropTypes.array,
        /** Wide filters (ex. Genres) */
        wide_filters: PropTypes.array,
        /** Ordering filters */
        order_filters: PropTypes.array,
        /** Preset filters for the user */
        presets: PropTypes.array,
    };

    constructor() {
        super(...arguments);
        this.onGenreEnter = this.onGenreEnter.bind(this);
        this.onGenreLeave = this.onGenreLeave.bind(this);
        this.onGenreChange = this.onGenreChange.bind(this);
        this.checkFormValuesFromCookies = this.checkFormValuesFromCookies.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onReset = this.onReset.bind(this);

        this.state = this.props.title_filters;
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (JSON.stringify(prevProps.title_filters) !== JSON.stringify(this.props.title_filters))
            this.setState(this.props.title_filters);
    }

    /**
     * Update the Formik values with the cookie filter values, because sometimes
     * when filtering from a user list the values don't update correctly.
     * @returns {boolean} True if the values should be updated
     */
    checkFormValuesFromCookies() {
        let filters = Object.assign({}, this.state);
        let cookie_filters = Object.assign({}, this.props.title_filters);
        return JSON.stringify(filters) !== JSON.stringify(cookie_filters);
    }

    /**
     * When entering a genre div, change the image .jpg to .gif.
     * @param genreFilter {String} name of the genre.
     */
    onGenreEnter(genreFilter) {
        let img = document.getElementById("img_" + genreFilter);
        if (!img.classList.contains('active-genre'))
            img.src = img.src.slice(0, -3) + 'gif';
    }

    /**
     * When leaving the genre div revert the .gif image back to .jpg.
     * @param genreFilter {String} name of the genre.
     */
    onGenreLeave(genreFilter) {
        let img = document.getElementById("img_" + genreFilter);
        if (!img.classList.contains('active-genre'))
            img.src = img.src.slice(0, -3) + 'jpg';
    }

    /**
     * When clicking on a genre set it as active by adding a css
     * class, or remove the class if it was already active.
     * @param e {Object} Mouse event.
     */
    onGenreChange(e) {
        if (e.target.checked)
            e.target.nextSibling.firstChild.classList.add("active-genre");
        else
            e.target.nextSibling.firstChild.classList.remove("active-genre");
    };

    /**
     * Called on submit of the filters, calls the filter
     * title action with the active filters
     * @param e {Object} Mouse event
     * @param preset {Object} Optional, Filter preset, default null
     */
    onSubmit(e, preset = null) {
        e.preventDefault();
        console.log('one order:', this.state);
        if (preset)
            this.setState(preset, () => {
                this.onSubmit(e)
            });
        else filterTitles(this.props.branch, 1, this.state, this.props.season, this.props.list);
    }

    /**
     * Called on reset of the filters, resets the active
     * filters and ordering to default, and calls the filter title action
     */
    onReset() {
        const reset_values = getResetValuesFromBranch(this.props.branch);
        this.setState(reset_values);
        setPreset(this.props.branch, {});
        filterTitles(this.props.branch, 1, reset_values, this.props.season, this.props.list);
    }

    render() {
        const uses_presets = this.props.presets !== undefined;
        const field_names = {
            search: 'search',
            ordering: 'ordering',
        };
        changeHeightOfActiveFiltersWrapperOnChange();

        return (
            <Grid id="Filters" container>
                <Grid item xs={12}>
                    <form className="filters-form" onSubmit={this.onSubmit}>
                        <Grid container>
                            <Grid item xs={12} sm={6} md={4} lg={3} className="search-field-container">
                                {/*<Virtualize titles={this.props.searchable_titles}/>*/}
                                {/*<SearchAutocomplete/>*/}
                                {/*<Autocomplete*/}
                                {/*    options={this.props.searchable_titles}*/}
                                {/*    id="controlled-demo"*/}
                                {/*    value={this.state[field_names.search]}*/}
                                {/*    onChange={(e, new_value) => {*/}
                                {/*                        this.setState({[e.target.name]: new_value});*/}
                                {/*        }}*/}
                                {/*    renderInput={(params) => <TextField {...params}*/}
                                {/*                    label="Search"*/}
                                {/*                    className="form-control search-field"*/}
                                {/*                    name={field_names.search}*/}
                                {/*                    />}*/}
                                {/*  />*/}
                                {/*<Autocomplete*/}
                                {/*    id="highlights-demo"*/}
                                {/*    style={{width: 300}}*/}
                                {/*    options={searchable_titles}*/}
                                {/*    getOptionLabel={(option) => option.title}*/}
                                {/*    renderInput={(params) => (*/}
                                {/*        <TextField {...params}*/}
                                {/*                    label="Search"*/}
                                {/*                    className="form-control search-field"*/}
                                {/*                    value={this.state[field_names.search]}*/}
                                {/*                    name={field_names.search}*/}
                                {/*                    onChange={(e) => {*/}
                                {/*                        this.setState({[e.target.name]: e.target.value});*/}
                                {/*        }}/>*/}
                                {/*    )}*/}
                                {/*    renderOption={(option, {inputValue}) => {*/}
                                {/*        const matches = match(option.title, inputValue);*/}
                                {/*        const parts = parse(option.title, matches);*/}

                                {/*        return (*/}
                                {/*            <div>*/}
                                {/*                {parts.map((part, index) => (*/}
                                {/*                    <span key={index} style={{fontWeight: part.highlight ? 700 : 400}}>*/}
                                {/*            {part.text}*/}
                                {/*          </span>*/}
                                {/*                ))}*/}
                                {/*            </div>*/}
                                {/*        );*/}
                                {/*    }}*/}
                                {/*/>*/}
                                <TextField
                                    label="Search"
                                    className="form-control search-field"
                                    value={this.state[field_names.search]}
                                    name={field_names.search}
                                    onChange={(e) => {
                                        this.setState({[e.target.name]: e.target.value});
                                    }}/>
                            </Grid>
                            <Grid item xs={0} md={uses_presets ? 3 : 5} lg={uses_presets ? 4 : 6}>
                                <p className="total-results">Results: <span className="total-results-span">
                                {!this.props.titles_loading ?
                                    this.props.filtered_titles[ApiResult.count] : 'Loading..'}</span>
                                </p>
                            </Grid>
                            {uses_presets ? <Grid item xs={12} md={2}>
                                <PresetFilters
                                    branch={this.props.branch}
                                    presets={this.props.presets}
                                    filters={this.state}
                                    onSubmit={this.onSubmit}/>
                            </Grid> : null}
                            <Grid item xs={4} md={1} className="filter-buttons">
                                <CyberButton variant="outlined" onClick={() => {
                                    store.dispatch(setCookieShowFilters(!this.props.show_filters))
                                }}><FontAwesomeIcon
                                    icon={this.props.show_filters ? faTimes : faArrowDown}/></CyberButton>
                            </Grid>
                            <Grid item xs={4} md={1} className="filter-buttons">
                                <CyberButton variant="outlined" onClick={this.onReset}>
                                    <FontAwesomeIcon icon={faUndoAlt}/></CyberButton>
                            </Grid>
                            <Grid item xs={4} md={1} className="filter-buttons">
                                <CyberButton type="submit" variant="outlined">
                                    <FontAwesomeIcon icon={faFilter}/></CyberButton>
                            </Grid>
                        </Grid>
                        <Grid container>
                            <Grid item xs={12} className="text-center" style={{height: 0}}>
                                <div id="Active-Filters" className="active-filters-wrapper">
                                    {this.props.main_filters ? this.props.main_filters.map((value, x) => (
                                        this.state[value[FilterSchema.filter_name]] ?
                                            <Chip key={x} className={`chip-${value[FilterSchema.color]}`}
                                                  size="small" label={value[FilterSchema.label]}/> : null
                                    )) : null}
                                    {this.props.regular_filters ? this.props.regular_filters.map((field) => (
                                        field[FilterSchema.type] === FilterSchemaType.standard ?
                                            this.state[field[FilterSchema.filter_name]].map((filter, i) => (
                                                <Chip key={i} className={`chip-${field[FilterSchema.color]}`}
                                                      size="small" label={filter}/>
                                            )) : field[FilterSchema.type] === FilterSchemaType.slider ?
                                            checkSliderDefaults(this.state[field[FilterSchema.filter_name]],
                                                field[FilterSchema.min], field[FilterSchema.max]) ?
                                                <Chip className={`chip-${field[FilterSchema.color]}`} size="small"
                                                      label={getSliderLabel(this.state[field[FilterSchema.filter_name]], field[FilterSchema.max], field[FilterSchema.filter_name])}/>
                                                : null : null
                                    )) : null}
                                    {this.props.wide_filters ? this.props.wide_filters.map((field) => (
                                        this.state[field[FilterSchema.filter_name]].map((filter, i) => (
                                            <Chip key={i} className={`chip-${field[FilterSchema.color]}`}
                                                  size="small" label={filter}/>
                                        ))
                                    )) : null}
                                </div>
                            </Grid>
                        </Grid>
                        <div id="filters-collapsable" className={this.props.show_filters ? 'show-filters' : null}>
                            <Grid container>
                                {this.props.main_filters ?
                                    <Grid item xs={12} md={6} lg={4} className="filter-group">
                                        <h6 className="filter-title">Main Filters</h6>
                                        {this.props.main_filters.map((value, x) => (
                                            <div key={x} className="input-group">
                                                <input id={value[FilterSchema.filter_name]}
                                                       type="checkbox"
                                                       name={value[FilterSchema.filter_name]}
                                                       hidden="hidden"
                                                       checked={this.state[value[FilterSchema.filter_name]]}
                                                       onChange={(e) => {
                                                           this.setState({
                                                               [e.target.name]: !this.state[value[FilterSchema.filter_name]]
                                                           });
                                                       }}
                                                />
                                                <label htmlFor={value[FilterSchema.filter_name]}
                                                       className={`label-${value[FilterSchema.color]}`}>
                                                    {value[FilterSchema.label]}</label>
                                            </div>
                                        ))}
                                    </Grid> : null}

                                {this.props.regular_filters ? this.props.regular_filters.map((field, x) => (
                                    <Grid key={x} item xs={12} md={6} lg={4} className="filter-group">
                                        <h6 className="filter-title">{field[FilterSchema.filter_name].replaceAll('_', ' ')}</h6>
                                        {field[FilterSchema.type] === FilterSchemaType.standard ? field[FilterSchema.content].map((filter, y) => (
                                            <div key={y} className="input-group">
                                                <input
                                                    id={`${field[FilterSchema.filter_name]}_${filter}`}
                                                    type="checkbox"
                                                    name={field[FilterSchema.filter_name]}
                                                    onChange={(e) => {
                                                        if (this.state[field[FilterSchema.filter_name]].indexOf(filter) >= 0) {
                                                            let filters = this.state[field[FilterSchema.filter_name]];
                                                            filters.splice(filters.indexOf(filter), 1);
                                                            this.setState({
                                                                [e.target.name]: filters
                                                            });
                                                        } else {
                                                            let filters = this.state[field[FilterSchema.filter_name]];
                                                            filters.push(filter);
                                                            this.setState({
                                                                [e.target.name]: filters
                                                            });
                                                        }
                                                    }}
                                                    checked={this.state[field[FilterSchema.filter_name]].indexOf(filter) >= 0}
                                                    hidden="hidden"/>
                                                <label
                                                    htmlFor={`${field[FilterSchema.filter_name]}_${filter}`}
                                                    className={`label-${field[FilterSchema.color]}`}>{filter}</label>
                                            </div>
                                        )) : field[FilterSchema.type] === FilterSchemaType.slider ?
                                            <Slider
                                                className="slider-group"
                                                id={field[FilterSchema.filter_name]}
                                                value={this.state[field[FilterSchema.filter_name]]}
                                                onChange={(event, value) => {
                                                    this.setState({
                                                        [field[FilterSchema.filter_name]]: value
                                                    })
                                                }}
                                                max={field[FilterSchema.max]}
                                                min={field[FilterSchema.min]}
                                                valueLabelDisplay="auto"
                                                aria-labelledby="range-slider"
                                                marks={field[FilterSchema.marks]}
                                            />
                                            : null}
                                    </Grid>
                                )) : null}
                            </Grid>

                            {this.props.wide_filters ? <Grid container>
                                {this.props.wide_filters.map((field, x) => (
                                    <Grid key={x} item xs={12} className="filter-group">
                                        <h6 className="filter-title">{field[FilterSchema.filter_name]}</h6>
                                        {field[FilterSchema.content].map((filter, i) => (
                                            <div key={i} className="genre-container">
                                                <input id={filter} type="checkbox" hidden="hidden"
                                                       name={field[FilterSchema.filter_name]}
                                                       onChange={(e) => {
                                                           if (this.state[field[FilterSchema.filter_name]].indexOf(filter) >= 0) {
                                                               let filters = this.state[field[FilterSchema.filter_name]];
                                                               filters.splice(filters.indexOf(filter), 1);
                                                               this.setState({
                                                                   [e.target.name]: filters
                                                               });
                                                           } else {
                                                               let filters = this.state[field[FilterSchema.filter_name]];
                                                               filters.push(filter);
                                                               this.setState({
                                                                   [e.target.name]: filters
                                                               });
                                                           }
                                                       }}
                                                       onClick={this.onGenreChange}
                                                       checked={this.state[field[FilterSchema.filter_name]].indexOf(filter) >= 0}
                                                />
                                                <label htmlFor={filter}
                                                       onMouseEnter={() => {
                                                           this.onGenreEnter(filter)
                                                       }}
                                                       onMouseLeave={() => {
                                                           this.onGenreLeave(filter)
                                                       }}>
                                                    <img id={`img_${filter}`}
                                                         className={this.state[field[FilterSchema.filter_name]].indexOf(filter) >= 0 ? 'active-genre' : null}
                                                         src={getGenreImgSrc(filter, this.state[field[FilterSchema.filter_name]])}
                                                         alt=""/>
                                                    <div><p>{filter}</p></div>
                                                </label>
                                            </div>
                                        ))}
                                    </Grid>
                                ))}
                            </Grid> : null}
                        </div>

                        {this.props.order_filters ? <Grid container className="ordering-container">
                            <Grid item xs={12}>
                                {this.props.order_filters.map((order, i) => (
                                    <div key={i} className="input-group" onMouseLeave={() => {
                                        let container = document.getElementById(`order_btn_container_${order}`);
                                        container.classList.remove('hovered-order-button-container')
                                    }}>
                                        <label id={`order_label_${order}`}
                                               className={getOrderValue(this.state[field_names.ordering], order) ? 'no-hover-color active-order-label' : 'no-hover-color no-border'}
                                               onMouseEnter={() => {
                                                   let container = document.getElementById(`order_btn_container_${order}`);
                                                   let width = document.getElementById(`order_label_${order}`).offsetWidth;
                                                   container.classList.add('hovered-order-button-container');
                                                   container.setAttribute('style', `margin-left: calc(${width / 2}px - 35px);`)
                                               }}>
                                            {getOrderValue(this.state[field_names.ordering], order) ? <FontAwesomeIcon
                                                icon={getOrderIcon(this.state[field_names.ordering], order)}/> : null}
                                            <span className="order-label">{order.replaceAll('_', ' ')}</span>
                                        </label>
                                        <div id={`order_btn_container_${order}`} className="order-buttons-container">
                                            <div className="order-btn">
                                                <input id={`order_dc_${order}`} type="radio" value={order}
                                                       checked={getOrderValue(this.state[field_names.ordering], order)}
                                                       hidden="hidden" onClick={(e) => {
                                                    playClickSound();
                                                    this.setState({
                                                        [field_names.ordering]: `-${order}`
                                                    }, () => {
                                                        this.onSubmit(e);
                                                    });
                                                }}/>
                                                <label htmlFor={`order_dc_${order}`}>
                                                    <FontAwesomeIcon icon={faSortAmountDown}/>
                                                </label>
                                            </div>
                                            <div className="order-btn">
                                                <input id={`order_ac_${order}`} type="radio" value={order}
                                                       checked={getOrderValue(this.state[field_names.ordering], order)}
                                                       hidden="hidden" onClick={(e) => {
                                                    playClickSound();
                                                    this.setState({
                                                        [field_names.ordering]: e.target.value
                                                    }, () => {
                                                        this.onSubmit(e);
                                                    });
                                                }}/>
                                                <label htmlFor={`order_ac_${order}`}>
                                                    <FontAwesomeIcon icon={faSortAmountDownAlt}/>
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </Grid>
                        </Grid> : null}
                    </form>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    show_filters: state.cookies.show_filters,
    searchable_titles: state.anime.searchable_titles,
});

export default connect(mapStateToProps, {})(TitlesFilter)
