import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {playClickSound, playHoverSound} from "../layout/sounds";


function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Material UI tabs. Supply it with tab information to render the tabs.
 * @param props.tabs {Object} Array of tab information.
 * @returns {JSX.Element}
 * @constructor
 */
export function DisplayTabs(props) {
    const {tabs} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root + ' tabs-root'}>
            <AppBar position="static" className="cyber-tabs">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    textColor="default"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    {tabs.map((tab, x) => (
                        <Tab disabled={tab.disabled} label={tab.label} {...a11yProps(x)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    ))}
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                {tabs.map((tab, x) => (
                    <TabPanel value={value} index={x} dir={theme.direction} className="box-light">
                        {tab.tab}
                    </TabPanel>
                ))}
            </SwipeableViews>
        </div>
    );
}