import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_CHARACTERS} from "../../../globals";
import Display from "../../display/Display";


class CharacterDisplay extends Component {
    render() {
        return <Display
            branch={BRANCH_CHARACTERS}
            param_id={this.props.match.params.id}
            title_loading={this.props.characters_loading}
            title_loaded={this.props.characters_loaded}
            title={this.props.character}
        />
    }
}

const mapStateToProps = state => ({
    character: state.characters.character,
    characters_loaded: state.characters.characters_loaded,
    characters_loading: state.characters.characters_loading,
});

export default connect(mapStateToProps, {})(CharacterDisplay)
