import React from 'react';
import {Button} from "@material-ui/core";
import {playClickSound, playHoverSound} from "../sounds";
import {Link} from "react-router-dom";
import {ANIME_GENRES} from "../../../globals";

/**
 * Cyber-Genesis version of the Button component from Material-ui
 * @param props.classNames {String}
 * @param props.variant {String}
 * @param props.component {String}
 * @param props.color {String}
 * @param props.type {String}
 * @param props.disabled {Boolean}
 * @param props.title {String}
 * @param props.onClick {Function}
 * @param props.children {JSX.Element}
 * @returns {JSX.Element}
 * @constructor
 */
export function CyberButton(props) {
    const {classNames, variant, component, color, type, disabled, title, onClick, children} = props;
    return <Button type={type} component={component} variant={variant} color={color} className={classNames} onMouseEnter={playHoverSound}
                   disabled={disabled} title={title}
                   onClick={() => {
                       playClickSound();
                       if (onClick) onClick()
                   }}>
        {children}
    </Button>
}

/**
 * Cyber-Genesis version of the Link component from Material-ui
 * @param props.classNames {Object}
 * @param props.to {Object}
 * @param props.children {Object}
 * @returns {JSX.Element}
 * @constructor
 */
export function CyberLink(props) {
    const {classNames, to, children} = props;
    return <Link to={to} className={classNames} onMouseEnter={playHoverSound} onClick={playClickSound}>
        {children}
    </Link>
}

/**
 *
 * @param props.src {string} Src of the image.
 * @param props.alt {string} Alt of the image.
 * @param props.className {string} Additional classes for the image.
 * @returns {JSX.Element}
 * @constructor
 */
export function PreLoadImage(props) {
    const {className, src, alt} = props;

    const [loaded, setLoaded] = React.useState(false);

    return <>
        <img src={src}
             className="lazy-loaded-img" alt="loader-img"
             onLoad={() => {setLoaded(true)}}
        />
        <img className={loaded ? className ? `fade-in ${className}` : 'fade-in' : className ? `before-fade-in ${className}` : 'before-fade-in'}
             src={src} alt={alt ? alt : 'Image'}
        />
    </>
}

/**
 *
 * @returns {JSX.Element}
 * @constructor
 */
export function LoadGifs(props) {
    console.log('loading gifs');
    return <div style={{position: 'absolute', opacity: 0, height: 0}}>
        {ANIME_GENRES.map(genre => (
            <img style={{position: 'absolute', opacity: 0, height: 0}} src={`/images/genre_icons/${genre}.gif`}/>
        ))}
    </div>
}