import axios from 'axios';

import {
    GET_ANIME,
    ANIME_LOADING,
    FILTER_ANIME,
    CANCEL_ANIME_LOADING,
    GET_ANIME_DATA,
    GET_ALL_CHARACTERS_FOR_ANIME,
    CHARACTERS_LOADING,
    GET_RELATIONS_FOR_ANIME,
    ANIME_FRANCHISE_LOADING,
    GET_CURRENT_ANIME_FRANCHISE,
    UPDATE_USER_ENTRY_IN_FILTERED_USER_ANIME_ENTRIES,
    GET_ANIME_DATA_STATISTICS,
    GET_ANIME_SEARCHABLE_TITLES, GET_ANIME_ACHIEVEMENT_THRESHOLDS
} from './types'
import {
    API_FILTER_ANIME,
    API_GET_ALL_CHARACTERS_FOR_ANIME, API_GET_ANIME_ACHIEVEMENT_THRESHOLDS,
    API_GET_ANIME_DATA,
    API_GET_ANIME_DATA_FOR_HOME_PAGE,
    API_GET_ANIME_DATA_WITH_STATISTICS,
    API_GET_ANIME_FRANCHISE,
    API_GET_ANIME_SEARCHABLE_TITLES,
    API_GET_ONE_ANIME,
    API_GET_RELATIONS_FOR_ANIME
} from "./api_urls";
import {returnErrors} from "./messages";
import {AnimeFilterNames, MangaFilterNames} from "../components/layout/filters/field_names";
import {setCookieAnimeFilters} from "./cookies";
import {isEmpty} from "../components/functions";
import {TYPE_BROWSE, TYPE_LIST, TYPE_SEASON, USER_ANIME_ORDERINGS} from "../globals";
import {tokenConfig} from "./auth";


// GET ANIME DATA
export const getAnimeData = () => (dispatch, getState) => {
    dispatch({type: ANIME_LOADING});
    axios
        .get(API_GET_ANIME_DATA, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_ANIME_DATA,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch(cancelAnimeLoading());
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


