// GET ALL CHARACTERS FOR ANIME
export const getAllCharactersForAnime = (id, page) => (dispatch, getState) => {
    const token = getState().auth.token;
    const config = {params: {}, headers: {'Content-Type': 'application/json'}};
    if (token) config.headers['Authorization'] = `Token ${token}`;
    if (page && page !== 1) config.params['page'] = page;

    axios
        .get(API_GET_ALL_CHARACTERS_FOR_ANIME(id), config)
        .then(res => {
            dispatch({
                type: GET_ALL_CHARACTERS_FOR_ANIME,
                payload: res.data
            })
        })
        .catch(err => {
        })
};


