// FILTER ALL ANIME
export const filterAnime = (page, filters, season, list, loading = true, save_cookie = true) => (dispatch, getState) => {
    if (loading)
        dispatch({type: ANIME_LOADING});

    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;

    let page_type = TYPE_BROWSE;
    if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
        config.params = setAnimeFilterParams(config.params, filters);
    if (season !== undefined && season !== 'undefined' && !isEmpty(season)) {
        config.params = setAnimeSeasonParams(config.params, season);
        page_type = TYPE_SEASON;
    }
    if (list !== undefined && list !== 'undefined' && !isEmpty(list)) {
        config.params = setAnimeListParams(config.params, list);
        page_type = TYPE_LIST;
    } else if (USER_ANIME_ORDERINGS.indexOf(config.params[AnimeFilterNames.ordering].replace('-', '')) > -1)
        config.params[AnimeFilterNames.ordering] = '-members';

    axios
        .get(API_FILTER_ANIME, config)
        .then(res => {
            dispatch({
                type: FILTER_ANIME,
                payload: res.data
            });
            if (save_cookie && filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
                dispatch(setCookieAnimeFilters(filters, page_type))
        })
        .catch(err => {
            dispatch(cancelAnimeLoading());
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// CANCEL ANIME LOADING
export const cancelAnimeLoading = () => dispatch => {
    dispatch({
        type: CANCEL_ANIME_LOADING,
    })
};


export function setAnimeFilterParams(params, filters) {
    if (filters[AnimeFilterNames.search] && typeof filters[AnimeFilterNames.search] == 'string') params[AnimeFilterNames.search] = filters[AnimeFilterNames.search];
    if (filters[AnimeFilterNames.downloaded]) params[AnimeFilterNames.downloaded] = filters[AnimeFilterNames.downloaded];
    if (filters[AnimeFilterNames.is_downloading]) params[AnimeFilterNames.is_downloading] = filters[AnimeFilterNames.is_downloading];
    if (filters[AnimeFilterNames.cannot_download]) params[AnimeFilterNames.cannot_download] = filters[AnimeFilterNames.cannot_download];
    if (filters[AnimeFilterNames.deleted_by_MAL]) params[AnimeFilterNames.deleted_by_MAL] = filters[AnimeFilterNames.deleted_by_MAL];
    if (filters[AnimeFilterNames.pending_by_MAL]) params[AnimeFilterNames.pending_by_MAL] = filters[AnimeFilterNames.pending_by_MAL];
    if (filters[AnimeFilterNames.anime_on_server__english_sub])
        params[AnimeFilterNames.anime_on_server__english_sub] = filters[AnimeFilterNames.anime_on_server__english_sub];
    if (filters[AnimeFilterNames.anime_on_server__english_dub])
        params[AnimeFilterNames.anime_on_server__english_dub] = filters[AnimeFilterNames.anime_on_server__english_dub];
    if (filters[AnimeFilterNames.anime_on_server__japanese_sub])
        params[AnimeFilterNames.anime_on_server__japanese_sub] = filters[AnimeFilterNames.anime_on_server__japanese_sub];
    if (filters[AnimeFilterNames.anime_on_server__japanese_dub])
        params[AnimeFilterNames.anime_on_server__japanese_dub] = filters[AnimeFilterNames.anime_on_server__japanese_dub];
    if (filters[AnimeFilterNames.episodes_range].length > 0) params[AnimeFilterNames.episodes_range] = filters[AnimeFilterNames.episodes_range].join();
    if (filters[AnimeFilterNames.length_range].length > 0) params[AnimeFilterNames.length_range] = filters[AnimeFilterNames.length_range].join();
    if (filters[AnimeFilterNames.genres].length > 0) params[AnimeFilterNames.genres] = filters[AnimeFilterNames.genres].join();
    if (filters[AnimeFilterNames.type].length > 0) params[AnimeFilterNames.type] = filters[AnimeFilterNames.type].join();
    if (filters[AnimeFilterNames.status].length > 0) params[AnimeFilterNames.status] = filters[AnimeFilterNames.status].join();
    if (filters[AnimeFilterNames.rating_short].length > 0) params[AnimeFilterNames.rating_short] = filters[AnimeFilterNames.rating_short].join();
    if (filters[AnimeFilterNames.video_quality].length > 0) params[AnimeFilterNames.video_quality] = filters[AnimeFilterNames.video_quality].join();
    if (filters[AnimeFilterNames.audio_channel].length > 0) params[AnimeFilterNames.audio_channel] = filters[AnimeFilterNames.audio_channel].join();
    if (filters[AnimeFilterNames.user_exclude].length > 0) params[AnimeFilterNames.user_exclude] = filters[AnimeFilterNames.user_exclude].join();
    if (filters[AnimeFilterNames.ordering]) params[AnimeFilterNames.ordering] = filters[AnimeFilterNames.ordering];
    return params
}

function setAnimeSeasonParams(params, season) {
    params[AnimeFilterNames.year] = season[AnimeFilterNames.year];
    params[AnimeFilterNames.season] = season[AnimeFilterNames.season];
    return params
}

function setAnimeListParams(params, list) {
    params[AnimeFilterNames.user_list_filter] = list[AnimeFilterNames.user_list_filter];
    params[AnimeFilterNames.user_list_pk] = list[AnimeFilterNames.user_list_pk];
    if (params[AnimeFilterNames.user_exclude] && params[AnimeFilterNames.user_exclude].length > 0)
        params[AnimeFilterNames.user_exclude] = null;
    return params
}
