import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME} from "../../../globals";
import Display from "../../display/Display";
import {getLoggedInUserAnimeEntryStatuses, getLoggedInUserMangaEntryStatuses} from "../../../actions/users";


class AnimeDisplay extends Component {
    componentDidMount() {
        this.props.getLoggedInUserAnimeEntryStatuses();
        this.props.getLoggedInUserMangaEntryStatuses();
    }

    render() {
        return <Display
            branch={BRANCH_ANIME}
            param_id={this.props.match.params.id}
            title_loading={this.props.anime_loading}
            title_loaded={this.props.anime_loaded}
            title={this.props.anime}
            user_title_status={this.props.user_anime_entry}
        />
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
    anime_loaded: state.anime.anime_loaded,
    anime_loading: state.anime.anime_loading,
    user_anime_entry: state.users.user_anime_entry,
});

export default connect(mapStateToProps, {
    getLoggedInUserAnimeEntryStatuses,
    getLoggedInUserMangaEntryStatuses
})(AnimeDisplay)
