import {
    CANCEL_PEOPLE_LOADING,
    FILTER_CHARACTERS,
    FILTER_PEOPLE,
    GET_ALL_CHARACTERS_FOR_ANIME, GET_ANIME_STAFF_POSITIONS_FOR_PERSON,
    GET_PERSON, GET_PUBLISHED_MANGA_FOR_PERSON, GET_VOICE_ACTOR_ROLES_FOR_PERSON,
    PEOPLE_LOADING
} from "../actions/types";
import {playExpandSound} from "../components/layout/sounds";
import {Anime, Person} from "../models";
import store from "../store";
import {cancelAnimeLoading} from "../actions/anime";
import {cancelCharactersLoading} from "../actions/characters";

const initialState = {
    people_loading: false,
    person: {},
    people_loaded: false,

    people_data: {},
    people_data_statistics: {},
    people_data_loaded: false,

    filtered_people: {},
    filtered_people_loaded: false,
};

export default function (state = initialState, action) {
    switch (action.type) {
        case PEOPLE_LOADING:
            return {
                ...state,
                people_loading: true,
                people_loaded: false,
                filtered_people_loaded: false,
            };
        case CANCEL_PEOPLE_LOADING:
            return {
                ...state,
                people_loading: false,
                people_loaded: false,
                filtered_people_loaded: false,
            };
        case GET_PERSON:
            playExpandSound();
            return {
                ...state,
                person: action.payload,
                people_loading: false,
                people_loaded: true
            };
        case GET_VOICE_ACTOR_ROLES_FOR_PERSON:
            state.person[Person.voice_actor_roles] = action.payload;
            return {
                ...state,
                person: state.person
            };
        case GET_ANIME_STAFF_POSITIONS_FOR_PERSON:
            state.person[Person.anime_staff_positions] = action.payload;
            return {
                ...state,
                person: state.person
            };
        case GET_PUBLISHED_MANGA_FOR_PERSON:
            state.person[Person.published_manga] = action.payload;
            return {
                ...state,
                person: state.person
            };
        case FILTER_PEOPLE:
            return {
                ...state,
                filtered_people: action.payload,
                people_loading: false,
                filtered_people_loaded: true
            };
        default:
            return state
    }
}