import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';

import {ANIME_TAB_NAME, EROGE_BROWSE_TAB_NAME, EROGE_TAB_NAME} from "../../../globals";
import {getErogeDataForHomePage} from "../../../actions/eroge";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";
import TopEroge from "./TopEroge";


class ErogeHome extends Component {
    componentDidMount() {
        document.title = EROGE_TAB_NAME;
        window.scrollTo(0, 0);
        this.props.getErogeDataForHomePage();
    }

    render() {
        return (
            <div id="Eroge-Home" className="title-home-page">
                <CyberNavbar/>
                <Grid container>
                    <TopEroge/>
                    <Grid xs={12}>
                        {/*<AnimeStatistics/>*/}
                    </Grid>
                </Grid>
                <CyberFooter/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {getErogeDataForHomePage})(ErogeHome)
