import axios from "axios";

import {
    ANIME_LOADING,
    CANCEL_EROGE_LOADING,
    EROGE_LOADING,
    FILTER_EROGE, GET_ANIME_DATA,
    GET_EROGE, GET_EROGE_DATA
} from "./types";
import {
    API_FILTER_EROGE,
    API_GET_ANIME_DATA_FOR_HOME_PAGE,
    API_GET_EROGE_DATA_FOR_HOME_PAGE,
    API_GET_ONE_EROGE
} from "./api_urls";
import {returnErrors} from "./messages";
import {tokenConfig} from "./auth";
import {isEmpty} from "../components/functions";
import {setCookieErogeFilters} from "./cookies";
import {ErogeFilterNames} from "../components/layout/filters/field_names";
import {TYPE_BROWSE} from "../globals";
import {cancelAnimeLoading} from "./anime";


// GET A ONE EROGE
export const getEroge = id => (dispatch, getState) => {
    dispatch({type: EROGE_LOADING});

    axios
        .get(API_GET_ONE_EROGE(id), tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_EROGE,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch({type: CANCEL_EROGE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// GET EROGE DATA FOR EROGE HOME PAGE
export const getErogeDataForHomePage = () => (dispatch, getState) => {
    dispatch({type: EROGE_LOADING});

    axios
        .get(API_GET_EROGE_DATA_FOR_HOME_PAGE, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_EROGE_DATA,
                payload: res.data
            })
        })
        .catch(err => {
            dispatch({type: CANCEL_EROGE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// FILTER ALL EROGE
export const filterEroge = (page, filters, no_loading) => (dispatch, getState) => {
    dispatch({type: EROGE_LOADING});

    let config = tokenConfig(getState);
    config.params = {};
    if (page && page !== 1) config.params['page'] = page;

    let page_type = TYPE_BROWSE;
    if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
        config.params = setErogeFilterParams(config.params, filters);

    axios
        .get(API_FILTER_EROGE, config)
        .then(res => {
            dispatch({
                type: FILTER_EROGE,
                payload: res.data
            });
            if (filters !== undefined && filters !== 'undefined' && !isEmpty(filters))
                dispatch(setCookieErogeFilters(filters, page_type))
        })
        .catch(err => {
            dispatch({type: CANCEL_EROGE_LOADING});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


export function setErogeFilterParams(params, filters) {
    if (filters[ErogeFilterNames.search] && typeof filters[ErogeFilterNames.search] == 'string') params[ErogeFilterNames.search] = filters[ErogeFilterNames.search];
    if (filters[ErogeFilterNames.downloaded]) params[ErogeFilterNames.downloaded] = filters[ErogeFilterNames.downloaded];
    if (filters[ErogeFilterNames.type].length > 0) params[ErogeFilterNames.type] = filters[ErogeFilterNames.type].join();
    if (filters[ErogeFilterNames.platforms].length > 0) params[ErogeFilterNames.platforms] = filters[ErogeFilterNames.platforms].join();
    if (filters[ErogeFilterNames.censorship].length > 0) params[ErogeFilterNames.censorship] = filters[ErogeFilterNames.censorship].join();
    if (filters[ErogeFilterNames.language].length > 0) params[ErogeFilterNames.language] = filters[ErogeFilterNames.language].join();
    if (filters[ErogeFilterNames.tags].length > 0) params[ErogeFilterNames.tags] = filters[ErogeFilterNames.tags].join();
    if (filters[ErogeFilterNames.ordering]) params[ErogeFilterNames.ordering] = filters[ErogeFilterNames.ordering];
    return params
}
