import Cookies from 'js-cookie'

import {
    TYPE_BROWSE, TYPE_LIST, TYPE_SEASON
} from "../globals";
import {
    SET_ANIME_FILTERS, SET_CHARACTERS_FILTERS,
    SET_COOKIE_SHOW_FILTERS, SET_EROGE_FILTERS,
    SET_MANGA_FILTERS, SET_PEOPLE_FILTERS
} from "../actions/types";
import {Cookie} from "../models";
import {loadCookies} from "../components/cookies/load_cookies";


const loaded_cookies = loadCookies();
const initialState = {
    show_filters: Cookies.get(Cookie.show_filters) === undefined ? true : Cookies.get(Cookie.show_filters) === 'true',
    anime_browse_filters: loaded_cookies.anime_browse_filters,
    anime_season_filters: loaded_cookies.anime_season_filters,
    anime_list_filters: loaded_cookies.anime_list_filters,
    manga_browse_filters: loaded_cookies.manga_browse_filters,
    manga_season_filters: loaded_cookies.manga_season_filters,
    manga_list_filters: loaded_cookies.manga_list_filters,
    characters_filters: loaded_cookies.characters_filters,
    people_filters: loaded_cookies.people_filters,
    eroge_browse_filters: loaded_cookies.eroge_browse_filters,
};


export default function (state = initialState, action) {
    switch (action.type) {
        case SET_COOKIE_SHOW_FILTERS:
            Cookies.set(Cookie.show_filters, action.value);
            return {
                ...state,
                show_filters: action.value,
            };
        case SET_ANIME_FILTERS:
            return update_anime_filter_state(state, action);
        case SET_MANGA_FILTERS:
            return update_manga_filter_state(state, action);
        case SET_CHARACTERS_FILTERS:
            Cookies.set(Cookie.characters_filters, action.value);
            return {
                ...state,
                characters_filters: action.value,
            };
        case SET_PEOPLE_FILTERS:
            Cookies.set(Cookie.people_filters, action.value);
            return {
                ...state,
                people_filters: action.value,
            };
        case SET_EROGE_FILTERS:
            Cookies.set(Cookie.eroge_browse_filters, action.value);
            return {
                ...state,
                eroge_browse_filters: action.value,
            };
        default:
            return state
    }
}

function update_anime_filter_state(state, action) {
    switch (action.page_type) {
        case TYPE_BROWSE:
            Cookies.set(Cookie.anime_browse_filters, action.value);
            return {
                ...state,
                anime_browse_filters: action.value,
            };
        case TYPE_SEASON:
            Cookies.set(Cookie.anime_season_filters, action.value);
            return {
                ...state,
                anime_season_filters: action.value,
            };
        case TYPE_LIST:
            Cookies.set(Cookie.anime_list_filters, action.value);
            return {
                ...state,
                anime_list_filters: action.value,
            };
        default:
            return {
                ...state,
                anime_browse_filters: action.value,
            };
    }
}

function update_manga_filter_state(state, action) {
    switch (action.page_type) {
        case TYPE_BROWSE:
            Cookies.set(Cookie.manga_browse_filters, action.value);
            return {
                ...state,
                manga_browse_filters: action.value,
            };
        case TYPE_SEASON:
            Cookies.set(Cookie.manga_season_filters, action.value);
            return {
                ...state,
                manga_season_filters: action.value,
            };
        case TYPE_LIST:
            Cookies.set(Cookie.manga_list_filters, action.value);
            return {
                ...state,
                manga_list_filters: action.value,
            };
        default:
            return {
                ...state,
                manga_browse_filters: action.value,
            };
    }
}
