import {
    ANIME_LOADING,
    GET_ANIME,
    CANCEL_ANIME_LOADING,
    FILTER_ANIME,
    GET_ANIME_DATA,
    GET_ALL_CHARACTERS_FOR_ANIME,
    GET_RELATIONS_FOR_ANIME,
    GET_CURRENT_ANIME_FRANCHISE,
    ANIME_FRANCHISE_LOADING,
    GET_ANIME_DATA_STATISTICS, GET_ANIME_SEARCHABLE_TITLES, GET_ANIME_ACHIEVEMENT_THRESHOLDS
} from "../actions/types";
import {playExpandSound} from "../components/layout/sounds";
import {Anime} from "../models";

const initialState = {
    searchable_titles: [],

    anime_loading: false,
    anime: {},
    anime_loaded: false,

    anime_data: {},
    anime_data_statistics: {},
    anime_data_loaded: false,

    filtered_anime: {},
    filtered_anime_loaded: false,

    anime_franchise_loading: false,
    anime_franchise: {}
};

// eslint-disable-next-line import/no-anonymous-default-export
export default function (state = initialState, action) {
    switch (action.type) {
        case ANIME_LOADING:
            return {
                ...state,
                anime_loading: true,
                anime_loaded: false,
                anime_data_loaded: false,
                filtered_anime_loaded: false,
            };
        case CANCEL_ANIME_LOADING:
            return {
                ...state,
                anime_loading: false,
                anime_loaded: false,
                anime_data_loaded: false,
                filtered_anime_loaded: false,
            };
        case GET_ANIME_SEARCHABLE_TITLES:
            return {
                ...state,
                searchable_titles: action.payload,
            };
        case GET_ANIME_DATA:
            return {
                ...state,
                anime_loading: false,
                anime_data: action.payload,
                anime_data_loaded: true,
            };
        case GET_ANIME_DATA_STATISTICS:
            return {
                ...state,
                anime_loading: false,
                anime_data_statistics: action.payload['anime_statistics'],
                anime_data_loaded: true,
            };
        case GET_ANIME_ACHIEVEMENT_THRESHOLDS:
            return {
                ...state,
                anime_loading: false,
                anime_achievement_thresholds: action.payload,
            };
        case GET_ANIME:
            playExpandSound();
            return {
                ...state,
                anime: action.payload,
                anime_loading: false,
                anime_loaded: true
            };
        case FILTER_ANIME:
            return {
                ...state,
                filtered_anime: action.payload,
                anime_loading: false,
                filtered_anime_loaded: true
            };
        case GET_ALL_CHARACTERS_FOR_ANIME:
            state.anime[Anime.all_characters] = action.payload;
            return {
                ...state,
                anime: state.anime
            };
        case GET_RELATIONS_FOR_ANIME:
            state.anime[Anime.anime_relations] = action.payload;
            return {
                ...state,
                anime: state.anime
            };
        case ANIME_FRANCHISE_LOADING:
            return {
                ...state,
                anime_franchise_loading: true
            };
        case GET_CURRENT_ANIME_FRANCHISE:
            return {
                ...state,
                anime_franchise: action.payload,
                anime_franchise_loading: false
            };
        default:
            return state
    }
}
