import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_EROGE} from "../../../globals";
import Display from "../../display/Display";


class CharacterDisplay extends Component {
    render() {
        return <Display
            branch={BRANCH_EROGE}
            param_id={this.props.match.params.id}
            title_loading={this.props.eroge_loading}
            title_loaded={this.props.eroge_loaded}
            title={this.props.eroge}
        />
    }
}

const mapStateToProps = state => ({
    eroge: state.eroge.eroge,
    eroge_loaded: state.eroge.eroge_loaded,
    eroge_loading: state.eroge.eroge_loading,
});

export default connect(mapStateToProps, {})(CharacterDisplay)
