import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Grid from '@material-ui/core/Grid';
import CyberNavbar from "../layout/navbar/CyberNavbar";
import GridListTile from "@material-ui/core/GridListTile";
import GridList from "@material-ui/core/GridList";
import Chip from "@material-ui/core/Chip";
import Grow from "@material-ui/core/Grow";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faLeaf, faSeedling, faSun} from "@fortawesome/free-solid-svg-icons";
import {faSnowflake} from "@fortawesome/free-solid-svg-icons/faSnowflake";

import {SeasonYear} from "../../models";
import {getBrowseGridListCols} from "../functions";
import {CyberLink} from "../layout/common/common";
import Loading from "../layout/common/Loading";

import '../../assets/css/seasons.css'
import {formatData, getSeasonUrl, getTitleData} from "./functions";
import CyberFooter from "../layout/footer/CyberFooter";


class BrowseSeasons extends Component {
    static propTypes = {
        /** Branch of component. (Anime, Manga, Eroge..) */
        branch: PropTypes.string.isRequired,
        /** Redux prop of titles_loading */
        data_loading: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        data_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        data: PropTypes.object.isRequired,
    };
    constructor() {
        super(...arguments);
        this.updateGridListCols = this.updateGridListCols.bind(this);
        this.state = {gridlist_cols: getBrowseGridListCols()};
    }

    componentDidMount() {
        document.title = `${this.props.branch} Seasons`;
        window.scrollTo(0, 0);
        window.addEventListener('resize', this.updateGridListCols);

        getTitleData(this.props.branch);
    }

    updateGridListCols() {
        if (this.state.gridlist_cols !== getBrowseGridListCols())
            this.setState({gridlist_cols: getBrowseGridListCols()})
    }

    render() {
        const data = this.props.data_loaded ? formatData(this.props.branch, this.props.data) : formatData();

        console.log('data', this.props.data);
        console.log(data);

        return (
            <>
                <CyberNavbar/>
                {this.props.data_loaded ? <Grid id="Seasons" container>
                    <Grid item xs={12}>
                        <GridList cellHeight={280} cols={this.state.gridlist_cols} style={{margin: 0}}>
                            {data.total_titles_per_season_json ?
                                JSON.parse(data.total_titles_per_season_json).reverse().map((year, x) => (
                                    <Grow in {...({timeout: 200 + (x * 50)})}>
                                        <GridListTile cols={1} className="season-card">
                                            <div className="box-light">
                                                {console.log(year)}
                                                <h1>{year[SeasonYear.year]}</h1>
                                                <Grid container>
                                                    <Grid item xs={7} className="label">
                                                        <FontAwesomeIcon icon={faSeedling} className="spring"/>
                                                        <CyberLink to={getSeasonUrl(this.props.branch, year[SeasonYear.year], 1)}>Spring</CyberLink>
                                                    </Grid>
                                                    <Grid item xs={5} className="chip">
                                                        <Chip size="small" variant="outlined"
                                                              label={year[SeasonYear.seasons][0]}/>
                                                    </Grid>
                                                </Grid>

                                                <Grid container>
                                                    <Grid item xs={7} className="label">
                                                        <FontAwesomeIcon icon={faSun} className="summer"/>
                                                        <CyberLink to={getSeasonUrl(this.props.branch, year[SeasonYear.year], 2)}>Summer</CyberLink>
                                                    </Grid>
                                                    <Grid item xs={5} className="chip">
                                                        <Chip size="small" variant="outlined"
                                                              label={year[SeasonYear.seasons][1]}/>
                                                    </Grid>
                                                </Grid>

                                                <Grid container>
                                                    <Grid item xs={7} className="label">
                                                        <FontAwesomeIcon icon={faLeaf} className="fall"/>
                                                        <CyberLink to={getSeasonUrl(this.props.branch, year[SeasonYear.year], 3)}>Fall</CyberLink>
                                                    </Grid>
                                                    <Grid item xs={5} className="chip">
                                                        <Chip size="small" variant="outlined"
                                                              label={year[SeasonYear.seasons][2]}/>
                                                    </Grid>
                                                </Grid>

                                                <Grid container>
                                                    <Grid item xs={7} className="label">
                                                        <FontAwesomeIcon icon={faSnowflake} className="winter"/>
                                                        <CyberLink to={getSeasonUrl(this.props.branch, year[SeasonYear.year], 4)}>Winter</CyberLink>
                                                    </Grid>
                                                    <Grid item xs={5} className="chip">
                                                        <Chip size="small" variant="outlined"
                                                              label={year[SeasonYear.seasons][3]}/>
                                                    </Grid>
                                                </Grid>

                                                <p className="total">Total {year[SeasonYear.seasons][0] + year[SeasonYear.seasons][1] + year[SeasonYear.seasons][2] + year[SeasonYear.seasons][3]}</p>
                                            </div>
                                        </GridListTile>
                                    </Grow>
                                )) : null}
                        </GridList>
                    </Grid>
                </Grid> : this.props.anime_loading ? <Loading text={'Seasons Loading'}/> : null}
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({});

export default connect(mapStateToProps, {})(BrowseSeasons)
