import React, {Component} from 'react'
import {connect} from 'react-redux'

import {Anime, ApiResult, Manga} from "../../../../models";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_CHARACTERS} from "../../../../globals";
import GridList from "@material-ui/core/GridList";

import '../../../../assets/css/anime.css'
import {getAllCharactersForManga} from "../../../../actions/manga";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../../layout/sounds";
import GoToPageDialog from "../../../layout/pagination/goToPageDialog";


class CharactersTab extends Component {
    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        if (!this.props.manga[Manga.all_characters] || this.props.all_characters[ApiResult.previous])
            this.props.getAllCharactersForManga(this.props.manga[Manga.id])
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.all_characters[ApiResult.count] / 50)) {
            window.scrollTo(0, 0);
            this.setState({current_page: page});
            this.props.getAllCharactersForManga(this.props.manga[Manga.id], page)
        }
    }

    render() {
        return (
            <div className="characters-tab">
                {this.props.all_characters ? <>
                    <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                        {this.props.all_characters[ApiResult.results].map((character, i) => (
                            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                                <GridListTile cols={1}>
                                    <TitleCard branch={BRANCH_CHARACTERS} title={character}/>
                                </GridListTile>
                            </Grow>
                        ))}
                    </GridList>
                    <div className="pagination">
                        <Pagination
                            count={Math.ceil(this.props.all_characters[ApiResult.count] / 50)}
                            color="primary"
                            siblingCount={3}
                            page={this.state.current_page}
                            onChange={(type, page) => {
                                playClickSound();
                                if (page !== this.state.current_page)
                                    this.goToPage(page);
                                else
                                    this.setState({page_dialog_open: true})
                            }}
                        />
                        <GoToPageDialog
                            open={this.state.page_dialog_open}
                            current_page={this.state.current_page}
                            total_pages={Math.ceil(this.props.all_characters[ApiResult.count] / 50)}
                            goToPage={this.goToPage}
                            onClose={this.closePageDialog}/>
                    </div>
                </> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    manga: state.manga.manga,
    all_characters: state.manga.manga[Manga.all_characters],
});

export default connect(mapStateToProps, {getAllCharactersForManga})(CharactersTab)
