export function getPriorityLabel(priority) {
    switch (priority) {
        case 0:
            return 'Very Low';
        case 1:
            return 'Low';
        case 2:
            return 'Medium';
        case 3:
            return 'High';
        case 4:
            return 'Very High';
        default:
            return 'Very Low'
    }
}
