import {CLEAR_MESSAGE, CREATE_MESSAGE} from "../actions/types";


const initialState = {
    message: null,
    type: null
};

export default function (state = initialState, action) {
    switch (action.type) {
        case CREATE_MESSAGE:
            return {
                message: action.payload.msg,
                type: action.payload.type
            };
        case CLEAR_MESSAGE:
            return {
                message: null,
                type: null
            };
        default:
            return state;
    }
}
