import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import {
    BRANCH_MANGA,
    FILTER_MANGA_INITIAL_VALUES,
    MANGA_GENRES, MANGA_ORDERINGS,
    MANGA_STATUSES,
    MANGA_TYPES, USER_MANGA_STATUSES_ARRAY
} from "../../../../globals";
import {FilterLabels, MangaFilterNames, FilterSchemaType} from "../field_names";
import {getMangaFilterPresets} from "../../../../actions/users";
import store from "../../../../store";
import TitlesFilter from "../TitlesFilter";
import {isEmpty} from "../../../functions";


class MangaFilters extends Component {
    static propTypes = {
        /** Season object {year: season:} */
        season: PropTypes.object,
        /** User List object, {pk: selected_filter:} */
        list: PropTypes.object,
    };

    componentDidMount() {
        store.dispatch(getMangaFilterPresets())
    }

    render() {
        const main_filters = [
            {'name': MangaFilterNames.downloaded, 'label': FilterLabels.downloaded, 'color': 'secondary'},
            {'name': MangaFilterNames.is_downloading, 'label': FilterLabels.is_downloading, 'color': 'default'},
            {'name': MangaFilterNames.cannot_download, 'label': FilterLabels.cannot_download, 'color': 'danger'},
            {'name': MangaFilterNames.deleted_by_MAL, 'label': FilterLabels.deleted_by_MAL, 'color': 'danger'},
            {'name': MangaFilterNames.pending_by_MAL, 'label': FilterLabels.pending_by_MAL, 'color': 'warning'},
        ];
        const volumes_marks = [
            {
                value: 1,
                label: '1',
            },
            {
                value: 12,
                label: '12',
            },
            {
                value: 24,
                label: '24',
            },
            {
                value: 36,
                label: '36',
            },
            {
                value: 53,
                label: '52+',
            },
        ];
        const chapters_marks = [
            {
                value: 1,
                label: '1',
            },
            {
                value: 12,
                label: '12',
            },
            {
                value: 24,
                label: '24',
            },
            {
                value: 30,
                label: '30',
            },
            {
                value: 53,
                label: '52+',
            },
        ];
        const initial = new FILTER_MANGA_INITIAL_VALUES();
        const values = initial.VALUES;
        let regular_filters = [
            {
                'name': MangaFilterNames.type,
                'type': FilterSchemaType.standard,
                'color': 'primary',
                'content': MANGA_TYPES
            },
            {
                'name': MangaFilterNames.status,
                'type': FilterSchemaType.standard,
                'color': 'primary',
                'content': MANGA_STATUSES
            },
            {
                'name': MangaFilterNames.volumes_range,
                'type': FilterSchemaType.slider,
                'marks': volumes_marks,
                'min': values.volumes_range[0],
                'max': values.volumes_range[1],
                'color': 'primary',
                'content': null
            },
            {
                'name': MangaFilterNames.chapters_range,
                'type': FilterSchemaType.slider,
                'marks': chapters_marks,
                'min': values.chapters_range[0],
                'max': values.chapters_range[1],
                'color': 'primary',
                'content': null
            },
        ];

        if (this.props.user_authenticated) {
            regular_filters.push(
                {
                    'name': MangaFilterNames.user_exclude,
                    'type': FilterSchemaType.standard,
                    'color': 'info',
                    'content': USER_MANGA_STATUSES_ARRAY
                },
            )
        }

        let wide_filters = [
            {
                'name': MangaFilterNames.genres,
                'type': FilterSchemaType.wide,
                'color': 'primary',
                'content': MANGA_GENRES
            },
        ];

        let manga_filters = this.props.manga_browse_filters;
        if (!isEmpty(this.props.season))
            manga_filters = this.props.manga_season_filters;
        else if (!isEmpty(this.props.list))
            manga_filters = this.props.manga_list_filters;

        return (
            <TitlesFilter
                branch={BRANCH_MANGA}
                titles_loading={this.props.manga_loading}
                filtered_titles_loaded={this.props.filtered_manga_loaded}
                filtered_titles={this.props.filtered_manga}
                title_filters={manga_filters}
                season={this.props.season}
                list={this.props.list}
                main_filters={main_filters}
                regular_filters={regular_filters}
                wide_filters={wide_filters}
                order_filters={MANGA_ORDERINGS}
                presets={this.props.manga_filter_presets}
            />
        )
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated,
    manga_loading: state.manga.manga_loading,
    filtered_manga_loaded: state.manga.filtered_manga_loaded,
    filtered_manga: state.manga.filtered_manga,
    manga_browse_filters: state.cookies.manga_browse_filters,
    manga_season_filters: state.cookies.manga_season_filters,
    manga_list_filters: state.cookies.manga_list_filters,
    manga_filter_presets: state.users.manga_filter_presets,
});

export default connect(mapStateToProps, {})(MangaFilters)
