import React, { Component } from 'react';

// https://bbbootstrap.com/snippets/circle-loader-63353068 BETTER LOADING!!!

import '../../../assets/css/layout.css';
import PropTypes from "prop-types";


class Loading extends Component {
    static propTypes = {
        /** String that will be displayed below the img */
        text: PropTypes.string,
        /** Removes all text */
        no_text: PropTypes.bool,
        /** Used for width and height for the img in pixels */
        size: PropTypes.number,
        /** Used for calculating the top and bottom offset of the container */
        offset: PropTypes.number,
        /** Applies a css class to the container to remove the relative position */
        block: PropTypes.bool,
    };

    render() {
        const {text, size, offset, block, no_text} = this.props;

        return (
            <div className={block ? 'triangle-loader triangle-loader-initial' : 'triangle-loader'}
                 style={offset ? {height: `calc(100vh - ${offset}px)`} : null}>
                <div className="triangle-loader-svg">
                    <svg id="triangle" width={size ? size : '160'} height={size ? size : '160'} viewBox="-3 -4 39 39">
                        <polygon fill="transparent" stroke="cyan" strokeWidth="1" points="16,0 32,32 0,32"/>
                    </svg>
                    {!no_text ? text ? text.toUpperCase() : 'LOADING' : null}
                </div>
            </div>
        )
    }
}

export default Loading
