/**
 *
 * @param props.selected {String}
 * @param props.setSelected {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function AnimeListStatusButtons(props) {
    const {selected, setSelected} = props;

    return <>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton disabled={true}>All</CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.watching ? 'btn-contained-success' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.watching)
                }}>
                <FontAwesomeIcon icon={faPlay}/><span className="btn-label">Watching</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.completed ? 'btn-contained-primary' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.completed)
                }}>
                <FontAwesomeIcon icon={faCheck}/><span className="btn-label">Completed</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.on_hold ? 'btn-contained-warning' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.on_hold)
                }}>
                <FontAwesomeIcon icon={faPause}/><span className="btn-label">On Hold</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.dropped ? 'btn-contained-danger' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.dropped)
                }}>
                <FontAwesomeIcon icon={faStop}/><span className="btn-label">Dropped</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.plan_to_watch ? 'btn-contained-default' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.plan_to_watch)
                }}>
                <FontAwesomeIcon icon={faCalendarAlt}/><span className="btn-label">Planned</span>
            </CyberButton>
        </Grid>
    </>
}

/**
 *
 * @returns {JSX.Element}
 * @constructor
 */
export function AnimeListTableHead(props) {
    const {list_owner} = props;
    return <TableHead>
        <TableRow>
            <TableCell>#</TableCell>
            <TableCell align="center"><FontAwesomeIcon icon={faImage}/></TableCell>
            <TableCell align="center" colSpan={list_owner ? 2 : 1}>Title</TableCell>
            <TableCell align="center">Score</TableCell>
            <TableCell align="center">Progress</TableCell>
            <TableCell align="center">Priority</TableCell>
            <TableCell align="center">Genres</TableCell>
            <TableCell align="center">Type</TableCell>
            <TableCell align="center">Rated</TableCell>
            <TableCell align="center">Started</TableCell>
            <TableCell align="center">Finished</TableCell>
            <TableCell align="center">Added</TableCell>
            {/*<TableCell align="center">Watch</TableCell>*/}
        </TableRow>
    </TableHead>
}


/**
 *
 * @param props.index {Number}
 * @param props.entry {Object}
 * @param props.list_owner {Boolean}
 * @param props.list {Object}
 * @returns {JSX.Element}
 * @constructor
 */
export function AnimeListTableRow(props) {
    const {index, entry, list_owner, list} = props;
    let image_url;
    if (!isEmpty(entry[UserAnimeEntry.anime]))
        image_url = getTitleCoverImage(entry[UserAnimeEntry.anime][Anime.anime_cdn_cover_images],
            entry[UserAnimeEntry.anime][Anime.anime_cover_images], true);

    return entry[UserAnimeEntry.anime] ? <TableRow>
        <TableCell component="th" scope="row">
            {index}
        </TableCell>
        <TableCell className={{width: '70px'}} align="center">
            <CyberLink to={ANIME_DISPLAY(entry[UserAnimeEntry.anime][Anime.id])} classNames="cover-img-link">
                <PreLoadImage src={image_url} alt={`Cover image`}/>
            </CyberLink>
        </TableCell>
        <TableCell>
            <CyberLink classNames="title-link"
                       to={ANIME_DISPLAY(entry[UserAnimeEntry.anime][Anime.id])}>
                {entry[UserAnimeEntry.anime][Anime.title]}
            </CyberLink>
        </TableCell>
        {list_owner ?
            <TableCell>
                <AnimeEntryModalForm
                    id={entry[UserAnimeEntry.anime][Anime.id]}
                    classNames="edit-btn"
                    user_anime_entry={entry}
                    edit={true}
                    list={list}/>
            </TableCell> : null}
        <TableCell align="center">
            <Chip className="chip-primary table-number" size="small"
                  label={entry[UserAnimeEntry.score]}/>
        </TableCell>
        <TableCell align="center">
            <p>
                <span className="chip-primary table-number">
                {entry[UserAnimeEntry.watched_episodes]}{' '}
                    / {entry[UserAnimeEntry.anime][Anime.episodes]}
                    {list_owner ?
                        <UpdateAnimeEpisodeButton
                            id={entry[UserAnimeEntry.anime][Anime.id]} entry={entry}
                            list={list}/> : null}
                </span>
            </p>
        </TableCell>
        <TableCell align="center">
            <Chip className="chip-primary" size="small"
                  label={getPriorityLabel(entry[UserAnimeEntry.priority])}/>
        </TableCell>
        <TableCell align="center">
            {entry[UserAnimeEntry.anime][Anime.genres_csv] ?
                entry[UserAnimeEntry.anime][Anime.genres_csv].split(', ')
                    .map(genre => (
                        <Chip className="chip-primary genre" size="small"
                              label={genre}/>
                    )) : null}
        </TableCell>
        <TableCell align="center">
            <Chip className="chip-primary"
                  label={entry[UserAnimeEntry.anime][Anime.type]} size="small"/>
        </TableCell>
        <TableCell align="center">
            <Chip className="chip-primary"
                  label={entry[UserAnimeEntry.anime][Anime.rating_short]}/>
        </TableCell>
        <TableCell align="center">
            {entry[UserAnimeEntry.start_date] ? <Moment date={entry[UserAnimeEntry.start_date]}
                                                        format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        <TableCell align="center">
            {entry[UserAnimeEntry.finish_date] ? <Moment date={entry[UserAnimeEntry.finish_date]}
                                                         format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        <TableCell align="center">
            {entry[UserAnimeEntry.date_added] ? <Moment date={entry[UserAnimeEntry.date_added]}
                                                        format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        {/*<TableCell align="center">*/}
        {/*    <CyberButton classNames="btn-contained-secondary"><FontAwesomeIcon*/}
        {/*        icon={faPlay}/></CyberButton>*/}
        {/*</TableCell>*/}
    </TableRow> : null
}