import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEdit, faPlus} from "@fortawesome/free-solid-svg-icons";

import {CyberButton} from "../../../layout/common/common";
import {addAnimeEntry, deleteAnimeEntry, updateAnimeEntry} from "../../../../actions/users";
import {getUserAnimeEntryFormInitialValues, UserAnimeEntryForm} from "./forms";
import {isEmpty} from "../../../functions";

import '../../../../assets/css/forms.css';


class AnimeEntryModalForm extends Component {
    static propTypes = {
        /** Classnames for the open dialog btn */
        classNames: PropTypes.object,
        /** Title of the Anime */
        title: PropTypes.string,
        /** ID of the Anime */
        id: PropTypes.number.isRequired,
        /** User entry of the anime */
        user_anime_entry: PropTypes.object,
        /** If the entry is being edited */
        edit: PropTypes.bool,
        /** User list info for filtering on update entry
         * {user_list_pk: list_pk, user_list_filter: selected_filter} */
        list: PropTypes.object,
    };

    constructor() {
        super(...arguments);
        this.handleClickOpen = this.handleClickOpen.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.state = {
            open: false,
        };
    }

    handleClickOpen() {
        this.setState({open: true})
    };

    handleClose() {
        this.setState({open: false})
    };

    render() {
        let edit = true;
        if (this.props.edit === undefined) {
            if (isEmpty(this.props.user_anime_entry))
                edit = false;
        } else edit = this.props.edit;

        const initial_values = getUserAnimeEntryFormInitialValues(this.props.id, this.props.user_anime_entry, edit);

        return (
                <div id="User-Title-Entry">
                    <CyberButton classNames={this.props.classNames} variant="outlined" color="primary"
                                 onClick={this.handleClickOpen}>
                        <FontAwesomeIcon icon={edit ? faEdit : faPlus}/>
                    </CyberButton>
                    <Dialog open={this.state.open} onClose={this.handleClose} aria-labelledby="form-dialog-title"
                            className="user-title-entry-dialog">
                        {edit ? <DialogTitle id="form-dialog-title">Edit {this.props.title}</DialogTitle>
                            : <DialogTitle id="form-dialog-title">Add {this.props.title}</DialogTitle>}
                        <DialogContent>
                            <UserAnimeEntryForm initial_values={initial_values} edit={edit} id={this.props.id}
                                                list={this.props.list} handleClose={this.handleClose}/>
                        </DialogContent>
                    </Dialog>
                </div>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {updateAnimeEntry, addAnimeEntry, deleteAnimeEntry})(AnimeEntryModalForm)
