import {Form, Formik} from "formik";
import store from "../../../../store";
import {
    addAnimeEntry,
    addToFavorites,
    deleteAnimeEntry,
    removeFromFavorites,
    updateAnimeEntry
} from "../../../../actions/users";
import Grid from "@material-ui/core/Grid";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import 'date-fns';
import {KeyboardDateTimePicker, MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import DialogActions from "@material-ui/core/DialogActions";
import {CyberButton} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faStar as faStarSolid, faTrash} from "@fortawesome/free-solid-svg-icons";
import React from "react";
import {UserAnimeEntry} from "../../../../models";
import {BRANCH_ANIME, USER_ANIME_STATUSES} from "../../../../globals";
import {isEmpty} from "../../../functions";
import {faStar} from "@fortawesome/free-regular-svg-icons";
import {isTitleInFavorites} from "../../../display/functions";
import {AddRemoveFromFavorites} from "../shortForms";



const FIELD_NAMES = {
    watched_episodes: UserAnimeEntry.watched_episodes,
    start_date: UserAnimeEntry.start_date,
    finish_date: UserAnimeEntry.finish_date,
    score: UserAnimeEntry.score,
    status: UserAnimeEntry.status,
    times_watched: UserAnimeEntry.times_watched,
    comments: UserAnimeEntry.comments,
    tags: UserAnimeEntry.tags,
    priority: UserAnimeEntry.priority,
    rewatching: UserAnimeEntry.rewatching,
    rewatching_episode: UserAnimeEntry.rewatching_episode,
};


/**
 *
 * @param {Number} anime_id
 * @param {Object} user_anime_entry
 * @param {boolean} edit - *Optional
 * @returns {{comments: string, times_watched: number, rewatching_episode: number, priority: string, tags: string, score: number, watched_episodes: number, rewatching: boolean, finish_date: string, anime: *, errors: boolean, start_date: string, status: string}}
 */
export function getUserAnimeEntryFormInitialValues(anime_id, user_anime_entry, edit = false) {
    let initial_values = {
        anime: anime_id,
        watched_episodes: 0,
        start_date: '',
        finish_date: '',
        score: 0,
        status: USER_ANIME_STATUSES.plan_to_watch,
        times_watched: 0,
        comments: '',
        tags: '',
        priority: '0',
        rewatching: false,
        rewatching_episode: 0,
        errors: false
    };

    if (edit && !isEmpty(user_anime_entry))
        initial_values = {
            anime: anime_id,
            watched_episodes: user_anime_entry[UserAnimeEntry.watched_episodes],
            start_date: user_anime_entry[UserAnimeEntry.start_date],
            finish_date: user_anime_entry[UserAnimeEntry.finish_date],
            score: user_anime_entry[UserAnimeEntry.score],
            status: user_anime_entry[UserAnimeEntry.status],
            times_watched: user_anime_entry[UserAnimeEntry.times_watched],
            comments: user_anime_entry[UserAnimeEntry.comments],
            tags: user_anime_entry[UserAnimeEntry.tags],
            priority: user_anime_entry[UserAnimeEntry.priority],
            rewatching: user_anime_entry[UserAnimeEntry.rewatching],
            rewatching_episode: user_anime_entry[UserAnimeEntry.rewatching_episode]
        };

    return initial_values
}


function deleteEntry(id, list) {
    if (window.confirm('Are you sure? This will also delete any episode history of this anime.'))
        store.dispatch(deleteAnimeEntry(id, list));
}


/**
 *
 * @param {Object} props.initial_values
 * @param {boolean} props.edit
 * @param {int} props.id
 * @param {Object} props.list
 * @param {function} props.handleClose
 * @returns {JSX.Element}
 * @constructor
 */
export function UserAnimeEntryForm(props) {
    const {initial_values, edit, id, list, handleClose} = props;

    return <MuiPickersUtilsProvider utils={DateFnsUtils}><Formik
        initialValues={initial_values}
        onSubmit={(values, {setSubmitting}) => {
            if (values.start_date === '') values.start_date = null;
            if (values.finish_date === '') values.finish_date = null;
            values[FIELD_NAMES.times_watched] = parseInt(values[FIELD_NAMES.times_watched]);
            if (isNaN(values[FIELD_NAMES.times_watched])) values[FIELD_NAMES.times_watched] = 0;
            values[FIELD_NAMES.watched_episodes] = parseInt(values[FIELD_NAMES.watched_episodes]);
            if (isNaN(values[FIELD_NAMES.watched_episodes])) values[FIELD_NAMES.watched_episodes] = 0;
            values.score = parseInt(values.score);

            if (edit) store.dispatch(updateAnimeEntry(id, values, list));
            else store.dispatch(addAnimeEntry(values));

            setSubmitting(false);
        }}>
        {({
              isSubmitting, values, handleChange,
              handleBlur, setValues
          }) => (
            <Form id="User-Title-Entry-Form">
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-status">Status</InputLabel>
                        <Select
                            labelId="user-status"
                            className="form-control"
                            label="Status"
                            value={values[FIELD_NAMES.status]}
                            name={FIELD_NAMES.status}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="Watching">Watching</MenuItem>
                            <MenuItem value="Completed">Completed</MenuItem>
                            <MenuItem value="On Hold">On Hold</MenuItem>
                            <MenuItem value="Dropped">Dropped</MenuItem>
                            <MenuItem value="Plan to Watch">Plan to Watch</MenuItem>
                        </Select>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-value">Priority</InputLabel>
                        <Select
                            labelId="user-value"
                            className="form-control"
                            value={values[FIELD_NAMES.priority]}
                            name={FIELD_NAMES.priority}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="4">VERY HIGH</MenuItem>
                            <MenuItem value="3">HIGH</MenuItem>
                            <MenuItem value="2">MEDIUM</MenuItem>
                            <MenuItem value="1">LOW</MenuItem>
                            <MenuItem value="0">VERY LOW</MenuItem>
                        </Select>
                    </Grid>
                </Grid>
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Episode"
                            type="number"
                            name={FIELD_NAMES.watched_episodes}
                            value={values[FIELD_NAMES.watched_episodes]}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>

                    <Grid item xs={4} md={2}>
                        <div className="form-group text-right">
                            <Checkbox
                                value={values[FIELD_NAMES.rewatching]}
                                inputProps={{'aria-label': 'primary checkbox'}}
                                name={FIELD_NAMES.rewatching}
                                onChange={handleChange}
                                onBlur={handleBlur}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={8} md={4}>
                        <TextField
                            // className="form-control"
                            label="Rewatch Episode"
                            type="number"
                            name={FIELD_NAMES.rewatching_episode}
                            value={values[FIELD_NAMES.rewatching_episode]}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            disabled={!values[FIELD_NAMES.rewatching]}/>
                    </Grid>
                </Grid>

                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-score">Score</InputLabel>
                        <Select
                            labelId="user-score"
                            className="form-control"
                            value={values[FIELD_NAMES.score]}
                            name={FIELD_NAMES.score}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="0">0</MenuItem>
                            <MenuItem value="1">1</MenuItem>
                            <MenuItem value="2">2</MenuItem>
                            <MenuItem value="3">3</MenuItem>
                            <MenuItem value="4">4</MenuItem>
                            <MenuItem value="5">5</MenuItem>
                            <MenuItem value="6">6</MenuItem>
                            <MenuItem value="7">7</MenuItem>
                            <MenuItem value="8">8</MenuItem>
                            <MenuItem value="9">9</MenuItem>
                            <MenuItem value="10">10</MenuItem>
                        </Select>
                    </Grid>

                    <Grid item xs={12} md={6}>
                        <KeyboardDateTimePicker
                            className="form-control datetime-control"
                            variant="inline"
                            format="do MMM yyyy HH:mm"
                            margin="normal"
                            id="date-picker-inline"
                            label="Start date"
                            value={values[FIELD_NAMES.start_date]}
                            onChange={(date) => {
                                values[FIELD_NAMES.start_date] = date;
                                setValues(values);
                            }}
                            KeyboardButtonProps={{'aria-label': 'change date'}}
                        />
                    </Grid>
                </Grid>

                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Times watched"
                            type="number"
                            name={FIELD_NAMES.times_watched}
                            value={values[FIELD_NAMES.times_watched]}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>

                    <Grid item xs={12} md={6}>
                        <KeyboardDateTimePicker
                            className="form-control datetime-control"
                            variant="inline"
                            format="do MMM yyyy HH:mm"
                            margin="normal"
                            id="date-picker-inline"
                            label="Finish date"
                            value={values[FIELD_NAMES.finish_date]}
                            onChange={(date) => {
                                values[FIELD_NAMES.finish_date] = date;
                                setValues(values);
                            }}
                            KeyboardButtonProps={{'aria-label': 'change date'}}
                        />
                    </Grid>
                </Grid>
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Comments"
                            multiline
                            value={values[FIELD_NAMES.comments]}
                            name={FIELD_NAMES.comments}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Tags"
                            multiline
                            value={values[FIELD_NAMES.tags]}
                            name={FIELD_NAMES.tags}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>
                </Grid>

                <DialogActions>
                    <AddRemoveFromFavorites branch={BRANCH_ANIME} id={parseInt(id)}/>
                    <CyberButton classNames="btn-success" variant="outlined" type="submit" onClick={handleClose}
                                 disabled={isSubmitting}><FontAwesomeIcon icon={faCheck}/></CyberButton>
                    {edit ? <CyberButton classNames="btn-danger" variant="outlined" onClick={(e) => {
                        deleteEntry(id, list);
                        handleClose()
                    }}><FontAwesomeIcon icon={faTrash}/></CyberButton> : null}
                </DialogActions>
            </Form>
        )}
    </Formik></MuiPickersUtilsProvider>
}
