import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {CyberButton} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faHistory, faTrash} from "@fortawesome/free-solid-svg-icons";
import {Anime, User, UserAnimeEntry} from "../../../../models";
import {isEmpty} from "../../../functions";
import Moment from "react-moment";
import Chip from "@material-ui/core/Chip";
import {playClickSound, playHoverSound} from "../../../layout/sounds";
import store from "../../../../store";
import {deleteAnimeHistoryEntry} from "../../../../actions/users";


/**
 *
 * @param props.entry {Object} Anime entry of the user
 * @param props.title {String} Title of the Anime
 * @returns {JSX.Element}
 * @constructor
 */
export default function AnimeEpisodeHistoryModal(props) {
    const {entry, title} = props;
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div id="History-Modal">
            <CyberButton disabled={isEmpty(entry) || entry[UserAnimeEntry.episode_history].length === 0}
                         variant="outlined" color="primary" onClick={handleClickOpen}>
                <FontAwesomeIcon icon={faHistory}/>
            </CyberButton>
            {!isEmpty(entry) && entry[UserAnimeEntry.episode_history].length > 0 ?
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" id="History-Modal-Dialog">
                    <DialogTitle id="form-dialog-title">{title}</DialogTitle>
                    <DialogContent>
                        <DialogContentText className="header-title">
                            History
                        </DialogContentText>
                        {entry[UserAnimeEntry.episode_history].map(history => (
                            <p>Episode <Chip className="chip-primary" size="small"
                                             label={history['episode']}/> completed on <Chip className="chip-primary"
                                                                                             size="small" label={<Moment
                                date={history['timestamp']} format={'Do MMM YYYY HH:mm:ss'}/>}/>
                                <DeleteAnimeEpisodeHistoryButton
                                    history_id={history['id']}
                                    anime_id={entry[UserAnimeEntry.anime][Anime.id]}/>
                            </p>
                        ))}
                    </DialogContent>
                    <DialogActions>
                        <CyberButton onClick={handleClose} variant="contained" color="primary">
                            Close
                        </CyberButton>
                    </DialogActions>
                </Dialog> : null}
        </div>
    );
}


/**
 *
 * @param props.history_id {Number} ID of the history object
 * @param props.anime_id {Number} ID of the anime
 * @returns {JSX.Element}
 * @constructor
 */
export function DeleteAnimeEpisodeHistoryButton(props) {
    const {history_id, anime_id} = props;

    return (
        <span className="delete-btn" onMouseEnter={playHoverSound} onClick={() => {
            playClickSound();
            if (window.confirm('Are you sure?'))
                store.dispatch(deleteAnimeHistoryEntry(history_id, anime_id));
        }}>
            <FontAwesomeIcon icon={faTrash}/>
        </span>
    );
}
