function compareByLabel(a, b) {
    // Compares the two values of stat and returns 1 or -1
    const first = a['label'];
    const second = b['label'];

    let comparison = 0;
    if (first > second) {
        comparison = 1;
    } else if (first < second) {
        comparison = -1;
    }
    return comparison;
}

function compareByStat(a, b) {
    // Compares the two values of stat and returns 1 or -1
    const first = b['stat'];
    const second = a['stat'];

    let comparison = 0;
    if (first > second) {
        comparison = 1;
    } else if (first < second) {
        comparison = -1;
    }
    return comparison;
}

export function getDataTypes(statistics) {

    let data = [
        {label: 'Movie', stat: statistics['total_movie'], color: '', hoverColor: ''},
        {label: 'Music', stat: statistics['total_music'], color: '', hoverColor: ''},
        {label: 'ONA', stat: statistics['total_ona'], color: '', hoverColor: ''},
        {label: 'OVA', stat: statistics['total_ova'], color: '', hoverColor: ''},
        {label: 'Special', stat: statistics['total_special'], color: '', hoverColor: ''},
        {label: 'TV', stat: statistics['total_tv'], color: '', hoverColor: ''},
    ];

    data = data.sort(compareByStat);
    let stat_data = [];
    let labels = [];
    for (let x = 0; x < data.length; x++) {
        stat_data.push(data[x].stat);
        labels.push(data[x].label);
    }

    return {
        labels: labels,
        datasets: [{
            data: stat_data,
            backgroundColor: [
                '#36A2EB',
                '#FF6384',
                '#FFCE56',
                '#559e83',
                '#ae5a41',
                '#ff94fc',
            ],
            hoverBackgroundColor: [
                'rgba(15, 67, 118, 0.3)',
                'rgba(15, 67, 118, 0.3)',
                'rgba(15, 67, 118, 0.3)',
                'rgba(15, 67, 118, 0.3)',
                'rgba(15, 67, 118, 0.3)',
                'rgba(15, 67, 118, 0.3)',
            ],
            // borderColor: 'white',
            // hoverBorderColor: 'rgba(54, 162, 235,0.2)',
            borderWidth: 0
        }]
    }
}


