export function getDataYears(statistics) {

    let parsed_data;
    let parsed_data_keys;
    let data = [];
    try {
        parsed_data = JSON.parse(statistics['total_anime_per_year']);
        parsed_data_keys = Object.keys(parsed_data);
        for (let x = 0; x < parsed_data_keys.length; x++) {
            let element = {label: parsed_data_keys[x], stat: parsed_data[parsed_data_keys[x]]};
            data.push(element)
        }
    } catch (e) {
    }

    let stat_data = [];
    let labels = [];
    for (let x = 0; x < data.length; x++) {
        stat_data.push(data[x].stat);
        labels.push(data[x].label);
    }

    return {
        labels: labels,
        datasets: [
            {
                label: '#',
                backgroundColor: 'rgba(54, 162, 235,0.2)',
                borderColor: 'rgba(54, 162, 235,1)',
                borderWidth: 1,
                hoverBackgroundColor: 'rgba(54, 162, 235,0.4)',
                hoverBorderColor: 'rgba(54, 162, 235,1)',
                data: stat_data
            }
        ]
    };
}