export const ANIME_HOME = '/anime';
export const ANIME_BROWSE = '/anime/browse';
export const ANIME_SEASONS = '/anime/seasons';
export function ANIME_SEASON_DISPLAY(year, season) {
    if (year && season >= 0) return `/anime/seasons/${year}/${season}`;
    return '/anime/seasons/:year/:season'
}
export const ANIME_FRANCHISES = '/anime/franchises';
