import React, {Component} from 'react'
import {connect} from 'react-redux'

import ReactHtmlParser from "react-html-parser";

import {Profile, User} from "../../../../models";
import {cyberBBCodeParser} from "../../settings/bbcode_editor/parser";
import {AnimeSimpleChartStats} from "../../statistics/statistics_snippets";
import {BBCODE_EDITOR} from "../../../../urls";
import {CyberButton, CyberLink} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTools} from "@fortawesome/free-solid-svg-icons";


class AboutTab extends Component {
    constructor(props) {
        super(props);
        this.toggleEditorButton = this.toggleEditorButton.bind(this);
        this.state = {show_editor_button: false};
    }

    toggleEditorButton(value) {
        this.setState({show_editor_button: value})
    }

    render() {
        let background;
        if (this.props.visited_user[User.profile][Profile.about_section_background_style])
            background = JSON.parse(this.props.visited_user[User.profile][Profile.about_section_background_style]);

        let style = null;
        if (background) {
            style = {
                 backgroundColor: `#${background.background_color['hex']}`,
                 backgroundImage: `url("${background.url}")`,
                 backgroundRepeat: background.repeat,
                 backgroundSize: background.size,
                 backgroundPosition: `${background.position_1} ${background.position_2}`,
            };
            if (background.position_x)
                style.backgroundPositionX = `${background.position_x}px`;
            if (background.position_y)
                style.backgroundPositionY = `${background.position_y}px`;
        }

        let owner = false;
        if (this.props.visited_user && this.props.user && this.props.visited_user[User.id] === this.props.user[User.id])
            owner = true;

        return <div onMouseOver={() => {this.toggleEditorButton(true)}}
                    onMouseEnter={() => {this.toggleEditorButton(true)}}
                    onMouseLeave={() => {this.toggleEditorButton(false)}}
        >
            {owner ? <div className="bbcode-editor-button">
                {this.state.show_editor_button ? <CyberLink to={BBCODE_EDITOR}>
                    <CyberButton title="BBCode Editor" variant="contained" classNames="btn-contained-primary">
                        <FontAwesomeIcon icon={faTools}/>
                    </CyberButton>
                </CyberLink> : null}
            </div> : null}
            <div id="BBCode-Editor" className="bbcode-container" style={style}>
                {cyberBBCodeParser(this.props.visited_user[User.profile][Profile.about_section_bbcode])}
            </div>
        </div>
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    visited_user: state.auth.visited_user,
});

export default connect(mapStateToProps, {})(AboutTab)
