import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import Chip from "@material-ui/core/Chip";
import Moment from "react-moment";
import {Bar} from 'react-chartjs-2';

import {updateAndGetUserAnimeStatistics} from "../../../actions/users";
import {
    getDataFavoriteGenres,
    getDataFavoriteTypes,
    getDataFavoriteYears,
    getDataRatedVsEpisodeCount,
    getDataRatedVsTimeSpent,
    getDataScoreDistribution,
    getTableHistory
} from "./data_calculations";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";
import Button from "@material-ui/core/Button";

import '../../../assets/css/users.css'
import {filterTitles} from "../../browse/functions";
import Loading from "../../layout/common/Loading";

const years_calc = 1000 * 60 * 60 * 24 * 30 * 12;


class Statistics extends Component {
    constructor() {
        super(...arguments);
        this.getFavoriteGenres = this.getFavoriteGenres.bind(this);
        this.sortFavoriteGenres = this.sortFavoriteGenres.bind(this);
        this.state = {
            favorite_genres_sorted_by: 'Total',
            data_favorite_genres: {},
            data_favorite_genres_options: {maintainAspectRatio: false},
            updated: false
        };
    }

    componentDidMount() {
        document.title = 'Statistics';
        window.scrollTo(0, 0);

        this.props.updateAndGetUserAnimeStatistics(this.props.match.params.id);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (!this.state.updated)
            this.getFavoriteGenres(this.state.favorite_genres_sorted_by);
        if (prevProps.match.params.id !== this.props.match.params.id) {
            window.scrollTo(0, 0);
            this.props.updateAndGetUserAnimeStatistics(this.props.match.params.id);
        }
    }

    sortFavoriteGenres() {
        if (this.state.favorite_genres_sorted_by === 'Total')
            this.getFavoriteGenres('Mean');
        else
            this.getFavoriteGenres('Total');
    }

    getFavoriteGenres(order) {
        try {
            let data_and_options = getDataFavoriteGenres(this.props.user_anime_statistics['user_statistics'], order);
            this.setState({
                favorite_genres_sorted_by: order,
                data_favorite_genres: data_and_options[0],
                data_favorite_genres_options: data_and_options[1],
                updated: true
            })
        } catch (e) {
        }
    }

    render() {
        let data_sources = {};
        let data_rated_vs_time_spent = {};
        let data_rated_vs_episode_count = {};
        let data_rated_vs_episode_count_options = {maintainAspectRatio: false};
        let data_completed_history;
        let data_favorite_years = {};
        let data_favorite_years_options = {maintainAspectRatio: false};
        let data_favorite_types = {};
        let data_favorite_types_options = {maintainAspectRatio: false};
        let completed_percent_from_all_anime = 0;
        let finished_percent_from_all_anime = 0;
        let episodes_percent_from_all_episodes = 0;
        let minutes_percent_from_all_minutes = 0;
        let all_anime_total_hours = 0;
        let mean_score;
        let hours_watching;
        let years_watching;
        let years_until_completion = 0;

        try {
            data_sources = getDataScoreDistribution(this.props.user_anime_statistics['user_statistics']);
            data_rated_vs_time_spent = getDataRatedVsTimeSpent(this.props.user_anime_statistics['user_statistics']);
            data_completed_history = getTableHistory(this.props.user_anime_statistics['user_statistics']);
            let data_and_options = getDataRatedVsEpisodeCount(this.props.user_anime_statistics['user_statistics']);
            data_rated_vs_episode_count = data_and_options[0];
            data_rated_vs_episode_count_options = data_and_options[1];
            data_and_options = getDataFavoriteYears(this.props.user_anime_statistics['user_statistics']);
            data_favorite_years = data_and_options[0];
            data_favorite_years_options = data_and_options[1];
            data_and_options = getDataFavoriteTypes(this.props.user_anime_statistics['user_statistics']);
            data_favorite_types = data_and_options[0];
            data_favorite_types_options = data_and_options[1];
            // completed_percent_from_all_anime = ((this.props.user_anime_statistics['anime_completed'] /
            //     anime_data_statistics['total_anime']) * 100).toFixed(1);
            // finished_percent_from_all_anime = ((this.props.user_anime_statistics['anime_total_finished'] /
            //     anime_data_statistics['total_anime']) * 100).toFixed(1);
            hours_watching = (this.props.user_anime_statistics['anime_mins_spent_watching'] / 60).toFixed(1);
            mean_score = this.props.user_anime_statistics['anime_mean_score'].toFixed(1);
            // episodes_percent_from_all_episodes = ((this.props.user_anime_statistics['anime_episodes_watched'] /
            //     anime_data_statistics['total_episodes']) * 100).toFixed(1);
            // all_anime_total_hours = (anime_data_statistics['total_minutes'] / 60).toFixed(1);
            minutes_percent_from_all_minutes = ((hours_watching / all_anime_total_hours) * 100).toFixed(1);

            const date1 = new Date(this.props.user_anime_statistics['anime_started_watching_on']);
            const date2 = new Date();
            const diffTime = Math.abs(date2 - date1);
            years_watching = (diffTime / years_calc).toFixed(1);
            years_until_completion = ((100 / finished_percent_from_all_anime) * years_watching).toFixed(1);
        } catch (e) {
        }

        return (
            <>
                <CyberNavbar/>
                <Grid id="Statistics" container>
                    <Grid item xs={12}>
                        <h1>Statistics</h1>
                    </Grid>

                    {!this.props.user_statistics_loading ? <>
                        {/*<Grid item xs={12} md={6}>*/}
                        {/*    <table className="generic-stats-table">*/}
                        {/*        <tbody>*/}
                        {/*        <tr>*/}
                        {/*            <td className="text-right"><Chip label={'Watching'}/></td>*/}
                        {/*            <td><p className="color-white">{this.props.user_anime_statistics['anime_watching']}</p></td>*/}
                        {/*        </tr>*/}
                        {/*        <tr>*/}
                        {/*            <td className="text-right"><Chip label={'Watching'}/></td>*/}
                        {/*            /!*<td><p className="color-white">{this.props.user_anime_statistics['anime_completed']} of{' '}*!/*/}
                        {/*            /!*    {anime_data_statistics['total_anime']} ({completed_percent_from_all_anime}%)</p>*!/*/}
                        {/*            /!*</td>*!/*/}
                        {/*        </tr>*/}
                        {/*        <tr>*/}
                        {/*            <td className="text-right"><Chip label={'Watching'}/></td>*/}
                        {/*            <td><p className="color-white">{this.props.user_anime_statistics['anime_on_hold']}</p></td>*/}
                        {/*        </tr>*/}
                        {/*        <tr>*/}
                        {/*            <td className="text-right"><Chip label={'Watching'}/></td>*/}
                        {/*            <td><p className="color-white">{this.props.user_anime_statistics['anime_dropped']}</p></td>*/}
                        {/*        </tr>*/}
                        {/*        <tr>*/}
                        {/*            <td className="text-right"><Chip label={'Watching'}/></td>*/}
                        {/*            <td>*/}
                        {/*                <p className="color-white">{this.props.user_anime_statistics['anime_plan_to_watch']}</p>*/}
                        {/*            </td>*/}
                        {/*        </tr>*/}
                        {/*        <tr className="text-right">*/}
                        {/*            <td><Chip label={'Total Entries'}/></td>*/}
                        {/*            <td><p className="color-white">{this.props.user_anime_statistics['anime_total_entries']}</p>*/}
                        {/*            </td>*/}
                        {/*        </tr>*/}
                        {/*        </tbody>*/}
                        {/*    </table>*/}
                        {/*</Grid>*/}
                        {/*<Grid item xs={12} md={6}>*/}
                        {/*        <table className="generic-stats-table">*/}
                        {/*            <tbody>*/}
                        {/*            <tr className="text-right">*/}
                        {/*                <td><Chip label={'Watching since'}/></td>*/}
                        {/*                <td><p className="color-white">*/}
                        {/*                    <Moment format="DD MMM YYYY"*/}
                        {/*                            date={this.props.user_anime_statistics['anime_started_watching_on']}/>{' '}*/}
                        {/*                            ({years_watching} years)</p></td>*/}
                        {/*            </tr>*/}
                        {/*            <tr className="text-right">*/}
                        {/*                <td><Chip label={'Total Finished'}/></td>*/}
                        {/*                /!*<td><p className="color-white">{this.props.user_anime_statistics['anime_total_finished']} of*!/*/}
                        {/*                /!*    {' '}{anime_data_statistics['total_anime']} (*!/*/}
                        {/*                /!*    {finished_percent_from_all_anime}%)</p></td>*!/*/}
                        {/*            </tr>*/}
                        {/*            <tr className="text-right">*/}
                        {/*                <td><Chip label={'Finish rate'}/></td>*/}
                        {/*                <td><p className="color-white">{years_until_completion} years until full*/}
                        {/*                    completion</p></td>*/}
                        {/*            </tr>*/}
                        {/*            <tr>*/}
                        {/*                <td className="text-right"><Chip label={'Mean score'}/></td>*/}
                        {/*                <td><p className="color-white">{mean_score}</p></td>*/}
                        {/*            </tr>*/}
                        {/*            <tr>*/}
                        {/*                <td className="text-right"><Chip label={'Total episodes'}/></td>*/}
                        {/*                /!*<td><p className="color-white">{this.props.user_anime_statistics['anime_episodes_watched']}*!/*/}
                        {/*                /!*    {' '}of {anime_data_statistics['total_episodes']}{' '}*!/*/}
                        {/*                /!*    ({episodes_percent_from_all_episodes}%)</p></td>*!/*/}
                        {/*            </tr>*/}
                        {/*            <tr>*/}
                        {/*                <td className="text-right"><Chip label={'Total hours'}/></td>*/}
                        {/*                <td><p className="color-white">{hours_watching} of {all_anime_total_hours}{' '}*/}
                        {/*                    ({minutes_percent_from_all_minutes}%)</p></td>*/}
                        {/*            </tr>*/}
                        {/*            </tbody>*/}
                        {/*        </table>*/}
                        {/*    </Grid>*/}

                        <Grid item xs={12}>
                            <div className="table-container">
                                <h5 id="History" className="text-center">History</h5>
                                {/*<UncontrolledTooltip placement="top" target="History">*/}
                                {/*    Total number of Anime you have finished in every month. Entries that have unknown*/}
                                {/*    finish date are not taken into account*/}
                                {/*</UncontrolledTooltip>*/}
                                <p>
                                    <Chip className="chip-default" size="small" label={'#'}/> Total&nbsp;&nbsp;
                                    <Chip className="chip-primary" size="small" label={'#'}/> Completed&nbsp;&nbsp;
                                    <Chip className="chip-danger" size="small" label={'#'}/> Dropped</p>
                                {data_completed_history}
                            </div>
                        </Grid>

                        <Grid item xs={12} md={6}>
                            <div className="bar-container">
                                <h5 id="Score-Distribution" className="text-center">Score Distribution</h5>
                                {/*<UncontrolledTooltip placement="top" target="Score-Distribution">*/}
                                {/*    How many Anime you have scored in each score grade. Every scored entry is taken*/}
                                {/*    into account.*/}
                                {/*</UncontrolledTooltip>*/}
                                <Bar
                                    data={data_sources}
                                    width={100}
                                    height={50}
                                    options={{maintainAspectRatio: false}}
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <div className="bar-container">
                                <h5 id="Rated-Time-Spent" className="text-center">Rated vs Time spent</h5>
                                {/*<UncontrolledTooltip placement="top" target="Rated-Time-Spent">*/}
                                {/*    How much time you have spent watching in all score grades. Every scored entry is*/}
                                {/*    taken into account. Anime with no episodes are not taken into account.*/}
                                {/*</UncontrolledTooltip>*/}
                                <Bar
                                    data={data_rated_vs_time_spent}
                                    width={100}
                                    height={50}
                                    options={{maintainAspectRatio: false}}
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <div className="bar-container">
                                <h5 id="Rated-Episode-Count" className="text-center">Rated vs Episode count</h5>
                                {/*<UncontrolledTooltip placement="top" target="Rated-Episode-Count">*/}
                                {/*    Number of Anime you have completed in various episode formats. Only "Completed"*/}
                                {/*    entries are taken, anime with unknown episodes are not taken into account*/}
                                {/*</UncontrolledTooltip>*/}
                                <Bar
                                    data={data_rated_vs_episode_count}
                                    width={100}
                                    height={50}
                                    options={data_rated_vs_episode_count_options}
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <div className="bar-container">
                                <h5 id="Favorite-Types" className="text-center">Favorite Types</h5>
                                {/*<UncontrolledTooltip placement="top" target="Favorite-Types">*/}
                                {/*    Number of Anime you have in your list from various types. All anime except the ones*/}
                                {/*    in "Plan to Watch" are taken into the calculation, entries that are not scored are*/}
                                {/*    not taken into account for the mean score*/}
                                {/*</UncontrolledTooltip>*/}
                                <Bar
                                    data={data_favorite_types}
                                    width={100}
                                    height={50}
                                    options={data_favorite_types_options}
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12}>
                            <div className="bar-container">
                                <h5 id="Favorite-Years" className="text-center">Favorite Years</h5>
                                {/*<UncontrolledTooltip placement="top" target="Favorite-Years">*/}
                                {/*    Number of Anime you have in your list from various years. All anime except the ones*/}
                                {/*    in "Plan to Watch" are taken into the calculation, entries that are not scored are*/}
                                {/*    not taken into account for the mean score*/}
                                {/*</UncontrolledTooltip>*/}
                                <Bar
                                    data={data_favorite_years}
                                    width={100}
                                    height={50}
                                    options={data_favorite_years_options}
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12}>
                            <div className="bar-container">
                                <h5 id="Favorite-Genres" className="text-center">Favorite Genres</h5>
                                {/*<UncontrolledTooltip placement="top" target="Favorite-Genres">*/}
                                {/*    Number of Anime you have in your list from various genres. All anime except the*/}
                                {/*    ones in "Plan to Watch" are taken into the calculation, entries that are not scored*/}
                                {/*    are not taken into account for the mean score*/}
                                {/*</UncontrolledTooltip>*/}
                                <Button variant="outlined"
                                        onClick={this.sortFavoriteGenres}>{this.state.favorite_genres_sorted_by}</Button>
                                <Bar
                                    data={this.state.data_favorite_genres}
                                    width={100}
                                    height={50}
                                    options={this.state.data_favorite_genres_options}
                                />
                            </div>
                        </Grid>
                    </> : <Loading text={'Statistics Loading'} offset={150}/>}
                </Grid>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    user_anime_statistics: state.users.user_anime_statistics,
    user_statistics_loading: state.users.user_statistics_loading,
});

export default connect(mapStateToProps, {updateAndGetUserAnimeStatistics})(Statistics)
