import React, {Component} from 'react'
import {connect} from 'react-redux'
import {getSeasonLabelFromSeasonNumber} from "../functions";
import PropTypes from "prop-types";

import {browseSeasonTitlesFromBranch, getNextSeasonUrl, getPrevSeasonUrl, getSeasonIcon} from "./functions";

import '../../assets/css/seasons.css'
import {CyberButton, CyberLink} from "../layout/common/common";
import {ANIME_SEASON_DISPLAY} from "../../urls";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faArrowLeft, faArrowRight, faSun} from "@fortawesome/free-solid-svg-icons";


class DisplaySeason extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
    };

    constructor() {
        super(...arguments);
        this.state = {
            year: parseInt(this.props.match.params.year),
            season_num: parseInt(this.props.match.params.season),
            season_name: getSeasonLabelFromSeasonNumber(parseInt(this.props.match.params.season))
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log('prev prop', prevProps);
        console.log('prev prop', prevProps.match.params);
        console.log('prev', prevState);
        console.log('new', this.props.match.params.season);
        if (prevProps.match.params.season !== this.props.match.params.season) {
            this.setState({
                year: parseInt(this.props.match.params.year),
                season_num: parseInt(this.props.match.params.season),
                season_name: getSeasonLabelFromSeasonNumber(parseInt(this.props.match.params.season))
            })
        }
    }

    render() {
        return (
            <div id="Seasons">
                <div className="title-segment">
                    <CyberLink to={getPrevSeasonUrl(this.props.branch, this.state.year, this.state.season_num)}>
                        <CyberButton variant="outlined">
                            <FontAwesomeIcon icon={faArrowLeft}/>
                        </CyberButton>
                    </CyberLink>
                    <h1>
                        {getSeasonIcon(this.state.season_num)} {this.state.season_name} {this.state.year}
                    </h1>
                    <CyberLink to={getNextSeasonUrl(this.props.branch, this.state.year, this.state.season_num)}>
                        <CyberButton variant="outlined">
                            <FontAwesomeIcon icon={faArrowRight}/>
                        </CyberButton>
                    </CyberLink>
                </div>
                {browseSeasonTitlesFromBranch(this.props.branch, this.state.year, this.state.season_num)}
            </div>
        )
    }
}

const mapStateToProps = state => ({});

export default connect(mapStateToProps, {})(DisplaySeason)
