import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";

import '../../../assets/css/list.css'
import PropTypes from "prop-types";
import {CyberButton, CyberLink, PreLoadImage} from "../../layout/common/common";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {filterTitles, getFiltersFromBranch} from "../../browse/functions";
import Loading from "../../layout/common/Loading";
import {Anime, UserAnimeEntry, ApiResult, CoverImages, User} from "../../../models";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import TitleCard from "../../layout/cards/TitleCard";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCalendarAlt, faCheck, faImage, faPause, faPlay, faPlus, faStop} from "@fortawesome/free-solid-svg-icons";
import Moment from "react-moment";
import {
    BRANCH_ANIME,
    BRANCH_CHARACTERS,
    BRANCH_EROGE,
    BRANCH_MANGA,
    CYBER_GENESIS_URL, FILTER_ANIME_INITIAL_VALUES, FILTER_MANGA_INITIAL_VALUES,
    USER_ANIME_STATUSES, USER_MANGA_STATUSES
} from "../../../globals";
import store from "../../../store";
import {cancelAnimeLoading, getAnime} from "../../../actions/anime";
import Chip from "@material-ui/core/Chip";
import {ANIME_DISPLAY} from "../../../urls";
import AnimeEntryModalForm from "../forms/anime/AnimeEntryModalForm";
import {updateAnimeEpisode} from "../../../actions/users";
import UpdateAnimeEpisodeButton from "../forms/anime/UpdateAnimeEpisodeButton";
import {isEmpty} from "../../functions";
import {getUser} from "../../../actions/auth";
import {getTitle} from "../../display/functions";
import {AnimeListStatusButtons, AnimeListTableHead, AnimeListTableRow} from "./anime/tableView";
import {getManga} from "../../../actions/manga";
import {getCharacter} from "../../../actions/characters";
import {getEroge} from "../../../actions/eroge";
import {MangaListStatusButtons, MangaListTableHead, MangaListTableRow} from "./manga/tableView";


function getInitialSelected(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return USER_ANIME_STATUSES.watching;
        case BRANCH_MANGA:
            return USER_MANGA_STATUSES.reading;
        default:
            return null
    }
}


class UserList extends Component {
    static propTypes = {
        /** params of the page, to get the user list id */
        params: PropTypes.object.isRequired,
        /** Redux prop of titles_loading */
        titles_loading: PropTypes.bool.isRequired,
        /** Redux prop of list_owner */
        list_owner: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        filtered_titles_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        filtered_titles: PropTypes.object.isRequired,
        /** Redux prop of title_filters */
        title_filters: PropTypes.object,
    };

    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.setSelected = this.setSelected.bind(this);
        this.state = {
            list_pk: parseInt(this.props.params.id),
            selected: getInitialSelected(this.props.branch),
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        document.title = `${this.props.visited_user[User.username]}'s ${this.props.branch} List`;
        window.scrollTo(0, 0);
        filterTitles(this.props.branch, 1, this.props.title_filters, null,
            {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.selected !== this.state.selected) {
            this.setState({current_page: 1});
            filterTitles(this.props.branch, 1, this.props.title_filters, null,
                {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
        }
        if (prevProps.params.id !== this.props.params.id) {
            window.scrollTo(0, 0);
            this.setState({list_pk: parseInt(this.props.params.id)}, () => {
                this.props.getUser(this.state.list_pk);
                filterTitles(this.props.branch, 1, this.props.title_filters, null,
                    {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
            });
        }
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)) {
            this.setState({current_page: page});
            filterTitles(this.props.branch, page, this.props.title_filters, null,
                {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
        }
    }

    setSelected(selected) {
        this.setState({selected: selected})
    }

    render() {
        const list = {
            user_list_pk: this.state.list_pk,
            user_list_filter: this.state.selected
        };

        return (
            <>
                <CyberNavbar/>
                <Grid id="User-List" container>
                    {this.props.branch === BRANCH_ANIME ?
                        <AnimeListStatusButtons selected={this.state.selected} setSelected={this.setSelected}/>
                        : this.props.branch === BRANCH_MANGA ?
                            <MangaListStatusButtons selected={this.state.selected} setSelected={this.setSelected}/>
                            : null}

                    {getFiltersFromBranch(this.props.branch, null, list)}

                    {this.props.filtered_titles_loaded && this.props.visited_user_loaded ?
                        <Grid item xs={12} className="list-container">
                            <TableContainer className="list-table">
                                <Table aria-label="user list table">
                                    {this.props.branch === BRANCH_ANIME ?
                                        <AnimeListTableHead list_owner={this.props.list_owner}/>
                                        : this.props.branch === BRANCH_MANGA ?
                                            <MangaListTableHead list_owner={this.props.list_owner}/> : null}
                                    <TableBody>
                                        {this.props.filtered_titles[ApiResult.results].map((entry, x) => (
                                            this.props.branch === BRANCH_ANIME ?
                                                <AnimeListTableRow
                                                    entry={entry}
                                                    index={x + ((this.state.current_page * 100) - 99)}
                                                    list_owner={this.props.list_owner}
                                                    list={list}
                                                />
                                                : this.props.branch === BRANCH_MANGA ?
                                                <MangaListTableRow
                                                    entry={entry}
                                                    index={x + ((this.state.current_page * 100) - 99)}
                                                    list_owner={this.props.list_owner}
                                                    list={list}
                                                /> : null
                                        ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            <div className="pagination">
                                <Pagination
                                    count={Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)}
                                    color="primary"
                                    siblingCount={3}
                                    page={this.state.current_page}
                                    onChange={(type, page) => {
                                        playClickSound();
                                        if (page !== this.state.current_page)
                                            this.goToPage(page);
                                        else
                                            this.setState({page_dialog_open: true})
                                    }}
                                />
                                <GoToPageDialog
                                    open={this.state.page_dialog_open}
                                    current_page={this.state.current_page}
                                    total_pages={Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)}
                                    goToPage={this.goToPage}
                                    onClose={this.closePageDialog}/>
                            </div>
                        </Grid> : this.props.titles_loading ?
                            <Loading text={`${this.props.branch} List Loading`} offset={250}/> : null}
                </Grid>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    user_loading: state.auth.user_loading,
    entry_loading: state.users.entry_loading, // This is here to rerender the small loading on the update episode btn
});

export default connect(mapStateToProps, {})(UserList)
