import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME} from "../../../../globals";
import UserList from "../UserList";
import {getUser} from "../../../../actions/auth";
import Loading from "../../../layout/common/Loading";
import {Profile, User} from "../../../../models";
import {PrivatePage} from "../PrivateList";


class AnimeList extends Component {
    componentDidMount() {
        this.props.getUser(parseInt(this.props.match.params.id));
    }

    render() {
        let list_owner = false;
        if (this.props.visited_user && this.props.user && this.props.visited_user[User.id] === this.props.user[User.id])
            list_owner = true;

        return this.props.user_loading ? <Loading/> : this.props.visited_user_loaded ?
            list_owner || !this.props.visited_user[User.profile][Profile.is_anime_list_private] ?
                <UserList
                    branch={BRANCH_ANIME}
                    params={this.props.match.params}
                    list_owner={list_owner}
                    titles_loading={this.props.anime_loading}
                    filtered_titles_loaded={this.props.filtered_anime_loaded}
                    filtered_titles={this.props.filtered_anime}
                    title_filters={this.props.anime_filters}
                /> : <PrivatePage text={`${this.props.visited_user[User.username]}'s ${BRANCH_ANIME} List is private.`}/> : null
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    anime_loading: state.anime.anime_loading,
    filtered_anime_loaded: state.anime.filtered_anime_loaded,
    filtered_anime: state.anime.filtered_anime,
    anime_filters: state.cookies.anime_list_filters
});

export default connect(mapStateToProps, {getUser})(AnimeList)
