// UPLOAD COVER IMAGE AND UPDATE THE USER
export const updateUserProfileImage = (file, id) => (dispatch, getState) => {
    dispatch({type: UPLOADING_FILE});

    const token = getState().auth.token;
    const formData = new FormData();
    formData.append('file', file);

    const config = {
        headers: {
            'content-type': 'multipart/form-data',
            'Content-Type': 'application/json'
        }
    };

    // If token, add to headers config
    if (token) {
        config.headers["Authorization"] = `Token ${token}`;
    }

    axios
        .post(API_CHANGE_USER_PROFILE_IMAGE, formData, config)
        .then(res => {
            dispatch(createMessage('Profile Image changed successfully!', Alert.type_success));
            dispatch({type: FINISH_UPLOADING_FILE});
            dispatch(getUser(id));
            dispatch(authUser());
        })
        .catch(err => {
            dispatch({type: FINISH_UPLOADING_FILE});
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// UPLOAD XML FILE IN MAL FORMAT TO UPDATE THE ANIME LIST
