// ADD/CREATE USER ANIME ENTRY
export const addAnimeEntry = entryInfo => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});
    const body = JSON.stringify(entryInfo);

    axios
        .post(API_USER_ANIME_ENTRY_NOID, body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Anime added!', Alert.type_success));
            dispatch({
                type: GET_USER_ANIME_ENTRY,
                payload: res.data
            });
            dispatch({
                type: UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// INSTANT DROP AN ANIME
export const addAnimeEntryInstantDrop = (id) => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});

    const body = JSON.stringify({
        anime: id,
        watched_episodes: 0,
        start_date: new Date(),
        finish_date: new Date(),
        score: 0,
        status: USER_ANIME_STATUSES.dropped,
        times_watched: 0,
        comments: '',
        tags: '',
        priority: '0',
        rewatching: false,
        rewatching_episode: 0,
    });

    axios
        .post(API_USER_ANIME_ENTRY_NOID, body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Anime dropped!', Alert.type_success));
            dispatch({
                type: GET_USER_ANIME_ENTRY,
                payload: res.data
            });
            dispatch({
                type: UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// UPDATE USER ANIME ENTRY
export const updateAnimeEntry = (id, entry_info, list) => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});
    const body = JSON.stringify(entry_info);

    axios
        .put(API_USER_ANIME_ENTRY(id), body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Entry updated!', Alert.type_success));
            if (isEmpty(list)) {
                dispatch({
                    type: GET_USER_ANIME_ENTRY,
                    payload: res.data
                });
            } else {
                let filters = store.getState()[store_reducers.cookies][store_reducer_cookies.anime_list_filters];
                dispatch(filterAnime(1, filters, null, list, false))
            }
            dispatch({
                type: UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// UPDATE ANIME EPISODE
export const updateAnimeEpisode = (id, list) => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});

    axios
        .post(API_USER_ANIME_UPDATE_EPISODE(id), null, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Episode updated!', Alert.type_success));
            if (isEmpty(list)) {
                dispatch({
                    type: GET_USER_ANIME_ENTRY,
                    payload: res.data
                });
            } else {
                dispatch({
                    type: FINISH_LOADING_USER_ENTRY,
                });
                let filters = store.getState()[store_reducers.cookies][store_reducer_cookies.anime_list_filters];
                dispatch(filterAnime(1, filters, null, list, false))
            }
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};

// DELETE USER ANIME ENTRY
/**
 *
 * @param {Number} id - ID of the anime
 * @param {Object} list
 * @returns {function(*, *=): void}
 */
export const deleteAnimeEntry = (id, list) => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});
    axios
        .delete(API_USER_ANIME_ENTRY(id), tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Anime deleted', Alert.type_success));
            if (isEmpty(list)) {
                dispatch({
                    type: CLEAR_USER_ANIME_ENTRY,
                });
            } else {
                let filters = store.getState()[store_reducers.cookies][store_reducer_cookies.anime_list_filters];
                dispatch(filterAnime(1, filters, null, list, false))
            }
            dispatch({
                type: UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
                payload: id
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


