// MANGA USER ACTIONS //////////////////////////////////////////////////////////////////////////////////////////////////

// GET USER VISITED USER MANGA HISTORY
export const getVisitedUserMangaHistory = (id, page) => (dispatch, getState) => {
    dispatch({type: USER_INFO_LOADING});

    let config = tokenConfig(getState);
    if (page && page !== 1) config.params = {'page': page};

    axios
        .get(API_GET_VISITED_USER_MANGA_HISTORY(id), config)
        .then(res => {
            dispatch({
                type: GET_VISITED_USER_MANGA_HISTORY,
                payload: res.data
            });
        })
        .catch(err => {});
};


// GET USER MANGA ENTRY
/**
 * Pass the Manga ID corresponding to the user entry
 * @param id {Number} Manga ID
 * @returns {function(*, *=): void}
 */
export const getUserMangaEntry = id => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});

    axios
        .get(API_USER_MANGA_ENTRY(id), tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_USER_MANGA_ENTRY,
                payload: res.data
            });
        })
        .catch(err => {
            dispatch({
                type: CLEAR_USER_MANGA_ENTRY,
            });
        });
};


// ADD/CREATE USER MANGA ENTRY
export const addMangaEntry = entryInfo => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});
    const body = JSON.stringify(entryInfo);

    axios
        .post(API_USER_MANGA_ENTRY_NOID, body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Manga added!', Alert.type_success));
            dispatch({
                type: GET_USER_MANGA_ENTRY,
                payload: res.data
            });
            dispatch({
                type: UPDATE_LOGGED_IN_USER_MANGA_ENTRY_STATUSES,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


// INSTANT DROP A MANGA
export const addMangaEntryInstantDrop = (id) => (dispatch, getState) => {
    dispatch({type: USER_ENTRY_LOADING});

    const initial_values = {
        manga: id,
        read_volumes: 0,
        read_chapters: 0,
        start_date: new Date(),
        finish_date: new Date(),
        score: 0,
        status: USER_MANGA_STATUSES.dropped,
        times_read: 0,
        comments: '',
        tags: '',
        priority: '0',
        rereading: false,
        rereading_volume: 0,
        rereading_chapter: 0,
        errors: false
    };
    const body = JSON.stringify(initial_values);

    axios
        .post(API_USER_MANGA_ENTRY_NOID, body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('Manga dropped!', Alert.type_success));
            dispatch({
                type: GET_USER_MANGA_ENTRY,
                payload: res.data
            });
            dispatch({
                type: UPDATE_LOGGED_IN_USER_MANGA_ENTRY_STATUSES,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


