// DELETE USER MANGA ENTRY HISTORY VOLUME/CHAPTER
export const deleteMangaVolumeChapterHistoryEntry = (history_id, manga_id, type) => (dispatch, getState) => {
    let url = API_DELETE_MANGA_VOLUME_HISTORY_ENTRY(history_id);

    if (type === 'chapter')
        url = API_DELETE_MANGA_CHAPTER_HISTORY_ENTRY(history_id);

    axios
        .delete(url, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage('History entry deleted!', Alert.type_success));
            dispatch(getUserMangaEntry(manga_id));
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        });
};


