// ADD TO FAVORITES
export const addToFavorites = (branch, id) => (dispatch, getState) => {
    let body = {'title_id': id};

    axios
        .post(API_ADD_TO_FAVORITES(branch), body, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage(`${branch} added to favorites`, Alert.type_success));
            dispatch(authUser())
        })
        .catch(err => {
            dispatch(returnErrors(err.response.data, err.response.status));
        })
};


