// UPDATE USER ANIME LIST PRIVACY
export const updateUserAnimeListPrivacy = (to_private) => (dispatch, getState) => {
    const api_url = to_private ? API_SET_ANIME_LIST_TO_PRIVATE : API_SET_ANIME_LIST_TO_PUBLIC;
    const text = to_private ? 'private' : 'public';
    axios
        .get(api_url, tokenConfig(getState))
        .then(res => {
            dispatch(createMessage(`Anime List has been set to ${text}.`, Alert.type_success));
            dispatch({
                type: GET_PROFILE_PRIVACY_SETTINGS,
                payload: res.data
            })
        })
        .catch(err => {
        })
};


