import React, {Component} from 'react'
import {connect} from 'react-redux'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';

import {Anime, AnimeOnServer, User} from "../../../../models";

import '../../../../assets/css/anime.css'
import Chip from "@material-ui/core/Chip";
import Moment from "react-moment";
import {Button} from "@material-ui/core";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {CyberButton, CyberLink} from "../../../layout/common/common";
import {faFolder, faMagnet, faTrash, faUserEdit} from "@fortawesome/free-solid-svg-icons";
import {deleteAnimeServerInfoAndTorrents, openAnimeFolder} from "../../../../actions/requiem";
import {ADMIN_EDIT_ANIME_ON_SERVER} from "../../../../urls";
import EditAnimeOnServerModalForm from "../../../requiem/forms/EditAnimeOnServerModalForm";


class ServerTab extends Component {
    static propTypes = {};

    constructor() {
        super(...arguments);
        this.state = {show: true};
    }

    componentDidMount() {
    }

    componentWillUnmount() {
    }

    render() {

        return (
            <div className="server-tab">
                {this.props.user && this.props.user[User.is_superuser] ? <div className="btn-group">
                    <CyberButton classNames="btn-contained-secondary" onClick={() => {
                         this.props.openAnimeFolder(this.props.anime[Anime.id]);
                     }}><FontAwesomeIcon icon={faFolder}/></CyberButton>
                    <EditAnimeOnServerModalForm classNames="edit-btn"/>
                    <a href={ADMIN_EDIT_ANIME_ON_SERVER(this.props.anime[Anime.id])} className="margin-left-10">
                        <CyberButton classNames="btn-contained-secondary" component="span">
                            <FontAwesomeIcon icon={faUserEdit}/>
                        </CyberButton>
                    </a>
                    <CyberButton classNames="btn-contained-danger" onClick={() => {
                         if (window.confirm("Are you sure?")) this.props.deleteAnimeServerInfoAndTorrents(this.props.anime[Anime.id]);
                     }}>
                        <FontAwesomeIcon icon={faTrash}/>
                    </CyberButton>
                </div> : null}

                <TableContainer>
                    <Table aria-label="simple table">
                        <TableBody>
                            <TableRow>
                                <TableCell>Status</TableCell>
                                <TableCell>
                                    {this.props.anime[Anime.anime_on_server][AnimeOnServer.approved] ?
                                        <span className="color-green">Approved</span> :
                                        <span className="color-danger">Pending</span>}
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Location</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.location]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Size</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.size_on_HDD_GB]}GB</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Video</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.video_quality]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Audio</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.audio_channel]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Dubs</TableCell>
                                <TableCell className="lang-chipset">
                                    {Object.keys(AnimeOnServer).map(key => (
                                        key.endsWith('_dub') ?
                                            this.props.anime[Anime.anime_on_server][key] ?
                                                <Chip variant="outlined" label={key.replace('_dub', '')}/> : null : null
                                    ))}
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Subs</TableCell>
                                <TableCell className="lang-chipset">
                                    {Object.keys(AnimeOnServer).map(key => (
                                        key.endsWith('_sub') ?
                                            this.props.anime[Anime.anime_on_server][key] ?
                                                <Chip variant="outlined" label={key.replace('_sub', '')}/> : null : null
                                    ))}
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Additional Info</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.additional_info]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Extract Subs</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.extract_subs] ? 'Yes' : 'No'}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Incomplete</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.incomplete] ? 'Yes' : 'No'}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Hash</TableCell>
                                <TableCell>{this.props.anime[Anime.anime_on_server][AnimeOnServer.torrent_hash]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Magnet</TableCell>
                                <TableCell>
                                    {this.props.anime[Anime.anime_on_server][AnimeOnServer.torrent_magnet] ?
                                        <CyberButton classNames="btn-secondary" variant="outlined" onClick={() => {
                                            window.open(this.props.anime[Anime.anime_on_server][AnimeOnServer.torrent_magnet]);
                                        }}>
                                            <FontAwesomeIcon icon={faMagnet}/>
                                        </CyberButton> : null}
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Downloaded on</TableCell>
                                <TableCell><Moment
                                    date={this.props.anime[Anime.anime_on_server][AnimeOnServer.date_added]}
                                    format={'Do MMM YYYY'}/></TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Last Modified</TableCell>
                                <TableCell><Moment
                                    date={this.props.anime[Anime.anime_on_server][AnimeOnServer.date_last_modified]}
                                    format={'Do MMM YYYY'}/></TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
    user: state.auth.user
});

export default connect(mapStateToProps, {deleteAnimeServerInfoAndTorrents, openAnimeFolder})(ServerTab)
