import React, { useState } from 'react';

import {Form, Formik} from "formik";
import CircularProgress from "@material-ui/core/CircularProgress";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import BBCodeQuickGuideDialog from "./snippets/snippets";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faFileUpload, faLevelDownAlt, faStar as faStarSolid} from "@fortawesome/free-solid-svg-icons";

import store from "../../../store";
import {
    addAnimeEntryInstantDrop,
    addMangaEntryInstantDrop, addToFavorites,
    editUserAboutMeBBCode,
    editUserBio,
    importMalXmlForAnimeList,
    importMalXmlForMangaList, removeFromFavorites,
    scrapeEpisodeHistoryFromMal
} from "../../../actions/users";
import {playClickSound, playHoverSound} from "../../layout/sounds";
import {CyberButton} from "../../layout/common/common";
import {BRANCH_ANIME, store_reducer_users, store_reducers} from "../../../globals";
import {faStar} from "@fortawesome/free-regular-svg-icons";
import {isTitleInFavorites} from "../../display/functions";


/**
 * @param props.branch
 * @param props.id
 * @returns {JSX.Element}
 * @constructor
 */
export function AddRemoveFromFavorites(props) {
    const {branch, id} = props;
    const initial = isTitleInFavorites(branch, id);
    const [inFavorites, setInFavorites] = useState(initial);

    return <CyberButton variant="outlined" color="primary" onClick={() => {
        if (inFavorites) store.dispatch(removeFromFavorites(branch, parseInt(id)));
        else store.dispatch(addToFavorites(branch, parseInt(id)));
        setInFavorites(!inFavorites)
    }} title={inFavorites ? 'Remove from favorites' : 'Add to favorites'}>
        <FontAwesomeIcon icon={inFavorites ? faStarSolid : faStar}/>
    </CyberButton>
}


/**
 * Form for editing user's bio
 * @param props {Object} user's bio
 * @param props.bio {String} user's bio
 * @param props.closeBio {Function} function to call on submit
 * @returns {JSX.Element}
 */
export function EditBioForm(props) {
    const {bio, closeBio} = props;
    const field_names = {
        bio: 'bio',
    };

    const initial_values = {
        bio: bio,
    };

    return <Formik
        initialValues={initial_values}
        onSubmit={(values, {setSubmitting}) => {
            store.dispatch(editUserBio(values.bio));
            setSubmitting(false);
            closeBio();
        }}>
        {({
              errors, touched, values,
              handleChange, handleBlur, handleSubmit
          }) => (
            <Form id="edit-user-bio-form">
                <TextField
                    onKeyPress={(e) => {
                        if (e.key === 'Enter' && !e.shiftKey) {
                            e.preventDefault();
                            handleSubmit();
                        }
                    }}
                    label="Bio"
                    className="form-control"
                    multiline
                    value={values.bio}
                    name={field_names.bio}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={errors.bio && touched.bio}
                    helperText={(errors.bio && touched.bio) && errors.bio}/>
            </Form>
        )}
    </Formik>
}


/**
 *
 * @param props {Object}
 * @param props.bbcode {String}
 * @param props.closeForm {Function} function to call on submit
 * @returns {JSX.Element}
 */
export function EditAboutMeBBCodeForm(props) {
    const {bbcode, closeForm} = props;
    const field_names = {
        bbcode: 'bbcode',
    };

    const initial_values = {
        bbcode: bbcode,
    };

    return <Formik
        initialValues={initial_values}
        onSubmit={(values, {setSubmitting}) => {
            values.bbcode = values.bbcode.replaceAll('\n', '[br]');
            store.dispatch(editUserAboutMeBBCode(values.bbcode));
            setSubmitting(false);
            closeForm();
        }}>
        {({
              errors, touched, values,
              handleChange, handleBlur, handleSubmit
          }) => (
            <Form id="edit-user-about-me-bbcode-form">
                <TextField
                    label="BBCode"
                    className="form-control"
                    multiline
                    value={values.bbcode.replaceAll('[br]', '\n')}
                    name={field_names.bbcode}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={errors.bbcode && touched.bbcode}
                    helperText={(errors.bbcode && touched.bbcode) && errors.bbcode}
                />
                <div className="button-group">
                    <BBCodeQuickGuideDialog/>
                    <CyberButton classNames="btn-contained-primary" type="submit"><FontAwesomeIcon icon={faCheck}/></CyberButton>
                </div>
            </Form>
        )}
    </Formik>
}


export function EditAboutMeBBCodeDialog(props) {
    const {open, onClose, bbcode} = props;

    return (
        <Dialog onClose={onClose} aria-labelledby="edit-user-about-me-bbcode-title" open={open}>
            <div id="edit-user-about-me-bbcode">
                <DialogTitle id="edit-user-about-me-bbcode-title">Edit BBCode</DialogTitle>
                <EditAboutMeBBCodeForm bbcode={bbcode} closeForm={onClose}/>
            </div>
        </Dialog>
    );
}


/**
 *
 * @param props {Object}
 * @param props.bbcode {String}
 * @param props.closeForm {Function} function to call on submit
 * @returns {JSX.Element}
 */
export function ScrapeEpisodeHistoryFromMalForm(props) {
    const {closeForm} = props;
    const field_names = {
        MALHLOGSESSID: 'MALHLOGSESSID',
        MALSESSIONID: 'MALSESSIONID',
    };

    const initial_values = {
        MALHLOGSESSID: '',
        MALSESSIONID: '',
    };

    return <Formik
        initialValues={initial_values}
        onSubmit={(values, {setSubmitting}) => {
            store.dispatch(scrapeEpisodeHistoryFromMal(values[field_names.MALHLOGSESSID], values[field_names.MALSESSIONID]));
            setSubmitting(false);
            closeForm();
        }}>
        {({
              errors, touched, values,
              handleChange, handleBlur, handleSubmit
          }) => (
            <Form id="scrape-episode-history-from-mal-form">
                <Grid container>
                    <Grid item xs={12}>
                        <TextField
                            label={field_names.MALHLOGSESSID}
                            className="form-control"
                            multiline
                            value={values[field_names.MALHLOGSESSID]}
                            name={field_names.MALHLOGSESSID}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={errors.MALHLOGSESSID && touched.MALHLOGSESSID}
                            helperText={(errors.MALHLOGSESSID && touched.MALHLOGSESSID) && errors.MALHLOGSESSID}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label={field_names.MALSESSIONID}
                            className="form-control"
                            multiline
                            value={values[field_names.MALSESSIONID]}
                            name={field_names.MALSESSIONID}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={errors.MALSESSIONID && touched.MALSESSIONID}
                            helperText={(errors.MALSESSIONID && touched.MALSESSIONID) && errors.MALSESSIONID}
                        />
                    </Grid>
                </Grid>
                <div className="button-group">
                    <CyberButton classNames="btn-contained-primary" type="submit">
                        <FontAwesomeIcon icon={faCheck}/>
                    </CyberButton>
                </div>
            </Form>
        )}
    </Formik>
}


export function ScrapeEpisodeHistoryFromMalDialog(props) {
    const {open, onClose} = props;

    return (
        <Dialog onClose={onClose} aria-labelledby="scrape-episode-history-from-mal-title" open={open}>
            <div id="scrape-episode-history-from-mal">
                <DialogTitle id="scrape-episode-history-from-mal-title">MAL Episode History scraper</DialogTitle>
                <ScrapeEpisodeHistoryFromMalForm closeForm={onClose}/>
            </div>
        </Dialog>
    );
}


/**
 * Button for importing xml file from MAL and updating the user's list
 * @returns {JSX.Element}
 */
export function ImportAnimeListFromMal(props) {
    const {} = props;
    const uploading = store.getState()[store_reducers.users][store_reducer_users.uploading_file];

    return <div>
        <input
            accept="text/xml"
            style={{display: 'none'}}
            id="import-mal-anime-list-xml"
            multiple={false}
            type="file"
            onChange={(e) => {
                store.dispatch(importMalXmlForAnimeList(e.target.files[0]))
            }}
        />
        {uploading ? <CircularProgress/> : <label htmlFor="import-mal-anime-list-xml" className="upload-img-btn">
            <Button variant="outlined" color="primary" component="span" onMouseEnter={playHoverSound}
                    onClick={playClickSound}>
                <FontAwesomeIcon icon={faFileUpload}/>
            </Button>
        </label>}
    </div>
}


/**
 * Button for importing xml file from MAL and updating the user's list
 * @returns {JSX.Element}
 */
export function ImportMangaListFromMal(props) {
    const {} = props;
    const uploading = store.getState()[store_reducers.users][store_reducer_users.uploading_file];

    return <div>
        <input
            accept="text/xml"
            style={{display: 'none'}}
            id="import-mal-manga-list-xml"
            multiple={false}
            type="file"
            onChange={(e) => {
                store.dispatch(importMalXmlForMangaList(e.target.files[0]))
            }}
        />
        {uploading ? <CircularProgress/> : <label htmlFor="import-mal-manga-list-xml" className="upload-img-btn">
            <Button variant="outlined" color="primary" component="span" onMouseEnter={playHoverSound}
                    onClick={playClickSound}>
                <FontAwesomeIcon icon={faFileUpload}/>
            </Button>
        </label>}
    </div>
}


/**
 * Button for instant dropping an anime
 * @param props.anime_id {Number} ID of the anime to be dropped
 * @param props.classNames {String} extra classname to pass to the btn
 * @param props.variant {String} variant of the MaterialUi btn
 * @returns {JSX.Element}
 */
export function AddAnimeEntryInstantDropButton(props) {
    const {anime_id, classNames, variant} = props;
    return <CyberButton variant={variant ? variant : 'outlined'} classNames={classNames} color="primary" title={'Instant Drop'}
                        onClick={() => {
        store.dispatch(addAnimeEntryInstantDrop(anime_id))
    }}>
        <FontAwesomeIcon icon={faLevelDownAlt}/>
    </CyberButton>
}


/**
 * Button for instant dropping a manga
 * @param props.manga_id {Number} ID of the manga to be dropped
 * @param props.classNames {String} extra classname to pass to the btn
 * @param props.variant {String} variant of the MaterialUi btn
 * @returns {JSX.Element}
 */
export function AddMangaEntryInstantDropButton(props) {
    const {manga_id, classNames, variant} = props;
    return <CyberButton variant={variant ? variant : 'outlined'} classNames={classNames} color="primary" title={'Instant Drop'} onClick={() => {
        store.dispatch(addMangaEntryInstantDrop(manga_id))
    }}>
        <FontAwesomeIcon icon={faLevelDownAlt}/>
    </CyberButton>
}
