import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME} from "../../../globals";
import Display from "../../display/Display";
import {Anime, AnimeOnServer} from "../../../models";
import moment from "moment";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    faArrowDown,
    faArrowUp,
    faBan,
    faClock,
    faDownload,
    faHdd,
    faPause, faPuzzlePiece,
    faSpinner, faUpload, faUsers
} from "@fortawesome/free-solid-svg-icons";
import LinearProgress from "@material-ui/core/LinearProgress";
import {getBytesToSize, getProgress, getSpeed, TorrentProgressInfo} from "./snippets";
import PropTypes from "prop-types";
import {getTorrentProgress} from "../../../actions/requiem";
import {sleep} from "../../functions";
import store from "../../../store";


class DisplayTorrentProgress extends Component {
    static propTypes = {
        /** ID of the title to get torrent progress for */
        id: PropTypes.string.isRequired,
    };

    constructor() {
        super(...arguments);
        this.state = {
            torrent_progress_reading: false,
            update_interval: null,
        };
    }

    componentDidMount() {
        this.props.getTorrentProgress(this.props.id);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {id, getTorrentProgress} = this.props;
        setTimeout(() => { getTorrentProgress(id) }, 1000);
    }

    render() {
        const torrent_filters = {
            'name': false, 'size': true, 'dl_speed': true, 'up_speed': true, 'remaining': true,
            'seeds': true, 'leechs': true, 'eta': true
        };

        return this.props.torrent_progress && this.props.torrent_progress.length > 0 ?
            this.props.torrent_progress.map(torrent => (
                <TorrentProgressInfo torrent={torrent} filters={torrent_filters}/>
            )) : null
    }
}

const mapStateToProps = state => ({
    torrent_progress: state.requiem.torrent_progress
});

export default connect(mapStateToProps, {getTorrentProgress})(DisplayTorrentProgress)
