import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';

import {CHARACTERS_TAB_NAME, INDUSTRY_TAB_NAME} from "../../../globals";
import {getCharactersDataForHomePage} from "../../../actions/characters";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";
import TopCharacters from "./TopCharacters";

import '../../../assets/css/characters.css'
import {getIndustryDataForHomePage} from "../../../actions/industry";


class CharactersHome extends Component {
    componentDidMount() {
        document.title = INDUSTRY_TAB_NAME;
        window.scrollTo(0, 0);
        this.props.getIndustryDataForHomePage();
    }

    render() {
        return (
            <div id="Characters-Home" className="title-home-page">
                <CyberNavbar/>
                <Grid container>
                    <TopCharacters/>
                    <Grid xs={12}>
                        {/*<AnimeStatistics/>*/}
                    </Grid>
                </Grid>
                <CyberFooter/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, {getIndustryDataForHomePage})(CharactersHome)
