import React, {Component} from 'react'
import Grid from "@material-ui/core/Grid";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import {connect} from "react-redux";
import {Profile as ProfileModel, User} from "../../../../models";
import {PreLoadImage} from "../../../layout/common/common";
import UploadProfileImageButton from "../../forms/UploadProfileImageButton";
import Switch from "@material-ui/core/Switch";
import {EditBioForm} from "../../forms/shortForms";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";


class UserSettings extends Component {
    constructor() {
        super(...arguments);
        this.editBio = this.editBio.bind(this);
        this.finishEditingBio = this.finishEditingBio.bind(this);
        this.state = {
            edit_bio: false
        };
    }

    editBio() {
        this.setState({edit_bio: true})
    }

    finishEditingBio() {
        this.setState({edit_bio: false})
    }

    render() {
        return (
            <Grid container>
                <Grid item xs={12}>
                    <h1>User Settings</h1>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={11}>
                    <h2>Personal settings</h2>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={5}>
                    <TableContainer>
                        <Table aria-label="simple table" className="box-light">
                            <TableBody>
                                <TableRow>
                                    <TableCell>
                                        Avatar
                                    </TableCell>
                                    <TableCell align="center">
                                        {!this.props.uploading_file ? <PreLoadImage src={this.props.user[User.profile][ProfileModel.profile_image]}
                                                      alt={'Avatar'} className="settings-avatar"/> : null}
                                        <UploadProfileImageButton/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Title
                                    </TableCell>
                                    <TableCell align="center">
                                        {this.props.user[User.profile][ProfileModel.title]}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Username
                                    </TableCell>
                                    <TableCell align="center">
                                        {this.props.user[User.username]}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Email
                                    </TableCell>
                                    <TableCell align="center">
                                        {this.props.user[User.email]}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Bio
                                    </TableCell>
                                    <TableCell align="left">
                                        <ClickAwayListener onClickAway={this.finishEditingBio}>
                                            <div>
                                                {!this.state.edit_bio ? <div onClick={this.editBio}>
                                                    {this.props.user[User.profile][ProfileModel.bio] ? this.props.user[User.profile][ProfileModel.bio] : 'No bio'}
                                                </div> : null}
                                                {this.state.edit_bio ? (
                                                    <EditBioForm bio={this.props.user[User.profile][ProfileModel.bio]}
                                                                 closeBio={this.finishEditingBio}/>
                                                ) : null}
                                            </div>
                                        </ClickAwayListener>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Show adult content
                                    </TableCell>
                                    <TableCell align="center">
                                        <Switch
                                            checked={true}
                                            color="primary"
                                            name="checkedB"
                                            inputProps={{'aria-label': 'primary checkbox'}}
                                        />
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
                <Grid item xs={6}/>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    cookies: state.cookies.sound_effects,
    uploading_file: state.users.uploading_file,
});

export default connect(mapStateToProps, {})(UserSettings)
