function RecentlyAdded(props) {
    const {manga_data, manga_loading, manga_data_loaded, user_manga_entry_statuses} = props;
    return <Grid xs={12} className="horizontal-titles">
        {/*<div className="horizontal-titles-header">*/}
        {/*    <h3>Recently Added Manga</h3>*/}
        {/*</div>*/}
        <div className="horizontal-titles-container horizontal-scroll">
            {manga_data_loaded && manga_data && manga_data[MangaData.recently_added_manga] ?
                <div className="horizontal-titles-wrapper">
                    {console.log('renders tab rendering: ', manga_data[MangaData.recently_added_manga])}

                    {manga_data[MangaData.recently_added_manga].map(manga => (
                        <TitleCard
                            containerClassNames="horizontal-titles-card-container"
                            branch={BRANCH_MANGA}
                            title={manga}
                            user_status={
                                !isEmpty(user_manga_entry_statuses) ?
                                    user_manga_entry_statuses[manga[Manga.id].toString()] : null}
                            skip_img_preload={true}
                        />
                    ))}
                </div> : manga_loading ? <Loading text={'Season Loading'} block={true}/> : null}
        </div>
    </Grid>;
}

