function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Tabs for the visited user
 * @param props.visited_user {Object} Visited user object
 * @returns {JSX.Element}
 * @constructor
 */
export function TopMangaTabs(props) {
    const {manga_data, manga_loading, manga_data_loaded, user_manga_entry_statuses} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root + ' tabs-root top-titles-tabs'}>
            <AppBar position="static" className="app-bar">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="default"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    <Tab label={<p>Recently Added</p>} {...a11yProps(0)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    {/*<Tab label={<p>Recently Downloaded</p>} {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>*/}
                    <Tab label={<p>Top Upcoming</p>} {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top Score</p>} {...a11yProps(2)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Most popular</p>} {...a11yProps(3)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top 2010s</p>} {...a11yProps(4)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top 2000s</p>} {...a11yProps(5)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top 90s</p>} {...a11yProps(6)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top 80s</p>} {...a11yProps(7)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label={<p>Top 70s</p>} {...a11yProps(8)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel className="top-titles-tab" value={value} index={0} dir={theme.direction}>
                    <RecentlyAdded
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                {/*<TabPanel className="top-titles-tab" value={value} index={1} dir={theme.direction}>*/}
                {/*    <RecentlyDownloaded*/}
                {/*        manga_data={manga_data}*/}
                {/*        manga_loading={manga_loading}*/}
                {/*        manga_data_loaded={manga_data_loaded}*/}
                {/*        user_manga_entry_statuses={user_manga_entry_statuses}*/}
                {/*    />*/}
                {/*</TabPanel>*/}
                <TabPanel className="top-titles-tab" value={value} index={1} dir={theme.direction}>
                    <TopUpcoming
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={2} dir={theme.direction}>
                    <TopScoredMal
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={3} dir={theme.direction}>
                    <MostPopularMal
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={4} dir={theme.direction}>
                    <Top2010s
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={5} dir={theme.direction}>
                    <Top2000s
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={6} dir={theme.direction}>
                    <Top90s
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={7} dir={theme.direction}>
                    <Top80s
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
                <TabPanel className="top-titles-tab" value={value} index={8} dir={theme.direction}>
                    <Top70s
                        manga_data={manga_data}
                        manga_loading={manga_loading}
                        manga_data_loaded={manga_data_loaded}
                        user_manga_entry_statuses={user_manga_entry_statuses}
                    />
                </TabPanel>
            </SwipeableViews>
        </div>
    );
}
