const new_tags = {
    'br': br,
    'p': paragraph,
    'img': img,
    'animestats': AnimeStats,
    'mangastats': MangaStats,
    'favoriteanime': FavoriteAnime,
    'favoritemanga': FavoriteManga,
    'favoritecharacters': FavoriteCharacters,
    'favoritepeople': FavoritePeople,
};

XBBCODE.addTags(new_tags);


export function cyberBBCodeParser(bbcode_string) {
    try {
        let result = XBBCODE.process({
            text: bbcode_string,
            removeMisalignedTags: false,
            addInLineBreaks: true
        });

        console.log('resul1t', result);
        return result.html;
    } catch (e) {
        return null;
    }
}
