import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom';

import Chip from '@material-ui/core/Chip';

import {playClickSound, playHoverSound} from "../sounds";
import {formatTitle, UserCardButtons} from "./functions";
import {getTitleCoverImage} from "../../functions";


class TitleCard extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
        /** Title Object (Anime, Manga, etc.) */
        title: PropTypes.object.isRequired,
        /** Index number for the card */
        index: PropTypes.number,
        /** Status of the user entry for this title (Watching, Reading, Completed, etc.) */
        user_status: PropTypes.string,
        /** If the card should be in a small format (No chipsets, only title) */
        mini: PropTypes.bool,
        /** If the user edit/add buttons for the title should appear */
        user_buttons: PropTypes.bool,
        /** Custom classname for the title card container */
        containerClassNames: PropTypes.string,
        /** Custom classname for the title card */
        classNames: PropTypes.string,
        /** If true it the card will not use state to check if the cover image is
         * loaded, it will directly add the img background to the card */
        skip_img_preload: PropTypes.bool,
    };

    constructor() {
        super(...arguments);
        this.showUserButtons = this.showUserButtons.bind(this);
        this.hideUserButtons = this.hideUserButtons.bind(this);
        this.cssHideUserButtons = this.cssHideUserButtons.bind(this);
        this.state = {
            img_loaded: false,
            show_user_buttons: false,
            css_hide_buttons: false,
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log(prevState.css_hide_buttons);
        if (prevState.css_hide_buttons)
            this.setState({css_hide_buttons: false, show_user_buttons: false})
    }

    showUserButtons() {
        this.setState({show_user_buttons: true, css_hide_buttons: false});
    }

    hideUserButtons() {
        this.setState({show_user_buttons: false});
    }

    cssHideUserButtons() {
        this.setState({css_hide_buttons: true});
    }

    render() {
        const card_title = formatTitle(this.props.branch, this.props.title, this.props.user_status);
        let img_url = getTitleCoverImage(card_title.cdn_cover_images, card_title.cover_images);
        let class_names = this.props.mini ? 'title-card-mini' : '';
        if (this.props.classNames)
            class_names = this.props.classNames;

        let container_class_names = '';
        if (this.props.containerClassNames)
            container_class_names = this.props.containerClassNames;

        let backgroundImage = this.state.img_loaded || this.props.skip_img_preload ? `url("${img_url}")` : 'none';

        return (
            <div className={`title-card-container ${container_class_names}`}
                 onMouseOver={this.showUserButtons}
                 onMouseEnter={this.showUserButtons}
                 onMouseLeave={this.hideUserButtons}
            >
                {this.props.user_buttons && this.state.show_user_buttons ?
                    <div className={this.state.css_hide_buttons ? 'user-buttons user-buttons-hidden' : 'user-buttons'}>
                        <UserCardButtons
                            branch={this.props.branch}
                            id={card_title.id}
                            title={this.props.title}
                            user_status={this.props.user_status}
                            hideUserButtons={this.cssHideUserButtons}
                        />
                    </div> : null}
                <Link to={card_title.link}
                      className={this.state.img_loaded ? `title-card fade-in ${class_names}` : `title-card ${class_names}`}
                      style={{backgroundImage: backgroundImage}}
                      onMouseEnter={playHoverSound} onClick={playClickSound}
                >
                    <img src={img_url} className="lazy-loaded-img" alt="loader-img" loading="lazy"
                         onLoad={() => {
                             this.setState({img_loaded: true})
                         }}/>
                    {!this.props.mini ? <div className="chip-array">
                        {this.props.index ? <Chip variant="default" size="small" label={this.props.index}/> : null}
                        {card_title.chipset_header ? card_title.chipset_header.map(chip => (
                            chip ? <Chip variant="default" size="small" className={`chip-${chip['color']}`}
                                         label={chip['label']}/> : null
                        )) : null}
                    </div> : null}
                    {!this.props.mini ? <h3>{card_title.title}</h3> : <h4>{card_title.title}</h4>}
                    {!this.props.mini ? <div className="title-card-body">
                        <div className="chip-array">
                            {card_title.chipset_one.map(chip => (
                                chip ? <Chip variant="default" size="small" label={chip}/> : null
                            ))}
                        </div>
                        <div className="chip-array">
                            {card_title.chipset_two.map(chip => (
                                chip ? <Chip variant="default" size="small" label={chip}/> : null
                            ))}
                        </div>
                        <div className="chip-array">
                            {card_title.chipset_three.map(chip => (
                                chip ? <Chip variant="default" size="small" label={chip}/> : null
                            ))}
                        </div>
                        <div className="chip-array">
                            {card_title.chipset_four.map(chip => (
                                chip ? <Chip variant="default" size="small" label={chip}/> : null
                            ))}
                        </div>
                    </div> : null}
                </Link>
            </div>
        )
    }
}

export default TitleCard
