import React, {Component} from 'react'
import {connect} from 'react-redux'

import Chart from 'chart.js';
import {Line} from 'react-chartjs-2';

import {User, UserList} from "../../../../models";
import {CyberLink} from "../../../layout/common/common";
import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faStar} from "@fortawesome/free-solid-svg-icons";
import CountUp from "react-countup";
import {BRANCH_ANIME} from "../../../../globals";
import Moment from "react-moment";
import {Button} from "@material-ui/core";
import {USER_ANIME_HISTORY} from "../../../../urls";
import {FavoriteTitles} from "../../statistics/favorites_snippets";
import {AnimeSimpleChartStats} from "../../statistics/statistics_snippets";


function getMinsPerDay(date, total_mins) {
    const date1 = new Date(date);
    const date2 = new Date();
    const time_diff = Math.abs(date2 - date1);
    const total_days = Math.ceil(time_diff / (1000 * 60 * 60 * 24));
    return Math.round(total_mins / total_days)
}


class AnimeTab extends Component {
    static propTypes = {};

    constructor() {
        super(...arguments);
        this.state = {show: true};
    }

    render() {
        Chart.defaults.global.legend.display = false;

        let raw = [
            {label: 'Watching', stat: this.props.user_list[UserList.anime_watching], color: '', hoverColor: ''},
            {label: 'Completed', stat: this.props.user_list[UserList.anime_completed], color: '', hoverColor: ''},
            {label: 'On Hold', stat: this.props.user_list[UserList.anime_on_hold], color: '', hoverColor: ''},
            {label: 'Dropped', stat: this.props.user_list[UserList.anime_dropped], color: '', hoverColor: ''},
            {label: 'Planned', stat: this.props.user_list[UserList.anime_plan_to_watch], color: '', hoverColor: ''},
        ];

        let stat_data = [];
        let labels = [];
        for (let x = 0; x < raw.length; x++) {
            stat_data.push(raw[x].stat);
            labels.push(raw[x].label);
        }

        let data = {
            labels: labels,
            legend: {
                display: false,
            },
            datasets: [{
                data: stat_data,
                backgroundColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.4)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.4)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.5)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.2)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.4)',
                ],
                hoverBackgroundColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.8)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.8)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.8)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.8)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.8)',
                ],
                borderColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.3)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.3)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.3)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.1)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.3)',
                ],
                hoverBorderColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 1)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 1)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 1)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 1)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 1)',
                ],
                // borderColor: 'rgba(255, 255, 255, 0.9)',
                // hoverBorderColor: '#156bb7',
                borderWidth: 0
            }]
        };


        const line_data = {
            labels: ['1', '2', '3', '4', '5', '6', '7'],
            datasets: [
                {
                    label: 'Episodes',
                    data: [12, 17, 9, 6, 8, 13, 14],
                    fill: false,
                    backgroundColor: 'rgb(255, 99, 132)',
                    borderColor: 'rgba(255, 99, 132, 0.2)',
                },
            ],
        };

        const options = {
            scales: {
                yAxes: [
                    {
                        ticks: {
                            beginAtZero: true,
                        },
                    },
                ],
            },
        };

        return (
            <Grid className="User-List-Stats-Container" container>
                <Grid item xs={5}>
                    <AnimeSimpleChartStats/>
                </Grid>

                <Grid item xs={4} className="pie-container">
                    <h2>Recent</h2>
                    <div className="">
                        <Line data={line_data} height={220} options={options}/>
                    </div>
                </Grid>
                <Grid item xs={3} className="recent-container">
                    <Grid container
                          direction="column"
                          justify="center"
                          alignItems="flex-start"
                          className="chip-container"
                    >
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span>
                                    <FontAwesomeIcon className="list-icon" icon={faStar}/>
                                    <span>{this.props.user_list[UserList.anime_mean_score].toFixed(2)}</span>
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span>
                                    <CountUp end={getMinsPerDay(
                                        this.props.user_list[UserList.anime_started_watching_on],
                                        this.props.user_list[UserList.anime_mins_spent_watching])}/>minutes per day
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span>
                                    <CountUp end={this.props.user_list[UserList.anime_mins_spent_watching]}/>
                                    total minutes
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span><CountUp
                                    end={this.props.user_list[UserList.anime_episodes_watched]}/>
                                episodes
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<>
                                    <span>
                                        Started Watching on <span><Moment format={'Do MMM YYYY'}
                                                                          date={this.props.user_list[UserList.anime_started_watching_on]}/></span>
                                    </span>
                                </>}
                            />
                        </div>
                        <div className="stat-chip">
                            <CyberLink to={USER_ANIME_HISTORY(this.props.visited_user[User.id])}>
                                <Button variant="outlined" className="btn-info">History</Button>
                            </CyberLink>
                        </div>
                    </Grid>
                </Grid>
                <Grid item xs={12} className="favorites-container">
                    <h2>Favorites</h2>
                    <FavoriteTitles branch={BRANCH_ANIME}/>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    visited_user: state.auth.visited_user,
    user_list: state.users.user_list,
});

export default connect(mapStateToProps, {})(AnimeTab)
