import * as Yup from 'yup';


export const LoginSchema = Yup.object().shape({
    username: Yup.string()
        .min(4, 'Too Short!')
        .max(12, 'Too Long!')
        .required('Required'),
    password: Yup.string()
        .min(4, 'Too Short!')
        .max(128, 'Too Long!')
        .required('Required'),
});

export const RegisterSchema = Yup.object().shape({
    username: Yup.string()
        .min(4, 'Too Short!')
        .max(12, 'Too Long!')
        .required('Required!'),
    email: Yup.string()
        .email('Invalid email')
        .required('Required!'),
    password: Yup.string()
        .min(6, 'Too Short!')
        .max(128, 'Too Long!')
        .required('Required!'),
    password2: Yup.string()
        .min(6, 'Too Short!')
        .max(128, 'Too Long!')
        .required('Required!')
        .test('passwords-match', 'Passwords must match!', function (value) {
            return this.parent.password === value;
        }),
});
