import {
    GET_INDUSTRY_DATA, INDUSTRY_DATA_LOADING
} from "../actions/types";

const initialState = {
    industry_data: {},
    industry_data_loading: {},
    industry_data_statistics: {},
    industry_data_loaded: false,
};


export default function (state = initialState, action) {
    switch (action.type) {
        case INDUSTRY_DATA_LOADING:
            return {
                ...state,
                industry_data_loading: true,
                industry_data_loaded: false,
            };
        case GET_INDUSTRY_DATA:
            return {
                ...state,
                industry_data_loading: false,
                industry_data: action.payload,
                industry_data_loaded: true,
            };
        default:
            return state
    }
}
