import React from "react";

import 'date-fns';
import {KeyboardDateTimePicker, MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {Form, Formik} from "formik";
import Grid from "@material-ui/core/Grid";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import {faCheck, faTrash} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import DialogActions from "@material-ui/core/DialogActions";

import {BRANCH_ANIME, BRANCH_MANGA, USER_MANGA_STATUSES} from "../../../../globals";
import {isEmpty} from "../../../functions";
import {UserMangaEntry} from "../../../../models";
import {CyberButton} from "../../../layout/common/common";
import store from "../../../../store";
import {
    addMangaEntry,
    deleteMangaEntry,
    updateMangaEntry
} from "../../../../actions/users";
import {AddRemoveFromFavorites} from "../shortForms";


const FIELD_NAMES = {
    read_volumes: UserMangaEntry.read_volumes,
    read_chapters: UserMangaEntry.read_chapters,
    start_date: UserMangaEntry.start_date,
    finish_date: UserMangaEntry.finish_date,
    score: UserMangaEntry.score,
    status: UserMangaEntry.status,
    times_read: UserMangaEntry.times_read,
    comments: UserMangaEntry.comments,
    tags: UserMangaEntry.tags,
    priority: UserMangaEntry.priority,
    rereading: UserMangaEntry.rereading,
    rereading_volume: UserMangaEntry.rereading_volume,
    rereading_chapter: UserMangaEntry.rereading_chapter,
};


/**
 *
 * @param manga_id
 * @param user_manga_entry
 * @param edit
 * @returns {{read_chapters: number, comments: string, priority: string, rereading: boolean, tags: string, score: number, times_read: number, finish_date: string, rereading_chapter: number, read_volumes: number, manga: *, rereading_volume: number, errors: boolean, start_date: string, status: string}}
 */
export function getUserMangaEntryFormInitialValues(manga_id, user_manga_entry, edit = false) {
    let initial_values = {
        manga: manga_id,
        read_volumes: 0,
        read_chapters: 0,
        start_date: '',
        finish_date: '',
        score: 0,
        status: USER_MANGA_STATUSES.plan_to_read,
        times_read: 0,
        comments: '',
        tags: '',
        priority: '0',
        rereading: false,
        rereading_volume: 0,
        rereading_chapter: 0,
        errors: false
    };

    if (edit && !isEmpty(user_manga_entry))
        initial_values = {
            manga: manga_id,
            read_volumes: user_manga_entry[UserMangaEntry.read_volumes],
            read_chapters: user_manga_entry[UserMangaEntry.read_chapters],
            start_date: user_manga_entry[UserMangaEntry.start_date],
            finish_date: user_manga_entry[UserMangaEntry.finish_date],
            score: user_manga_entry[UserMangaEntry.score],
            status: user_manga_entry[UserMangaEntry.status],
            times_read: user_manga_entry[UserMangaEntry.times_read],
            comments: user_manga_entry[UserMangaEntry.comments],
            tags: user_manga_entry[UserMangaEntry.tags],
            priority: user_manga_entry[UserMangaEntry.priority],
            rereading: user_manga_entry[UserMangaEntry.rereading],
            rereading_volume: user_manga_entry[UserMangaEntry.rereading_volume],
            rereading_chapter: user_manga_entry[UserMangaEntry.rereading_chapter],
        };

    return initial_values
}


function deleteEntry(id, list) {
    if (window.confirm('Are you sure? This will also delete any chapter history of this manga.'))
        store.dispatch(deleteMangaEntry(id, list));
}


/**
 *
 * @param {Object} props.initial_values
 * @param {boolean} props.edit
 * @param {int} props.id
 * @param {Object} props.list
 * @param {function} props.handleClose
 * @returns {JSX.Element}
 * @constructor
 */
export function UserMangaEntryForm(props) {
    const {initial_values, edit, id, list, handleClose} = props;

    return <MuiPickersUtilsProvider utils={DateFnsUtils}><Formik
        initialValues={initial_values}
        onSubmit={(values, {setSubmitting}) => {
            if (values.start_date === '') values.start_date = null;
            if (values.finish_date === '') values.finish_date = null;
            values.score = parseInt(values.score);

            if (edit) store.dispatch(updateMangaEntry(id, values, list));
            else store.dispatch(addMangaEntry(values));

            setSubmitting(false);
        }}>
        {({
              isSubmitting, values, handleChange,
              handleBlur, setValues
          }) => (
            <Form id="User-Title-Entry-Form">
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-status">Status</InputLabel>
                        <Select
                            labelId="user-status"
                            className="form-control"
                            label="Status"
                            value={values[FIELD_NAMES.status]}
                            name={FIELD_NAMES.status}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="Reading">Reading</MenuItem>
                            <MenuItem value="Completed">Completed</MenuItem>
                            <MenuItem value="On Hold">On Hold</MenuItem>
                            <MenuItem value="Dropped">Dropped</MenuItem>
                            <MenuItem value="Plan to Read">Plan to Read</MenuItem>
                        </Select>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-value">Priority</InputLabel>
                        <Select
                            labelId="user-value"
                            className="form-control"
                            value={values[FIELD_NAMES.priority]}
                            name={FIELD_NAMES.priority}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="4">VERY HIGH</MenuItem>
                            <MenuItem value="3">HIGH</MenuItem>
                            <MenuItem value="2">MEDIUM</MenuItem>
                            <MenuItem value="1">LOW</MenuItem>
                            <MenuItem value="0">VERY LOW</MenuItem>
                        </Select>
                    </Grid>
                </Grid>
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Volume"
                            type="number"
                            name={FIELD_NAMES.read_volumes}
                            value={values[FIELD_NAMES.read_volumes]}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>

                    <Grid item xs={4} md={2}>
                        <div className="form-group text-right">
                            <Checkbox
                                value={values[FIELD_NAMES.rereading]}
                                inputProps={{'aria-label': 'primary checkbox'}}
                                name={FIELD_NAMES.rereading}
                                onChange={handleChange}
                                onBlur={handleBlur}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={8} md={4}>
                        <TextField
                            // className="form-control"
                            label="Reread Volume"
                            type="number"
                            name={FIELD_NAMES.rereading_volume}
                            value={values[FIELD_NAMES.rereading_volume]}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            disabled={!values[FIELD_NAMES.rereading]}/>
                    </Grid>
                </Grid>
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Chapter"
                            type="number"
                            name={FIELD_NAMES.read_chapters}
                            value={values[FIELD_NAMES.read_chapters]}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>

                    <Grid item xs={12} md={6}>
                        <TextField
                            // className="form-control"
                            label="Reread Chapter"
                            type="number"
                            name={FIELD_NAMES.rereading_chapter}
                            value={values[FIELD_NAMES.rereading_chapter]}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            disabled={!values[FIELD_NAMES.rereading]}/>
                    </Grid>
                </Grid>

                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <InputLabel id="user-score">Score</InputLabel>
                        <Select
                            labelId="user-score"
                            className="form-control"
                            value={values[FIELD_NAMES.score]}
                            name={FIELD_NAMES.score}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        >
                            <MenuItem value="0">0</MenuItem>
                            <MenuItem value="1">1</MenuItem>
                            <MenuItem value="2">2</MenuItem>
                            <MenuItem value="3">3</MenuItem>
                            <MenuItem value="4">4</MenuItem>
                            <MenuItem value="5">5</MenuItem>
                            <MenuItem value="6">6</MenuItem>
                            <MenuItem value="7">7</MenuItem>
                            <MenuItem value="8">8</MenuItem>
                            <MenuItem value="9">9</MenuItem>
                            <MenuItem value="10">10</MenuItem>
                        </Select>
                    </Grid>

                    <Grid item xs={12} md={6}>
                        <KeyboardDateTimePicker
                            className="form-control datetime-control"
                            variant="inline"
                            format="do MMM yyyy HH:mm"
                            margin="normal"
                            id="date-picker-inline"
                            label="Start date"
                            value={values[FIELD_NAMES.start_date]}
                            onChange={(date) => {
                                values[FIELD_NAMES.start_date] = date;
                                setValues(values);
                            }}
                            KeyboardButtonProps={{'aria-label': 'change date'}}
                        />
                    </Grid>
                </Grid>

                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Times watched"
                            type="number"
                            name={FIELD_NAMES.times_read}
                            value={values[FIELD_NAMES.times_read]}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>

                    <Grid item xs={12} md={6}>
                        <KeyboardDateTimePicker
                            className="form-control datetime-control"
                            variant="inline"
                            format="do MMM yyyy HH:mm"
                            margin="normal"
                            id="date-picker-inline"
                            label="Finish date"
                            value={values[FIELD_NAMES.finish_date]}
                            onChange={(date) => {
                                values[FIELD_NAMES.finish_date] = date;
                                setValues(values);
                            }}
                            KeyboardButtonProps={{'aria-label': 'change date'}}
                        />
                    </Grid>
                </Grid>
                <Grid container className="form-group">
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Comments"
                            multiline
                            value={values[FIELD_NAMES.comments]}
                            name={FIELD_NAMES.comments}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <TextField
                            className="form-control"
                            label="Tags"
                            multiline
                            value={values[FIELD_NAMES.tags]}
                            name={FIELD_NAMES.tags}
                            onChange={handleChange}
                            onBlur={handleBlur}/>
                    </Grid>
                </Grid>

                <DialogActions>
                    <AddRemoveFromFavorites branch={BRANCH_MANGA} id={parseInt(id)}/>
                    <CyberButton classNames="btn-success" variant="outlined" type="submit" onClick={handleClose}
                                 disabled={isSubmitting}><FontAwesomeIcon icon={faCheck}/></CyberButton>
                    {edit ? <CyberButton classNames="btn-danger" variant="outlined" onClick={(e) => {
                        deleteEntry(id, list);
                        handleClose()
                    }}><FontAwesomeIcon icon={faTrash}/></CyberButton> : null}
                </DialogActions>
            </Form>
        )}
    </Formik></MuiPickersUtilsProvider>
}
