import React, {Component} from 'react'
import {connect} from 'react-redux'

import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import Grow from "@material-ui/core/Grow";

import {Anime, Manga, VALID_ANIME_RELATION_NAMES, VALID_ANIME_RELATIONS} from "../../../../models";
import {getRelationsForAnime} from "../../../../actions/anime";
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../../globals";
import {isEmpty} from "../../../functions";
import TitleCard from "../../../layout/cards/TitleCard";

import '../../../../assets/css/anime.css'


class RelationsTab extends Component {

    constructor() {
        super(...arguments);
        this.state = {};
    }

    componentDidMount() {
        if (isEmpty(this.props.anime_relations))
            this.props.getRelationsForAnime(this.props.anime[Anime.id])
    }

    render() {
        return (
            <div className="relations-tab">
                {!isEmpty(this.props.anime_relations) ? <>
                    {this.props.anime_relations['anime_relation_adaptation'].length > 0 ?
                        <div>
                            <h2 className="relation-name">Adaptation</h2>
                            <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                                {this.props.anime_relations['anime_relation_adaptation'].map((manga, x) => (
                                    <Grow key={x} in {...({timeout: 200 + (x * 50)})}>
                                        <GridListTile cols={1}>
                                            <TitleCard
                                                branch={BRANCH_MANGA}
                                                title={manga}
                                                user_status={!isEmpty(this.props.user_manga_entry_statuses) ?
                                                        this.props.user_manga_entry_statuses[`${manga[Manga.id]}`] : null}
                                            />
                                        </GridListTile>
                                    </Grow>
                                ))}
                            </GridList>
                        </div> : null}

                    {VALID_ANIME_RELATIONS.map((relation_name, i) => (
                        relation_name !== 'anime_relation_adaptation' ?
                            <div key={i}>
                                {this.props.anime_relations[relation_name].length > 0 ?
                                    <h2 className="relation-name">{VALID_ANIME_RELATION_NAMES[relation_name]}</h2> : null}

                                <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                                    {this.props.anime_relations[relation_name].map((anime, y) => (
                                        <Grow key={y} in {...({timeout: 200 + (y * 50)})}>
                                            <GridListTile cols={1}>
                                                <TitleCard
                                                    branch={BRANCH_ANIME}
                                                    title={anime}
                                                    user_status={!isEmpty(this.props.user_anime_entry_statuses) ?
                                                        this.props.user_anime_entry_statuses[`${anime[Anime.id]}`] : null}
                                                />
                                            </GridListTile>
                                        </Grow>
                                    ))}
                                </GridList>
                            </div> : null))}
                </> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
    anime_relations: state.anime.anime[Anime.anime_relations],
    user_anime_entry_statuses: state.users.user_anime_entry_statuses,
    user_manga_entry_statuses: state.users.user_manga_entry_statuses,
});

export default connect(mapStateToProps, {getRelationsForAnime})(RelationsTab)
