import Cookies from "js-cookie";
import {Cookie} from "../../models";


// noinspection JSClassNamingConvention
export class FILTER_ANIME_INITIAL_VALUES {
    VALUES = {
        search: '',
        downloaded: false,
        is_downloading: false,
        cannot_download: false,
        deleted_by_MAL: false,
        pending_by_MAL: false,
        anime_on_server__english_sub: false,
        anime_on_server__english_dub: false,
        anime_on_server__japanese_dub: false,
        anime_on_server__japanese_sub: false,
        episodes_range: [0, 53],
        length_range: [0, 61],
        genres: [],
        type: [],
        status: [],
        rating_short: [],
        video_quality: [],
        audio_channel: [],
        user_exclude: [],
        ordering: '-members',
    };
}


// noinspection JSClassNamingConvention
export class FILTER_MANGA_INITIAL_VALUES {
    VALUES = {
        search: '',
        downloaded: false,
        is_downloading: false,
        cannot_download: false,
        deleted_by_MAL: false,
        pending_by_MAL: false,
        volumes_range: [0, 53],
        chapters_range: [0, 53],
        genres: [],
        type: [],
        status: [],
        user_exclude: [],
        ordering: '-members',
    };
}


// noinspection JSClassNamingConvention
export class FILTER_CHARACTERS_INITIAL_VALUES {
    VALUES = {
        search: '',
        ordering: '-favorites',
    };
}


// noinspection JSClassNamingConvention
export class FILTER_PEOPLE_INITIAL_VALUES {
    VALUES = {
        search: '',
        ordering: '-favorites',
    };
}


// noinspection JSClassNamingConvention
export class FILTER_EROGE_INITIAL_VALUES {
    VALUES = {
        search: '',
        type: [],
        downloaded: '',
        platforms: [],
        language: [],
        censorship: [],
        tags: [],
        ordering: '-date_added',
    };
}


export function loadCookies() {
    let loaded_cookies = {
        anime_browse_filters: {},
        anime_season_filters: {},
        anime_list_filters: {},
        manga_browse_filters: {},
        manga_season_filters: {},
        manga_list_filters: {},
        characters_filters: {},
        people_filters: {},
        eroge_browse_filters: {},
    };

    if (Cookies.get(Cookie.show_filters) === undefined || Cookies.get(Cookie.show_filters) === 'undefined')
        Cookies.set(Cookie.show_filters, true);

    const new_anime_values = new FILTER_ANIME_INITIAL_VALUES();
    let anime_browse_filters = new_anime_values.VALUES;
    if (Cookies.get(Cookie.anime_browse_filters) === undefined || Cookies.get(Cookie.anime_browse_filters) === 'undefined')
        Cookies.set(Cookie.anime_browse_filters, new_anime_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            anime_browse_filters = JSON.parse(Cookies.get(Cookie.anime_browse_filters));
        } catch (e) {
        }
    }

    let anime_season_filters = new_anime_values.VALUES;
    if (Cookies.get(Cookie.anime_season_filters) === undefined || Cookies.get(Cookie.anime_season_filters) === 'undefined')
        Cookies.set(Cookie.anime_season_filters, new_anime_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            anime_season_filters = JSON.parse(Cookies.get(Cookie.anime_season_filters));
        } catch (e) {
        }
    }

    let anime_list_filters = new_anime_values.VALUES;
    if (Cookies.get(Cookie.anime_list_filters) === undefined || Cookies.get(Cookie.anime_list_filters) === 'undefined')
        Cookies.set(Cookie.anime_list_filters, new_anime_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            anime_list_filters = JSON.parse(Cookies.get(Cookie.anime_list_filters));
        } catch (e) {
        }
    }
    loaded_cookies.anime_browse_filters = anime_browse_filters;
    loaded_cookies.anime_season_filters = anime_season_filters;
    loaded_cookies.anime_list_filters = anime_list_filters;

    const new_manga_values = new FILTER_MANGA_INITIAL_VALUES();
    let manga_browse_filters = new_manga_values.VALUES;
    if (Cookies.get(Cookie.manga_browse_filters) === undefined || Cookies.get(Cookie.manga_browse_filters) === 'undefined')
        Cookies.set(Cookie.manga_browse_filters, new_manga_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            manga_browse_filters = JSON.parse(Cookies.get(Cookie.manga_browse_filters));
        } catch (e) {
        }
    }
    let manga_season_filters = new_manga_values.VALUES;
    if (Cookies.get(Cookie.manga_season_filters) === undefined || Cookies.get(Cookie.manga_season_filters) === 'undefined')
        Cookies.set(Cookie.manga_season_filters, new_manga_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            manga_season_filters = JSON.parse(Cookies.get(Cookie.manga_season_filters));
        } catch (e) {
        }
    }
    let manga_list_filters = new_manga_values.VALUES;
    if (Cookies.get(Cookie.manga_list_filters) === undefined || Cookies.get(Cookie.manga_list_filters) === 'undefined')
        Cookies.set(Cookie.manga_list_filters, new_manga_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            manga_list_filters = JSON.parse(Cookies.get(Cookie.manga_list_filters));
        } catch (e) {
        }
    }
    loaded_cookies.manga_browse_filters = manga_browse_filters;
    loaded_cookies.manga_season_filters = manga_season_filters;
    loaded_cookies.manga_list_filters = manga_list_filters;

    const new_characters_values = new FILTER_CHARACTERS_INITIAL_VALUES();
    let characters_filters = new_characters_values.VALUES;
    if (Cookies.get(Cookie.characters_filters) === undefined || Cookies.get(Cookie.characters_filters) === 'undefined')
        Cookies.set(Cookie.characters_filters, new_characters_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            characters_filters = JSON.parse(Cookies.get(Cookie.characters_filters));
        } catch (e) {
        }
    }
    loaded_cookies.characters_filters = characters_filters;

    const new_people_values = new FILTER_PEOPLE_INITIAL_VALUES();
    let people_filters = new_people_values.VALUES;
    if (Cookies.get(Cookie.people_filters) === undefined || Cookies.get(Cookie.people_filters) === 'undefined')
        Cookies.set(Cookie.people_filters, new_people_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            people_filters = JSON.parse(Cookies.get(Cookie.people_filters));
        } catch (e) {
        }
    }
    loaded_cookies.people_filters = people_filters;

    const new_eroge_values = new FILTER_EROGE_INITIAL_VALUES();
    let eroge_filters = new_eroge_values.VALUES;
    if (Cookies.get(Cookie.eroge_browse_filters) === undefined || Cookies.get(Cookie.eroge_browse_filters) === 'undefined')
        Cookies.set(Cookie.eroge_browse_filters, new_eroge_values.VALUES);
    else { // noinspection UnusedCatchParameterJS
        try {
            eroge_filters = JSON.parse(Cookies.get(Cookie.eroge_browse_filters));
        } catch (e) {
        }
    }
    loaded_cookies.eroge_browse_filters = eroge_filters;

    return loaded_cookies
}
