import {
    GET_REQUIEM,
    REQUIEM_LOADING,
    GET_REPORTS,
    REPORTS_LOADING,
    GET_NEWS,
    NEWS_LOADING,
    GET_TORRENT_PROGRESS,
    GET_ACTIVE_TORRENTS_PROGRESS,
    GET_STORAGE_INFO,
    GET_TARGETS,
    CLEAR_ANIME_TORRENT_PROGRESS
} from '../actions/types'


const initialState = {
    requiem_is_loading: false,
    requiem: {},

    targets: [],
    reports: {},
    news: {},
    storage: [],
    torrent_progress: [],
    active_torrents: [],
};

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_REQUIEM:
            return {
                ...state,
                requiem: action.payload,
                requiem_is_loading: false
            };
        case GET_REPORTS:
            return {
                ...state,
                reports: action.payload,
                reports_is_loading: false
            };
        case GET_TARGETS:
            return {
                ...state,
                targets: action.payload,
                requiem_is_loading: false
            };
        case GET_NEWS:
            return {
                ...state,
                news: action.payload,
                news_is_loading: false
            };
        case GET_STORAGE_INFO:
            return {
                ...state,
                storage: action.payload,
                requiem_is_loading: false
            };
        case GET_TORRENT_PROGRESS:
            return {
                ...state,
                torrent_progress: action.payload,
            };
        case GET_ACTIVE_TORRENTS_PROGRESS:
            return {
                ...state,
                active_torrents: action.payload,
            };
        case CLEAR_ANIME_TORRENT_PROGRESS:
            return {
                ...state,
                torrent_progress: [],
            };
        case REQUIEM_LOADING:
            return {
                ...state,
                requiem_is_loading: true
            };
        case REPORTS_LOADING:
            return {
                ...state,
                reports_is_loading: true
            };
        case NEWS_LOADING:
            return {
                ...state,
                news_is_loading: true
            };
        default:
            return state
    }
}
