import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';

import {NOT_FOUND_404_TAB_NAME} from "../../../globals";
import CyberNavbar from "../../layout/navbar/CyberNavbar";


class NotFound extends Component {
    componentDidMount() {
        document.title = NOT_FOUND_404_TAB_NAME;
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <>
                <CyberNavbar/>
                <div id="Not-Found">
                    <Grid container className="container">
                        <Grid xs={12}>
                            <h1>404</h1>
                            <p>Not found</p>
                        </Grid>
                    </Grid>
                </div>
            </>
        )
    }
}

const mapStateToProps = state => ({});

export default connect(mapStateToProps, {})(NotFound)
