import React, {Component} from 'react'
import {connect} from 'react-redux'

import reactPreset from "@bbob/preset-react";
import BBCode from "@bbob/react/lib/Component";

import {Person} from "../../../../models";
import {NONE} from "../../../display/Display";

import '../../../../assets/css/people.css'


class GeneralTab extends Component {
    render() {
        return (
            <div>
                <div className="general-description">
                    <h2>More Info</h2>
                    {this.props.person[Person.more_info] ? <BBCode plugins={[reactPreset()]}>
                        {this.props.person[Person.more_info]}
                    </BBCode> : NONE}
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    person: state.people.person,
});

export default connect(mapStateToProps, {})(GeneralTab)
