import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_MANGA} from "../../../globals";
import Display from "../../display/Display";
import {getLoggedInUserAnimeEntryStatuses, getLoggedInUserMangaEntryStatuses} from "../../../actions/users";


class MangaDisplay extends Component {
    componentDidMount() {
        this.props.getLoggedInUserAnimeEntryStatuses();
        this.props.getLoggedInUserMangaEntryStatuses();
    }

    render() {
        return <Display
            branch={BRANCH_MANGA}
            param_id={this.props.match.params.id}
            title_loading={this.props.manga_loading}
            title_loaded={this.props.manga_loaded}
            title={this.props.manga}
            user_title_status={this.props.user_manga_entry}
        />
    }
}

const mapStateToProps = state => ({
    manga: state.manga.manga,
    manga_loaded: state.manga.manga_loaded,
    manga_loading: state.manga.manga_loading,
    user_manga_entry: state.users.user_manga_entry,
});

export default connect(mapStateToProps, {
    getLoggedInUserAnimeEntryStatuses,
    getLoggedInUserMangaEntryStatuses
})(MangaDisplay)
