import Moment from "react-moment";
import React from "react";

import moment from 'moment'
import {CYBER_GENESIS_URL} from "../globals";
import {CDNCoverImages, CoverImages} from "../models";


/**
 * Check if an object is empty.
 * @param obj {Object} Object to check
 * @returns {boolean} True if the object is empty.
 */
let hasOwnProperty = Object.prototype.hasOwnProperty;
export function isEmpty(obj) {
    if (obj == null) return true;
    if (obj.length > 0) return false;
    if (obj.length === 0) return true;
    if (typeof obj !== "object") return true;
    for (let key in obj) {
        if (hasOwnProperty.call(obj, key)) return false;
    }
    return true;
}

 /**
  * Formats the date based on the known info about the date, this is used for titles from MAL
  * some titles only have the year of the date, so only the year will be displayed.
  * @param  {String} date          The actual date.
  * @param  {String} known_date    The known date.
  * @param  {Boolean} str          If the return should be string instead.
  * @return {JSX.Element || String}          Moment.js component or string.
  */
export function formatContentDateByKnown(date, known_date, str = false) {
    if (!known_date)
        return null;
    if (known_date.indexOf('day') >= 0 && known_date.indexOf('month') && known_date.indexOf('year')) {
        if (str)
            return moment(date).format('Do MMM YYYY');
        return <Moment format="Do MMM YYYY" date={date}/>;
    } else if (known_date.indexOf('month') >= 0 && known_date.indexOf('year')) {
        if (str)
            return moment(date).format('MMM YYYY');
        return <Moment format="MMM YYYY" date={date}/>
    }
    else if (known_date.indexOf('year')) {
        if (str)
            return moment(date).format('YYYY');
        return <Moment format="YYYY" date={date}/>
    }
    return null
}

/**
 * Get cols number for browse titles cards based on window width.
 * @returns {number}
 */
export function getBrowseGridListCols() {
    if (window.innerWidth >= 2800)
        return 9;
    if (window.innerWidth >= 2400)
        return 8;
    else if (window.innerWidth >= 1800)
        return 7;
    else if (window.innerWidth >= 1450)
        return 6;
    else if (window.innerWidth >= 1200)
        return 5;
    else if (window.innerWidth >= 980)
        return 4;
    else if (window.innerWidth >= 720)
        return 3;
    return 2
}

/**
 * Parse number of bytes to human readable format.
 * @param size {Number}
 * @returns {string}
 */
export function formatBytes(size) {
    const terabyte = 1099511627776;
    const gigabyte = 1073741824;
    const megabyte = 1048576;

    if (size >= 10995116277760)  // More than 10TB
        return `${(size / terabyte).toFixed(1)}TB`;
    else if (size >= 1099511627776)  // More than 1TB
        return `${(size / terabyte).toFixed(2)}TB`;
    else if (size >= 107374182400) // More than 100GB
        return `${Math.ceil(size / gigabyte)}GB`;
    else if (size >= 10737418240) // More than 10GB
        return `${(size / gigabyte).toFixed(1)}GB`;
    else if (size >= 1073741824) // More than 1GB
        return `${(size / gigabyte).toFixed(2)}GB`;
    else  // Less then 1GB
        return `${Math.ceil(size / megabyte)}MB`;
}


/**
 * Returns a label representation of the season number sent.
 * @param season {Number} Season number.
 * @returns {string} Season label.
 */
export function getSeasonLabelFromSeasonNumber(season) {
    switch (season) {
        case 1:
            return 'Spring';
        case 2:
            return 'Summer';
        case 3:
            return 'Fall';
        case 4:
            return 'Winter';
        default:
            return 'Spring';
    }
}


/**
 * The program sleeps for the X ms provided.
 * @param ms {Number} Milliseconds for the program to sleep
 */
export function sleep(ms) {
    let start = new Date().getTime();
    let end = start;
    while (end < start + ms) end = new Date().getTime();
}


/**
 * Converts a given date from CEST/UTC to local user time
 * @param date {Date} Date to convert
 * @returns {Date} returns a calculated Date in local user time
 */
export function convertUTCDateToLocalDate(date) {
    let newDate = new Date(date.getTime()+date.getTimezoneOffset()*60*1000);

    let offset = date.getTimezoneOffset() / 60;
    let hours = date.getHours();

    newDate.setHours(hours - offset);

    return newDate;
}


/**
 * Get a cover picture for a title, prioritise the CDN image, and prioritise them by size.
 * @param {array} cdn_cover_images
 * @param {array} cover_images
 * @param {boolean} thumbnail: If true, try to return the thumbnail image
 * @returns {string}
 */
export function getTitleCoverImage(cdn_cover_images=[], cover_images=[], thumbnail=false) {
    let img_url = `${CYBER_GENESIS_URL}/frontend/static/frontend/images/image-not-found.png`;
    const len_cdn_images = cdn_cover_images.length;
    const len_images = cover_images.length;

    if (len_cdn_images > 0) {
        if (len_cdn_images > 0) {
            let largest = 0;
            for (let x = 0; x < len_cdn_images; x ++) {
                if (cdn_cover_images[x][CDNCoverImages.size] > largest) {
                    largest = cdn_cover_images[x][CDNCoverImages.size];
                    if (thumbnail)
                        img_url = cdn_cover_images[x][CDNCoverImages.thumbnail_url];
                    else
                        img_url = cdn_cover_images[x][CDNCoverImages.image_url];
                }
            }
        }
    } else if (len_images > 0) {
        if (len_images > 0) {
            if (cover_images[len_images - 1][CoverImages.image][0] === '/')
                img_url = `${CYBER_GENESIS_URL}${cover_images[len_images - 1][CoverImages.image]}`;
            else
                img_url = cover_images[len_images - 1][CoverImages.image];
        }
    }

    return img_url
}

/**
 *
 * @param {Object} statuses
 * @param {Number} title_id
 */
export function isIdInStatuses(statuses, title_id) {
    return statuses.hasOwnProperty(title_id)
}


