const REFERENCE = moment();
const TODAY = REFERENCE.clone().startOf('day');
const YESTERDAY = REFERENCE.clone().subtract(1, 'days').startOf('day');
const A_WEEK_OLD = REFERENCE.clone().subtract(7, 'days').startOf('day');
const A_MONTH_OLD = REFERENCE.clone().subtract(30, 'days').startOf('day');
const SIX_MONTHS_OLD = REFERENCE.clone().subtract(180, 'days').startOf('day');
const A_YEAR_OLD = REFERENCE.clone().subtract(1, 'years').startOf('years');
const TWO_YEARS_OLD = REFERENCE.clone().subtract(2, 'years').startOf('years');

export function isTimeLessThanAnHourAgo(last_login) {
    const ONE_HOUR = 60 * 60 * 1000; /* ms */
    return ((new Date) - last_login) < ONE_HOUR;
}

export function isTimeLessThanSixHoursAgo(last_login) {
    const ONE_HOUR = 60 * 60 * 1000; /* ms */
    return ((new Date) - last_login) < ONE_HOUR * 6;
}

export function isToday(date) {
    const moment_date = moment(date);
    return moment_date.isSame(TODAY, 'd');
}
export function isYesterday(date) {
    const moment_date = moment(date);
    return moment_date.isSame(YESTERDAY, 'd');
}
export function isWithinAWeek(date) {
    const moment_date = moment(date);
    return moment_date.isAfter(A_WEEK_OLD);
}
export function isWithinAMonth(date) {
    const moment_date = moment(date);
    return moment_date.isAfter(A_MONTH_OLD);
}
export function isWithinSixMonths(date) {
    const moment_date = moment(date);
    return moment_date.isAfter(SIX_MONTHS_OLD);
}
export function isWithinAYear(date) {
    const moment_date = moment(date);
    return moment_date.isAfter(A_YEAR_OLD);
}
export function isMoreThanAYear(date) {
    const moment_date = moment(date);
    return moment_date.isAfter(TWO_YEARS_OLD);
}