import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_EROGE} from "../../../globals";
import Browse from "../../browse/Browse";
import {isEmpty} from "../../functions";


class ErogeBrowse extends Component {
    render() {
        return <Browse
            branch={BRANCH_EROGE}
            titles_loading={this.props.eroge_loading}
            filtered_titles_loaded={this.props.filtered_eroge_loaded}
            filtered_titles={this.props.filtered_eroge}
            title_filters={this.props.eroge_filters}
        />
    }
}

const mapStateToProps = state => ({
    eroge_loading: state.eroge.eroge_loading,
    filtered_eroge_loaded: state.eroge.filtered_eroge_loaded,
    filtered_eroge: state.eroge.filtered_eroge,
    eroge_filters: state.cookies.eroge_browse_filters,
});

export default connect(mapStateToProps, {})(ErogeBrowse)
