import React, {Component, useState} from 'react'
import {connect} from 'react-redux'

import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import GridList from "@material-ui/core/GridList";
import {faExpandArrowsAlt} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {CyberButton} from "../../../layout/common/common";
import {Character} from "../../../../models";
import TitleCard from "../../../layout/cards/TitleCard";
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../../globals";
import {NONE} from "../../../display/Display";

import '../../../../assets/css/anime.css'


function DisplayVoiceActors(props) {
    const {voice_actors} = props;
    const formatted = voice_actors.split('window._taboola')[0];
    const [expand, setExpand] = useState(false);
    return <>
        <h2>Voice Actors{' '}
            <CyberButton component="span" onClick={() => {setExpand(!expand)}}>
                <FontAwesomeIcon icon={faExpandArrowsAlt}/>
            </CyberButton>
        </h2>
        {expand ? <pre>{formatted}</pre> : <p>{formatted}</p>}
    </>
}


function getDescription(description) {
    if (!description || description.indexOf('No biography written') >= 0)
        return null;
    return description
}


class GeneralTab extends Component {
    render() {
        const description = getDescription(this.props.character[Character.description]);

        return (
            <div>
                <div className="general-description">
                    <h2>Description</h2>
                    <p>{description ? description : NONE}</p>

                    {this.props.character[Character.voice_actors] ?
                        <DisplayVoiceActors voice_actors={this.props.character[Character.voice_actors]}/> : null}

                    {this.props.character[Character.anime_roles] &&
                    this.props.character[Character.anime_roles].length > 0 ? <h2>Animeography</h2> : null}
                </div>
                <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                    {this.props.character[Character.anime_roles].map((anime, i) => (
                        <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                            <GridListTile cols={1}>
                                <TitleCard branch={BRANCH_ANIME} title={anime}/>
                            </GridListTile>
                        </Grow>
                    ))}
                </GridList>
                <div className="general-description">
                    {this.props.character[Character.manga_roles] &&
                    this.props.character[Character.manga_roles].length > 0 ? <h2>Mangaography</h2> : null}
                </div>
                <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                    {this.props.character[Character.manga_roles].map((manga, i) => (
                        <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                            <GridListTile cols={1}>
                                <TitleCard branch={BRANCH_MANGA} title={manga}/>
                            </GridListTile>
                        </Grow>
                    ))}
                </GridList>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    character: state.characters.character,
});

export default connect(mapStateToProps, {})(GeneralTab)
