import {CoverImages, Manga, UserMangaEntry} from "../../../../models";
import {CYBER_GENESIS_URL, getPriorityLabel, USER_MANGA_STATUSES} from "../../../../globals";
import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import Moment from "react-moment";
import React from "react";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBookOpen, faCalendarAlt, faCheck, faImage, faPause, faPlay, faStop} from "@fortawesome/free-solid-svg-icons";
import {CyberButton, CyberLink, PreLoadImage} from "../../../layout/common/common";
import {ANIME_DISPLAY, MANGA_DISPLAY} from "../../../../urls";
import AnimeEntryModalForm from "../../forms/anime/AnimeEntryModalForm";
import UpdateAnimeEpisodeButton from "../../forms/anime/UpdateAnimeEpisodeButton";
import MangaEntryModalForm from "../../forms/manga/MangaEntryModalForm";
import {UpdateMangaVolumeButton, UpdateMangaChapterButton} from "../../forms/manga/UpdateMangaVolumeChapterButtons";


function getCoverImgUrl(entry) {
    try {
        return `${CYBER_GENESIS_URL}${entry[UserMangaEntry.manga][Manga.manga_cover_images][entry[UserMangaEntry.manga][Manga.manga_cover_images].length - 1][CoverImages.image]}`
    } catch (e) {
        return ''
    }
}

/**
 *
 * @param props.selected {String}
 * @param props.setSelected {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function MangaListStatusButtons(props) {
    const {selected, setSelected} = props;

    return <>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton disabled={true}>All</CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_MANGA_STATUSES.reading ? 'btn-contained-success' : ''}
                onClick={() => {
                    setSelected(USER_MANGA_STATUSES.reading)
                }}>
                <FontAwesomeIcon icon={faBookOpen}/><span className="btn-label">Reading</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_MANGA_STATUSES.completed ? 'btn-contained-primary' : ''}
                onClick={() => {
                    setSelected(USER_MANGA_STATUSES.completed)
                }}>
                <FontAwesomeIcon icon={faCheck}/><span className="btn-label">Completed</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_MANGA_STATUSES.on_hold ? 'btn-contained-warning' : ''}
                onClick={() => {
                    setSelected(USER_MANGA_STATUSES.on_hold)
                }}>
                <FontAwesomeIcon icon={faPause}/><span className="btn-label">On Hold</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_MANGA_STATUSES.dropped ? 'btn-contained-danger' : ''}
                onClick={() => {
                    setSelected(USER_MANGA_STATUSES.dropped)
                }}>
                <FontAwesomeIcon icon={faStop}/><span className="btn-label">Dropped</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_MANGA_STATUSES.plan_to_read ? 'btn-contained-default' : ''}
                onClick={() => {
                    setSelected(USER_MANGA_STATUSES.plan_to_read)
                }}>
                <FontAwesomeIcon icon={faCalendarAlt}/><span className="btn-label">Planned</span>
            </CyberButton>
        </Grid>
    </>
}

/**
 *
 * @returns {JSX.Element}
 * @constructor
 */
export function MangaListTableHead(props) {
    const {list_owner} = props;
    return <TableHead>
        <TableRow>
            <TableCell>#</TableCell>
            <TableCell align="center"><FontAwesomeIcon icon={faImage}/></TableCell>
            <TableCell align="center" colSpan={list_owner ? 2 : 1}>Title</TableCell>
            <TableCell align="center">Score</TableCell>
            <TableCell align="center">Volumes</TableCell>
            <TableCell align="center">Chapters</TableCell>
            <TableCell align="center">Priority</TableCell>
            <TableCell align="center">Genres</TableCell>
            <TableCell align="center">Type</TableCell>
            <TableCell align="center">Started</TableCell>
            <TableCell align="center">Finished</TableCell>
            <TableCell align="center">Added</TableCell>
            {/*<TableCell align="center">Watch</TableCell>*/}
        </TableRow>
    </TableHead>
}


/**
 *
 * @param props.index {Number}
 * @param props.entry {Object}
 * @param props.list_owner {Boolean}
 * @param props.list {Object}
 * @returns {JSX.Element}
 * @constructor
 */
export function MangaListTableRow(props) {
    const {index, entry, list_owner, list} = props;

    return entry[UserMangaEntry.manga] ? <TableRow>
        <TableCell component="th" scope="row">
            {index}
        </TableCell>
        <TableCell className={{width: '70px'}} align="center">
            <CyberLink to={MANGA_DISPLAY(entry[UserMangaEntry.manga][Manga.id])} classNames="cover-img-link">
                <PreLoadImage src={getCoverImgUrl(entry)} alt={`Cover image`}/>
            </CyberLink>
        </TableCell>
        <TableCell>
            <CyberLink classNames="title-link"
                       to={MANGA_DISPLAY(entry[UserMangaEntry.manga][Manga.id])}>
                {entry[UserMangaEntry.manga][Manga.title]}
            </CyberLink>
        </TableCell>
        {list_owner ?
            <TableCell>
                <MangaEntryModalForm
                    id={entry[UserMangaEntry.manga][Manga.id]}
                    classNames="edit-btn"
                    user_manga_entry={entry}
                    edit={true}
                    list={list}/>
            </TableCell> : null}
        <TableCell align="center">
            <Chip className="chip-primary table-number" size="small"
                  label={entry[UserMangaEntry.score]}/>
        </TableCell>
        <TableCell align="center">
            <p>
                <span className="chip-primary table-number">
                {entry[UserMangaEntry.read_volumes]}{' '}
                    / {entry[UserMangaEntry.manga][Manga.volumes]}
                    {list_owner ?
                        <UpdateMangaVolumeButton
                            id={entry[UserMangaEntry.manga][Manga.id]}
                            entry={entry}
                            list={list}/> : null}
                </span>
            </p>
        </TableCell>
        <TableCell align="center">
            <p>
                <span className="chip-primary table-number">
                {entry[UserMangaEntry.read_chapters]}{' '}
                    / {entry[UserMangaEntry.manga][Manga.chapters]}
                    {list_owner ?
                        <UpdateMangaChapterButton
                            id={entry[UserMangaEntry.manga][Manga.id]}
                            entry={entry}
                            list={list}/> : null}
                </span>
            </p>
        </TableCell>
        <TableCell align="center">
            <Chip className="chip-primary" size="small"
                  label={getPriorityLabel(entry[UserMangaEntry.priority])}/>
        </TableCell>
        <TableCell align="center">
            {entry[UserMangaEntry.manga][Manga.genres_csv] ?
                entry[UserMangaEntry.manga][Manga.genres_csv].split(', ')
                    .map(genre => (
                        <Chip className="chip-primary genre" size="small"
                              label={genre}/>
                    )) : null}
        </TableCell>
        <TableCell align="center">
            <Chip className="chip-primary"
                  label={entry[UserMangaEntry.manga][Manga.type]} size="small"/>
        </TableCell>
        <TableCell align="center">
            {entry[UserMangaEntry.start_date] ? <Moment date={entry[UserMangaEntry.start_date]}
                                                        format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        <TableCell align="center">
            {entry[UserMangaEntry.finish_date] ? <Moment date={entry[UserMangaEntry.finish_date]}
                                                         format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        <TableCell align="center">
            {entry[UserMangaEntry.date_added] ? <Moment date={entry[UserMangaEntry.date_added]}
                                                        format={'Do MMM YYYY'}/> : 'None'}
        </TableCell>
        {/*<TableCell align="center">*/}
        {/*    <CyberButton classNames="btn-contained-secondary"><FontAwesomeIcon*/}
        {/*        icon={faPlay}/></CyberButton>*/}
        {/*</TableCell>*/}
    </TableRow> : null
}