import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME} from "../../../globals";
import Browse from "../../browse/Browse";
import PropTypes from "prop-types";
import store from "../../../store";
import {isEmpty} from "../../functions";


class AnimeBrowse extends Component {
    static propTypes = {
        /** Season object {year: season:} */
        season: PropTypes.object,
    };

    render() {
        let anime_filters = this.props.anime_browse_filters;
        if (!isEmpty(this.props.season))
            anime_filters = this.props.anime_season_filters;
        return <Browse
            branch={BRANCH_ANIME}
            titles_loading={this.props.anime_loading}
            filtered_titles_loaded={this.props.filtered_anime_loaded}
            filtered_titles={this.props.filtered_anime}
            title_filters={anime_filters}
            user_entry_statuses={this.props.user_anime_entry_statuses}
            season={this.props.season}
        />
    }
}

const mapStateToProps = state => ({
    anime_loading: state.anime.anime_loading,
    filtered_anime_loaded: state.anime.filtered_anime_loaded,
    filtered_anime: state.anime.filtered_anime,
    anime_browse_filters: state.cookies.anime_browse_filters,
    anime_season_filters: state.cookies.anime_season_filters,
    user_anime_entry_statuses: state.users.user_anime_entry_statuses,
});

export default connect(mapStateToProps, {})(AnimeBrowse)
