import React, {useState} from "react";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlus} from "@fortawesome/free-solid-svg-icons";
import CircularProgress from "@material-ui/core/CircularProgress";

import store from "../../../../store";
import {playClickSound, playHoverSound} from "../../../layout/sounds";
import {updateMangaChapter, updateMangaVolume} from "../../../../actions/users";
import {Manga, UserMangaEntry} from "../../../../models";

import '../../../../assets/css/users.css'


/**
 * Increase volume number button.
 * @param props.classNames {String} Classnames for the button
 * @param props.id {Number} ID of the manga to update
 * @param props.entry {Object} The user manga entry
 * @param props.list {Object} List object of {user_list_pk: user_list_filter:} if its called from an anime list
 * @returns {JSX.Element}
 * @constructor
 */
export function UpdateMangaVolumeButton(props) {
    const {classNames, id, entry, list} = props;
    const [loading, setLoading] = useState(false);

    if (!store.getState().users.entry_loading && loading)
        setLoading(false);

    if (entry[UserMangaEntry.read_volumes] === entry[UserMangaEntry.manga][Manga.volumes] &&
        entry[UserMangaEntry.manga][Manga.volumes] !== 0)
        return null;
    return !loading ?
        <span
            className="update-anime-episode-btn"
            onMouseEnter={playHoverSound}
            onClick={() => {
                playClickSound();
                setLoading(true);
                if (entry[UserMangaEntry.read_volumes] !== entry[UserMangaEntry.manga][Manga.volumes] ||
                    entry[UserMangaEntry.manga][Manga.volumes] === 0)
                    store.dispatch(updateMangaVolume(id, list))
            }}
        >
            <FontAwesomeIcon icon={faPlus}/>
        </span> : <span className="update-anime-episode-btn"><CircularProgress thickness={6}/></span>
}


