import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_MANGA} from "../../../../globals";
import {Anime, ApiResult, Manga, Person} from "../../../../models";
import {getPublishedMangaForPerson} from "../../../../actions/people";
import TitleCard from "../../../layout/cards/TitleCard";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../../layout/sounds";
import GoToPageDialog from "../../../layout/pagination/goToPageDialog";

import '../../../../assets/css/people.css'
import {getLoggedInUserMangaEntryStatuses} from "../../../../actions/users";
import {isEmpty} from "../../../functions";
import Loading from "../../../layout/common/Loading";

const ITEMS_PER_PAGE = 50;


class PublishedMangaTab extends Component {
    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        if (!this.props.person[Person.published_manga] || this.props.published_manga[ApiResult.previous])
            this.props.getPublishedMangaForPerson(this.props.person[Person.id]);
        if (isEmpty(this.props.user_manga_entry_statuses))
            this.props.getLoggedInUserMangaEntryStatuses()
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.published_manga[ApiResult.count] / ITEMS_PER_PAGE)) {
            window.scrollTo(0, 0);
            this.setState({current_page: page});
            this.props.getPublishedMangaForPerson(this.props.person[Person.id], page)
        }
    }

    render() {
        const manga_roles = JSON.parse(this.props.person[Person.published_manga_roles]);
        return (
            <div className="person-tab">
                {this.props.published_manga ? <>
                    <table>
                        <tbody>
                        {this.props.published_manga[ApiResult.results].map((manga, i) => (
                            <tr key={i}>
                                <td>
                                    <TitleCard
                                        key={i} branch={BRANCH_MANGA} title={manga}
                                        containerClassNames="character-role-wrapper-card-container"
                                        user_status={!isEmpty(this.props.user_manga_entry_statuses) ?
                                            this.props.user_manga_entry_statuses[`${manga[Manga.id]}`] : null}
                                    />
                                </td>
                                <td><p className="role">{manga_roles[manga[Manga.id]]}</p></td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                    <div className="pagination">
                        <Pagination
                            count={Math.ceil(this.props.published_manga[ApiResult.count] / ITEMS_PER_PAGE)}
                            color="primary"
                            siblingCount={3}
                            page={this.state.current_page}
                            onChange={(type, page) => {
                                playClickSound();
                                if (page !== this.state.current_page)
                                    this.goToPage(page);
                                else
                                    this.setState({page_dialog_open: true})
                            }}
                        />
                        <GoToPageDialog
                            open={this.state.page_dialog_open}
                            current_page={this.state.current_page}
                            total_pages={Math.ceil(this.props.published_manga[ApiResult.count] / ITEMS_PER_PAGE)}
                            goToPage={this.goToPage}
                            onClose={this.closePageDialog}/>
                    </div>
                </> : this.props.manga_loading ? <Loading text={'Manga loading'} block={true}/> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    person: state.people.person,
    published_manga: state.people.person[Person.published_manga],
    user_manga_entry_statuses: state.users.user_manga_entry_statuses,
    manga_loading: state.manga.manga_loading,
});

export default connect(mapStateToProps, {
    getPublishedMangaForPerson, getLoggedInUserMangaEntryStatuses
})(PublishedMangaTab)
