import Alert from "@material-ui/lab/Alert";
import store from "../../../store";
import {cleanErrors, clearMessage} from "../../../actions/messages";
import AlertTitle from "@material-ui/lab/AlertTitle";
import React from "react";


export const AlertOptions = {
    timeout: 3000,
    position: 'bottom left'
};

export const MyAlertTemplate = ({style, options, message, close}) => (
    <Alert severity={
        options.type === 'error' ? 'error' :
            options.type === 'success' ? 'success' : 'info'}
           className="alert"
           onClose={close}>
        <AlertTitle>{options.type}</AlertTitle>
        <p>{message}</p>
    </Alert>
);