import React, {Component} from 'react'
import {connect} from 'react-redux'
// noinspection ES6CheckImport
import {Redirect} from "react-router-dom";

import Grid from '@material-ui/core/Grid';

import {REGISTER_TAB_NAME} from "../../globals";
import {INDEX} from "../../urls";
import RegisterForm from "./RegisterForm";

import '../../assets/css/auth.css';


class Register extends Component {
    constructor() {
        super(...arguments);
        this.state = {img_loaded: false};
    }

    componentDidMount() {
        document.title = REGISTER_TAB_NAME;
        window.scrollTo(0, 0);
    }

    render() {
        if (this.props.user_authenticated)
            return <Redirect to={INDEX}/>;

        return (
            <div id="Register" className={this.state.img_loaded ? 'fade-in' : ''}>
                <img src={'/static/media/girl-window.1732e4f9.jpg'} className="lazy-loaded-img" alt="loader-img"
                     onLoad={() => {this.setState({img_loaded: true})}} />
                <Grid container>
                    <Grid item xs={3} className="form-container box-light">
                        <h1>Register</h1>
                        <RegisterForm/>
                    </Grid>
                </Grid>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated
});

export default connect(mapStateToProps, {})(Register)
