import {
    MANGA_LOADING,
    GET_MANGA,
    CANCEL_MANGA_LOADING,
    FILTER_MANGA,
    GET_MANGA_DATA,
    GET_ALL_CHARACTERS_FOR_MANGA,
    GET_RELATIONS_FOR_MANGA
} from "../actions/types";
import {playExpandSound} from "../components/layout/sounds";
import {Manga} from "../models";

const initialState = {
    manga_loading: false,
    manga: {},
    manga_loaded: false,
    manga_data: {},
    manga_data_loaded: {},
    filtered_manga: {},
    filtered_manga_loaded: false,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default function (state = initialState, action) {
    switch (action.type) {
        case MANGA_LOADING:
            return {
                ...state,
                manga_loading: true,
                manga_loaded: false,
                manga_data_loaded: false,
                filtered_manga_loaded: false,
            };
        case CANCEL_MANGA_LOADING:
            return {
                ...state,
                manga_loading: false,
                manga_loaded: false,
                manga_data_loaded: false,
                filtered_manga_loaded: false,
            };
        case GET_MANGA_DATA:
            return {
                ...state,
                manga_loading: false,
                manga_data: action.payload,
                manga_data_loaded: true,
            };
        case GET_MANGA:
            playExpandSound();
            return {
                ...state,
                manga: action.payload,
                manga_loading: false,
                manga_loaded: true
            };
        case FILTER_MANGA:
            return {
                ...state,
                filtered_manga: action.payload,
                manga_loading: false,
                filtered_manga_loaded: true
            };
        case GET_ALL_CHARACTERS_FOR_MANGA:
            state.manga[Manga.all_characters] = action.payload;
            return {
                ...state,
                manga: state.manga
            };
        case GET_RELATIONS_FOR_MANGA:
            state.manga[Manga.manga_relations] = action.payload;
            return {
                ...state,
                manga: state.manga
            };
        default:
            return state
    }
}
