import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faDownload, faEdit, faExpandArrowsAlt, faPlus, faTrash} from "@fortawesome/free-solid-svg-icons";

import {CyberButton} from "../../layout/common/common";
import DownloadTorrentForm from "./DownloadTorrentForm";
import {Form, Formik} from "formik";
import {Anime, AnimeOnServer, UserAnimeEntry} from "../../../models";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Checkbox from "@material-ui/core/Checkbox";
import {LANGUAGES} from "../../../globals";
import DialogActions from "@material-ui/core/DialogActions";
import {addAnimeToServer, updateAnimeToServer} from "../../../actions/requiem";


class DownloadTorrentModalForm extends Component {
    static propTypes = {
        /** classNames */
        classNames: PropTypes.string,
    };

    constructor() {
        super(...arguments);
        this.toggleExpandLanguages = this.toggleExpandLanguages.bind(this);
        this.handleClickOpen = this.handleClickOpen.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.state = {
            open: false,
            expand_languages: false,
        };
    }

    toggleExpandLanguages() {
        this.setState({expand_languages: !this.state.expand_languages})
    }

    handleClickOpen() {
        this.setState({open: true})
    };

    handleClose() {
        this.setState({open: false})
    };

    render() {
        const edit = !!this.props.anime[Anime.anime_on_server];
        const languages = this.state.expand_languages ? LANGUAGES : [LANGUAGES[0], LANGUAGES[1]];
        const field_names = {
            anime: 'anime',
            location: 'location',
            size_on_HDD_GB: 'size_on_HDD_GB',
            additional_info: 'additional_info',
            audio_channel: 'audio_channel',
            video_quality: 'video_quality',
            incomplete: 'incomplete',
            incomplete_progress: 'incomplete_progress',
        };

        let initial_values = {
            anime: this.props.anime[Anime.id],
            location: '',
            size_on_HDD_GB: 0.00,
            additional_info: '',
            audio_channel: '2.0',
            video_quality: '1080p',
            incomplete: false,
            incomplete_progress: 0,
        };

        LANGUAGES.forEach((language) => {
            const lang = language.toLowerCase();
            initial_values[lang + '_dub'] = false;
            initial_values[lang + '_sub'] = false;
        });

        if (edit) {
            initial_values = {
                anime: this.props.anime[Anime.id],
                location: this.props.anime[Anime.anime_on_server][AnimeOnServer.location],
                size_on_HDD_GB: this.props.anime[Anime.anime_on_server][AnimeOnServer.size_on_HDD_GB],
                additional_info: this.props.anime[Anime.anime_on_server][AnimeOnServer.additional_info],
                audio_channel: this.props.anime[Anime.anime_on_server][AnimeOnServer.audio_channel],
                video_quality: this.props.anime[Anime.anime_on_server][AnimeOnServer.video_quality],
                incomplete: this.props.anime[Anime.anime_on_server][AnimeOnServer.incomplete],
                incomplete_progress: this.props.anime[Anime.anime_on_server][AnimeOnServer.incomplete_progress],
            };
            LANGUAGES.forEach((language) => {
                const lang = language.toLowerCase();
                initial_values[lang + '_dub'] = this.props.anime[Anime.anime_on_server][`${lang}_dub`];
                initial_values[lang + '_sub'] = this.props.anime[Anime.anime_on_server][`${lang}_sub`];
            });
        }

        return (
            <div id="Edit-Anime-On-Server-Modal" className={this.props.classNames}>
                <CyberButton classNames="btn-contained-secondary" onClick={this.handleClickOpen}>
                    <FontAwesomeIcon icon={edit ? faEdit : faPlus}/>
                </CyberButton>
                <Dialog open={this.state.open} onClose={this.handleClose} aria-labelledby="form-dialog-title"
                        className="edit-anime-on-server-dialog">
                    <DialogTitle id="form-dialog-title">{edit ? 'Edit' : 'Add'} anime to server</DialogTitle>
                    <DialogContent>
                        <Formik
                            initialValues={initial_values}
                            onSubmit={(values, {setSubmitting}) => {
                                console.log('values', values);

                                if (edit) this.props.updateAnimeToServer(values);
                                else this.props.addAnimeToServer(values);

                                setSubmitting(false);
                            }}>
                            {({
                                  isSubmitting, values, handleChange,
                                  handleBlur
                              }) => (
                                <Form id="Edit-Anime-On-Server-Form">
                                    <Grid container className="form-group">
                                        <Grid item xs={12}>
                                            <TextField
                                                className="form-control"
                                                label="Location"
                                                color="secondary"
                                                autoComplete={false}
                                                name={field_names.location}
                                                value={values[field_names.location]}
                                                onChange={handleChange}
                                                onBlur={handleBlur}/>
                                        </Grid>
                                    </Grid>

                                    <Grid container className="form-group">
                                        <Grid item xs={2}>
                                            <TextField
                                                className="form-control"
                                                label="Size"
                                                color="secondary"
                                                type="number"
                                                name={field_names.size_on_HDD_GB}
                                                value={values[field_names.size_on_HDD_GB]}
                                                onChange={handleChange}
                                                onBlur={handleBlur}/>
                                        </Grid>
                                        <Grid item xs={5}>
                                            <InputLabel id="video-quality" className="input-label">Video
                                                Quality</InputLabel>
                                            <Select
                                                labelId="video-quality"
                                                className="form-control"
                                                color="secondary"
                                                value={values[field_names.video_quality]}
                                                name={field_names.video_quality}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            >
                                                <MenuItem value="2160p">2160p</MenuItem>
                                                <MenuItem value="1080p">1080p</MenuItem>
                                                <MenuItem value="720p">720p</MenuItem>
                                                <MenuItem value="580p">580p</MenuItem>
                                                <MenuItem value="480p">480p</MenuItem>
                                                <MenuItem value="360p">360p</MenuItem>
                                                <MenuItem value="240p">240p</MenuItem>
                                            </Select>
                                        </Grid>
                                        <Grid item xs={5}>
                                            <InputLabel id="audio-quality" className="input-label">Audio
                                                Channel</InputLabel>
                                            <Select
                                                labelId="audio-quality"
                                                className="form-control"
                                                color="secondary"
                                                value={values[field_names.audio_channel]}
                                                name={field_names.audio_channel}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            >
                                                <MenuItem value="2.0">2.0</MenuItem>
                                                <MenuItem value="5.1">5.1</MenuItem>
                                            </Select>
                                        </Grid>
                                    </Grid>

                                    <Grid container className="form-group">
                                        <Grid item xs={6}>
                                            <label>Incomplete</label>
                                            <Checkbox
                                                value={values[field_names.incomplete]}
                                                inputProps={{'aria-label': 'primary checkbox'}}
                                                name={field_names.incomplete}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <TextField
                                                className="form-control"
                                                label="Incomplete Progress"
                                                color="secondary"
                                                type="number"
                                                disabled={!values[field_names.incomplete]}
                                                name={field_names.incomplete_progress}
                                                value={values[field_names.incomplete_progress]}
                                                onChange={handleChange}
                                                onBlur={handleBlur}/>
                                        </Grid>
                                    </Grid>

                                    <Grid container className="form-group">
                                        <Grid item xs={12}>
                                            <label>Dubs</label>
                                            <CyberButton classNames="expand-btn" onClick={this.toggleExpandLanguages}>
                                                <FontAwesomeIcon icon={faExpandArrowsAlt}/>
                                            </CyberButton>
                                        </Grid>
                                        {languages.map((language, x) => (
                                            <Grid item xs={3}>
                                                <div className="language-box" key={x}>
                                                    <Checkbox
                                                        value={values[language.toLowerCase() + '_dub']}
                                                        checked={values[language.toLowerCase() + '_dub']}
                                                        inputProps={{'aria-label': 'primary checkbox'}}
                                                        name={language.toLowerCase() + '_dub'}
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                    />
                                                    <label>{language}</label>
                                                </div>
                                            </Grid>
                                        ))}
                                    </Grid>

                                    <Grid container className="form-group">
                                        <Grid item xs={12}>
                                            <label>Subs</label>
                                        </Grid>
                                        {languages.map((language, x) => (
                                            <Grid item xs={3}>
                                                <div className="language-box" key={x}>
                                                    <Checkbox
                                                        value={values[language.toLowerCase() + '_sub']}
                                                        checked={values[language.toLowerCase() + '_sub']}
                                                        inputProps={{'aria-label': 'primary checkbox'}}
                                                        name={language.toLowerCase() + '_sub'}
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                    />
                                                    <label>{language}</label>
                                                </div>
                                            </Grid>
                                        ))}
                                    </Grid>

                                    <Grid container className="form-group">
                                        <Grid item xs={12}>
                                            <CyberButton classNames="btn-success" variant="outlined" type="submit"
                                                         onClick={this.handleClose} disabled={isSubmitting}>
                                                <FontAwesomeIcon icon={faCheck}/>
                                            </CyberButton>
                                        </Grid>
                                    </Grid>
                                </Form>
                            )}
                        </Formik>
                    </DialogContent>
                </Dialog>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime
});

export default connect(mapStateToProps, {addAnimeToServer, updateAnimeToServer})(DownloadTorrentModalForm)
