import React, {Component} from 'react'
import PropTypes from "prop-types";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCog, faPlus, faSave, faTrash} from "@fortawesome/free-solid-svg-icons";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";

import {FiltersPreset} from "../../../models";
import {isEmpty} from "../../functions";
import {getResetValuesFromBranch} from "../../browse/functions";
import {deleteFilterPreset, getSelectedPreset, saveFilterPreset, setPreset, updateFilterPreset} from "./functions";
import {CyberButton} from "../common/common";


class PresetFilters extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
        /** Preset filters for the user */
        presets: PropTypes.array.isRequired,
        /** Currently active filters, from state */
        filters: PropTypes.object.isRequired,
        /** Function to run on submit when changing the preset */
        onSubmit: PropTypes.func.isRequired,
    };

    constructor() {
        super(...arguments);
        this.onPresetChange = this.onPresetChange.bind(this);
        this.onPresetNew = this.onPresetNew.bind(this);
        this.onPresetUpdate = this.onPresetUpdate.bind(this);
        this.onPresetDelete = this.onPresetDelete.bind(this);
        this.state = {
            preset: getSelectedPreset(this.props.branch)
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.preset !== getSelectedPreset(this.props.branch))
            this.setState({preset: getSelectedPreset(this.props.branch)})
    }

    onPresetChange(e) {
        if (e.target.value === -1) {
            this.setState({preset: {}});
            return;
        }
        for (let x = 0; x < this.props.presets.length; x++) {
            if (this.props.presets[x][FiltersPreset.id] === e.target.value) {
                let new_values = getResetValuesFromBranch(this.props.branch);

                let filters = this.props.presets[x][FiltersPreset.filters];
                if (typeof this.props.presets[x][FiltersPreset.filters] !== 'object')
                    filters = JSON.parse(this.props.presets[x][FiltersPreset.filters]);

                new_values = setPreset(this.props.branch, this.props.presets[x], new_values, filters);

                this.setState({preset: this.props.presets[x]}, () => {
                    this.props.onSubmit(e, new_values);
                });
                break
            }
        }
    }

    onPresetUpdate() {
        if (!isEmpty(this.state.preset))
            updateFilterPreset(this.props.branch, this.state.preset[FiltersPreset.id], this.props.filters);
    }

    onPresetNew() {
        let preset_name = prompt('Enter the preset name:', 'New Preset');
        if (preset_name)
            saveFilterPreset(this.props.branch, preset_name, this.props.filters)
    }

    onPresetDelete() {
        if (window.confirm(`Delete ${this.state.preset[FiltersPreset.preset_name]} preset?`))
            deleteFilterPreset(this.props.branch, this.state.preset[FiltersPreset.id])
    }

    render() {
        return (
            <div id="Preset-Filters">
                <Select label="Options" className="preset-settings" value={-1}>
                    <MenuItem style={{display: 'none'}} value={-1}>
                        <FontAwesomeIcon icon={faCog}/>
                    </MenuItem>
                    <MenuItem className="settings-buttons" value={1}>
                        <CyberButton title={'Create preset with current filters'} onClick={this.onPresetNew}>
                            <FontAwesomeIcon icon={faPlus}/>
                        </CyberButton>
                    </MenuItem>
                    <MenuItem className="settings-buttons" value={2}>
                        <CyberButton title={'Update selected preset with current filters'} onClick={this.onPresetUpdate}>
                            <FontAwesomeIcon icon={faSave}/>
                        </CyberButton>
                    </MenuItem>
                    <MenuItem className="settings-buttons" value={3}>
                        <CyberButton title={'Delete selected preset'} onClick={this.onPresetDelete}>
                            <FontAwesomeIcon icon={faTrash}/>
                        </CyberButton>
                    </MenuItem>
                </Select>
                {this.props.presets ? <>
                    <Select
                        id="user-filters-preset"
                        className="preset-selector"
                        label="Preset"
                        value={this.state.preset[FiltersPreset.id] ? this.state.preset[FiltersPreset.id] : -1}
                        onChange={this.onPresetChange}
                    >
                        <MenuItem value={-1}>
                            Select Preset
                        </MenuItem>
                        {this.props.presets.map(preset => (
                            <MenuItem value={preset[FiltersPreset.id]}>
                                {preset[FiltersPreset.preset_name]}
                            </MenuItem>
                        ))}
                    </Select>
                </> : null}
            </div>
        )
    }
}

export default PresetFilters
