import React, {Component} from 'react'
import Grid from "@material-ui/core/Grid";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEdit, faFileUpload, faTools, faVolumeMute, faVolumeUp} from "@fortawesome/free-solid-svg-icons";
import {connect} from "react-redux";
import {CyberButton, CyberLink} from "../../../layout/common/common";
import {setCookieSoundEffects} from "../../../../actions/cookies";
import Button from "@material-ui/core/Button";
import {Profile as ProfileModel, Profile, User} from "../../../../models";
import {muteUnmuteThemeSounds} from "../../../../actions/users";
import {EditAboutMeBBCodeDialog, EditBioForm} from "../../forms/shortForms";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import GoToPageDialog from "../../../layout/pagination/goToPageDialog";
import {getBrowseGridListCols} from "../../../functions";
import {BBCODE_EDITOR, USER_ANIME_STATISTICS} from "../../../../urls";


class ProfileSettings extends Component {
    constructor() {
        super(...arguments);
        this.openBBCodeDialog = this.openBBCodeDialog.bind(this);
        this.closeBBCodeDialog = this.closeBBCodeDialog.bind(this);
        this.state = {
            bbcode_dialog_open: false,
        };
    }

    openBBCodeDialog() {
        this.setState({bbcode_dialog_open: true})
    }

    closeBBCodeDialog() {
        this.setState({bbcode_dialog_open: false})
    }

    render() {
        return (
            <Grid container>
                <Grid item xs={12}>
                    <h1>Profile Settings</h1>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={11}>
                    <h2>Theme settings</h2>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={5}>
                    <TableContainer>
                        <Table aria-label="simple table" className="box-light">
                            <TableBody>
                                <TableRow>
                                    <TableCell>
                                        Theme
                                    </TableCell>
                                    <TableCell align="center">
                                        <FormControl variant="filled">
                                            <InputLabel id="select-theme">Theme</InputLabel>
                                            <Select
                                                labelId="select-theme"
                                                id="demo-simple-select-outlined"
                                                value={'Cyber-Genesis'}
                                                label="Theme"
                                            >
                                                <MenuItem value={'Cyber-Genesis'}>Cyber-Genesis</MenuItem>
                                                <MenuItem value={'Legacy'}>Legacy</MenuItem>
                                                <MenuItem value={'Oceanic Blue'}>Oceanic Blue</MenuItem>
                                            </Select>
                                        </FormControl>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Sounds
                                    </TableCell>
                                    <TableCell align="center">
                                        <CyberButton onClick={this.props.muteUnmuteThemeSounds}>
                                            <FontAwesomeIcon icon={this.props.user[User.profile][Profile.theme_sounds] ?
                                                    faVolumeUp : faVolumeMute}/>
                                        </CyberButton>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Show adult content
                                    </TableCell>
                                    <TableCell align="center">
                                        Yes
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
                <Grid item xs={6}/>

                <Grid item xs={1}/>
                <Grid item xs={11}>
                    <h2>About section</h2>
                </Grid>

                <Grid item xs={1}/>
                <Grid item xs={5}>
                    <TableContainer>
                        <Table aria-label="simple table" className="box-light">
                            <TableBody>
                                <TableRow>
                                    <TableCell>
                                        Edit About Me bbCode
                                    </TableCell>
                                    <TableCell align="center">
                                        <CyberLink to={BBCODE_EDITOR}>
                                            <CyberButton title="BBCode Editor" variant="outlined" >
                                                <FontAwesomeIcon icon={faTools}/>
                                            </CyberButton>
                                        </CyberLink>
                                        {/*<CyberButton variant="outlined" onClick={this.openBBCodeDialog}>*/}
                                        {/*    <FontAwesomeIcon icon={faEdit}/>*/}
                                        {/*</CyberButton>*/}
                                        {/*<EditAboutMeBBCodeDialog*/}
                                        {/*    bbcode={this.props.user[User.profile][ProfileModel.about_section_bbcode]}*/}
                                        {/*    open={this.state.bbcode_dialog_open}*/}
                                        {/*    onClose={this.closeBBCodeDialog}/>*/}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Display Anime stats summary
                                    </TableCell>
                                    <TableCell align="center">
                                        No
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Display Manga stats summary
                                    </TableCell>
                                    <TableCell align="center">
                                        No
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>
                                        Display Eroge stats summary
                                    </TableCell>
                                    <TableCell align="center">
                                        No
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
                <Grid item xs={6}/>
            </Grid>

        )
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    sound_effects: state.cookies.sound_effects,
});

export default connect(mapStateToProps, {muteUnmuteThemeSounds})(ProfileSettings)
