import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import {
    BRANCH_EROGE,
    EROGE_CENSORSHIPS,
    EROGE_LANGUAGES, EROGE_ORDERINGS,
    EROGE_PLATFORMS, EROGE_TAGS,
    EROGE_TYPES,
} from "../../../../globals";
import TitlesFilter from "../TitlesFilter";
import {ErogeFilterNames, FilterLabels, FilterSchemaType} from "../field_names";
import {getErogeFilterPresets} from "../../../../actions/users";
import store from "../../../../store";


class ErogeFilters extends Component {
    static propTypes = {
    };

    componentDidMount() {
        store.dispatch(getErogeFilterPresets())
    }

    render() {
        const main_filters = [
            {'name': ErogeFilterNames.downloaded, 'label': FilterLabels.downloaded, 'color': 'secondary'},
        ];
        let regular_filters = [
            {'name': ErogeFilterNames.type, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': EROGE_TYPES},
            {'name': ErogeFilterNames.platforms, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': EROGE_PLATFORMS},
            {'name': ErogeFilterNames.censorship, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': EROGE_CENSORSHIPS},
            {'name': ErogeFilterNames.language, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': EROGE_LANGUAGES},
        ];
        let wide_filters = [
            {'name': ErogeFilterNames.tags, 'type': FilterSchemaType.wide, 'color': 'primary', 'content': EROGE_TAGS},
        ];

        return (
            <TitlesFilter
                branch={BRANCH_EROGE}
                titles_loading={this.props.eroge_loading}
                filtered_titles_loaded={this.props.filtered_eroge_loaded}
                filtered_titles={this.props.filtered_eroge}
                title_filters={this.props.eroge_browse_filters}
                main_filters={main_filters}
                regular_filters={regular_filters}
                wide_filters={wide_filters}
                order_filters={EROGE_ORDERINGS}
                presets={this.props.eroge_filter_presets}
            />
        )
    }
}

const mapStateToProps = state => ({
    eroge_loading: state.eroge.eroge_loading,
    filtered_eroge_loaded: state.eroge.filtered_eroge_loaded,
    filtered_eroge: state.eroge.filtered_eroge,
    eroge_browse_filters: state.cookies.eroge_browse_filters,
    eroge_filter_presets: state.users.eroge_filter_presets,
});

export default connect(mapStateToProps, {})(ErogeFilters)
