import React, {Component} from 'react'
import {connect} from "react-redux";
import PropTypes from 'prop-types'

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import CircularProgress from "@material-ui/core/CircularProgress";
import {faEdit, faPlus} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {BRANCH_ANIME, BRANCH_MANGA} from "../../../globals";
import {Anime, Manga} from "../../../models";
import {CyberButton} from "../common/common";
import {getUserAnimeEntry, getUserMangaEntry} from "../../../actions/users";
import {getUserAnimeEntryFormInitialValues, UserAnimeEntryForm} from "../../user/forms/anime/forms";
import {AddAnimeEntryInstantDropButton, AddMangaEntryInstantDropButton} from "../../user/forms/shortForms";
import {getUserMangaEntryFormInitialValues, UserMangaEntryForm} from "../../user/forms/manga/forms";


function getTitleNameFromTitle(branch, title) {
    switch (branch) {
        case BRANCH_ANIME:
            return title[Anime.title];
        case BRANCH_MANGA:
            return title[Manga.title];
        default:
            return null;
    }
}


/**
 *
 * @param {Number} props.anime_id
 * @param {Object} props.user_anime_entry
 * @param {boolean} props.edit
 * @param {Object} props.list
 * @param {function} props.handleClose
 * @returns {JSX.Element}
 * @constructor
 */
function QuickUserAnimeEntryForm(props) {
    const {anime_id, user_anime_entry, edit, handleClose} = props;
    const initial_values = getUserAnimeEntryFormInitialValues(anime_id, user_anime_entry, edit);
    return <UserAnimeEntryForm initial_values={initial_values} id={anime_id} edit={edit} handleClose={handleClose}/>
}


/**
 *
 * @param {Number} props.manga_id
 * @param {Object} props.user_manga_entry
 * @param {boolean} props.edit
 * @param {Object} props.list
 * @param {function} props.handleClose
 * @returns {JSX.Element}
 * @constructor
 */
function QuickUserMangaEntryForm(props) {
    const {manga_id, user_manga_entry, edit, handleClose} = props;
    const initial_values = getUserMangaEntryFormInitialValues(manga_id, user_manga_entry, edit);
    return <UserMangaEntryForm initial_values={initial_values} id={manga_id} edit={edit} handleClose={handleClose}/>
}


/**
 *
 * @param {Number} props.title_id
 * @param {String} props.branch
 * @returns {JSX.Element}
 * @constructor
 */
function InstantDropTitleButton(props) {
    const {title_id, branch} = props;
    switch (branch) {
        case BRANCH_ANIME:
            return <AddAnimeEntryInstantDropButton
                classNames="drop-button btn-contained-danger-dh" variant={'contained'} anime_id={title_id}/>;
        case BRANCH_MANGA:
            return <AddMangaEntryInstantDropButton
                classNames="drop-button btn-contained-danger-dh" variant={'contained'} manga_id={title_id}/>;
        default:
            return null;
    }

}


class CardUserEditTitleEntry extends Component {
    static propTypes = {
        /** Branch */
        branch: PropTypes.string.isRequired,
        /** Title Id */
        id: PropTypes.number.isRequired,
        /** Title Object */
        title: PropTypes.object.isRequired,
        /** Status of the user for this title (Completed, Dropped, etc) */
        user_status: PropTypes.string,
        /** Hide the user buttons from the Title Card */
        hideUserButtons: PropTypes.func.isRequired,
    };

    constructor() {
        super(...arguments);
        this.handleClose = this.handleClose.bind(this);
        this.getUserTitleEntry = this.getUserTitleEntry.bind(this);
        this.getQuickUserTitleEntryForm = this.getQuickUserTitleEntryForm.bind(this);
        this.state = {open: false};
    }

    handleClose() {
        this.setState({open: false});
        this.props.hideUserButtons();
    };

    getUserTitleEntry() {
        this.setState({open: true});
        switch (this.props.branch) {
            case BRANCH_ANIME:
                this.props.getUserAnimeEntry(this.props.id);
                return;
            case BRANCH_MANGA:
                this.props.getUserMangaEntry(this.props.id);
                return;
            default:
                return;
        }
    }

    getQuickUserTitleEntryForm(edit) {
        switch (this.props.branch) {
            case BRANCH_ANIME:
                return <QuickUserAnimeEntryForm
                    anime_id={this.props.id}
                    user_anime_entry={this.props.user_anime_entry}
                    edit={edit}
                    handleClose={this.handleClose}
                />;
            case BRANCH_MANGA:
                return <QuickUserMangaEntryForm
                    manga_id={this.props.id}
                    user_manga_entry={this.props.user_manga_entry}
                    edit={edit}
                    handleClose={this.handleClose}
                />;
            default:
                return;
        }
    }

    render() {
        let edit = !!this.props.user_status;
        const add_button_title = edit ? 'Edit entry' : 'Add to list';
        console.log('branch:', this.props.branch);

        return (
            <>
                {this.props.entry_loading ?
                    <div className="user-buttons-loading"><CircularProgress thickness={6} size={30}/></div> : <>
                        <CyberButton classNames="add-button btn-contained-primary-dh" color={'primary'}
                                     variant={'contained'} title={add_button_title} onClick={this.getUserTitleEntry}>
                            <FontAwesomeIcon icon={edit ? faEdit : faPlus}/>
                        </CyberButton>
                        {!edit ? <InstantDropTitleButton branch={this.props.branch} title_id={this.props.id}/> : null}
                    </>}

                <Dialog open={this.state.open} onClose={this.handleClose}
                        aria-labelledby="form-dialog-title" className="user-title-entry-dialog">
                    {!this.props.entry_loading ?
                        <DialogTitle>
                            {edit ? 'Edit' : 'Add'} {getTitleNameFromTitle(this.props.branch, this.props.title)}
                        </DialogTitle> : null}
                    <DialogContent>
                        {this.props.entry_loading ? <CircularProgress/> : this.getQuickUserTitleEntryForm(edit)}
                    </DialogContent>
                </Dialog>
            </>
        )
    }
}

const mapStateToProps = state => ({
    user_anime_entry: state.users.user_anime_entry,
    user_manga_entry: state.users.user_manga_entry,
    entry_loading: state.users.entry_loading,
});

export default connect(mapStateToProps, {getUserAnimeEntry, getUserMangaEntry})(CardUserEditTitleEntry)
