import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Parallax} from "react-parallax";

import '../../../assets/css/layout.css'


class CyberFooter extends Component {
    render() {
        return (
            <div style={this.props.margin === false ? null : {marginTop: '150px'}} id="Footer">
                <Parallax
                    blur={0}
                    bgImage={'/images/object.png'}
                    bgImageAlt="footer"
                    strength={200}>
                    <div className="logos-container">
                        <p className="">Powered by</p>
                        <div className="logo-container logo-container-side">
                            <img src="/images/cyber-administration-logo.png" alt={''}/>
                            <h5 className="color-">Cyber-Admin</h5>
                        </div>
                        <div className="logo-container logo-container-main">
                            <img src="/images/cyber-genesis-logo.png" alt={''}/>
                            <h3 className="color-">Cyber-Genesis</h3>
                        </div>
                        <div className="logo-container logo-container-side">
                            <img src="/images/cyber-requiem-logo.png" alt={''}/>
                            <h5 className="color-">Cyber-Requiem</h5>
                        </div>
                    </div>
                </Parallax>
            </div>
        )
    }
}

const mapStateToProps = state => ({});

export default connect(mapStateToProps, {})(CyberFooter)
