import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME, BRANCH_CHARACTERS} from "../../../../globals";
import {Anime, ApiResult, Character, Person} from "../../../../models";
import {getAnimeStaffPositionsForPerson, getVoiceActorRolesForPerson} from "../../../../actions/people";
import TitleCard from "../../../layout/cards/TitleCard";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../../layout/sounds";
import GoToPageDialog from "../../../layout/pagination/goToPageDialog";

import '../../../../assets/css/people.css'
import {getLoggedInUserAnimeEntryStatuses} from "../../../../actions/users";
import {isEmpty} from "../../../functions";
import Loading from "../../../layout/common/Loading";

const ITEMS_PER_PAGE = 50;


class AnimeStaffPositionsTab extends Component {
    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        if (!this.props.person[Person.anime_staff_positions] || this.props.anime_staff_positions[ApiResult.previous])
            this.props.getAnimeStaffPositionsForPerson(this.props.person[Person.id]);
        if (isEmpty(this.props.user_anime_entry_statuses))
            this.props.getLoggedInUserAnimeEntryStatuses()
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.anime_staff_positions[ApiResult.count] / ITEMS_PER_PAGE)) {
            window.scrollTo(0, 0);
            this.setState({current_page: page});
            this.props.getAnimeStaffPositionsForPerson(this.props.person[Person.id], page)
        }
    }

    render() {
        const positions_roles = JSON.parse(this.props.person[Person.anime_staff_positions_roles]);
        return (
            <div className="person-tab">
                {this.props.anime_staff_positions ? <>
                    <table>
                        <tbody>
                        {this.props.anime_staff_positions[ApiResult.results].map((anime, i) => (
                            <tr key={i}>
                                <td>
                                    <TitleCard
                                        key={i} branch={BRANCH_ANIME} title={anime}
                                        containerClassNames="character-role-wrapper-card-container"
                                        user_status={!isEmpty(this.props.user_anime_entry_statuses) ?
                                            this.props.user_anime_entry_statuses[`${anime[Anime.id]}`] : null}
                                    />
                                </td>
                                <td><p className="role">{positions_roles[anime[Anime.id]]}</p></td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                    <div className="pagination">
                        <Pagination
                            count={Math.ceil(this.props.anime_staff_positions[ApiResult.count] / ITEMS_PER_PAGE)}
                            color="primary"
                            siblingCount={3}
                            page={this.state.current_page}
                            onChange={(type, page) => {
                                playClickSound();
                                if (page !== this.state.current_page)
                                    this.goToPage(page);
                                else
                                    this.setState({page_dialog_open: true})
                            }}
                        />
                        <GoToPageDialog
                            open={this.state.page_dialog_open}
                            current_page={this.state.current_page}
                            total_pages={Math.ceil(this.props.anime_staff_positions[ApiResult.count] / ITEMS_PER_PAGE)}
                            goToPage={this.goToPage}
                            onClose={this.closePageDialog}/>
                    </div>
                </> : this.props.anime_loading ? <Loading text={'Anime loading'} block={true}/> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    person: state.people.person,
    anime_staff_positions: state.people.person[Person.anime_staff_positions],
    user_anime_entry_statuses: state.users.user_anime_entry_statuses,
    anime_loading: state.anime.anime_loading,
});

export default connect(mapStateToProps, {
    getAnimeStaffPositionsForPerson, getLoggedInUserAnimeEntryStatuses
})(AnimeStaffPositionsTab)
