import React, {Component} from 'react'
import {connect} from 'react-redux'

import {
    ANIME_AUDIO_CHANNELS, ANIME_GENRES, ANIME_ORDERINGS,
    ANIME_RATING_SHORT,
    ANIME_STATUSES,
    ANIME_TYPES,
    ANIME_VIDEO_QUALITIES,
    BRANCH_ANIME, FILTER_ANIME_INITIAL_VALUES, USER_ANIME_ORDERINGS, USER_ANIME_STATUSES, USER_ANIME_STATUSES_ARRAY
} from "../../../../globals";
import TitlesFilter from "../TitlesFilter";
import {FilterLabels, AnimeFilterNames, FilterSchemaType} from "../field_names";
import PropTypes from "prop-types";
import {User} from "../../../../models";
import {isEmpty} from "../../../functions";
import {getAnimeFilterPresets} from "../../../../actions/users";
import store from "../../../../store";


class AnimeFilters extends Component {
    static propTypes = {
        /** Season object {year: season:} */
        season: PropTypes.object,
        /** User List object, {pk: selected_filter:} */
        list: PropTypes.object,
    };

    componentDidMount() {
        store.dispatch(getAnimeFilterPresets())
    }

    render() {
        const main_filters = [
            {'name': AnimeFilterNames.downloaded, 'label': FilterLabels.downloaded, 'color': 'secondary'},
            {'name': AnimeFilterNames.is_downloading, 'label': FilterLabels.is_downloading, 'color': 'default'},
            {'name': AnimeFilterNames.cannot_download, 'label': FilterLabels.cannot_download, 'color': 'danger'},
            {'name': AnimeFilterNames.deleted_by_MAL, 'label': FilterLabels.deleted_by_MAL, 'color': 'danger'},
            {'name': AnimeFilterNames.pending_by_MAL, 'label': FilterLabels.pending_by_MAL, 'color': 'warning'},
        ];
        const episode_marks = [
            {
                value: 1,
                label: '1',
            },
            {
                value: 12,
                label: '12',
            },
            {
                value: 24,
                label: '24',
            },
            {
                value: 36,
                label: '36',
            },
            {
                value: 53,
                label: '52+',
            },
        ];
        const length_marks = [
            {
                value: 1,
                label: '1',
            },
            {
                value: 12,
                label: '12',
            },
            {
                value: 24,
                label: '24',
            },
            {
                value: 30,
                label: '30',
            },
            {
                value: 61,
                label: '60+',
            },
        ];
        const initial = new FILTER_ANIME_INITIAL_VALUES();
        const values = initial.VALUES;
        let regular_filters = [
            {'name': AnimeFilterNames.type, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': ANIME_TYPES},
            {'name': AnimeFilterNames.status, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': ANIME_STATUSES},
            {'name': AnimeFilterNames.rating_short, 'type': FilterSchemaType.standard, 'color': 'primary', 'content': ANIME_RATING_SHORT},
            {'name': AnimeFilterNames.episodes_range, 'type': FilterSchemaType.slider, 'marks': episode_marks, 'min': values.episodes_range[0], 'max': values.episodes_range[1], 'color': 'primary', 'content': null},
            {'name': AnimeFilterNames.length_range, 'type': FilterSchemaType.slider, 'marks': length_marks, 'min': values.length_range[0], 'max': values.length_range[1], 'color': 'primary', 'content': null},
            {'name': AnimeFilterNames.video_quality, 'type': FilterSchemaType.standard, 'color': 'secondary', 'content': ANIME_VIDEO_QUALITIES},
            {'name': AnimeFilterNames.audio_channel, 'type': FilterSchemaType.standard, 'color': 'secondary', 'content': ANIME_AUDIO_CHANNELS},
        ];

        let wide_filters = [
            {'name': AnimeFilterNames.genres, 'type': FilterSchemaType.wide, 'color': 'primary', 'content': ANIME_GENRES},
        ];

        let orderings = ANIME_ORDERINGS;
        if (!isEmpty(this.props.list)) {
            orderings = orderings.concat(USER_ANIME_ORDERINGS);
        } else {
            if (this.props.user_authenticated)
                regular_filters.push(
                    {'name': AnimeFilterNames.user_exclude, 'type': FilterSchemaType.standard, 'color': 'info', 'content': USER_ANIME_STATUSES_ARRAY},
                );
        }

        let anime_filters = this.props.anime_browse_filters;
        if (!isEmpty(this.props.season))
            anime_filters = this.props.anime_season_filters;
        else if (!isEmpty(this.props.list))
            anime_filters = this.props.anime_list_filters;
        
        return (
            <TitlesFilter
                branch={BRANCH_ANIME}
                titles_loading={this.props.anime_loading}
                filtered_titles_loaded={this.props.filtered_anime_loaded}
                filtered_titles={this.props.filtered_anime}
                title_filters={anime_filters}
                season={this.props.season}
                list={this.props.list}
                main_filters={main_filters}
                regular_filters={regular_filters}
                wide_filters={wide_filters}
                order_filters={orderings}
                presets={this.props.anime_filter_presets}
            />
        )
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated,
    anime_loading: state.anime.anime_loading,
    filtered_anime_loaded: state.anime.filtered_anime_loaded,
    filtered_anime: state.anime.filtered_anime,
    anime_browse_filters: state.cookies.anime_browse_filters,
    anime_season_filters: state.cookies.anime_season_filters,
    anime_list_filters: state.cookies.anime_list_filters,
    anime_filter_presets: state.users.anime_filter_presets,
});

export default connect(mapStateToProps, {})(AnimeFilters)
