import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Link as RouterLink} from 'react-router-dom';

import TextField from '@material-ui/core/TextField';
import {Form, Formik} from 'formik';

import {LoginSchema} from "./functions";
import {login} from '../../actions/auth'
import {REGISTER} from "../../urls";
import {CyberButton} from "../layout/common/common";
import {playClickSound, playHoverSound, playWarningSound} from "../layout/sounds";
import CircularProgress from "@material-ui/core/CircularProgress";


class LoginForm extends Component {
    constructor() {
        super(...arguments);
        this.state = {};
    }

    render() {
        const field_names = {
            username: 'username',
            password: 'password'
        };

        const initial_values = {
            username: '',
            password: ''
        };

        return (
            <Formik
                initialValues={initial_values}
                validationSchema={LoginSchema}
                onSubmit={(values, {setSubmitting}) => {
                    this.props.login(values.username, values.password);
                    setSubmitting(false);
                }}>
                {({errors, touched, values, handleChange, handleBlur, setSubmitting}) => (
                    !this.props.user_loading ? <Form id="Login-Form">
                        <TextField
                            label="Username"
                            className="form-control"
                            value={values.username}
                            name={field_names.username}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={errors.username && touched.username}
                            helperText={(errors.username && touched.username) && errors.username}/>
                        <TextField
                            label="Password"
                            type="password"
                            className="form-control"
                            name={field_names.password}
                            value={values.password}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={errors.password && touched.password}
                            helperText={(errors.password && touched.password) && errors.password}/>
                        <CyberButton type="submit" onClick={() => {
                            if (errors.password || errors.username)
                                playWarningSound()
                        }}>
                            Login
                        </CyberButton>
                        <p>Don't have an account? <RouterLink to={REGISTER} onClick={playClickSound}
                                                              onMouseOver={playHoverSound}>Register</RouterLink></p>
                    </Form> : <div className="loading"><CircularProgress/></div>
                )}
            </Formik>
        )
    }
}

const mapStateToProps = state => ({
    user_loading: state.auth.user_loading,
});

export default connect(mapStateToProps, {login})(LoginForm)
