import {
    AUTH_ERROR_CLEAR_USER_INFO,
    CANCEL_USER_ACHIEVEMENTS_LOADING,
    CANCEL_USER_LIST_LOADING,
    CLEAR_USER_ACHIEVEMENTS,
    CLEAR_USER_ANIME_ENTRY,
    CLEAR_USER_MANGA_ENTRY,
    FINISH_LOADING_USER_ENTRY,
    FINISH_UPLOADING_FILE,
    GET_ALL_USERS,
    GET_ANIME_FILTER_PRESETS,
    GET_EROGE_FILTER_PRESETS,
    GET_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
    GET_LOGGED_IN_USER_MANGA_ENTRY_STATUSES,
    GET_MANGA_FILTER_PRESETS, GET_PROFILE_PRIVACY_SETTINGS,
    GET_USER_ACHIEVEMENTS,
    GET_USER_ANIME_ENTRY,
    GET_USER_EPISODE_HISTORY_SCRAPER_PROGRESS,
    GET_USER_LIST, GET_USER_LIST_PRIVACY_SETTINGS,
    GET_USER_MANGA_ENTRY,
    GET_VISITED_USER_ANIME_HISTORY,
    GET_VISITED_USER_MANGA_HISTORY,
    SET_ANIME_PRESET,
    SET_EROGE_PRESET,
    SET_MANGA_PRESET,
    UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES,
    UPDATE_LOGGED_IN_USER_MANGA_ENTRY_STATUSES,
    UPDATE_USER_ANIME_STATISTICS,
    UPDATE_USER_LIST,
    UPLOAD_ANIME_LIST_MAL_XML, UPLOAD_MANGA_LIST_MAL_XML,
    UPLOADING_FILE,
    USER_ACHIEVEMENTS_LOADING,
    USER_ENTRY_LOADING,
    USER_INFO_LOADING,
    USER_LIST_LOADING,
    USER_STATISTICS_LOADING,
    USERS_LOADING
} from "../actions/types";
import {Anime, Manga, UserAnimeEntry, UserList, UserMangaEntry} from "../models";


const initialState = {
    all_users: [],
    users_loading: false,
    all_users_loaded: false,

    entry_loading: false,
    user_anime_entry: {},
    user_anime_entry_loaded: false,
    user_manga_entry: {},
    user_manga_entry_loaded: false,

    uploading_file: false,

    user_list: {},
    user_list_loading: false,
    user_statistics_loading: false,
    user_anime_statistics: {},
    user_manga_statistics: {},

    user_info_loading: false,

    user_anime_history: {},
    user_manga_history: {},

    anime_filter_presets: [],
    manga_filter_presets: [],
    eroge_filter_presets: [],
    selected_anime_preset: {},
    selected_manga_preset: {},
    selected_eroge_preset: {},

    user_anime_entry_statuses: {},
    user_manga_entry_statuses: {},

    user_achievements_loading: false,
    user_achievements: {},
};

export default function (state = initialState, action) {
    switch (action.type) {
        case USERS_LOADING:
            return {
                ...state,
                users_loading: true,
                all_users_loaded: false
            };
        case USER_LIST_LOADING:
            return {
                ...state,
                user_list_loading: true,
            };
        case CANCEL_USER_LIST_LOADING:
            return {
                ...state,
                user_list_loading: false,
            };
        case GET_ALL_USERS:
            return {
                ...state,
                all_users: action.payload,
                users_loading: false,
                all_users_loaded: true
            };
        case USER_ENTRY_LOADING:
            return {
                ...state,
                entry_loading: true,
                user_anime_entry_loaded: false,
                user_manga_entry_loaded: false
            };
        case USER_STATISTICS_LOADING:
            return {
                ...state,
                user_statistics_loading: true,
            };
        case FINISH_LOADING_USER_ENTRY:
            return {
                ...state,
                entry_loading: false,
            };
        case GET_USER_ANIME_ENTRY:
            return {
                ...state,
                entry_loading: false,
                user_anime_entry_loaded: true,
                user_anime_entry: action.payload
            };
        case CLEAR_USER_ANIME_ENTRY:
            return {
                ...state,
                entry_loading: false,
                user_anime_entry_loaded: false,
                user_anime_entry: {}
            };
        case GET_USER_MANGA_ENTRY:
            return {
                ...state,
                entry_loading: false,
                user_manga_entry_loaded: true,
                user_manga_entry: action.payload
            };
        case CLEAR_USER_MANGA_ENTRY:
            return {
                ...state,
                entry_loading: false,
                user_manga_entry_loaded: false,
                user_manga_entry: {}
            };
        case UPDATE_USER_LIST:
        case GET_USER_LIST:
            return {
                ...state,
                user_list: action.payload,
                user_list_loading: false,
            };
        case UPDATE_USER_ANIME_STATISTICS:
            return {
                ...state,
                user_statistics_loading: false,
                user_anime_statistics: action.payload,
            };
        case UPLOADING_FILE:
            return {
                ...state,
                uploading_file: true
            };
        case FINISH_UPLOADING_FILE:
            return {
                ...state,
                uploading_file: false
            };
        case UPLOAD_ANIME_LIST_MAL_XML:
        case UPLOAD_MANGA_LIST_MAL_XML:
            return {
                ...state,
                uploading_file: false
            };
        case USER_INFO_LOADING:
            return {
                ...state,
                user_info_loading: true
            };
        case GET_VISITED_USER_ANIME_HISTORY:
            return {
                ...state,
                user_anime_history: action.payload,
                user_info_loading: false
            };
        case GET_VISITED_USER_MANGA_HISTORY:
            return {
                ...state,
                user_manga_history: action.payload,
                user_info_loading: false
            };
        case GET_ANIME_FILTER_PRESETS:
            return {
                ...state,
                anime_filter_presets: action.payload,
            };
        case GET_MANGA_FILTER_PRESETS:
            return {
                ...state,
                manga_filter_presets: action.payload,
            };
        case GET_EROGE_FILTER_PRESETS:
            return {
                ...state,
                eroge_filter_presets: action.payload,
            };
        case SET_ANIME_PRESET:
            return {
                ...state,
                selected_anime_preset: action.payload,
            };
        case SET_MANGA_PRESET:
            return {
                ...state,
                selected_manga_preset: action.payload,
            };
        case SET_EROGE_PRESET:
            return {
                ...state,
                selected_eroge_preset: action.payload,
            };
        case GET_LOGGED_IN_USER_ANIME_ENTRY_STATUSES:
            return {
                ...state,
                user_anime_entry_statuses: action.payload,
            };
        case UPDATE_LOGGED_IN_USER_ANIME_ENTRY_STATUSES:
            let current_statuses = {};
            if (typeof action.payload === 'object' && action.payload !== null) {
                Object.assign(current_statuses, state.user_anime_entry_statuses);
                current_statuses[action.payload[UserAnimeEntry.anime][Anime.id]] = action.payload[UserAnimeEntry.status];
            } else if (!isNaN(action.payload)) {
                delete state.user_anime_entry_statuses[action.payload];
                Object.assign(current_statuses, state.user_anime_entry_statuses);
            }
            return {
                ...state,
                user_anime_entry_statuses: current_statuses,
            };
        case GET_LOGGED_IN_USER_MANGA_ENTRY_STATUSES:
            return {
                ...state,
                user_manga_entry_statuses: action.payload,
            };
        case UPDATE_LOGGED_IN_USER_MANGA_ENTRY_STATUSES:
            let current_manga_statuses = {};
            if (typeof action.payload === 'object' && action.payload !== null) {
                Object.assign(current_manga_statuses, state.user_manga_entry_statuses);
                current_manga_statuses[action.payload[UserMangaEntry.manga][Manga.id]] = action.payload[UserMangaEntry.status];
            } else if (!isNaN(action.payload)) {
                delete state.user_manga_entry_statuses[action.payload];
                Object.assign(current_manga_statuses, state.user_manga_entry_statuses);
            }
            return {
                ...state,
                user_manga_entry_statuses: current_manga_statuses,
            };
        case GET_USER_EPISODE_HISTORY_SCRAPER_PROGRESS:
            let this_user_list = state.user_list;
            this_user_list[UserList.scraping_episode_history_from_mal_progress] = action.payload;
            return {
                ...state,
                user_list: this_user_list,
            };
        case USER_ACHIEVEMENTS_LOADING:
            return {
                ...state,
                user_achievements_loading: true
            };
        case CANCEL_USER_ACHIEVEMENTS_LOADING:
            return {
                ...state,
                user_achievements_loading: false,
                user_achievements: {}
            };
        case GET_USER_ACHIEVEMENTS:
            return {
                ...state,
                user_achievements_loading: false,
                user_achievements: action.payload
            };
        case CLEAR_USER_ACHIEVEMENTS:
            return {
                ...state,
                user_achievements_loading: false,
                user_achievements: {}
            };
        case AUTH_ERROR_CLEAR_USER_INFO:
            return {
                ...state,
                current_anime_entry: {},
                current_manga_entry: {},
                user_anime_entry_statuses: {},
                user_manga_entry_statuses: {},
            };
        default:
            return state;
    }
}
