import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {playClickSound, playHoverSound} from "../../../../layout/sounds";
import BBCodeEditorTab from "./BBCodeEditorTab";
import BackgroundEditorTab from "./BackgroundEditorTab";


function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Material UI tabs. Supply it with tab information to render the tabs.
 * @param props.tabs {Object} Array of tab information.
 * @returns {JSX.Element}
 * @constructor
 */
export function BBCodeEditorTabs(props) {
    const {bbcode, background_image, updateEditor, updateBackgroundImage, submitAboutMe} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root + ' tabs-root'}>
            <AppBar position="static" color="primary" className="cyber-tabs">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    textColor="default"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    <Tab label="bbCode" {...a11yProps(0)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                    <Tab label="Background" {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel value={value} index={0} dir={theme.direction}>
                    <BBCodeEditorTab
                        bbcode={bbcode}
                        background_image={background_image}
                        updateEditor={updateEditor}
                        updateBackgroundImage={updateBackgroundImage}
                        submitAboutMe={submitAboutMe}
                    />
                </TabPanel>
                <TabPanel value={value} index={1} dir={theme.direction}>
                    <BackgroundEditorTab
                        background_image={background_image}
                        updateBackgroundImage={updateBackgroundImage}
                        submitAboutMe={submitAboutMe}
                    />
                </TabPanel>
            </SwipeableViews>
        </div>
    );
}