import {BRANCH_ANIME, BRANCH_MANGA} from "../../globals";
import store from "../../store";
import {getAnimeData} from "../../actions/anime";
import {getMangaData} from "../../actions/manga";
import {AnimeData, MangaData} from "../../models";
import {ANIME_SEASON_DISPLAY, HOME, MANGA_SEASON_DISPLAY} from "../../urls";
import AnimeBrowse from "../anime/browse/AnimeBrowse";
import React from "react";
import MangaBrowse from "../manga/browse/MangaBrowse";
import {faLeaf, faSeedling, faSnowflake, faSun, faTimes} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";


export function getSeasonIcon(season) {
    switch (season) {
        case 1:
            return <FontAwesomeIcon icon={faSeedling} className="spring"/>;
        case 2:
            return <FontAwesomeIcon icon={faSun} className="summer"/>;
        case 3:
            return <FontAwesomeIcon icon={faLeaf} className="fall"/>;
        case 4:
            return <FontAwesomeIcon icon={faSnowflake} className="winter"/>;
        default:
            return <FontAwesomeIcon icon={faTimes} className="winter"/>;
    }
}


export function getTitleData(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            // noinspection JSCheckFunctionSignatures
            store.dispatch(getAnimeData());
            return;
        case BRANCH_MANGA:
            // noinspection JSCheckFunctionSignatures
            store.dispatch(getMangaData());
            return;
        default:
            return
    }
}


export function browseSeasonTitlesFromBranch(branch, year, season) {
    switch (branch) {
        case BRANCH_ANIME:
            return <AnimeBrowse season={{year: year, season: season}}/>;
        case BRANCH_MANGA:
            return <MangaBrowse season={{year: year, season: season}}/>;
        default:
            return
    }
}


export function getPrevSeasonUrl(branch, year, season) {
    let prev_year = year;
    let prev_season = season - 1;
    if (prev_season === 0) {
        prev_season = 4;
        prev_year = year - 1
    }
    switch (branch) {
        case BRANCH_ANIME:
            return ANIME_SEASON_DISPLAY(prev_year, prev_season);
        case BRANCH_MANGA:
            return MANGA_SEASON_DISPLAY(prev_year, prev_season);
        default:
            return
    }
}


export function getNextSeasonUrl(branch, year, season) {
    let next_year = year;
    let next_season = season + 1;
    if (next_season === 5) {
        next_season = 1;
        next_year = year + 1
    }
    switch (branch) {
        case BRANCH_ANIME:
            return ANIME_SEASON_DISPLAY(next_year, next_season);
        case BRANCH_MANGA:
            return MANGA_SEASON_DISPLAY(next_year, next_season);
        default:
            return
    }
}

export function formatData(branch, data) {
    let display_data = {
        total_titles_per_season_json: null,
    };

    switch (branch) {
        case BRANCH_ANIME:
            display_data.total_titles_per_season_json = data[AnimeData.total_anime_per_season_json];
            return display_data;
        case BRANCH_MANGA:
            display_data.total_titles_per_season_json = data[MangaData.total_manga_per_season_json];
            return display_data;
        default:
            return display_data;
    }
}

export function getSeasonUrl(branch, year, season) {
    switch (branch) {
        case BRANCH_ANIME:
            return ANIME_SEASON_DISPLAY(year, season);
        case BRANCH_MANGA:
            return MANGA_SEASON_DISPLAY(year, season);
        default:
            return HOME;
    }
}
