import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_MANGA} from "../../../../globals";
import UserList from "../UserList";
import Loading from "../../../layout/common/Loading";
import {Profile, User} from "../../../../models";
import {getUser} from "../../../../actions/auth";
import {PrivatePage} from "../PrivateList";


class MangaList extends Component {
    componentDidMount() {
        this.props.getUser(parseInt(this.props.match.params.id));
    }

    render() {
        let list_owner = false;
        if (this.props.visited_user && this.props.user && this.props.visited_user[User.id] === this.props.user[User.id])
            list_owner = true;

        return this.props.user_loading ? <Loading/> : this.props.visited_user_loaded ?
        list_owner || !this.props.visited_user[User.profile][Profile.is_manga_list_private] ?
            <UserList
                branch={BRANCH_MANGA}
                params={this.props.match.params}
                list_owner={list_owner}
                titles_loading={this.props.manga_loading}
                filtered_titles_loaded={this.props.filtered_manga_loaded}
                filtered_titles={this.props.filtered_manga}
                title_filters={this.props.manga_filters}
            /> : <PrivatePage text={`${this.props.visited_user[User.username]}'s ${BRANCH_MANGA} List is private.`}/> : null
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    manga_loading: state.manga.manga_loading,
    filtered_manga_loaded: state.manga.filtered_manga_loaded,
    filtered_manga: state.manga.filtered_manga,
    manga_filters: state.cookies.manga_list_filters
});

export default connect(mapStateToProps, {getUser})(MangaList)
