import moment from "moment";
import {
    faArrowDown,
    faArrowUp,
    faBan,
    faClock, faDownload, faHdd, faInfinity,
    faPause, faPuzzlePiece,
    faSpinner, faUpload, faUsers
} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React from "react";
import LinearProgress from "@material-ui/core/LinearProgress";


export function getBytesToSize(size) {
    const terabyte = 1099511627776;
    const gigabyte = 1073741824;
    const megabyte = 1048576;

    if (size >= 10995116277760)  // More than 10TB
        return `${(size / terabyte).toFixed(1)}TB`;
    else if (size >= 1099511627776)  // More than 1TB
        return `${(size / terabyte).toFixed(2)}TB`;
    else if (size >= 107374182400) // More than 100GB
        return `${Math.ceil(size / gigabyte)}GB`;
    else if (size >= 10737418240) // More than 10GB
        return `${(size / gigabyte).toFixed(1)}GB`;
    else if (size >= 1073741824) // More than 1GB
        return `${(size / gigabyte).toFixed(2)}GB`;
    else  // Less then 1GB
        return `${Math.ceil(size / megabyte)}MB`;
}


export function getTime(sec) {
    let hrs = Math.floor(sec / 3600);
    let min = Math.floor((sec - (hrs * 3600)) / 60);
    let seconds = sec - (hrs * 3600) - (min * 60);
    seconds = Math.round(seconds * 100) / 100;

    let result = (hrs < 10 ? "0" + hrs : hrs);
    result += ":" + (min < 10 ? "0" + min : min);
    result += ":" + (seconds < 10 ? "0" + seconds : seconds);
    return result;
}


export function getProgress(progress) {
    return (progress * 100).toFixed(1);
}


export function getSpeed(speed) {
    if (speed < 1000000) return `${(speed / 1024.0).toFixed(1)}Kb/s`;
    else return `${(speed / (1024 * 1024.0)).toFixed(1)}Mb/s`;
}


/**
 *
 * @param props.torrent {Object}
 * @param props.filters {Object}
 * @returns {JSX.Element}
 * @constructor
 */
export function TorrentProgressInfo(props) {
    const {torrent, filters} = props;
    let days = parseInt(moment.utc(torrent['eta'] * 1000).format('DDD')) - 1;
    let hours = parseInt(moment.utc(torrent['eta'] * 1000).format('HH'));
    let minutes = parseInt(moment.utc(torrent['eta'] * 1000).format('mm'));
    let eta_str = '';
    let status_icon;

    if (days > 0) eta_str += `${days}d `;
    if (hours > 0) eta_str += `${hours}h `;
    if (minutes > 0) eta_str += `${minutes}m`;

    if (torrent['torrent_status'].indexOf('downloading') >= 0)
        status_icon = <FontAwesomeIcon icon={faArrowDown}/>;
    else if (torrent['torrent_status'].indexOf('forcedDL') >= 0)
        status_icon = <FontAwesomeIcon icon={faArrowDown}/>;
    else if (torrent['torrent_status'].indexOf('upload') >= 0)
        status_icon = <FontAwesomeIcon icon={faArrowUp}/>;
    else if (torrent['torrent_status'].indexOf('pause') >= 0)
        status_icon = <FontAwesomeIcon icon={faPause}/>;
    else if (torrent['torrent_status'].indexOf('check') >= 0)
        status_icon = <FontAwesomeIcon icon={faSpinner} title="Checking"/>;
    else if (torrent['torrent_status'].indexOf('stall') >= 0)
        status_icon = <FontAwesomeIcon icon={faBan} title="Stalled"/>;
    else if (torrent['torrent_status'].indexOf('queued') >= 0)
        status_icon = <FontAwesomeIcon icon={faClock} title="Queued"/>;
    else
        status_icon = <span className="margin-left-10">{torrent['torrent_status']}</span>;

    return filters['name'] ?
        <tbody className="torrent-info-container">
        <tr>
            <td>
                {torrent['name'].replace('[NotAdded]', '')}
            </td>
            {filters['size'] ? <td>
                <FontAwesomeIcon icon={faHdd}/>
                <span className="color-secondary">{getBytesToSize(torrent['size'])}</span>
            </td> : null}
            <td>
                {status_icon} <span className="color-secondary">{getProgress(torrent['progress'])}%</span>
            </td>
            {filters['dl_speed'] ? <td>
                            <span><FontAwesomeIcon icon={faDownload}/>{' '}
                                <span className={torrent['dl_speed'] > 0 ? 'color-secondary' : null}>
                                {getSpeed(torrent['dl_speed'])}</span></span>
            </td> : null}
            {filters['up_speed'] ? <td>
                            <span><FontAwesomeIcon icon={faUpload}/>{' '}
                                <span className={torrent['up_speed'] > 0 ? 'color-secondary' : null}>
                                {getSpeed(torrent['up_speed'])}</span></span>
            </td> : null}
            {filters['remaining'] ? <td>
                <FontAwesomeIcon icon={faPuzzlePiece} title="Remaining"/>{' '}
                <span className="color-secondary">{getBytesToSize(torrent['amount_left'])}</span>
            </td> : null}
            {filters['seeds'] ? <td>
                <FontAwesomeIcon icon={faUsers} className="color-green"/>{' '}
                <span className="color-secondary">{torrent['num_seeds']}</span>
            </td> : null}
            {filters['leechs'] ? <td>
                <FontAwesomeIcon icon={faUsers} className="color-danger"/>{' '}
                <span className="color-secondary">{torrent['num_leechs']}</span>
            </td> : null}
            {filters['eta'] ? <td>
                <FontAwesomeIcon icon={faClock}/>{' '}
                <span className="color-secondary">{torrent['eta'] < 8640000 ? eta_str :
                    <FontAwesomeIcon icon={faInfinity}/>}</span>
            </td> : null}
        </tr>
        <tr>
            <td className="padding-bottom-10" colSpan="20">
                <LinearProgress variant="determinate" color="secondary" value={getProgress(torrent['progress'])}/>
            </td>
        </tr>
        </tbody> :
        <div className="torrent-info-container">
            <div>
                <p className="color-secondary-dark">
                    {filters['name'] ? <span className="torrent-info">
                    {torrent['name'].replace('[NotAdded]', '')}
                </span> : null}

                    {filters['size'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faHdd}/>{' '}
                        <span>{getBytesToSize(torrent['size'])}</span>
                </span> : null}

                    <span className="torrent-info">{status_icon} {getProgress(torrent['progress'])}%</span>

                    {filters['dl_speed'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faDownload}/>{' '}
                        <span className={torrent['dl_speed'] > 0 ? 'color-secondary' : null}>
                        {getSpeed(torrent['dl_speed'])}</span>
                </span> : null}

                    {filters['up_speed'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faUpload}/>{' '}
                        <span className={torrent['up_speed'] > 0 ? 'color-secondary' : null}>
                        {getSpeed(torrent['up_speed'])}</span>
                </span> : null}

                    {filters['remaining'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faPuzzlePiece} title="Remaining"/>{' '}
                        <span>{getBytesToSize(torrent['amount_left'])}</span>
                </span> : null}

                    {filters['seeds'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faUsers} className="color-green"/>{' '}
                        <span className="color-secondary">{torrent['num_seeds']}</span>
                </span> : null}
                    {filters['leechs'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faUsers} className="color-danger"/>{' '}
                        <span className="color-secondary">{torrent['num_leechs']}</span>
                </span> : null}

                    {filters['eta'] ? <span className="torrent-info">
                        <FontAwesomeIcon icon={faClock}/>{' '}
                        <span className="color-secondary">{torrent['eta'] < 8640000 ? eta_str :
                            <FontAwesomeIcon icon={faInfinity}/>}</span>&nbsp;
                </span> : null}
                </p>
            </div>
            <LinearProgress variant="determinate" color="secondary" value={getProgress(torrent['progress'])}/>
        </div>;
}