import {
    AUTH_USER,
    USER_LOADING,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    GET_USER, GET_PROFILE_PRIVACY_SETTINGS
} from "../actions/types";
import {playExpand2Sound} from "../components/layout/sounds";
import {Profile, User} from "../models";


const initialState = {
    token: localStorage.getItem('token'),
    initial_auth: true,
    user_loading: false,
    user: null,
    user_authenticated: null,
    visited_user_loaded: false,
    visited_user: null,
};


export default function (state = initialState, action) {
    switch (action.type) {
        case USER_LOADING:
            return {
                ...state,
                user_loading: true
            };
        case GET_USER:
            return {
                ...state,
                user_loading: false,
                visited_user_loaded: true,
                visited_user: action.payload,
            };
        case AUTH_USER:
            return {
                ...state,
                user_authenticated: true,
                user_loading: false,
                user: action.payload,
                initial_auth: false,
            };
        case GET_PROFILE_PRIVACY_SETTINGS:
            let updated_user = {};
            Object.assign(updated_user, state.user);
            updated_user[User.profile][Profile.is_anime_list_private] = action.payload[Profile.is_anime_list_private];
            updated_user[User.profile][Profile.is_anime_history_private] = action.payload[Profile.is_anime_history_private];
            updated_user[User.profile][Profile.is_manga_list_private] = action.payload[Profile.is_manga_list_private];
            updated_user[User.profile][Profile.is_manga_history_private] = action.payload[Profile.is_manga_history_private];
            return {
                ...state,
                user_loading: false,
                user: updated_user,
            };
        case LOGIN_SUCCESS:
        case REGISTER_SUCCESS:
            playExpand2Sound();
            localStorage.setItem('token', action.payload.token);
            return {
                ...state,
                ...action.payload,
                user_authenticated: true,
                user_loading: false,
            };
        case AUTH_ERROR:
        case LOGIN_FAIL:
        case LOGOUT_SUCCESS:
        case REGISTER_FAIL:
            localStorage.removeItem('token');
            return {
                ...state,
                token: null,
                user: null,
                user_authenticated: false,
                user_loading: false,
                initial_auth: false,
            };
        default:
            return state;
    }
}
