import React, {Component} from 'react'
import {connect} from 'react-redux'

import Chart from 'chart.js';
import {Line, Doughnut} from 'react-chartjs-2';

import {Character, Profile, User, UserList} from "../../../../models";
import {CyberLink, PreLoadImage} from "../../../layout/common/common";
import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faCircle, faPause, faPlay, faStar, faStop} from "@fortawesome/free-solid-svg-icons";
import CountUp from "react-countup";
import {faCalendarAlt} from "@fortawesome/free-regular-svg-icons";
import {
    BRANCH_ANIME,
    BRANCH_MANGA,
    COLOR_DANGER,
    COLOR_GRAY,
    COLOR_INFO,
    COLOR_PRIMARY,
    COLOR_WARNING
} from "../../../../globals";
import Moment from "react-moment";
import {Button} from "@material-ui/core";
import {USER_ANIME_HISTORY, USER_ANIME_STATISTICS, USER_MANGA_HISTORY} from "../../../../urls";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import TitleCard from "../../../layout/cards/TitleCard";
import GridList from "@material-ui/core/GridList";
import {MangaSimpleChartStats} from "../../statistics/statistics_snippets";


class MangaTab extends Component {
    static propTypes = {};

    constructor() {
        super(...arguments);
        this.state = {show: true};
    }

    render() {
        Chart.defaults.global.legend.display = false;

        let raw = [
            {label: 'Reading', stat: this.props.user_list[UserList.manga_reading], color: '', hoverColor: ''},
            {label: 'Completed', stat: this.props.user_list[UserList.manga_completed], color: '', hoverColor: ''},
            {label: 'On Hold', stat: this.props.user_list[UserList.manga_on_hold], color: '', hoverColor: ''},
            {label: 'Dropped', stat: this.props.user_list[UserList.manga_dropped], color: '', hoverColor: ''},
            {label: 'Planned', stat: this.props.user_list[UserList.manga_plan_to_read], color: '', hoverColor: ''},
        ];

        let stat_data = [];
        let labels = [];
        for (let x = 0; x < raw.length; x++) {
            stat_data.push(raw[x].stat);
            labels.push(raw[x].label);
        }

        let data = {
            labels: labels,
            legend: {
                display: false,
            },
            datasets: [{
                data: stat_data,
                backgroundColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.4)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.4)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.5)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.2)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.4)',
                ],
                hoverBackgroundColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.8)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.8)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.8)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.8)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.8)',
                ],
                borderColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 0.3)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 0.3)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 0.3)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 0.1)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 0.3)',
                ],
                hoverBorderColor: [
                    // COLOR_INFO,
                    'rgba(21, 255, 255, 1)',
                    // COLOR_PRIMARY,
                    'rgba(21, 107, 183, 1)',
                    // COLOR_WARNING,
                    'rgba(208, 156, 0, 1)',
                    // COLOR_DANGER,
                    'rgba(255, 0, 0, 1)',
                    // COLOR_GRAY,
                    'rgba(123, 123, 123, 1)',
                ],
                // borderColor: 'rgba(255, 255, 255, 0.9)',
                // hoverBorderColor: '#156bb7',
                borderWidth: 0
            }]
        };


        const line_data = {
            labels: ['1', '2', '3', '4', '5', '6', '7'],
            datasets: [
                {
                    label: 'Chapters',
                    data: [12, 17, 9, 6, 8, 13, 14],
                    fill: false,
                    backgroundColor: 'rgb(255, 99, 132)',
                    borderColor: 'rgba(255, 99, 132, 0.2)',
                },
            ],
        };

        const options = {
            scales: {
                yAxes: [
                    {
                        ticks: {
                            beginAtZero: true,
                        },
                    },
                ],
            },
        };

        return (
            <Grid container>
                <Grid item xs={5}>
                    <MangaSimpleChartStats/>
                </Grid>
                {/*<Grid item xs={5}>*/}
                {/*    <h2>Stats</h2>*/}
                {/*</Grid>*/}
                {/*<Grid item xs={7}>*/}
                {/*    <h2>Recent</h2>*/}
                {/*</Grid>*/}
                {/*<Grid item xs={3} className="pie-container">*/}
                {/*    <div className="pie-wrapper">*/}
                {/*        <Doughnut height={100} width={100} data={data}/>*/}
                {/*    </div>*/}
                {/*</Grid>*/}
                {/*<Grid item xs={2} className="User-List-Stats-Container">*/}
                {/*    <Grid container*/}
                {/*          direction="column"*/}
                {/*          justify="center"*/}
                {/*          alignItems="flex-start"*/}
                {/*          className="chip-container"*/}
                {/*    >*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip className="border-info color-info"*/}
                {/*                  variant="outlined"*/}
                {/*                  label={<>*/}
                {/*                      <FontAwesomeIcon className="list-icon" icon={faPlay}/>*/}
                {/*                      <span>Reading*/}
                {/*                          <CountUp end={this.props.user_list[UserList.manga_reading]}/>*/}
                {/*                      </span>*/}
                {/*                  </>}/>*/}
                {/*        </div>*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip*/}
                {/*                className="border-primary color-primary"*/}
                {/*                variant="outlined"*/}
                {/*                label={<>*/}
                {/*                    <FontAwesomeIcon className="list-icon" icon={faCheck}/>*/}
                {/*                    <span>Completed*/}
                {/*                        <CountUp end={this.props.user_list[UserList.manga_completed]}/>*/}
                {/*                    </span>*/}
                {/*                </>}/>*/}
                {/*        </div>*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip*/}
                {/*                className="border-warning color-warning"*/}
                {/*                variant="outlined"*/}
                {/*                label={<>*/}
                {/*                    <FontAwesomeIcon className="list-icon" icon={faPause}/>*/}
                {/*                    <span>On Hold<CountUp end={this.props.user_list[UserList.manga_on_hold]}/>*/}
                {/*                    </span>*/}
                {/*                </>}/>*/}
                {/*        </div>*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip*/}
                {/*                className="border-danger color-danger"*/}
                {/*                variant="outlined"*/}
                {/*                label={<>*/}
                {/*                    <FontAwesomeIcon className="list-icon" icon={faStop}/>*/}
                {/*                    <span>Dropped<CountUp end={this.props.user_list[UserList.manga_dropped]}/>*/}
                {/*                    </span>*/}
                {/*                </>}/>*/}
                {/*        </div>*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip*/}
                {/*                className="border-gray color-gray"*/}
                {/*                variant="outlined"*/}
                {/*                label={<>*/}
                {/*                    <FontAwesomeIcon className="list-icon" icon={faCalendarAlt}/>*/}
                {/*                    <span>Planned<CountUp end={this.props.user_list[UserList.manga_plan_to_read]}/>*/}
                {/*                    </span>*/}
                {/*                </>}/>*/}
                {/*        </div>*/}
                {/*        <div className="stat-chip">*/}
                {/*            <Chip*/}
                {/*                className="border-white color-white"*/}
                {/*                variant="outlined"*/}
                {/*                label={<>*/}
                {/*                    <FontAwesomeIcon className="list-icon" icon={faCircle}/>*/}
                {/*                    <span>Total<CountUp end={this.props.user_list[UserList.manga_total_entries]}/>*/}
                {/*                    </span>*/}
                {/*                </>}/>*/}
                {/*        </div>*/}
                {/*    </Grid>*/}
                {/*</Grid>*/}

                <Grid item xs={4} className="pie-container">
                    <h2>Recent</h2>
                    <div className="">
                        <Line data={line_data} height={220} options={options}/>
                    </div>
                </Grid>
                <Grid item xs={3} className="recent-container">
                    <Grid container
                          direction="column"
                          justify="center"
                          alignItems="flex-start"
                          className="chip-container"
                    >
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span>
                                    <FontAwesomeIcon className="list-icon" icon={faStar}/>
                                    <span>{this.props.user_list[UserList.manga_mean_score].toFixed(2)}</span>
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span>
                                    <CountUp end={this.props.user_list[UserList.manga_mins_spent_reading]}/>
                                    total minutes
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span><CountUp
                                    end={this.props.user_list[UserList.manga_volumes_read]}/>
                                volumes
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<><span><CountUp
                                    end={this.props.user_list[UserList.manga_chapters_read]}/>
                                chapters
                                </span></>}
                            />
                        </div>
                        <div className="stat-chip">
                            <Chip
                                className="border-primary color-primary"
                                variant="outlined"
                                label={<>
                                    <span>
                                        Started Reading on <span><Moment format={'Do MMM YYYY'}
                                                                          date={this.props.user_list[UserList.manga_started_reading_on]}/></span>
                                    </span>
                                </>}
                            />
                        </div>
                        <div className="stat-chip">
                            <CyberLink to={USER_MANGA_HISTORY(this.props.visited_user[User.id])}>
                                <Button variant="outlined" className="btn-info">History</Button>
                            </CyberLink>
                        </div>
                    </Grid>
                </Grid>
                <Grid item xs={12} className="favorites-container">
                    <h2>Favorites</h2>

                    <GridList cellHeight={300} cols={5} style={{margin: 0}}>
                        {this.props.visited_user[User.profile][Profile.favorite_manga].map((manga, i) => (
                            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                                <GridListTile cols={1}>
                                    <TitleCard branch={BRANCH_MANGA} title={manga}/>
                                </GridListTile>
                            </Grow>
                        ))}
                    </GridList>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    visited_user: state.auth.visited_user,
    user_list: state.users.user_list,
});

export default connect(mapStateToProps, {})(MangaTab)
