import React, {Component} from 'react'
import PropTypes, {func} from 'prop-types'
import {connect} from 'react-redux'
import {Link} from "react-router-dom";

import {faLeaf, faSeedling, faSun} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {BRANCH_ANIME, FILTER_ANIME_INITIAL_VALUES} from "../../../globals";
import {getSeasonLabelFromSeasonNumber, isEmpty, isTimeLessThanAnHourAgo} from "../../functions";
import {getLoggedInUserAnimeEntryStatuses} from "../../../actions/users";
import {ANIME_SEASON_DISPLAY} from "../../../urls";
import {Anime, AnimeData, ApiResult} from "../../../models";
import TitleCard from "../../layout/cards/TitleCard";
import Loading from "../../layout/common/Loading";
import {filterAnime} from "../../../actions/anime";
import {CyberLink} from "../../layout/common/common";

import '../../../assets/css/home.css'
import '../../../assets/css/seasons.css'
import {faSnowflake} from "@fortawesome/free-solid-svg-icons/faSnowflake";
import Grid from "@material-ui/core/Grid";
import {TopAnimeTabs} from "./tabs/TopAnimeTabs";
import Divider from "@material-ui/core/Divider";


function horizontalScroll(e, container) {
    e.preventDefault();
    if (e.deltaY > 0) container.scrollLeft += 100;
    else container.scrollLeft -= 100;
}

function addHorizontalScrollEventListener() {
    const containers = document.getElementsByClassName('horizontal-scroll');
    for (let x = 0; x < containers.length; x++)
        containers[x].addEventListener('wheel', (e) => horizontalScroll(e, containers[x]));
}

function removeHorizontalScrollEventListener() {
    const containers = document.getElementsByClassName('horizontal-scroll');
    for (let x = 0; x < containers.length; x++)
        containers[x].removeEventListener('wheel', (e) => horizontalScroll(e, containers[x]));
}

function getIconAndClassFromSeasonNumber(season) {
    switch (season) {
        case 1: return {icon: faSeedling, className: 'spring'};
        case 2: return {icon: faSun, className: 'summer'};
        case 3: return {icon: faLeaf, className: 'fall'};
        case 4: return {icon: faSnowflake, className: 'winter'};
        default: return {icon: faSeedling, className: 'spring'};
    }
}

function CurrentSeason(props) {
    const {anime_data, anime_loading, anime_data_loaded, user_anime_entry_statuses} = props;
    const current_year = new Date().getFullYear();
    let current_month = new Date().getMonth() + 1;
    let current_season_number = 1;
    if (current_month >= 1 && current_month <= 3) current_season_number = 1;
    else if (current_month >= 4 && current_month <= 6) current_season_number = 2;
    else if (current_month >= 7 && current_month <= 9) current_season_number = 3;
    else if (current_month >= 10 && current_month <= 12) current_season_number = 4;
    const current_season = getSeasonLabelFromSeasonNumber(current_season_number);

    const season_icon_and_class = getIconAndClassFromSeasonNumber(current_season_number);
    return <Grid xs={12} id="Current-Season">
        <div className="current-season-header">
            <h3>
                Current Season{' '}
                <CyberLink to={ANIME_SEASON_DISPLAY(current_year, current_season_number)}>
                    {current_year} {current_season}
                </CyberLink>{' '}
                <FontAwesomeIcon icon={season_icon_and_class.icon}
                                 className={season_icon_and_class.className}/>
            </h3>
        </div>
        <div id="season-titles-container" className="current-season-titles-container horizontal-scroll">
            {anime_data_loaded && anime_data && anime_data[AnimeData.current_anime_season] ?
                <div className="current-season-titles-wrapper">
                    {anime_data[AnimeData.current_anime_season].map(anime => (
                        <TitleCard
                            containerClassNames="current-season-card-container"
                            branch={BRANCH_ANIME}
                            title={anime}
                            user_status={
                                !isEmpty(user_anime_entry_statuses) ?
                                    user_anime_entry_statuses[anime[Anime.id].toString()] : null}
                        />
                    ))}
                </div> : anime_loading ? <Loading text={'Season Loading'} block={true}/> : null}
        </div>
    </Grid>;
}

class TopAnime extends Component {
    constructor() {
        super(...arguments);
    }

    // componentDidMount() {
    //     addHorizontalScrollEventListener()
    // }
    //
    // componentWillUnmount() {
    //     removeHorizontalScrollEventListener()
    // }

    render() {
        return <>
            <CurrentSeason
                anime_data={this.props.anime_data}
                anime_loading={this.props.anime_loading}
                anime_data_loaded={this.props.anime_data_loaded}
                user_anime_entry_statuses={this.props.user_anime_entry_statuses}
            />
            <Grid item xs={12}>
                <Divider variant="middle" className="charts-divider"/>
            </Grid>
            <TopAnimeTabs
                anime_data={this.props.anime_data}
                anime_loading={this.props.anime_loading}
                anime_data_loaded={this.props.anime_data_loaded}
                user_anime_entry_statuses={this.props.user_anime_entry_statuses}
            />
        </>
    }
}

const mapStateToProps = state => ({
    anime_data: state.anime.anime_data,
    anime_loading: state.anime.anime_loading,
    anime_data_loaded: state.anime.anime_data_loaded,
    user_anime_entry_statuses: state.users.user_anime_entry_statuses
});

export default connect(mapStateToProps, {})(TopAnime)
