import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_ANIME, BRANCH_CHARACTERS} from "../../../../globals";
import {Anime, ApiResult, Character, Person} from "../../../../models";
import {getVoiceActorRolesForPerson} from "../../../../actions/people";
import TitleCard from "../../../layout/cards/TitleCard";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../../layout/sounds";
import GoToPageDialog from "../../../layout/pagination/goToPageDialog";

import '../../../../assets/css/people.css'
import {isEmpty} from "../../../functions";
import {getLoggedInUserAnimeEntryStatuses} from "../../../../actions/users";
import Loading from "../../../layout/common/Loading";

const ITEMS_PER_PAGE = 20;


class VoiceActorRolesTab extends Component {
    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.state = {
            current_page: 1,
            page_dialog_open: false,
        };
    }

    componentDidMount() {
        if (!this.props.person[Person.voice_actor_roles] || this.props.voice_actor_roles[ApiResult.previous])
            this.props.getVoiceActorRolesForPerson(this.props.person[Person.id]);
        if (isEmpty(this.props.user_anime_entry_statuses))
            this.props.getLoggedInUserAnimeEntryStatuses()
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.voice_actor_roles[ApiResult.count] / ITEMS_PER_PAGE)) {
            window.scrollTo(0, 0);
            this.setState({current_page: page});
            this.props.getVoiceActorRolesForPerson(this.props.person[Person.id], page)
        }
    }

    render() {
        return (
            <div className="person-voice-actor-roles-tab person-tab">
                {this.props.voice_actor_roles ? <>
                    <table>
                        <tbody>
                        {this.props.voice_actor_roles[ApiResult.results].map((character, i) => (
                            <tr key={i}>
                                <td>
                                    {character[Character.anime_roles].map((anime, x) => (
                                        <TitleCard
                                            key={x} branch={BRANCH_ANIME} title={anime}
                                            containerClassNames="character-role-wrapper-card-container"
                                            user_status={!isEmpty(this.props.user_anime_entry_statuses) ?
                                                this.props.user_anime_entry_statuses[`${anime[Anime.id]}`] : null}
                                        />
                                    ))}
                                </td>
                                <td>
                                    <TitleCard
                                        containerClassNames="character-role-wrapper-card-container"
                                        branch={BRANCH_CHARACTERS} title={character}/>
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                    {/*</GridList>*/}
                    <div className="pagination">
                        <Pagination
                            count={Math.ceil(this.props.voice_actor_roles[ApiResult.count] / ITEMS_PER_PAGE)}
                            color="primary"
                            siblingCount={3}
                            page={this.state.current_page}
                            onChange={(type, page) => {
                                playClickSound();
                                if (page !== this.state.current_page)
                                    this.goToPage(page);
                                else
                                    this.setState({page_dialog_open: true})
                            }}
                        />
                        <GoToPageDialog
                            open={this.state.page_dialog_open}
                            current_page={this.state.current_page}
                            total_pages={Math.ceil(this.props.voice_actor_roles[ApiResult.count] / ITEMS_PER_PAGE)}
                            goToPage={this.goToPage}
                            onClose={this.closePageDialog}/>
                    </div>
                </> : this.props.characters_loading ? <Loading text={'Characters loading'} block={true}/> : null}
            </div>
        )
    }
}

const mapStateToProps = state => ({
    person: state.people.person,
    voice_actor_roles: state.people.person[Person.voice_actor_roles],
    user_anime_entry_statuses: state.users.user_anime_entry_statuses,
    characters_loading: state.characters.characters_loading,
});

export default connect(mapStateToProps, {
    getVoiceActorRolesForPerson, getLoggedInUserAnimeEntryStatuses
})(VoiceActorRolesTab)
