import React, {Component} from 'react'
import PropTypes, {func} from 'prop-types'
import {connect} from 'react-redux'
import {Link} from "react-router-dom";

import {faLeaf, faSeedling, faSun} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {BRANCH_ANIME, FILTER_ANIME_INITIAL_VALUES} from "../../../globals";
import {getSeasonLabelFromSeasonNumber, isEmpty} from "../../functions";
import {getLoggedInUserAnimeEntryStatuses} from "../../../actions/users";
import {ANIME_SEASON_DISPLAY} from "../../../urls";
import {Anime, ApiResult} from "../../../models";
import TitleCard from "../../layout/cards/TitleCard";
import Loading from "../../layout/common/Loading";
import {filterAnime} from "../../../actions/anime";
import {CyberLink} from "../../layout/common/common";

import '../../../assets/css/anime.css'
import '../../../assets/css/seasons.css'
import {faSnowflake} from "@fortawesome/free-solid-svg-icons/faSnowflake";


function checkForKnownDate(known_date) {
    return known_date.indexOf('day') >= 0 && known_date.indexOf('month') >= 0 && known_date.indexOf('year');
}

function horizontalScroll(e, container) {
    e.preventDefault();
    if (e.deltaY > 0) container.scrollLeft += 100;
    else container.scrollLeft -= 100;
}

function addHorizontalScrollEventListener() {
    const container = document.getElementById('season-titles-container');
    container.addEventListener('wheel', (e) => horizontalScroll(e, container));
}

function removeHorizontalScrollEventListener() {
    const container = document.getElementById('season-titles-container');
    container.removeEventListener('wheel', (e) => horizontalScroll(e, container));
}

function getIconAndClassFromSeasonNumber(season) {
    switch (season) {
        case 1: return {icon: faSeedling, className: 'spring'};
        case 2: return {icon: faSun, className: 'summer'};
        case 3: return {icon: faLeaf, className: 'fall'};
        case 4: return {icon: faSnowflake, className: 'winter'};
        default: return {icon: faSeedling, className: 'spring'};
    }
}


class AnimeCurrentSeason extends Component {
    constructor() {
        super(...arguments);

        this.state = {
            current_year: new Date().getFullYear(),
            current_month: new Date().getMonth() + 1,
            current_season: '',
            current_season_number: 1
        }
    }

    componentDidMount() {
        let season;
        if (this.state.current_month >= 1 && this.state.current_month <= 3) season = 1;
        else if (this.state.current_month >= 4 && this.state.current_month <= 6) season = 2;
        else if (this.state.current_month >= 7 && this.state.current_month <= 9) season = 3;
        else if (this.state.current_month >= 10 && this.state.current_month <= 12) season = 4;

        this.setState({current_season: getSeasonLabelFromSeasonNumber(season), current_season_number: season});

        const season_filter = {year: this.state.current_year, season: season};
        let filters = new FILTER_ANIME_INITIAL_VALUES();
        filters = filters.VALUES;
        this.props.filterAnime(null, filters, season_filter, null, true, false);
        this.props.getLoggedInUserAnimeEntryStatuses();

        addHorizontalScrollEventListener()
    }

    componentWillUnmount() {
        removeHorizontalScrollEventListener()
    }

    render() {
        const season_icon_and_class = getIconAndClassFromSeasonNumber(this.state.current_season_number);
        return (
            <div id="Current-Season">
                <div className="current-season-header">
                    <h3>
                        Current Season{' '}
                        <CyberLink to={ANIME_SEASON_DISPLAY(this.state.current_year, this.state.current_season_number)}>
                            {this.state.current_year} {this.state.current_season}
                        </CyberLink>{' '}
                        <FontAwesomeIcon icon={season_icon_and_class.icon}
                                         className={season_icon_and_class.className}/>
                    </h3>
                </div>
                <div id="season-titles-container" className="current-season-titles-container">
                    {this.props.filtered_anime_loaded ?
                        <div className="current-season-titles-wrapper">
                            {this.props.filtered_anime[ApiResult.results].map(anime => (
                                checkForKnownDate(anime[Anime.air_start_known]) ? <TitleCard
                                    containerClassNames="current-season-card-container"
                                    branch={BRANCH_ANIME}
                                    title={anime}
                                    user_status={
                                        !isEmpty(this.props.user_anime_entry_statuses) ?
                                            this.props.user_anime_entry_statuses[anime[Anime.id].toString()] : null}
                                /> : null
                            ))}
                        </div> : this.props.anime_loading ? <Loading text={'Season Loading'} block={true}/> : null}
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    filtered_anime: state.anime.filtered_anime,
    filtered_anime_loaded: state.anime.filtered_anime_loaded,
    anime_loading: state.anime.anime_loading,
    user_anime_entry_statuses: state.users.user_anime_entry_statuses
});

export default connect(mapStateToProps, {filterAnime, getLoggedInUserAnimeEntryStatuses})(AnimeCurrentSeason)
