import React, {Component} from "react";
import {connect} from "react-redux";
// noinspection ES6CheckImport
import {BrowserRouter, Redirect, Route, Switch} from "react-router-dom";

import Home from "./home/Home";

import 'fontsource-roboto';
import {
    ANIME_BROWSE,
    ANIME_FRANCHISES,
    ANIME_HOME,
    ANIME_SEASONS,
    HOME,
    LOGIN,
    MANGA_BROWSE,
    MANGA_FRANCHISES,
    MANGA_HOME,
    MANGA_SEASONS,
    PROFILE,
    REGISTER,
    INDEX,
    NOT_FOUND,
    ANIME_DISPLAY,
    MANGA_DISPLAY,
    CHARACTERS_HOME,
    CHARACTERS_BROWSE,
    CHARACTERS_DISPLAY,
    EROGE_HOME,
    EROGE_BROWSE,
    EROGE_DISPLAY,
    ANIME_SEASON_DISPLAY,
    SETTINGS,
    MANGA_SEASON_DISPLAY,
    USER_ANIME_LIST,
    USER_MANGA_LIST,
    USER_EROGE_LIST,
    USER_ANIME_STATISTICS,
    USER_ANIME_HISTORY,
    USER_MANGA_HISTORY,
    ANIME_FRANCHISE_DISPLAY, PEOPLE_HOME, PEOPLE_BROWSE, PERSON_DISPLAY, USER_ACHIEVEMENTS, BBCODE_EDITOR
} from "../urls";

import store from '../store'
import {authUser} from "../actions/auth";
import {loadSounds} from "./layout/sounds";
import AnimeHome from "./anime/home/AnimeHome";
import Login from "./auth/Login";
import Register from "./auth/Register";
import AnimeBrowse from "./anime/browse/AnimeBrowse";
import Index from "./index/Index";
import Profile from "./user/profile/Profile";
import Settings from "./user/settings/Settings";
import MangaBrowse from "./manga/browse/MangaBrowse";
import AnimeDisplay from "./anime/display/AnimeDisplay";
import MangaDisplay from "./manga/display/MangaDisplay";
import CharactersBrowse from "./characters/browse/CharactersBrowse";
import CharacterDisplay from "./characters/display/CharacterDisplay";
import ErogeBrowse from "./eroge/browse/ErogeBrowse";
import ErogeDisplay from "./eroge/display/ErogeDisplay";
import BrowseAnimeSeasons from "./anime/seasons/BrowseAnimeSeasons";
import BrowseMangaSeasons from "./manga/seasons/BrowseMangaSeasons";
import NotFound from "./layout/404/404";
import Alerts from "./layout/alerts/Alerts";
import DisplaySeason from "./seasons/DisplaySeason";
import {BRANCH_ANIME, BRANCH_MANGA} from "../globals";
import AnimeList from "./user/user_list/anime/AnimeList";
import Statistics from "./user/statistics/Statistics";
import History from "./user/history/History";
import {LoadGifs} from "./layout/common/common";
import MangaList from "./user/user_list/manga/MangaList";
import DisplayFranchise from "./franchises/display/DisplayFranchise";
import Loading from "./layout/common/Loading";
import MangaHome from "./manga/home/MangaHome";
import CharactersHome from "./characters/home/CharactersHome";
import ErogeHome from "./eroge/home/ErogeHome";
import PeopleBrowse from "./people/browse/PeopleBrowse";
import PersonDisplay from "./people/display/PersonDisplay";
import BBCodeEditor from "./user/settings/bbcode_editor/BBCodeEditor";


// noinspection JSClassNamingConvention,JSCheckFunctionSignatures
class App extends Component {
    componentDidMount() {
        store.dispatch(authUser());
        loadSounds();
    }

    render() {
        if (this.props.initial_auth)
            return <Loading text={'Initializing...'}/>;
        else return (
            <BrowserRouter>
                <Alerts/>
                <LoadGifs/>
                <Switch>
                    <Route exact path={INDEX} component={Index}/>
                    <Route exact path={LOGIN} component={Login}/>
                    <Route exact path={REGISTER} component={Register}/>
                    {!this.props.user_authenticated ? <Redirect to={LOGIN}/> : null}
                    <Route exact path={HOME} component={Home}/>

                    <Route exact path={SETTINGS} component={Settings}/>
                    <Route exact path={PROFILE()} component={Profile}/>
                    <Route exact path={USER_ANIME_LIST()} component={AnimeList}/>
                    <Route exact path={USER_MANGA_LIST()} component={MangaList}/>
                    <Route exact path={USER_EROGE_LIST()} component={NotFound}/>
                    <Route exact path={USER_ANIME_STATISTICS()} component={Statistics}/>
                    <Route path={USER_ANIME_HISTORY()}
                           render={(props) => <History {...props} branch={BRANCH_ANIME}/>}/>
                    <Route path={USER_MANGA_HISTORY()}
                           render={(props) => <History {...props} branch={BRANCH_MANGA}/>}/>

                    <Route exact path={BBCODE_EDITOR} component={BBCodeEditor}/>

                    <Route exact path={ANIME_HOME} component={AnimeHome}/>
                    <Route exact path={ANIME_BROWSE} component={AnimeBrowse}/>
                    <Route exact path={ANIME_SEASONS} component={BrowseAnimeSeasons}/>
                    <Route exact path={ANIME_SEASON_DISPLAY()}
                           render={(props) => <DisplaySeason {...props} branch={BRANCH_ANIME}/>}/>
                    <Route exact path={ANIME_FRANCHISES} component={NotFound}/>
                    <Route exact path={ANIME_FRANCHISE_DISPLAY()} component={DisplayFranchise}/>
                    <Route exact path={ANIME_DISPLAY()} component={AnimeDisplay}/>

                    <Route exact path={MANGA_HOME} component={MangaHome}/>
                    <Route exact path={MANGA_BROWSE} component={MangaBrowse}/>
                    <Route exact path={MANGA_SEASONS} component={BrowseMangaSeasons}/>
                    <Route exact path={MANGA_SEASON_DISPLAY()}
                           render={(props) => <DisplaySeason {...props} branch={BRANCH_MANGA}/>}/>
                    <Route exact path={MANGA_FRANCHISES} component={NotFound}/>
                    <Route exact path={MANGA_DISPLAY()} component={MangaDisplay}/>

                    <Route exact path={CHARACTERS_HOME} component={CharactersHome}/>
                    <Route exact path={CHARACTERS_BROWSE} component={CharactersBrowse}/>
                    <Route exact path={CHARACTERS_DISPLAY()} component={CharacterDisplay}/>

                    {/*<Route exact path={PEOPLE_HOME} component={CharactersHome}/>*/}
                    <Route exact path={PEOPLE_BROWSE} component={PeopleBrowse}/>
                    <Route exact path={PERSON_DISPLAY()} component={PersonDisplay}/>

                    <Route exact path={EROGE_HOME} component={ErogeHome}/>
                    <Route exact path={EROGE_BROWSE} component={ErogeBrowse}/>
                    <Route exact path={EROGE_DISPLAY()} component={ErogeDisplay}/>

                    <Route exact path={NOT_FOUND} component={NotFound}/>
                </Switch>
            </BrowserRouter>
        );
    }
}

const mapStateToProps = state => ({
    message: state.errors.message,
    status: state.errors.status,
    initial_auth: state.auth.initial_auth,
    user_authenticated: state.auth.user_authenticated,
});

export default connect(mapStateToProps, {})(App)
