import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";
// noinspection ES6CheckImport
import {Redirect} from "react-router-dom";

import Grid from '@material-ui/core/Grid';
import {Button} from "@material-ui/core";
import {faEdit, faMinusSquare, faStar} from "@fortawesome/free-regular-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlay} from "@fortawesome/free-solid-svg-icons/faPlay";

import {ANIME_TAB_NAME, CYBER_GENESIS_TAB_NAME, CYBER_GENESIS_URL} from "../../globals";
import {NOT_FOUND} from "../../urls";
import {Anime, AnimeOnServer, CDNCoverImages, CoverImages, User, UserAnimeEntry} from "../../models";
import {playClickSound, playHoverSound} from "../layout/sounds";
import {formatContentDateByKnown, getTitleCoverImage, isEmpty} from "../functions";
import {getAnime} from '../../actions/anime'
import {CopyTextToClipboard} from "./snippets";
import {CyberButton} from "../layout/common/common";
import {DisplayTabs} from "./TabsRoot";
import CyberNavbar from "../layout/navbar/CyberNavbar";
import {PreLoadImage} from "../layout/common/common";
import Loading from "../layout/common/Loading";
import {
    AdminModalForms,
    formatTitle,
    getTitle,
    getUserModalForms,
    getUserTitleStatus,
    getUserTitleStatusSummary, UserModalForms
} from "./functions";
import CyberFooter from "../layout/footer/CyberFooter";

import '../../assets/css/display.css'
import {faDownload, faPlus, faPlusSquare, faStar as faStarSolid} from "@fortawesome/free-solid-svg-icons";
import AnimeEntryModalForm from "../user/forms/anime/AnimeEntryModalForm";
import {getUserAnimeEntry} from "../../actions/users";


export const NONE = <span className="color-gray">---</span>;


class Display extends Component {
    static propTypes = {
        /** Branch of component. (Anime, Manga, Eroge..) */
        branch: PropTypes.string.isRequired,
        /** ID of the title */
        param_id: PropTypes.number.isRequired,
        /** Redux prop of titles_loading */
        title_loading: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        title_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        title: PropTypes.object.isRequired,
        /** Redux prop of user_title_status */
        user_title_status: PropTypes.object,
    };

    componentDidMount() {
        window.scrollTo(0, 0);
        getTitle(this.props.branch, this.props.param_id);
        if (this.props.user_authenticated)
            getUserTitleStatus(this.props.branch, this.props.param_id)
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.user_authenticated !== this.props.user_authenticated)
            getUserTitleStatus(this.props.branch, this.props.param_id);
        if (prevProps.param_id !== this.props.param_id) {
            window.scrollTo(0, 0);
            getTitle(this.props.branch, this.props.param_id);
            getUserTitleStatus(this.props.branch, this.props.param_id)
        }
    }

    render() {
        const superuser = this.props.user && this.props.user[User.is_superuser];
        const display_title = this.props.title_loaded ?
            formatTitle(this.props.branch, this.props.title, superuser) : formatTitle();
        const img_url = getTitleCoverImage(display_title.cdn_cover_images, display_title.cover_images);
        const tabs_margin = !!(display_title.primary_set || display_title.primary_set_wide ||
            display_title.secondary_set);
        document.title = display_title.title ? display_title.title : CYBER_GENESIS_TAB_NAME;

        if (this.props.status && this.props.status === 404)
            return <Redirect to={NOT_FOUND}/>;
        return (
            <>
                <CyberNavbar/>
                <div id="Display">
                    {this.props.title_loaded ? <Grid container style={{marginBottom: 100}}>
                        <Grid item xs={12} className="title-container">
                            <h1><CopyTextToClipboard text={display_title.title}/></h1>
                            {superuser ? <AdminModalForms
                                id={this.props.param_id}
                                branch={this.props.branch}
                                title={this.props.title}/> : null}
                        </Grid>
                        <Grid item xs={3}>
                            <div className="details-container">
                                <div className="img-container">
                                    <PreLoadImage src={img_url} alt={`${this.props.branch} cover image`}/>
                                </div>
                                {this.props.user_authenticated ? <div className="user-buttons-container">
                                    <UserModalForms branch={this.props.branch} id={this.props.param_id} title={display_title.title} entry={this.props.user_title_status}/>
                                    </div> : null}
                                <div className="info-container box-light">
                                    <h2>Alternative Titles</h2>
                                    <table>
                                        <tbody>
                                        {display_title.title_1 ? <tr>
                                            <td>{display_title.title_1.label}</td>
                                            <td>{display_title.title_1.title ?
                                                <CopyTextToClipboard text={display_title.title_1.title}/> : NONE}</td>
                                        </tr> : null}
                                        {display_title.title_2 ? <tr>
                                            <td>{display_title.title_2.label}</td>
                                            <td>{display_title.title_2.title ?
                                                <CopyTextToClipboard text={display_title.title_2.title}/> : NONE}</td>
                                        </tr> : null}
                                        {display_title.title_3 ? <tr>
                                            <td>{display_title.title_3.label}</td>
                                            <td>{display_title.title_3.title ?
                                                <CopyTextToClipboard text={display_title.title_3.title}/> : NONE}</td>
                                        </tr> : null}
                                        </tbody>
                                    </table>
                                    <h2>Information</h2>
                                    <h4>{this.props.branch} Information</h4>
                                    {display_title.info_set ? <table>
                                        <tbody>
                                        {display_title.info_set.map(item => (
                                            <tr>
                                                <td>{item.label}</td>
                                                <td>{item.info ? item.info : NONE}</td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table> : null}
                                    <h4>Database Information</h4>
                                    {display_title.db_info_set ? <table>
                                        <tbody>
                                        {display_title.db_info_set.map(item => (
                                            <tr>
                                                <td>{item.label}</td>
                                                <td>{item.info ? item.info : NONE}</td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table> : null}
                                    <h4>Users Information</h4>
                                    {display_title.users_info_set ? <table>
                                        <tbody>
                                        {display_title.users_info_set.map(item => (
                                            <tr>
                                                <td>{item.label}</td>
                                                <td>{item.info ? item.info : NONE}</td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table> : null}
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={9} className="main-container">
                            {this.props.user_authenticated && !isEmpty(this.props.user_title_status) ?
                                getUserTitleStatusSummary(this.props.branch, this.props.user_title_status) : null}
                            {display_title.primary_set ? <Grid container className="summery-container box-light">
                                {display_title.primary_set.map(item => (
                                    <Grid item xs={2}>
                                        <p>{item.label}</p>
                                        <p>{item.info ? item.info : NONE}</p>
                                    </Grid>
                                ))}
                                {display_title.primary_set_wide ? display_title.primary_set_wide.map(item => (
                                    <Grid item xs={12} className="genres-container">
                                        <p>{item.label}</p>
                                        <p>{item.info ? item.info : NONE}</p>
                                    </Grid>
                                )) : null}
                            </Grid> : null}
                            {display_title.secondary_set ?
                                <Grid container className="summery-container box-light color-secondary">
                                    {display_title.secondary_set.map(item => (
                                        <Grid item xs={Math.floor(12 / display_title.secondary_set.length)}>
                                            <p>{item.label}</p>
                                            <p>{item.info ? item.info : NONE}</p>
                                        </Grid>
                                    ))}
                                </Grid> : null}

                            {display_title.tabs ? <Grid container className="tabs-container"
                                                        style={{marginTop: tabs_margin ? 15 : 0}}>
                                <DisplayTabs tabs={display_title.tabs}/>
                            </Grid> : null}
                        </Grid>
                    </Grid> : this.props.title_loading ?
                        <Loading text={`${this.props.branch} Loading`} offset={100}/> : null}
                </div>
                <CyberFooter/>
            </>
        )
    }
}

const mapStateToProps = state => ({
    status: state.errors.status,
    user_authenticated: state.auth.user_authenticated,

    /**
     * @user: state.auth.user is here for checking if the logged in user is admin and to update the component
     * when adding/removing title from favorites etc.
     * @torrent_progress: is here for re-rendering the component on change
     */
    user: state.auth.user,
    // torrent_progress: state.requiem.torrent_progress
});

export default connect(mapStateToProps, {getAnime})(Display)
