import {
    SET_ANIME_FILTERS, SET_CHARACTERS_FILTERS,
    SET_COOKIE_SHOW_FILTERS, SET_EROGE_FILTERS,
    SET_MANGA_FILTERS, SET_PEOPLE_FILTERS
} from "./types";


// SET COOKIE SHOW FILTERS
export const setCookieShowFilters = value => dispatch => {
    dispatch({
        type: SET_COOKIE_SHOW_FILTERS,
        value: value
    })
};


// SET COOKIE ANIME FILTERS
export const setCookieAnimeFilters = (value, type) => dispatch => {
    dispatch({
        type: SET_ANIME_FILTERS,
        value: value,
        page_type: type
    })
};


// SET COOKIE MANGA FILTERS
export const setCookieMangaFilters = (value, type) => dispatch => {
    dispatch({
        type: SET_MANGA_FILTERS,
        value: value,
        page_type: type
    })
};


// SET COOKIE CHARACTERS FILTERS
export const setCookieCharactersFilters = value => dispatch => {
    dispatch({
        type: SET_CHARACTERS_FILTERS,
        value: value
    })
};


// SET COOKIE CHARACTERS FILTERS
export const setCookiePeopleFilters = value => dispatch => {
    dispatch({
        type: SET_PEOPLE_FILTERS,
        value: value
    })
};


// SET COOKIE EROGE FILTERS
export const setCookieErogeFilters = (value, type) => dispatch => {
    dispatch({
        type: SET_EROGE_FILTERS,
        value: value,
        page_type: type
    })
};