import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'

import Moment from 'react-moment'
import moment from 'moment'
import {Grid} from "@material-ui/core";

import {getVisitedUserAnimeHistory, getVisitedUserMangaHistory} from '../../../actions/users'
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";

import '../../../assets/css/users.css'
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../globals";
import TitleCard from "../../layout/cards/TitleCard";
import Loading from "../../layout/common/Loading";
import Chip from "@material-ui/core/Chip";
import Pagination from "@material-ui/lab/Pagination";
import {ApiResult, Profile, User} from "../../../models";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";
import {filterTitles, getTabName} from "../../browse/functions";
import {getBrowseGridListCols} from "../../functions";
import {getUser} from "../../../actions/auth";
import DisplayUserHistory from "./DisplayUserHistory";
import UserList from "../user_list/UserList";
import {PrivatePage} from "../user_list/PrivateList";


function isHistoryPrivate(branch, visited_user) {
    switch (branch) {
        case BRANCH_ANIME:
            return visited_user[User.profile][Profile.is_anime_history_private];
        case BRANCH_MANGA:
            return visited_user[User.profile][Profile.is_manga_history_private];
        default:
            return true
    }
}


class History extends Component {
    static propTypes = {
        /** Branch of component. (Anime, Manga, Eroge..) */
        branch: PropTypes.string.isRequired,
    };

    constructor() {
        super(...arguments);
        this.state = {};
    }

    componentDidMount() {
        document.title = `History`;
        window.scrollTo(0, 0);

        this.props.getUser(this.props.match.params.id);
    }

    render() {
        let list_owner = false;
        if (this.props.visited_user && this.props.user && this.props.visited_user[User.id] === this.props.user[User.id])
            list_owner = true;

        console.log('this.props.visited_user', this.props.visited_user)
        console.log('this.props.user', this.props.user)
        // console.log('this.props.visited_user[User.id]', this.props.visited_user[User.id])
        // console.log('this.props.user[User.id]', this.props.user[User.id])
        console.log('list_owner', list_owner)

        return this.props.user_loading ? <Loading/> : this.props.visited_user_loaded ?
            list_owner || !isHistoryPrivate(this.props.branch, this.props.visited_user) ?
                <DisplayUserHistory branch={this.props.branch} visited_user_id={this.props.match.params.id}/> :
                <PrivatePage text={`${this.props.visited_user[User.username]}'s ${this.props.branch} History is private.`}/> : null
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    user_loading: state.auth.user_loading,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    user_anime_history: state.users.user_anime_history,
    user_manga_history: state.users.user_manga_history,
    user_info_loading: state.users.user_info_loading,
});

export default connect(mapStateToProps, {getUser})(History)