function compareByTotal(a, b) {
    // Compares the two values of stat and returns 1 or -1
    const first = b['total_anime'];
    const second = a['total_anime'];

    let comparison = 0;
    if (first > second) {
        comparison = 1;
    } else if (first < second) {
        comparison = -1;
    }
    return comparison;
}


function compareByMean(a, b) {
    // Compares the two values of stat and returns 1 or -1
    const first = b['mean_score'];
    const second = a['mean_score'];

    let comparison = 0;
    if (first > second) {
        comparison = 1;
    } else if (first < second) {
        comparison = -1;
    }
    return comparison;
}


export function getDataFavoriteGenres(statistics, order) {
    let parsed_data = statistics['anime_favorite_genres'];
    let parsed_data_keys = Object.keys(parsed_data);
    let stat_data = [];
    let stat_mean_score = [];
    let labels = [];
    let data = [];

    for (let x = 0; x < parsed_data_keys.length; x++) {
        data.push({
            'label': parsed_data_keys[x],
            'total_anime': parsed_data[parsed_data_keys[x]]['total_anime'],
            'mean_score': (parsed_data[parsed_data_keys[x]]['total_score'] /
                parsed_data[parsed_data_keys[x]]['total_scored']).toFixed(2),
        })
    }

    if (order === 'Mean')
        data.sort(compareByMean);
    else
        data.sort(compareByTotal);

    for (let x = 0; x < data.length; x++) {
        labels.push(data[x]['label']);
        stat_data.push(data[x]['total_anime']);
        stat_mean_score.push(data[x]['mean_score']);
    }

    return [
        {
            labels: labels,
            datasets: [
                {
                    type: 'line',
                    label: 'Mean Score',
                    data: stat_mean_score,
                    fill: false,
                    backgroundColor: 'rgba(235,53,45,0.2)',
                    borderColor: 'rgb(235,53,45)',
                    hoverBackgroundColor: 'rgba(235,53,45,0.4)',
                    hoverBorderColor: 'rgba(235,53,45,1)',
                    pointHoverBackgroundColor: '#ff0000',
                    pointHoverBorderColor: '#ff0000',
                    yAxisID: 'y-axis-2'
                },
                {
                    label: '# of Anime',
                    data: stat_data,
                    fill: false,
                    backgroundColor: 'rgba(54, 162, 235,0.2)',
                    borderColor: 'rgba(54, 162, 235,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(54, 162, 235,0.4)',
                    hoverBorderColor: 'rgba(54, 162, 235,1)',
                    yAxisID: 'y-axis-1'
                }
            ]
        },
        {
            maintainAspectRatio: false,
            responsive: true,
            tooltips: {mode: 'label'},
            elements: {line: {fill: false}},
            scales: {
                yAxes: [
                    {
                        type: 'linear',
                        display: true,
                        position: 'left',
                        id: 'y-axis-1',
                        gridLines: {display: false},
                        labels: {show: true}
                    },
                    {
                        type: 'linear',
                        display: true,
                        position: 'right',
                        id: 'y-axis-2',
                        gridLines: {display: false},
                        labels: {show: true},
                        ticks: {min: 0, max: 10, stepSize: 1}
                    }
                ]
            }
        }
    ];
}


