import React, {Component} from 'react'
import {connect} from 'react-redux'

import {BRANCH_MANGA} from "../../../globals";
import BrowseSeasons from "../../seasons/BrowseSeasons";


class BrowseMangaSeasons extends Component {
    render() {
        return <BrowseSeasons
            branch={BRANCH_MANGA}
            data_loading={this.props.manga_loading}
            data_loaded={this.props.manga_data_loaded}
            data={this.props.manga_data}
        />
    }
}

const mapStateToProps = state => ({
    manga_data: state.manga.manga_data,
    manga_data_loaded: state.manga.manga_data_loaded,
    manga_loading: state.manga.manga_loading,
});

export default connect(mapStateToProps, {})(BrowseMangaSeasons)
